import React, { ReactNode } from 'react';

import { uniqueId } from 'shared/utils/javascript';
import DisplayField, { DisplayFieldProps } from 'shared/components/DisplayField';

import { Row, Label } from './styles';

interface RenderProps {
  required: boolean;
  name: string;
  id: string;
}

interface FormRowProps {
  name: string;
  label: string;
  required?: boolean;
  isEditing: boolean;
  children: (props: RenderProps) => ReactNode;
}

const FormRow: React.FC<FormRowProps & DisplayFieldProps> = ({
  name,
  label,
  required = false,
  isEditing,
  children,
  ...props
}) => {
  const fieldId = uniqueId(`field-${name}`);

  return (
    <Row data-cy-form-row-label={label}>
      {isEditing ? (
        <>
          <Label as="label" htmlFor={fieldId}>
            {label}
            {required && ' *'}
          </Label>
          {children({ required, name, id: fieldId })}
        </>
      ) : (
        <>
          <Label as="div">{label}</Label>
          <DisplayField name={name} {...props} />
        </>
      )}
    </Row>
  );
};

export default FormRow;
