import React, { ReactNode, ReactElement } from 'react';
import gql from 'graphql-tag';
import { useTranslation } from 'react-i18next';

import { getContactNameWithTitle } from 'shared/utils/getContactName';

import { Wrapper, StyledAvatar, Name, JobTitle, TextWrapper } from './Styles';
import { ContactInfo_Contact } from './__graphql__/ContactInfo_Contact';

export const fragmentContactInfo = gql`
  fragment ContactInfo_Contact on Contact {
    id
    isPerson
    email
    firstName
    lastName
    title
    jobTitle
  }
`;

export interface ContactInfoProps {
  className?: string;
  action: ReactNode;
  contact: ContactInfo_Contact;
  centerAvatar?: boolean;
}

const ContactInfo = ({
  className,
  action,
  contact,
  centerAvatar = false,
}: ContactInfoProps): ReactElement => {
  const { t } = useTranslation();
  const name = getContactNameWithTitle(contact) || t('subcontractor.unknownContact');

  return (
    <Wrapper className={className}>
      {action}
      {!action && <StyledAvatar name={name} size={24} isCentered={centerAvatar} />}
      <TextWrapper isAlignedWithAvatar={!action && !centerAvatar}>
        <Name>{name}</Name>
        {contact.jobTitle && <JobTitle>{contact.jobTitle}</JobTitle>}
      </TextWrapper>
    </Wrapper>
  );
};

export default ContactInfo;
