import React from 'react';
import { useQuery } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import { Table, Cell, PageLoader, QueryErrors, UsersTableDropdown } from 'shared/components';
import { TableSearch } from 'shared/components/Styles';
import { UserStatusTag } from 'shared/components/TagStyles';
import { nullString, TABLE_ITEMS_PER_PAGE } from 'shared/constants';
import { isDoneLoading } from 'shared/utils/apollo';
import { queryCurrentUser } from 'shared/utils/queries';
import { CurrentUser as CurrentUserData } from 'shared/utils/__graphql__/CurrentUser';

import { queryAdminUsers } from './api';
import { AdminUsers as AdminUsersData } from './__graphql__/AdminUsers';

const formatUserName = (firstName: string | null, lastName: string | null) =>
  `${firstName || nullString} ${lastName || ''}`;

const AdminUsers = () => {
  const { t } = useTranslation('admin');

  // For components that have multiple useQuery hooks, we have to use the
  // no-cache fetch policy, otherwise errors are not handled correctly.
  // We do not know the reason for this issue.

  const adminUsersRequest = useQuery<AdminUsersData>(queryAdminUsers, {
    variables: { offset: 0, limit: TABLE_ITEMS_PER_PAGE },
    fetchPolicy: 'no-cache',
  });

  const currentUserRequest = useQuery<CurrentUserData>(queryCurrentUser, {
    fetchPolicy: 'no-cache',
  });

  for (const error of [adminUsersRequest.error, currentUserRequest.error]) {
    if (error) return <QueryErrors error={error} />;
  }

  if (!isDoneLoading(adminUsersRequest) || !isDoneLoading(currentUserRequest)) {
    return <PageLoader />;
  }

  const { data, refetch, variables } = adminUsersRequest;

  return (
    <>
      <TableSearch onChange={(search: string) => refetch({ query: search })} />
      <Table
        columns={[
          { label: t('user.name'), sortField: 'name' },
          { label: t('user.companyName'), sortField: 'company' },
          { label: t('user.email') },
          { label: t('user.type') },
          { label: t('user.status') },
          { fixedWidth: 50 },
        ]}
        rows={data.adminUsers.users.map(user => (
          <Table.Row rowId={user.id}>
            <Cell.Text>{formatUserName(user.firstName, user.lastName)}</Cell.Text>
            <Cell.Text>{user.company.name}</Cell.Text>
            <Cell.Text>{user.email}</Cell.Text>
            <Cell.Text>{t(`user.${user.type}`)}</Cell.Text>
            <UserStatusTag inList status={user.status}>
              {t(`userStatus.${user.status}`)}
            </UserStatusTag>
            <UsersTableDropdown
              user={user}
              isAdmin
              isCurrentUser={user.id === currentUserRequest.data.me.id}
            />
          </Table.Row>
        ))}
        totalRecords={data.adminUsers.totalRecords}
        refetch={refetch}
        variables={variables}
      />
    </>
  );
};

export default AdminUsers;
