import React from 'react';

import { Avatar, Icon } from 'shared/components';

import {
  Name,
  Role,
  ContactInfoItem,
  ContactInfoItems,
  Anchor,
  ProjectContacts,
  ProjectContact,
  ContactInfo,
} from './Contacts.styles';
import { BidRequestForSubcontractor_bidRequestForSubcontractor_bidPackage_project_projectUsers } from './__graphql__/BidRequestForSubcontractor';

interface ContactsInput {
  contacts: BidRequestForSubcontractor_bidRequestForSubcontractor_bidPackage_project_projectUsers[];
}

export const Contacts = ({ contacts }: ContactsInput) => (
  <ProjectContacts>
    {contacts.map(({ user: { firstName, lastName, email, phone, fax }, userRole: role }, i) => (
      <ProjectContact key={i}>
        <Avatar name={`${firstName} ${lastName}`} size={36} />
        <ContactInfo>
          <Name>{`${firstName} ${lastName}`}</Name>
          <Role>{role}</Role>
          <ContactInfoItems>
            <ContactInfoItem>
              <Icon type="email" /> <Anchor to={`mailto:${email}`}>{email}</Anchor>
            </ContactInfoItem>
            {phone && (
              <ContactInfoItem>
                <Icon type="phone" /> <Anchor to={`tel:${phone}`}>{phone}</Anchor>
              </ContactInfoItem>
            )}
            {fax && (
              <ContactInfoItem>
                <Icon type="printer" /> <Anchor to={`tel:${fax}`}>{fax}</Anchor>
              </ContactInfoItem>
            )}
          </ContactInfoItems>
        </ContactInfo>
      </ProjectContact>
    ))}
  </ProjectContacts>
);
