import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import { Form, Field, Button } from 'shared/components';
import toast from 'shared/utils/toast';

import { mutationUpdateCompany } from './api';
import { Company_company } from './__graphql__/Company';
import { UpdateCompany, UpdateCompanyVariables } from './__graphql__/UpdateCompany';

interface Props {
  company: Company_company;
}

interface FormValues {
  name: string;
}

const CompanyProfile: React.FC<Props> = ({ company }) => {
  const { t } = useTranslation('company');
  const [updateCompany, { loading: mutating }] = useMutation<UpdateCompany, UpdateCompanyVariables>(
    mutationUpdateCompany,
    {
      refetchQueries: () => ['Company'],
      awaitRefetchQueries: true,
    },
  );

  return (
    <Form<FormValues>
      initialValues={{
        name: company.name,
      }}
      validations={{
        name: [Form.is.required(), Form.is.maxLength(200)],
      }}
      onSubmit={async values => {
        try {
          await updateCompany({ variables: { id: company.id, input: values } });
          toast.success(t('updated'));
        } catch (error) {
          toast.error(error);
        }
      }}
    >
      <Form.Element style={{ maxWidth: 350 }}>
        <Field.Input name="name" label={t('company.name')} />
        <Button type="submit" working={mutating}>
          {t('save')}
        </Button>
      </Form.Element>
    </Form>
  );
};

export default CompanyProfile;
