import { useState } from 'react';

import useStack from 'shared/hooks/useStack';

import Folder from '../classes/Folder';
import { DocumentsTable_documents } from '../__graphql__/DocumentsTable_documents';

interface Args {
  rootFolder: Folder;
  documents: DocumentsTable_documents[];
}

export interface MobileNavigation {
  getIsOpen: (value: string) => boolean;
  toggleFolder: (value: string) => void;
  getIsCurrentlySelected: (folderId: string) => boolean;
  currentlyVisibleRows: number;
  setCurrentlyVisibleRows: (currentVisibleRows: number) => void;
  rootId: string;
}

const useMobileNavigation = ({ rootFolder, documents }: Args): MobileNavigation => {
  const rootId = 'root';
  const { peek, toggle, contains } = useStack(rootId);
  const [currentlyVisibleRows, setCurrentlyVisibleRows] = useState<number>(() => {
    const rootFiles = rootFolder.getFilesWithDocs(documents);
    const rootFolders = rootFolder.getChildFolders();

    return rootFiles.length + rootFolders.length;
  });

  const getIsCurrentlySelected = (folderId: string) => peek() === folderId;

  return {
    getIsOpen: contains,
    toggleFolder: toggle,
    getIsCurrentlySelected,
    currentlyVisibleRows,
    setCurrentlyVisibleRows,
    rootId,
  };
};

export default useMobileNavigation;
