import React from 'react';
import styled, { css } from 'styled-components/macro';

import Icon from 'shared/components/Icon';
import DropdownMenu from 'shared/components/DropdownMenu';
import Button from 'shared/components/Button';
import { COLOR, font, mixin, zIndexValues, mediaQueries } from 'shared/utils/styles';

const rowHeightMobile = 60;
const rowHeightDesktop = 43;
const iconWidth = 28;
const iconHorizontalMargin = 20;
const itemActionsTriggerSize = 24;
export const transitionTime = 200;

export const DocumentsTableContainer = styled.div`
  position: relative;
`;

interface StyledProgressProps {
  paddingTop: number;
}

export const StyledProgress = styled.div<StyledProgressProps>`
  z-index: ${zIndexValues.documentsTableProgress};
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding: ${props => props.paddingTop}px 40px 0;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.85);
`;

interface ProgressLabelProps {
  fontSize?: number;
}

export const ProgressLabel = styled.div<ProgressLabelProps>`
  padding-bottom: 6px;
  word-wrap: break-word;
  ${font.medium};
  ${props => font.size(props.fontSize || 15)};
`;

interface ProgressBarProps {
  percentComplete: number;
}

export const ProgressBar = styled.div<ProgressBarProps>`
  display: inline-block;
  margin-bottom: 6px;
  width: 25%;
  height: 20px;
  border-radius: 4px;
  box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.1);
  background: linear-gradient(
    to right,
    ${COLOR.primary} 0%,
    ${COLOR.primary} ${props => props.percentComplete || 0}%,
    ${COLOR.backgroundLight} ${props => props.percentComplete || 0}%,
    ${COLOR.backgroundLight} 100%
  );
`;

export const StyledTopControls = styled.div`
  position: relative;
  display: flex;
  justify-content: space-between;
  margin: 0 -5px;
  padding-bottom: 10px;
`;

export const TopControlsButton = styled(({ isLink, ...props }) =>
  isLink ? <Button as="a" {...props} /> : <Button {...props} />,
)`
  margin: 0 5px 10px 5px;
  height: 28px;
  line-height: 26px;
  padding: 0 10px;
  ${font.size(12)}
  i {
    margin-right: 2px;
  }
`;

export const TopControlsDropdownTrigger = styled(Button)`
  height: 28px;
  width: 28px;
  margin: 0 5px 10px 5px;
  padding: 0;
  line-height: 28px;

  i {
    margin-right: 0;
  }
`;

export const StyledDocumentsTable = styled.div`
  position: relative;
  border-radius: 4px;
  border: 1px solid ${COLOR.borderLight};
  background: #fff;

  /* Don't let content's square corners go over our rounded corners */
  overflow: hidden;
`;

interface MobileContentContainerProps {
  currentlyVisibleRows: number;
}

export const MobileContentContainer = styled.div<MobileContentContainerProps>`
  height: ${props => props.currentlyVisibleRows * rowHeightMobile}px;
  overflow: hidden;
  transition: height ${transitionTime}ms;

  /* Capture MobileRowsContainer */
  position: relative;
`;

export const NoResultsReadOnly = styled.div`
  padding: 50px 0;
  text-align: center;
  color: ${COLOR.textMedium};
  ${font.size(17)}
  i {
    margin-right: 13px;
    font-size: 20px;
  }
`;

export const NoResultsUploadable = styled.div`
  padding: 50px 0;
  text-align: center;
  color: ${COLOR.textMedium};
  background: ${COLOR.backgroundLight};
  ${font.size(16)}
  &:focus {
    outline: none;
  }
  i {
    margin-right: 15px;
    font-size: 20px;
  }
`;

export const RootDropzoneArea = styled.div`
  padding: 20px 0;
  text-align: center;
  color: ${COLOR.textMedium};
  background: ${COLOR.backgroundLight};
  ${font.size(15)}
  &:focus {
    outline: none;
  }
  i {
    margin-right: 10px;
  }
`;

export const StyledHeaders = styled.div`
  display: flex;
  border-bottom: 1px solid ${COLOR.borderLight};
`;

interface HeaderProps {
  percentWidth: number;
}

export const Header = styled.div<HeaderProps>`
  padding: 14px 12px 14px 17px;
  line-height: 1.2;
  color: ${COLOR.textMedium};
  width: ${props => props.percentWidth}%;
  ${mixin.truncateText}
  ${font.size(13)};
`;

export const IconContainer = styled.div`
  position: relative;
  padding-left: ${iconWidth}px;
  ${mixin.truncateText}
`;

interface StyledIconProps {
  nestingLevel: number;
}

export const StyledIcon = styled(Icon)<StyledIconProps>`
  @media ${mediaQueries.mobile} {
    padding-left: 17px;
  }

  @media ${mediaQueries.desktop} {
    transform: translateX(${props => props.nestingLevel * 17}px);
  }
`;

export const DropArea = styled.div`
  &:focus {
    outline: none;
  }
`;

interface StyledDragHighlightZoneProps {
  isHighlighted: boolean;
}

export const StyledDragHighlightZone = styled.div<StyledDragHighlightZoneProps>`
  ${props => (props.isHighlighted ? highlightStyles : '')};
`;

const highlightStyles = `
  background: ${mixin.rgba(COLOR.primary, 0.08)};
  & > div {
    background: none;
  }
`;

export interface RowProps {
  index?: number;
  isAtTopOfList?: boolean;
  isFolderAndOpen?: boolean;
  isShown?: boolean;
  isAboveOthers?: boolean;
}

export const Row = styled.div<RowProps>`
  position: relative;
  width: 100%;
  border-top: 1px solid ${COLOR.borderLight};
  color: ${COLOR.textDark};
  background-color: white;
  ${mixin.clickable}
  ${font.size(13)}

  ${StyledHeaders} + & {
    border-top: 0;
  }

  @media ${mediaQueries.mobile} {
    position: absolute;
    top: 0;
    left: 0;
    height: ${rowHeightMobile}px;
    opacity: 0;
    transform: translateY(${props => (props.index || 0) * rowHeightMobile}px);
    transition: transform ${transitionTime}ms, opacity ${transitionTime}ms;
    pointer-events: none;

    ${props =>
      props.isAtTopOfList &&
      css`
        border-top: 0;
      `}

    ${props =>
      props.isFolderAndOpen &&
      css`
        background: ${COLOR.backgroundLight};
        ${font.bold};
        ${font.size(15)};

        ${ItemActions} {
          display: none;
        }
      `}

    ${props =>
      props.isShown &&
      css`
        opacity: 1;
        pointer-events: auto;
      `}

    ${props =>
      props.isAboveOthers &&
      !props.isFolderAndOpen &&
      css`
        z-index: ${zIndexValues.visibleMobileDocumentsTableRow};
      `}

    ${props =>
      props.isAboveOthers &&
      props.isFolderAndOpen &&
      css`
        z-index: ${zIndexValues.mainVisibleMobileDocumentsTableRow};
      `}
  }

  @media ${mediaQueries.desktop} {
    height: ${rowHeightDesktop}px;
  }
`;

export const MobileRowsContainer = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
`;

interface StyledFolderProps {
  nestingLevel: number;
}

export const StyledFolder = styled.div<StyledFolderProps>`
  display: flex;
  align-items: center;
  height: 100%;
  padding-left: ${props => props.nestingLevel * 17}px;
  ${mixin.clickable};
`;

export const FolderName = styled.div`
  width: 100%;
  padding-left: 17px;
  ${mixin.truncateText}
`;

export const StyledFile = styled.div`
  display: flex;
  align-items: center;
  height: 100%;
`;

export const FileColumns = styled.div`
  display: flex;
  width: calc(100% - 17px);

  @media ${mediaQueries.mobile} {
    flex-direction: column;
    padding-right: ${iconWidth + iconHorizontalMargin * 2}px;
  }

  @media ${mediaQueries.desktop} {
    flex-direction: row;
  }
`;

interface FileColumnNameProps {
  nestingLevel: number;
}

export const FileColumnName = styled.a<FileColumnNameProps>`
  padding-left: 17px;
  user-select: none;
  ${mixin.link()};
  ${mixin.truncateText};

  @media ${mediaQueries.desktop} {
    width: 55%;
    padding-right: ${props => props.nestingLevel * 17}px;
    transform: translateX(${props => props.nestingLevel * 17}px);
  }
`;

export const FileColumnSize = styled.div`
  width: 10%;
  padding-left: 17px;
  text-align: right;
  pointer-events: none;
  ${mixin.truncateText}

  @media ${mediaQueries.mobile} {
    display: none;
  }
`;

export const FileColumnLast = styled.div`
  padding-left: 17px;
  pointer-events: none;
  ${mixin.truncateText}

  @media ${mediaQueries.mobile} {
    margin-top: 4px;
    color: ${COLOR.textMedium};
  }

  @media ${mediaQueries.desktop} {
    width: 30%;
    padding-right: 60px;
  }
`;

export const ItemActions = styled(DropdownMenu.Container)`
  position: absolute;
  top: calc(50% - ${itemActionsTriggerSize / 2}px);
  right: ${iconHorizontalMargin}px;
  display: inline-block;
`;

export const ItemActionsAction = DropdownMenu.Action;

export const ItemActionsTrigger = styled(Button)`
  height: ${itemActionsTriggerSize}px;
  width: ${itemActionsTriggerSize}px;
  line-height: 22px;
  padding: 0;
`;
