import React from 'react';
import { Redirect, Route, RouteProps, useLocation } from 'react-router-dom';

import Navbar from 'shared/components/Navbar';
import { ContentWithScroll } from 'shared/components/Styles';
import useUserAuthStatus from 'shared/hooks/useUserAuthStatus';

import { Main } from './Styles';

interface AuthRouteProps {
  path?: string;
  component: RouteProps['component'];
  exact?: boolean;
  withLayout?: boolean;
  mountRoute: boolean;
}

type ExposedRouteProps = Omit<AuthRouteProps, 'mountRoute'>;

const AuthRoute: React.FC<AuthRouteProps> = ({
  path,
  component,
  exact,
  withLayout = false,
  mountRoute,
}) => {
  const { pathname } = useLocation();
  const { companyType, userBaseUrl } = useUserAuthStatus();

  if (!mountRoute) {
    return <Redirect from={path} to={{ pathname: userBaseUrl, state: { redirect: pathname } }} />;
  }

  const route = <Route path={path} component={component} exact={exact} />;

  if (withLayout && companyType) {
    return (
      <Main>
        <Navbar companyType={companyType} />
        <ContentWithScroll>{route}</ContentWithScroll>
      </Main>
    );
  }
  return route;
};
const PrincipalRoute = (props: ExposedRouteProps) => {
  const { isUserPrincipal } = useUserAuthStatus();
  return <AuthRoute {...props} mountRoute={isUserPrincipal} />;
};
const AgentRoute = (props: ExposedRouteProps) => {
  const { isUserAgent } = useUserAuthStatus();
  return <AuthRoute {...props} mountRoute={isUserAgent} />;
};
const UserRoute = (props: ExposedRouteProps) => {
  const { isUserAuthenticated } = useUserAuthStatus();
  return <AuthRoute {...props} mountRoute={isUserAuthenticated} />;
};
const EmployeeRoute = (props: ExposedRouteProps) => {
  const { isUserEmployee } = useUserAuthStatus();
  return <AuthRoute {...props} mountRoute={isUserEmployee} />;
};
const AdminRoute = (props: ExposedRouteProps) => {
  const { isUserAdmin } = useUserAuthStatus();
  return <AuthRoute {...props} mountRoute={isUserAdmin} />;
};
const AnonymousRoute = (props: ExposedRouteProps) => {
  const { isUserAnonymous } = useUserAuthStatus();
  return <AuthRoute {...props} mountRoute={isUserAnonymous} />;
};

export { PrincipalRoute, AgentRoute, UserRoute, EmployeeRoute, AdminRoute, AnonymousRoute };
