import React from 'react';
import { useTranslation } from 'react-i18next';

import Icon from 'shared/components/Icon';
import { DisabledIcon } from 'shared/components/Styles';

import { StyledPagination, Arrow, Text } from './Styles';

// Elasticsearch does not support pagination past 10,000 records
// so we need to limit pagination
const maxRecords = 10000;

interface PaginationProps {
  className?: string;
  currentOffset: number;
  itemsPerPage: number;
  totalRecords: number;
  onChange: (offset: number) => void;
}

const Pagination: React.FC<PaginationProps> = ({
  className,
  currentOffset,
  itemsPerPage,
  totalRecords,
  onChange,
}) => {
  const { t } = useTranslation();
  const accessibleRecords = totalRecords > maxRecords ? maxRecords : totalRecords;
  const currentPage = Math.floor(currentOffset / itemsPerPage) + 1;
  const totalPages = Math.ceil(accessibleRecords / itemsPerPage);

  const startIndex = totalRecords ? (currentPage - 1) * itemsPerPage + 1 : 0;
  const endIndex = Math.min(currentPage * itemsPerPage, totalRecords);

  const handleChange = (page: number): void => {
    const newOffset = (page - 1) * itemsPerPage;
    onChange(newOffset);
  };

  return (
    <StyledPagination className={className} data-cy-pagination>
      <Text>
        {totalRecords > maxRecords
          ? t('showingManyResults', { count: maxRecords, startIndex, endIndex })
          : t('showingResults', { count: totalRecords, startIndex, endIndex })}
      </Text>
      {currentPage > 1 ? (
        <Arrow
          as={Icon}
          type="arrow-left"
          onClick={() => handleChange(currentPage - 1)}
          data-cy-pagination-prev-page
        />
      ) : (
        <Arrow as={DisabledIcon} type="arrow-left" data-cy-pagination-prev-page />
      )}
      {currentPage < totalPages ? (
        <Arrow
          as={Icon}
          type="arrow-right"
          onClick={() => handleChange(currentPage + 1)}
          data-cy-pagination-next-page
        />
      ) : (
        <Arrow as={DisabledIcon} type="arrow-right" data-cy-pagination-next-page />
      )}
    </StyledPagination>
  );
};

export default Pagination;
