import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import toast from 'shared/utils/toast';
import { ConfirmationModal, ClickableText } from 'shared/components';

import { ResendIconWrapper, ResendIcon } from './Styles';
import { mutationSendInvitation } from './api';

interface Props {
  bidInviteId: string;
}

const ResendInvitationLink = ({ bidInviteId }: Props) => {
  const { t } = useTranslation('bidPackages');
  const [sendInvitation, { loading }] = useMutation(mutationSendInvitation, {
    refetchQueries: () => ['BidPackageForShow', 'BidRequestsForBidPackage'],
    awaitRefetchQueries: true,
  });

  return (
    <ConfirmationModal
      title={t('resendInvitationConfirmTitle')}
      message={null}
      confirmText={t('resendInvitation')}
      renderLink={modal => (
        <ResendIconWrapper>
          <ResendIcon />
          <ClickableText onClick={modal.open}>{t('resendInvitation')}</ClickableText>
        </ResendIconWrapper>
      )}
      isWorking={loading}
      onConfirm={async modal => {
        try {
          await sendInvitation({ variables: { bidInviteId } });
          modal.close();
          toast.success(t('resendInvitationSuccess'));
        } catch (error) {
          toast.error(error);
        }
      }}
    />
  );
};

export default ResendInvitationLink;
