import React from 'react';
import { Formik, Form as FormikForm, FormikConfig, FormikFormProps } from 'formik';

import { is, generateErrors } from 'shared/utils/validation';
import { Validator, Validations } from 'shared/utils/validation/types';

import RequiredFieldsNote from './RequiredFieldsNote';

type MapValuesToValidations<Values, AllValues = Values> = {
  [K in keyof Values]?: Values[K] extends Array<{}>
    ?
        | Validator<Values[K], Values, AllValues>
        | Validator<Values[K], Values, AllValues>[]
        | MapValuesToValidations<Values[K], AllValues>
    : Validator<Values[K], Values, AllValues> | Validator<Values[K], Values, AllValues>[];
};

type FormProps<Values> = Omit<FormikConfig<Values>, 'validate' | 'initialValues'> & {
  validations?: MapValuesToValidations<Values>;
  initialValues: Required<Values>;
};

const Form = <Values extends {}>({
  validations,
  validateOnBlur = false,
  ...props
}: FormProps<Values>): ReturnType<React.FC<FormProps<Values>>> => {
  return (
    <Formik
      {...props}
      validateOnBlur={validateOnBlur}
      validate={values => {
        if (validations) {
          return generateErrors(values, validations as Validations);
        }

        return {};
      }}
    />
  );
};

const FormElement: React.FC<FormikFormProps> = props => <FormikForm noValidate {...props} />;

Form.Element = FormElement;

Form.is = is;

Form.RequiredFieldsNote = RequiredFieldsNote;

export default Form;
