import moment, { Moment } from 'moment';

export type AnyDate = string | Date | Moment;

export const formatDate = (date: AnyDate, format = 'll'): string =>
  date ? moment(date).format(format) : date;

export const formatDateTime = (date: AnyDate, format = 'lll'): string =>
  date ? moment(date).format(format) : date;

export const formatDateTimeForAPI = (date: AnyDate): string =>
  date
    ? moment(date)
        .utc()
        .format()
    : date;

export const formatDateForAPI = (date: AnyDate): string =>
  date ? moment(date).format('YYYY-MM-DD') : date;

export function isAnyDate(value: unknown): value is AnyDate {
  return typeof value === 'string' || value instanceof Date || moment.isMoment(value);
}
