import { useEffect } from 'react';
import { useMutation, useQuery } from '@apollo/react-hooks';

import {
  mutationUpdateBidRequestStatusBySubcontractor,
  mutationUpdateBidRequestDeclinedReasonBySubcontractor,
} from 'shared/utils/queries';
import { BID_REQUEST_STATUS } from '__graphql__/globalTypes';
import toast from 'shared/utils/toast';
import { BID_INVITE_ACTION_ROUTE_TYPE } from 'shared/constants/routes';
import { isLoading, isDoneLoading } from 'shared/utils/apollo';

import { queryBidRequestForBidInviteFeedback } from './api';
import {
  BidRequestForBidInviteFeedback,
  BidRequestForBidInviteFeedbackVariables,
} from './__graphql__/BidRequestForBidInviteFeedback';

export interface MatchParams {
  bidInviteId: string;
  action: BID_INVITE_ACTION_ROUTE_TYPE;
}

const getBidRequestStatus = (action: BID_INVITE_ACTION_ROUTE_TYPE): BID_REQUEST_STATUS | null => {
  switch (action) {
    case 'decline':
      return BID_REQUEST_STATUS.inviteDeclined;

    case 'accept':
      return BID_REQUEST_STATUS.inviteAccepted;

    case 'decide-later':
      return BID_REQUEST_STATUS.undecided;

    default:
      return null;
  }
};

export const useBidInviteFeedback = ({ action, bidInviteId }: MatchParams) => {
  const bidRequestQuery = useQuery<
    BidRequestForBidInviteFeedback,
    BidRequestForBidInviteFeedbackVariables
  >(queryBidRequestForBidInviteFeedback, {
    variables: { bidInviteId },
  });

  const bidRequestId = isDoneLoading(bidRequestQuery)
    ? bidRequestQuery.data.bidRequestForSubcontractor.id
    : null;

  const [setBidRequestStatus, statusMutationResult] = useMutation(
    mutationUpdateBidRequestStatusBySubcontractor,
  );

  const [setBidRequestDeclinedReason, declinedReasonMutationResult] = useMutation(
    mutationUpdateBidRequestDeclinedReasonBySubcontractor,
  );

  useEffect(() => {
    const status = getBidRequestStatus(action);
    if (status && bidRequestId) {
      setBidRequestStatus({ variables: { id: bidRequestId, status } }).catch(toast.error);
    }
  }, [action, bidRequestId, setBidRequestStatus]);

  const isFeedbackNeeded = action === 'decline' && !declinedReasonMutationResult.called;

  return {
    isFeedbackNeeded,
    isLoading:
      declinedReasonMutationResult.loading ||
      statusMutationResult.loading ||
      isLoading(bidRequestQuery),
    hasGivenFeedback: declinedReasonMutationResult.called,
    setDeclinedReason({ declinedReason }: { declinedReason: string }) {
      setBidRequestDeclinedReason({
        variables: { id: bidRequestId, declinedReason },
      }).catch(toast.error);
    },
  };
};
