import { useState } from 'react';

import { SortDirection } from 'shared/constants';

import {
  QueryVariables,
  SortVariables,
  Refetch,
  GetSortVariables,
  GetSortIcon,
  ChangeSort,
} from './types';

interface UseSortArgs {
  refetch: Refetch;
  variables: QueryVariables;
}

const useSort = ({ refetch, variables }: UseSortArgs) => {
  const [sort, setSort] = useState<Partial<SortVariables>>(variables);

  const getSortVariables: GetSortVariables = sortField => {
    const { sortBy, sortDirection } = sort;

    if (sortField === sortBy && sortDirection === SortDirection.asc) {
      return { sortBy, sortDirection: SortDirection.desc };
    }

    return { sortBy: sortField, sortDirection: SortDirection.asc };
  };

  const getSortIcon: GetSortIcon = sortField => {
    const { sortBy, sortDirection } = sort;

    if (sortField === sortBy) {
      return sortDirection === SortDirection.asc ? 'sort-down' : 'sort-up';
    }
    return 'sort';
  };

  const changeSort: ChangeSort = (sortField?) => {
    if (sortField) {
      const sortVariables = getSortVariables(sortField);

      setSort(sortVariables);
      refetch({ offset: 0, ...sortVariables });
    }
  };

  return { getSortIcon, changeSort };
};

export default useSort;
