import React, { useContext } from 'react';

import DocumentsTableContext from './DocumentsTableContext';
import DropzoneArea from './DropzoneArea';
import Folder from './classes/Folder';

interface Props {
  folder: Folder;
  children: React.ReactNode;
}

const FolderDropzone: React.FC<Props> = ({ folder, children }) => {
  const { isReadOnly } = useContext(DocumentsTableContext);

  if (isReadOnly) {
    return <>{children}</>;
  }

  return <DropzoneArea targetFolder={folder}>{children}</DropzoneArea>;
};

export default FolderDropzone;
