import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import moment from 'moment';
import { Field as FormikField, FieldProps } from 'formik';

import { ProjectInput } from '__graphql__/globalTypes';
import Form from 'shared/components/Form';
import Field from 'shared/components/Field';
import FormRow from 'shared/components/FormRow';
import CurrencyFormatter from 'shared/components/CurrencyFormatter';
import {
  Controls,
  ControlsButton,
  Wrapper,
  FormWrapper,
  Heading,
  StyledFormCheckbox,
  StyledImage,
} from 'shared/components/Overview';
import { SectionHint } from 'shared/components/Styles';
import { DATE_FORMAT_WITHOUT_TIME, DATE_FORMAT_WITH_TIME } from 'shared/components/DatePicker';
import { CurrentUser_me } from 'shared/utils/__graphql__/CurrentUser';
import { getImageUrl, IMAGE_TYPE } from 'shared/utils/images';
import { omit } from 'shared/utils/javascript';

import ProjectUsers from './Users';
import {
  ProjectForOverview_project,
  ProjectForOverview_project_projectUsers,
} from './__graphql__/ProjectForOverview';
import { OfficesForProjectOverview_offices } from './__graphql__/OfficesForProjectOverview';

interface FormValues {
  number: string;
  name: string;
  imageKey: string | null;
  customer: string;
  location: string;
  startsOn: string | null;
  endsOn: string | null;
  projectUsers: Array<{
    user: {
      id: string;
      firstName: string | null;
      lastName: string | null;
    };
    userRole: string;
  }>;
  primaryUserId: string;
  description: string;
  isEstimating: boolean;
  budget: string;
  officeId: string;
  forceSubcontractorSignup: boolean;
  notes: string;
  createdAt: string | null;
}

interface OverviewProps {
  project: Partial<ProjectForOverview_project>;
  offices: OfficesForProjectOverview_offices;
  currentUser: CurrentUser_me;
  submitForm: (input: ProjectInput) => Promise<void>;
  onlyAllowEdit?: boolean;
  editByDefault?: boolean;
}

const Overview: React.FC<OverviewProps> = ({
  project,
  offices,
  currentUser: { id: initialUserId, firstName, lastName },
  submitForm,
  onlyAllowEdit = false,
  editByDefault = false,
}) => {
  const { t } = useTranslation('projects');
  const [isEditing, setIsEditing] = useState(onlyAllowEdit || editByDefault);

  const initialProjectUsers = [
    {
      user: {
        id: initialUserId,
        firstName,
        lastName,
      },
      userRole: t('project.defaultUserRole'),
    },
  ];

  const officeSelectOptions = offices.offices.map(office => ({
    value: office.id,
    label: office.name,
  }));

  return (
    <Form<FormValues>
      initialValues={{
        number: project.number || '',
        name: project.name || '',
        imageKey: project.imageKey || null,
        customer: project.customer || '',
        location: project.location || '',
        startsOn: project.startsOn || null,
        endsOn: project.endsOn || null,
        projectUsers: project.projectUsers || initialProjectUsers,
        primaryUserId: project.primaryUser ? project.primaryUser.id : initialUserId,
        description: project.description || '',
        isEstimating: project.isEstimating || false,
        budget: String(project.budget || ''),
        officeId: project.officeId || '',
        forceSubcontractorSignup: project.forceSubcontractorSignup || false,
        notes: project.notes || '',
        createdAt: project.createdAt || null,
      }}
      validations={{
        number: [Form.is.required(), Form.is.maxLength(100)],
        name: [Form.is.required(), Form.is.maxLength(100)],
        customer: [Form.is.maxLength(100)],
        location: [Form.is.required(), Form.is.maxLength(200)],
        projectUsers: Form.is.notEmptyArray(),
        description: Form.is.maxLength(60000),
        isEstimating: Form.is.required(),
        officeId: Form.is.required(),
        startsOn: Form.is.notAfter<FormValues>(
          values => values.endsOn,
          t('project.validation.mustBeBeforeEndDate'),
        ),
        endsOn: Form.is.notBefore<FormValues>(
          values => values.startsOn,
          t('project.validation.mustBeAfterStartDate'),
        ),
      }}
      onSubmit={({ budget, projectUsers, ...remainingValues }) =>
        submitForm({
          ...omit(remainingValues, 'createdAt'),
          projectUsers: projectUsers.map(({ userRole, user: { id: userId } }) => ({
            userRole,
            userId,
          })),
          budget: budget ? Number(parseFloat(budget).toFixed(2)) : null,
        })
      }
    >
      {({ resetForm, values }) => (
        <Wrapper as={isEditing ? Form.Element : 'div'}>
          <Controls>
            {!isEditing && (
              <ControlsButton hollow onClick={() => setIsEditing(true)}>
                {t('overview.buttonLabels.edit')}
              </ControlsButton>
            )}
            {isEditing && !onlyAllowEdit && (
              <>
                <ControlsButton
                  hollow
                  onClick={() => {
                    setIsEditing(false);
                    resetForm();
                  }}
                >
                  {t('overview.buttonLabels.cancel')}
                </ControlsButton>
                <ControlsButton type="submit">{t('overview.buttonLabels.save')}</ControlsButton>
              </>
            )}
          </Controls>
          <FormWrapper>
            <Heading>{t('overview.headings.basicInfo')}</Heading>
            <FormRow isEditing={isEditing} required name="number" label={t('project.number')}>
              {props => <Field.Input {...props} />}
            </FormRow>
            <FormRow isEditing={isEditing} required name="name" label={t('project.name')}>
              {props => <Field.Input {...props} />}
            </FormRow>
            <FormRow isEditing={isEditing} name="customer" label={t('project.customer')}>
              {props => <Field.Input {...props} />}
            </FormRow>
            <FormRow isEditing={isEditing} required name="location" label={t('project.location')}>
              {props => <Field.LocationSearch {...props} />}
            </FormRow>
            <FormRow
              isEditing={isEditing}
              name="startsOn"
              label={t('project.startsOn')}
              mapValue={value => moment(value).format(DATE_FORMAT_WITHOUT_TIME)}
            >
              {props => <Field.DatePicker {...props} withTime={false} />}
            </FormRow>
            <FormRow
              isEditing={isEditing}
              name="endsOn"
              label={t('project.endsOn')}
              mapValue={value => moment(value).format(DATE_FORMAT_WITHOUT_TIME)}
            >
              {props => <Field.DatePicker {...props} withTime={false} />}
            </FormRow>
            <FormRow
              isEditing={isEditing}
              required
              name="officeId"
              label={t('project.office')}
              // The selected value has to be in the options array
              // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
              mapValue={value => officeSelectOptions.find(option => option.value === value)!.label}
            >
              {props => <Field.SelectSingle {...props} options={officeSelectOptions} />}
            </FormRow>
            <FormRow
              isEditing={isEditing}
              required
              name="isEstimating"
              label={t('phase')}
              mapValue={value => (value ? t('estimationPhase') : t('bidPhase'))}
            >
              {props => (
                <Field.RadioButtonGroup
                  {...props}
                  values={[
                    { optionValue: false, label: t('bidPhase') },
                    { optionValue: true, label: t('estimationPhase') },
                  ]}
                />
              )}
            </FormRow>
            <FormRow isEditing={isEditing} name="description" label={t('project.description')}>
              {props => <Field.Textarea {...props} />}
            </FormRow>
            <FormRow
              isEditing={isEditing}
              name="imageKey"
              label={t('project.image')}
              mapValue={value => (
                <StyledImage
                  data-cy-project-image
                  src={getImageUrl(value, IMAGE_TYPE.imageUploader)}
                />
              )}
            >
              {props => <Field.ImageUploader {...props} />}
            </FormRow>
            <StyledFormCheckbox
              name="forceSubcontractorSignup"
              checkboxLabel={t('project.forceSubcontractorSignup')}
              disabled={!isEditing}
            />
            <Heading>{t('usersTitle')}</Heading>
            <FormikField name="projectUsers">
              {({
                field: { value: projectUsers },
              }: FieldProps<ProjectForOverview_project_projectUsers[], FormValues>) => (
                <FormikField name="primaryUserId">
                  {({
                    field: { value: primaryUserId },
                    form: { setFieldValue },
                  }: FieldProps<string, FormValues>) => (
                    <ProjectUsers
                      projectUsers={projectUsers}
                      primaryUserId={primaryUserId}
                      setPrimaryUserId={newId => setFieldValue('primaryUserId', newId)}
                      isEditing={isEditing}
                    />
                  )}
                </FormikField>
              )}
            </FormikField>
            <Heading>{t('overview.headings.internalData')}</Heading>
            <SectionHint>{t('internalDataHint')}</SectionHint>
            {values.createdAt && (
              <FormRow
                isEditing={false}
                name="createdAt"
                label={t('project.createdAt')}
                mapValue={value => moment(value).format(DATE_FORMAT_WITH_TIME)}
              >
                {() => null}
              </FormRow>
            )}
            <FormRow
              isEditing={isEditing}
              name="budget"
              label={t('project.budget')}
              mapValue={value => <CurrencyFormatter value={Number(value)} />}
            >
              {props => <Field.CurrencyInput icon="euro" {...props} />}
            </FormRow>
            <FormRow isEditing={isEditing} name="notes" label={t('project.notes')}>
              {props => <Field.Textarea {...props} />}
            </FormRow>
            {isEditing && <Form.RequiredFieldsNote />}
          </FormWrapper>
          {isEditing && (
            <Controls>
              {!onlyAllowEdit && (
                <ControlsButton
                  hollow
                  onClick={() => {
                    setIsEditing(false);
                    resetForm();
                  }}
                >
                  {t('overview.buttonLabels.cancel')}
                </ControlsButton>
              )}
              <ControlsButton type="submit">{t('overview.buttonLabels.save')}</ControlsButton>
            </Controls>
          )}
        </Wrapper>
      )}
    </Form>
  );
};

export default Overview;
