import { useEffect } from 'react';
import { useLocation, useHistory } from 'react-router-dom';

import { queryStringToObject } from 'shared/utils/url';

const useQueryParamInitialAction = <T>(paramName: string, callback: (param: T) => void): void => {
  const location = useLocation();
  const history = useHistory();

  useEffect(() => {
    const params = queryStringToObject<{ [key: string]: T }>(location.search);
    const paramValue = params[paramName];

    if (paramValue) {
      callback(paramValue);
      history.replace(location.pathname);
    }
    // We only need this to trigger once, when the component mounts
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
};

export default useQueryParamInitialAction;
