import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { ApolloError } from 'apollo-client';

import { Form, Field } from 'shared/components';
import { Message, Tip, SubmitButton, BottomLink } from 'shared/components/UnauthenticatedStyles';
import UnauthenticatedPage from 'shared/components/UnauthenticatedPage';
import { ContentBox } from 'shared/components/UnauthenticatedPage/Styles';
import routes from 'shared/constants/routes';
import { COLOR } from 'shared/utils/styles';

import { mutationResetPassword } from './api';
import { ResetPassword, ResetPasswordVariables } from './__graphql__/ResetPassword';

interface FormValues {
  email: string;
}

const AuthResetPassword: React.FC<void> = () => {
  const { t } = useTranslation('auth');
  const [resetPassword, { error, loading: mutating }] = useMutation<
    ResetPassword,
    ResetPasswordVariables
  >(mutationResetPassword);

  const getErrorMessage = (errorObj: ApolloError) => {
    if (
      errorObj.graphQLErrors[0].extensions?.code === 'NOT_FOUND' &&
      errorObj.graphQLErrors[0].extensions.exception.entityName === 'User'
    ) {
      return t('resetPassword.emailNotFound');
    }

    return errorObj.message;
  };

  return (
    <UnauthenticatedPage title="resetPassword">
      <Form<FormValues>
        initialValues={{
          email: '',
        }}
        validations={{
          email: [Form.is.required(), Form.is.maxLength(200), Form.is.email()],
        }}
        onSubmit={(values, { setStatus }) => {
          resetPassword({ variables: values }).then(
            () => setStatus({ completed: true }),
            () => {},
          );
        }}
      >
        {({ status = {} }: { status: { completed?: boolean } }) => (
          <ContentBox as={Form.Element}>
            {error && <Message color={COLOR.danger}>{getErrorMessage(error)}</Message>}
            {status.completed && (
              <Message color={COLOR.success}>{t('resetPassword.success')}</Message>
            )}
            <Field.Input name="email" label={t('email')} type="email" icon="email" />
            <Tip>{t('resetPassword.instructions')}</Tip>
            <SubmitButton type="submit" working={mutating}>
              {t('resetPassword.submit')}
            </SubmitButton>
            <BottomLink>
              {`${t('resetPassword.remember')} `}
              <Link to={routes.login()}>{t('login')}</Link>
            </BottomLink>
          </ContentBox>
        )}
      </Form>
    </UnauthenticatedPage>
  );
};

export default AuthResetPassword;
