import React, { useEffect } from 'react';
import { useMutation } from '@apollo/react-hooks';

import { PageLoader, QueryErrors } from 'shared/components';

import { mutationClaimBidInvite } from './api';
import {
  ClaimBidInvite as ClaimBidInviteQuery,
  ClaimBidInviteVariables,
} from './__graphql__/ClaimBidInvite';

interface Props {
  bidInviteId: string;
  children: React.ReactNode;
}

const ClaimBidInvite: React.FC<Props> = ({ bidInviteId, children }) => {
  const [claimBidInvite, { error, loading }] = useMutation<
    ClaimBidInviteQuery,
    ClaimBidInviteVariables
  >(mutationClaimBidInvite, {
    variables: { bidInviteId },
  });

  useEffect(() => {
    // eslint-disable-next-line @typescript-eslint/no-floating-promises
    claimBidInvite();
  }, [claimBidInvite]);

  if (error) {
    return <QueryErrors error={error} />;
  }

  if (loading) {
    return <PageLoader />;
  }

  return <>{children}</>;
};

export default ClaimBidInvite;
