import i18n from 'i18next';

interface Contact {
  isPerson: boolean;
  title?: string | null;
  email?: string | null;
  firstName?: string | null;
  lastName?: string | null;
}

const getContactName = (contact: Contact): string | null => {
  if (contact.isPerson) {
    return [contact.firstName, contact.lastName].filter(Boolean).join(' ') || null;
  }

  return contact.email || null;
};

const getContactNameWithTitle = (contact: Contact): string | null => {
  if (contact.isPerson && contact.title && contact.lastName) {
    const title = i18n.t(`companyInfo.title.${contact.title}`);
    const name = [contact.firstName, contact.lastName].filter(Boolean).join(' ');

    return [title, name].filter(Boolean).join(' ') || null;
  }

  return getContactName(contact);
};

export { getContactNameWithTitle };
export default getContactName;
