import React from 'react';
import gql from 'graphql-tag';
import { useTranslation } from 'react-i18next';

import { CONTACT_TITLE } from 'shared/constants/contact';
import { BID_INVITE_STATUS } from '__graphql__/globalTypes';
import getContactName, { getContactNameWithTitle } from 'shared/utils/getContactName';
import Avatar from 'shared/components/Avatar';
import Icon from 'shared/components/Icon';

import ResendInvitationLink from './ResendInvitationLink';
import { Text, Anchor, Contact, Info, IconAvatar, Status, StatusIcon } from './Styles';

const NameAvatar: React.FC<{ name: string | null }> = ({ name }) => {
  if (name) {
    return <Avatar name={name} />;
  }
  return (
    <IconAvatar>
      <Icon type="email" top={4} left={4} />
    </IconAvatar>
  );
};

const fragmentContactsList = gql`
  fragment ContactsList_bidRequest on BidRequest {
    id
    bidInvites {
      id
      status
      contact {
        id
        isPerson
        title
        firstName
        lastName
        email
        phone
        mobilePhone
        fax
      }
    }
  }
`;

export interface ContactsListProps {
  bidInvites: {
    id: string;
    status: BID_INVITE_STATUS;
    contact: {
      id: string;
      isPerson: boolean;
      title?: CONTACT_TITLE | null;
      firstName?: string | null;
      lastName?: string | null;
      email?: string | null;
      phone?: string | null;
      mobilePhone?: string | null;
      fax?: string | null;
    };
  }[];
  isProjectClosed?: boolean;
  showResendLink?: boolean;
}

const ContactsList: React.FC<ContactsListProps> = ({
  bidInvites,
  isProjectClosed,
  showResendLink,
}) => {
  const { t } = useTranslation();

  return (
    <div>
      {bidInvites.map(bidInvite => {
        const { id, email, phone, mobilePhone, fax } = bidInvite.contact;
        const { status } = bidInvite;
        const name = getContactName(bidInvite.contact);
        const nameWithTitle = getContactNameWithTitle(bidInvite.contact);

        return (
          <Contact key={id}>
            <NameAvatar name={name} />
            <Info>
              {nameWithTitle && <Text>{nameWithTitle}</Text>}
              {email && (
                <Text>
                  {`${t('companyInfo.email')}: `}
                  <Anchor to={`mailto:${email}`}>{email}</Anchor>
                </Text>
              )}
              {phone && (
                <Text>
                  {`${t('companyInfo.phone')}: `}
                  <Anchor to={`tel:${phone}`}>{phone}</Anchor>
                </Text>
              )}
              {mobilePhone && (
                <Text>
                  {`${t('companyInfo.mobilePhone')}: `}
                  <Anchor to={`tel:${mobilePhone}`}>{mobilePhone}</Anchor>
                </Text>
              )}
              {fax && (
                <Text>
                  {`${t('companyInfo.fax')}: `}
                  <Anchor to={`tel:${fax}`}>{fax}</Anchor>
                </Text>
              )}
              <Status>
                <StatusIcon status={status} />
                {t(`bidInviteStatus.${status}`)}
              </Status>
              {showResendLink && status !== BID_INVITE_STATUS.draft && !isProjectClosed && (
                <ResendInvitationLink bidInviteId={bidInvite.id} />
              )}
            </Info>
          </Contact>
        );
      })}
    </div>
  );
};

export { fragmentContactsList };
export default ContactsList;
