import Console from 'shared/utils/console';

import { Validator, Validations, Values, Errors } from './types';

const isValidator = (obj: object): obj is Validator => obj instanceof Function;
const isMultipleValidators = (obj: object): obj is Validator[] => Array.isArray(obj);
const isNestedValidators = (obj: object): obj is Validations =>
  !isValidator(obj) && !isMultipleValidators(obj);

const assignErrorBuilder = (errors: Errors) => (
  validator: Validator,
  fieldName: string,
  fieldValues: Values,
  allFieldValues: Values,
): void => {
  const value = fieldValues[fieldName];
  const errorMessage = validator(value, fieldValues, allFieldValues);
  if (errorMessage && !errors[fieldName]) {
    errors[fieldName] = errorMessage;
  }
};

// We assume here that nested validation is used only for field arrays
const assignNestedErrorBuilder = (errors: Errors) => (
  nestedValidators: Validations,
  fieldName: string,
  allValues: Values,
): void => {
  const values = allValues[fieldName] as Values[];
  const errorMessages = values.map(nestedValues =>
    generateErrors(nestedValues, nestedValidators, allValues),
  );
  const hasErrorMessages = errorMessages.find(item => Object.keys(item).length > 0);

  if (hasErrorMessages && !errors[fieldName]) {
    errors[fieldName] = errorMessages;
  } else if (!hasErrorMessages && !errors[fieldName]) {
    delete errors[fieldName];
  }
};

export const generateErrors = (
  fieldValues: Values,
  fieldValidators: Validations,
  allFieldValues?: Values,
): Errors => {
  const errors: Errors = {};
  const assignError = assignErrorBuilder(errors);
  const asignNestedError = assignNestedErrorBuilder(errors);

  try {
    Object.entries(fieldValidators).forEach(([fieldName, validators]) => {
      if (!validators) {
        return;
      }

      if (isNestedValidators(validators)) {
        asignNestedError(validators, fieldName, fieldValues);
      } else {
        [validators]
          .flat()
          .forEach(validator =>
            assignError(validator, fieldName, fieldValues, allFieldValues || fieldValues),
          );
      }
    });
  } catch (error) {
    // Errors in validators get caught somewhere (in Formik?) and muted.
    Console.error(error);
    throw error;
  }

  return errors;
};
