import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import moment from 'moment';

import { Form, CurrencyFormatter, Field } from 'shared/components';
import { SectionHint } from 'shared/components/Styles';
import FormRow from 'shared/components/FormRow';
import {
  Wrapper,
  FormWrapper,
  Controls,
  ControlsButton,
  Heading,
} from 'shared/components/Overview';
import { DATE_FORMAT_WITH_TIME, DATE_FORMAT_WITHOUT_TIME } from 'shared/components/DatePicker';
import { omit } from 'shared/utils/javascript';
import { BidPackageInput } from '__graphql__/globalTypes';

import { BidPackageForOverview_bidPackage } from './__graphql__/BidPackageForOverview';

interface FormValues {
  number: string;
  name: string;
  bidsDueAt: string | null;
  startsOn: string | null;
  endsOn: string | null;
  budget: string;
  description: string;
  notes: string;
  projectId: string;
  createdAt: string | null;
}

interface OverviewProps {
  bidPackage: Partial<BidPackageForOverview_bidPackage>;
  projectId: string;
  submitForm: (input: BidPackageInput) => Promise<void>;
  onlyAllowEdit?: boolean;
  editByDefault?: boolean;
}

const Overview: React.FC<OverviewProps> = ({
  bidPackage,
  projectId,
  submitForm,
  onlyAllowEdit = false,
  editByDefault = false,
}) => {
  const { t } = useTranslation('bidPackages');
  const [isEditing, setIsEditing] = useState(onlyAllowEdit || editByDefault);

  return (
    <Form<FormValues>
      initialValues={{
        number: bidPackage.number || '',
        name: bidPackage.name || '',
        bidsDueAt: bidPackage.bidsDueAt || null,
        startsOn: bidPackage.startsOn || null,
        endsOn: bidPackage.endsOn || null,
        budget: String(bidPackage.budget || ''),
        description: bidPackage.description || '',
        notes: bidPackage.notes || '',
        projectId,
        createdAt: bidPackage.createdAt || null,
      }}
      validations={{
        number: [Form.is.maxLength(100)],
        name: [Form.is.required(), Form.is.maxLength(100)],
        bidsDueAt: [
          Form.is.required(),
          Form.is.notAfter<FormValues>(
            values => values.startsOn,
            t('bidPackage.validation.mustBeBeforeStartDate'),
          ),
          Form.is.notAfter<FormValues>(
            values => values.endsOn,
            t('bidPackage.validation.mustBeBeforeEndDate'),
          ),
        ],
        startsOn: Form.is.notAfter<FormValues>(
          values => values.endsOn,
          t('bidPackage.validation.mustBeBeforeEndDate'),
        ),
        endsOn: Form.is.notBefore<FormValues>(
          values => values.startsOn,
          t('bidPackage.validation.mustBeAfterStartDate'),
        ),
      }}
      onSubmit={({ budget, ...remainingValues }) =>
        submitForm({
          ...omit(remainingValues, 'createdAt'),
          budget: budget ? Number(parseFloat(budget).toFixed(2)) : null,
        })
      }
    >
      {({ resetForm, values }) => (
        <Wrapper as={isEditing ? Form.Element : 'div'}>
          <Controls>
            {!isEditing && (
              <ControlsButton hollow onClick={() => setIsEditing(true)}>
                {t('overview.buttonLabels.edit')}
              </ControlsButton>
            )}
            {isEditing && !onlyAllowEdit && (
              <>
                <ControlsButton
                  hollow
                  onClick={() => {
                    setIsEditing(false);
                    resetForm();
                  }}
                >
                  {t('overview.buttonLabels.cancel')}
                </ControlsButton>
                <ControlsButton type="submit">{t('overview.buttonLabels.save')}</ControlsButton>
              </>
            )}
          </Controls>
          <FormWrapper>
            <Heading>{t('overview.headings.basicInfo')}</Heading>
            <FormRow isEditing={isEditing} name="number" label={t('overview.formFields.number')}>
              {props => <Field.Input {...props} />}
            </FormRow>
            <FormRow
              isEditing={isEditing}
              name="name"
              label={t('overview.formFields.name')}
              required
            >
              {props => <Field.Input {...props} />}
            </FormRow>
            <FormRow
              isEditing={isEditing}
              name="bidsDueAt"
              label={t('overview.formFields.bidsDueAt')}
              required
              mapValue={value => moment(value).format(DATE_FORMAT_WITH_TIME)}
            >
              {props => <Field.DatePicker {...props} />}
            </FormRow>
            <FormRow
              isEditing={isEditing}
              name="startsOn"
              label={t('overview.formFields.startsOn')}
              mapValue={value => moment(value).format(DATE_FORMAT_WITHOUT_TIME)}
            >
              {props => <Field.DatePicker withTime={false} {...props} />}
            </FormRow>
            <FormRow
              isEditing={isEditing}
              name="endsOn"
              label={t('overview.formFields.endsOn')}
              mapValue={value => moment(value).format(DATE_FORMAT_WITHOUT_TIME)}
            >
              {props => <Field.DatePicker withTime={false} {...props} />}
            </FormRow>
            <FormRow
              isEditing={isEditing}
              name="description"
              label={t('overview.formFields.description')}
            >
              {props => <Field.Textarea {...props} />}
            </FormRow>
            <Heading>{t('overview.headings.internalData')}</Heading>
            <SectionHint>{t('internalDataHint')}</SectionHint>
            {values.createdAt && (
              <FormRow
                isEditing={false}
                name="createdAt"
                label={t('overview.formFields.createdAt')}
                mapValue={value => moment(value).format(DATE_FORMAT_WITH_TIME)}
              >
                {() => null}
              </FormRow>
            )}
            <FormRow
              isEditing={isEditing}
              name="budget"
              label={t('overview.formFields.budget')}
              mapValue={value => <CurrencyFormatter value={Number(value)} />}
            >
              {props => <Field.CurrencyInput icon="euro" {...props} />}
            </FormRow>
            <FormRow isEditing={isEditing} name="notes" label={t('overview.formFields.notes')}>
              {props => <Field.Textarea {...props} />}
            </FormRow>
            {isEditing && <Form.RequiredFieldsNote />}
          </FormWrapper>
          {isEditing && (
            <Controls>
              {!onlyAllowEdit && (
                <ControlsButton
                  hollow
                  onClick={() => {
                    setIsEditing(false);
                    resetForm();
                  }}
                >
                  {t('overview.buttonLabels.cancel')}
                </ControlsButton>
              )}
              <ControlsButton type="submit">{t('overview.buttonLabels.save')}</ControlsButton>
            </Controls>
          )}
        </Wrapper>
      )}
    </Form>
  );
};

export default Overview;
