import React from 'react';
import { useTranslation } from 'react-i18next';
import { ArrayHelpers } from 'formik';

import { Modal, ConfirmationModal, Table } from 'shared/components';
import { TableProps } from 'shared/components/Table';
import { MODAL_VARIANTS } from 'shared/constants';

import { ProjectForOverview_project_projectUsers } from '../__graphql__/ProjectForOverview';

import UsersForm from './Form';
import {
  Role,
  Name,
  ProjectUserAvatar,
  ProjectUserBadgeWrapper,
  RadioButtonCircle,
  UserActionIcon,
  EmptyTableExplanation,
  EmptyTable,
} from './Style';

interface ProjectUsersTableProps {
  projectUsers: ProjectForOverview_project_projectUsers[];
  arrayHelpers: Pick<ArrayHelpers, 'remove' | 'replace'>;
  primaryUserId: string;
  setPrimaryUserId: (id: string | undefined) => void;
  isEditing: boolean;
}

const ProjectUsersTable: React.FC<ProjectUsersTableProps> = ({
  projectUsers,
  arrayHelpers: { remove, replace },
  primaryUserId,
  setPrimaryUserId,
  isEditing,
}) => {
  const { t } = useTranslation('projects');

  if (projectUsers.length === 0) {
    return (
      <EmptyTable>
        <EmptyTableExplanation>{t('emptyUsersList')}</EmptyTableExplanation>
      </EmptyTable>
    );
  }

  const columns: TableProps['columns'] = [
    { label: t('project.user'), percentWidth: 50 },
    { label: t('project.manager'), textAlign: 'center' },
  ];

  if (isEditing) {
    columns.push({ fixedWidth: 40 }, { fixedWidth: 40 });
  }

  return (
    <>
      <Table
        columns={columns}
        rows={projectUsers.map(({ user: { id, firstName, lastName }, userRole }, i) => {
          const name = `${firstName} ${lastName}`;
          return (
            <Table.Row rowId={id}>
              <ProjectUserBadgeWrapper>
                <ProjectUserAvatar name={name} />
                <div>
                  <Name>{name}</Name>
                  <Role>{userRole}</Role>
                </div>
              </ProjectUserBadgeWrapper>
              <RadioButtonCircle
                data-cy-radio-button
                selected={id === primaryUserId}
                onClick={() => {
                  if (!isEditing) {
                    return;
                  }

                  if (id !== primaryUserId) {
                    setPrimaryUserId(id);
                  }
                }}
                disabled={!isEditing}
              />
              {isEditing && (
                <Modal
                  variant={MODAL_VARIANTS.center}
                  renderLink={modal => <UserActionIcon type="edit" onClick={modal.open} />}
                  renderContent={modal => (
                    <UsersForm
                      closeModal={modal.close}
                      assignedUsers={projectUsers}
                      formUser={{ id, name, userRole }}
                      modifyUser={editedProjectUser => {
                        replace(i, editedProjectUser);
                      }}
                    />
                  )}
                />
              )}
              {isEditing && (
                <ConfirmationModal
                  title={t('removeUserTitle')}
                  message={t('removeUserMessage', {
                    name: `${firstName} ${lastName}`,
                  })}
                  renderLink={modal => <UserActionIcon type="delete" onClick={modal.open} />}
                  onConfirm={({ close }) => {
                    if (id === primaryUserId) {
                      const headUser = i === 0 ? projectUsers[1] : projectUsers[0];
                      setPrimaryUserId(headUser?.user?.id || undefined);
                    }
                    remove(i);
                    close();
                  }}
                />
              )}
            </Table.Row>
          );
        })}
      />
    </>
  );
};

export default ProjectUsersTable;
