const colors = [
  '#DA7657',
  '#6ADA57',
  '#5784DA',
  '#AA57DA',
  '#DA5757',
  '#DA5792',
  '#57DACA',
  '#57A5DA',
];

export type Color = typeof colors[number];

export const getColorFromName = (name: string | null): Color =>
  colors[(name || '').toLocaleLowerCase().charCodeAt(0) % colors.length];
