import { useContext } from 'react';

import AuthContext, {
  AuthContextBase,
  isAuthenticated,
  isAnonymous,
} from 'shared/contexts/authContext';
import { COMPANY_TYPE, USER_TYPE } from '__graphql__/globalTypes';
import routes, { PROJECTS_ROUTE_TYPE } from 'shared/constants/routes';
import { AUTH_STATUS } from 'shared/constants';

const useUserAuthStatus = () => {
  const authContext = useContext<AuthContextBase>(AuthContext);

  const isUserAuthenticated = isAuthenticated(authContext);

  const isUserEmployee =
    isAuthenticated(authContext) && authContext.authState.user.type === USER_TYPE.employee;

  const isUserAnonymous = isAnonymous(authContext);

  const isUserAuthenticating = authContext.authState.status === AUTH_STATUS.authenticating;

  const isUserPrincipal =
    isAuthenticated(authContext) && authContext.authState.companyType === COMPANY_TYPE.principal;

  const isUserAgent =
    isAuthenticated(authContext) && authContext.authState.companyType === COMPANY_TYPE.agent;

  const isUserAdmin = isAuthenticated(authContext) && authContext.authState.isAdmin;

  const companyType = isAuthenticated(authContext) ? authContext.authState.companyType : undefined;

  let userBaseUrl = routes.root();
  if (isUserPrincipal) userBaseUrl = routes.projects(PROJECTS_ROUTE_TYPE.open);
  if (isUserAgent) userBaseUrl = routes.bidRequests();
  if (isUserAnonymous) userBaseUrl = routes.login();

  return {
    isUserPrincipal,
    isUserAgent,
    isUserAdmin,
    isUserAuthenticated,
    isUserEmployee,
    isUserAuthenticating,
    isUserAnonymous,
    companyType,
    userBaseUrl,
  };
};

export default useUserAuthStatus;
