import React, { Dispatch, SetStateAction } from 'react';
import { useQuery } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import {
  CheckboxGroup,
  LocationSearch,
  SelectMulti,
  PageLoader,
  QueryErrors,
} from 'shared/components';
import { orderedCompanySizes } from 'shared/constants';
import { queryWorkCategories } from 'shared/utils/queries';
import { isLoading } from 'shared/utils/apollo';
import { getHumanReadableCompanySize } from 'shared/utils/enumHelpers';

import {
  FiltersWrapper,
  Distance,
  DistanceSelect,
  DistanceLabel,
  FilterLabel,
  CompanySizeCheckbox,
} from './Styles';
import { FilterValues } from './types';

interface FiltersProps {
  refetch: (options: Record<string, unknown>) => void;
  filters: FilterValues;
  setFilters: Dispatch<SetStateAction<FilterValues>>;
  locationString: string;
  setLocationString: Dispatch<SetStateAction<string>>;
}

const Filters = ({ filters, setFilters, locationString, setLocationString }: FiltersProps) => {
  const { t } = useTranslation('bidPackages');

  const { data, loading, error } = useQuery(queryWorkCategories, {
    notifyOnNetworkStatusChange: true,
    partialRefetch: true,
  });

  if (error) return <QueryErrors error={error} />;
  if (isLoading({ data, loading })) return <PageLoader />;

  const { workCategories } = data;

  return (
    <FiltersWrapper>
      <FilterLabel>{t('basedWithin')}</FilterLabel>
      <Distance>
        <DistanceSelect
          name="distance"
          value={filters.distance}
          options={[
            ...['20', '50', '100', '200', '350', '500', '750', '1000', '2000', '3000'].map(
              value => ({
                value,
                label: value,
              }),
            ),
            { value: 'NaN_3000+', label: '3000+' },
          ]}
          onChange={(distance: FilterValues['distance']) => setFilters({ ...filters, distance })}
        />
        <DistanceLabel>{t('kilometersOf')}</DistanceLabel>
      </Distance>
      <LocationSearch
        value={locationString}
        onChange={setLocationString}
        onPlaceSelect={location => {
          setLocationString(location.location);
          setFilters({ ...filters, location });
        }}
      />
      <FilterLabel>{t('subcontractor.workCategories')}</FilterLabel>
      <SelectMulti
        placeholder={t('select')}
        options={workCategories.map(({ id, name }: { id: string; name: string }) => ({
          value: id,
          label: name,
        }))}
        name="workCategories"
        value={filters.workCategoryIds}
        onChange={(workCategoryIds: FilterValues['workCategoryIds']) =>
          setFilters({ ...filters, workCategoryIds })
        }
      />
      {process.env.REACT_APP_FEATURE_DISABLED_BIDDER_INVITE_FILTERS !== 'true' && (
        <>
          <FilterLabel>{t('subcontractor.companySize')}</FilterLabel>
          <CheckboxGroup
            value={filters.companySizes}
            onChange={(companySizes: FilterValues['companySizes']) =>
              setFilters({ ...filters, companySizes })
            }
          >
            {({ isChecked, onCheck }) => (
              <>
                {orderedCompanySizes.map(size => (
                  <CompanySizeCheckbox
                    key={size}
                    checkboxLabel={getHumanReadableCompanySize(size)}
                    value={size}
                    checked={isChecked(size)}
                    onChange={onCheck}
                  />
                ))}
              </>
            )}
          </CheckboxGroup>
        </>
      )}
    </FiltersWrapper>
  );
};

export default Filters;
