import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import toast from 'shared/utils/toast';
import { Form, Field, Button } from 'shared/components';

import { mutationUpdatePassword } from './api';

interface FormValues {
  currentPassword: string;
  newPassword: string;
}

const ProfilePassword: React.FC<{}> = () => {
  const { t } = useTranslation('profile');
  const [updatePassword, { loading: mutating }] = useMutation(mutationUpdatePassword);

  return (
    <Form<FormValues>
      initialValues={{
        currentPassword: '',
        newPassword: '',
      }}
      validations={{
        currentPassword: Form.is.required(),
        newPassword: [Form.is.required(), Form.is.maxLength(250)],
      }}
      onSubmit={(values, { resetForm }) =>
        updatePassword({ variables: values }).then(() => {
          resetForm();
          toast.success(t('passwordUpdated'));
        }, toast.error)
      }
    >
      <Form.Element style={{ maxWidth: 500 }}>
        <Field.Input name="currentPassword" label={t('currentPassword')} type="password" />
        <Field.Input name="newPassword" label={t('newPassword')} type="password" />
        <Button type="submit" working={mutating}>
          {t('updatePassword')}
        </Button>
      </Form.Element>
    </Form>
  );
};

export default ProfilePassword;
