import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import axios from 'axios';

import Console from 'shared/utils/console';

import { AppOutdatedBar } from './Styles';

export const CheckPeriodInSeconds = 300;

const useIsOutdated = () => {
  const [isOutdated, setIsOutdated] = useState(false);

  useEffect(() => {
    let version: string | null = null;

    function checkVersion() {
      axios.get('/version').then(
        ({ data, status }: { data: string; status: number }) => {
          if (status === 200 && data) {
            const latestVersion = data.trim();
            if (version && version !== latestVersion) {
              setIsOutdated(true);
            }
            version = latestVersion;
          }
        },
        error => {
          Console.error(`Error while retrieving version number: ${error}`);
        },
      );
    }

    checkVersion();
    setInterval(checkVersion, CheckPeriodInSeconds * 1000);
  }, []);

  return isOutdated;
};

const VersionCheck: React.FC = () => {
  const { t } = useTranslation();
  const isOutdated = useIsOutdated();

  const reload = () => window.location.reload();

  if (isOutdated) {
    return <AppOutdatedBar onClick={reload}>{t('appOutdated')}</AppOutdatedBar>;
  }
  return null;
};

export default VersionCheck;
