import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import toast from 'shared/utils/toast';
import { Form, Field, ModalFormBody } from 'shared/components';
import { HeadingMedium } from 'shared/components/Styles';
import { countryCodes, FormTypes } from 'shared/constants';

import { mutationUpdateOffice, mutationCreateOffice } from './api';
import { OfficesForIndex_offices_offices } from './__graphql__/OfficesForIndex';

interface Props {
  type: FormTypes;
  office?: OfficesForIndex_offices_offices;
  closeModal: () => void;
}

interface FormValues {
  name: string;
  logoKey: string | null;
  email: string;
  phone: string;
  fax: string;
  website: string;
  street: string;
  postalCode: string;
  city: string;
  countryCode: string;
}

const OfficesForm: React.FC<Props> = ({ type, office = {}, closeModal }) => {
  const { t } = useTranslation('company');

  const [createOrUpdateOffice, { loading: mutating }] = useMutation(
    type === FormTypes.edit ? mutationUpdateOffice : mutationCreateOffice,
    {
      refetchQueries: () => ['OfficesForIndex', 'OfficesForProjectForm'],
      awaitRefetchQueries: true,
    },
  );

  return (
    <Form<FormValues>
      initialValues={{
        name: office.name || '',
        logoKey: office.logoKey || null,
        email: office.email || '',
        phone: office.phone || '',
        fax: office.fax || '',
        website: office.website || '',
        street: office.street || '',
        postalCode: office.postalCode || '',
        city: office.city || '',
        countryCode: office.countryCode || '',
      }}
      validations={{
        name: [Form.is.required(), Form.is.maxLength(100)],
        email: Form.is.email(),
      }}
      onSubmit={async (values, form) => {
        await createOrUpdateOffice({ variables: { id: office.id, input: values } })
          .then(() => {
            toast.success(t(type === FormTypes.edit ? 'officeUpdated' : 'officeCreated'));
            closeModal();
          }, toast.error)
          .then(() => form.setSubmitting(false));
      }}
    >
      <ModalFormBody close={closeModal} working={mutating}>
        <HeadingMedium>{t(type === FormTypes.edit ? 'editOffice' : 'newOffice')}</HeadingMedium>
        <Field.Input required name="name" label={t('office.name')} />
        <Field.Input name="email" label={t('office.email')} type="email" />
        <Field.Input name="phone" label={t('office.phone')} />
        <Field.Input name="fax" label={t('office.fax')} />
        <Field.Input name="website" label={t('office.website')} />
        <Field.Input name="street" label={t('office.street')} />
        <Field.Input name="postalCode" label={t('office.postalCode')} />
        <Field.Input name="city" label={t('office.city')} />
        <Field.SelectSingle
          name="countryCode"
          label={t('office.country')}
          options={countryCodes
            .map(code => ({ value: code, label: t(`countries:${code}`) }))
            .sort((country, otherCountry) => country.label.localeCompare(otherCountry.label))}
        />
        <Field.ImageUploader name="logoKey" label={t('office.logo')} />
        <Form.RequiredFieldsNote />
      </ModalFormBody>
    </Form>
  );
};

export default OfficesForm;
