import gql from 'graphql-tag';

export const queryBidPackages = gql`
  query BidPackages(
    $query: String
    $offset: Int!
    $limit: Int!
    $sortBy: String
    $sortDirection: SortDirection
    $projectId: ID!
  ) {
    bidPackages(
      query: $query
      offset: $offset
      limit: $limit
      sortBy: $sortBy
      sortDirection: $sortDirection
      projectId: $projectId
    ) {
      bidPackages {
        id
        number
        name
        status
        bidsDueAt
        requestStatusCount {
          bidders
          viewed
          accepted
          bids
          inviteDeclined
        }
      }
      totalRecords
    }
  }
`;

export const mutationDeleteBidPackage = gql`
  mutation DeleteBidPackage($id: ID!) {
    deleteBidPackage(id: $id)
  }
`;
