import { css } from 'styled-components/macro';
import Color from 'color';

import { COLOR } from './color';
import { font } from './font';

const darken = (colorValue: string, amount: number): string =>
  Color(colorValue)
    .darken(amount)
    .string();

const lighten = (colorValue: string, amount: number): string =>
  Color(colorValue)
    .lighten(amount)
    .string();

const rgba = (colorValue: string, opacity: number): string =>
  Color(colorValue)
    .alpha(opacity)
    .string();

const boxShadowMedium = css`
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1);
`;

const boxShadowBorderMedium = css`
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid ${COLOR.borderLight};
  border-top: 1px solid ${COLOR.borderLightest};
`;

const truncateText = css`
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
`;

const clickable = css`
  cursor: pointer;
  user-select: none;
`;

const hardwareAccelerate = css`
  transform: translateZ(0);
`;

const clearfix = css`
  *zoom: 1;
  &::before,
  &::after {
    content: ' ';
    display: table;
  }
  &::after {
    clear: both;
  }
`;

const cover = css`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
`;

const placeholderColor = (colorValue: COLOR) => css`
  ::-webkit-input-placeholder {
    color: ${colorValue} !important;
    opacity: 1 !important;
  }
  :-moz-placeholder {
    color: ${colorValue} !important;
    opacity: 1 !important;
  }
  ::-moz-placeholder {
    color: ${colorValue} !important;
    opacity: 1 !important;
  }
  :-ms-input-placeholder {
    color: ${colorValue} !important;
    opacity: 1 !important;
  }
`;

const scrollableY = css`
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
`;

interface CustomScrollbarOptions {
  width?: number;
  background?: string;
}
const customScrollbar = ({
  width = 8,
  background = darken(COLOR.backgroundMedium, 0.2),
}: CustomScrollbarOptions = {}) => css`
  &::-webkit-scrollbar {
    width: ${width}px;
  }
  &::-webkit-scrollbar-track {
    background: none;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 99px;
    background: ${background};
  }
`;

const backgroundImage = css`
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: ${COLOR.backgroundLight};
`;

const link = (colorValue: string = COLOR.primary) => css`
  cursor: pointer;
  color: ${colorValue};
  ${font.medium}
  &:hover, &:visited, &:active {
    color: ${colorValue};
  }
  &:hover,
  &:focus {
    text-decoration: underline;
  }
`;

const tag = css`
  display: inline-block;
  height: 24px;
  line-height: 22px;
  padding: 0 6px 0 8px;
  border: 1px solid ${COLOR.borderLight};
  border-radius: 4px;
  cursor: pointer;
  user-select: none;
  background: ${COLOR.backgroundLight};
  ${font.medium}
  ${font.size(12)}
  i {
    margin-left: 4px;
    vertical-align: middle;
    font-size: 14px;
  }
`;

export const mixin = {
  darken,
  lighten,
  rgba,
  boxShadowMedium,
  boxShadowBorderMedium,
  truncateText,
  clickable,
  hardwareAccelerate,
  clearfix,
  cover,
  placeholderColor,
  scrollableY,
  customScrollbar,
  backgroundImage,
  link,
  tag,
};
