import React from 'react';
import { useTranslation } from 'react-i18next';
import { FieldArray } from 'formik';

import { Modal } from 'shared/components';
import { MODAL_VARIANTS } from 'shared/constants';
import useUserAuthStatus from 'shared/hooks/useUserAuthStatus';

import { ProjectForOverview_project_projectUsers } from '../__graphql__/ProjectForOverview';

import ProjectUsersTable from './UsersTable';
import UsersForm from './Form';
import { AddMore } from './Style';
import { UsersForProjectForm_users_users } from './__graphql__/UsersForProjectForm';

interface ProjectUsersProps {
  projectUsers: ProjectForOverview_project_projectUsers[];
  primaryUserId: string;
  setPrimaryUserId: (id: string | undefined) => void;
  isEditing: boolean;
}

export const ProjectUsers: React.FC<ProjectUsersProps> = ({
  projectUsers,
  primaryUserId,
  setPrimaryUserId,
  isEditing,
}) => {
  const { t } = useTranslation('projects');
  const { isUserEmployee } = useUserAuthStatus();

  return (
    <FieldArray name="projectUsers">
      {fieldArrayRenderProps => (
        <>
          <ProjectUsersTable
            projectUsers={projectUsers}
            arrayHelpers={fieldArrayRenderProps}
            primaryUserId={primaryUserId}
            setPrimaryUserId={setPrimaryUserId}
            isEditing={isEditing}
          />
          {isEditing && isUserEmployee && (
            <Modal
              variant={MODAL_VARIANTS.center}
              renderLink={modal => <AddMore onClick={modal.open}>{t('addUserLink')}</AddMore>}
              renderContent={modal => (
                <UsersForm
                  closeModal={modal.close}
                  assignedUsers={projectUsers}
                  modifyUser={(newUser: {
                    user: UsersForProjectForm_users_users;
                    userRole: string;
                  }) => {
                    if (projectUsers.length === 0) {
                      setPrimaryUserId(newUser.user.id);
                    }
                    fieldArrayRenderProps.push(newUser);
                  }}
                />
              )}
            />
          )}
        </>
      )}
    </FieldArray>
  );
};

export default ProjectUsers;
