import React, { forwardRef } from 'react';
import styled from 'styled-components/macro';

import Clickable, { ClickableProps } from 'shared/components/Clickable';
import { COLOR, font, mixin, mediaQueries } from 'shared/utils/styles';
import { As } from 'shared/constants/styledComponents';

export type DropdownActionProps = ClickableProps & {
  isDisplayed: boolean;
  forwardedAs?: As;
};

const DropdownActionBase = forwardRef<HTMLButtonElement, DropdownActionProps>(
  ({ isDisplayed, ...props }, ref) => {
    if (isDisplayed) {
      return <Clickable ref={ref} {...props} />;
    }

    return <Clickable ref={ref} tabIndex={-1} aria-hidden {...props} />;
  },
);

const DropdownAction = styled(DropdownActionBase)<DropdownActionProps>`
  display: block;
  width: 100%;
  color: ${COLOR.textDark};
  white-space: nowrap;
  ${mixin.clickable};

  &.active {
    color: ${COLOR.accent};
  }

  @media ${mediaQueries.mobile} {
    padding: 15px 30px;
    text-align: center;
    ${font.size(16)};
  }

  @media ${mediaQueries.desktop} {
    padding: 6px 20px;
    text-align: left;
    ${font.size(14)};

    &:hover {
      background: ${COLOR.highlightWhite};
    }
  }
`;

export default DropdownAction;
