import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import toast from 'shared/utils/toast';
import { ConfirmationModal } from 'shared/components';

import { mutationPublishBidPackage } from './api';
import { MainActionButton } from './Styles';

interface Props {
  bidPackageId: string;
}

const PublishBidPackageButton: React.FC<Props> = ({ bidPackageId }) => {
  const { t } = useTranslation('bidPackages');
  const [publishBidPackage, { loading }] = useMutation(mutationPublishBidPackage, {
    refetchQueries: () => ['BidRequestsForBidPackage', 'BidPackageForShow', 'ProjectForShow'],
    awaitRefetchQueries: true,
  });

  return (
    <ConfirmationModal
      title={t('publishConfirmTitle')}
      message={t('publishConfirmMessage')}
      confirmText={t('publishAndSendInvitations')}
      isWorking={loading}
      onConfirm={async () => {
        try {
          await publishBidPackage({ variables: { id: bidPackageId } });
          toast.success(t('publishSuccess'));
        } catch (error) {
          toast.error(error);
        }
      }}
      renderLink={modal => (
        <MainActionButton hollow data-cy-button-name="publish" onClick={modal.open}>
          {t('publishAndSendInvitations')}
        </MainActionButton>
      )}
    />
  );
};

export default PublishBidPackageButton;
