import gql from 'graphql-tag';

import { fragmentUsersTableDropdown } from 'shared/components/UsersTableDropdown';

export const queryUsers = gql`
  query Users(
    $query: String
    $offset: Int!
    $limit: Int!
    $sortBy: String
    $sortDirection: SortDirection
  ) {
    users(
      query: $query
      offset: $offset
      limit: $limit
      sortBy: $sortBy
      sortDirection: $sortDirection
    ) {
      users {
        id
        email
        firstName
        lastName
        jobTitle
        type
        status
        ...UsersTableDropdown_user
      }
      totalRecords
    }
  }
  ${fragmentUsersTableDropdown}
`;

export const queryCompany = gql`
  query Company {
    company {
      name
      id
    }
  }
`;

export const mutationUpdateCompany = gql`
  mutation UpdateCompany($id: ID!, $input: CompanyInput!) {
    updateCompany(id: $id, input: $input) {
      name
    }
  }
`;

export const mutationInviteUsers = gql`
  mutation InviteUsers($users: [UserToInviteInput!]!) {
    inviteUsers(users: $users)
  }
`;
