import queryString from 'query-string';

export const queryStringToObject = <S extends object>(str: string, options: object = {}): S =>
  queryString.parse(str, {
    arrayFormat: 'bracket',
    ...options,
  }) as S;
export const objectToQueryString = (obj: object, options: object = {}): string =>
  queryString.stringify(obj, {
    arrayFormat: 'bracket',
    ...options,
  });
