import styled, { css } from 'styled-components/macro';
import { TransitionStatus } from 'react-transition-group/Transition';

import Icon from 'shared/components/Icon';
import { COLOR, mixin, zIndexValues, mediaQueries, sizes } from 'shared/utils/styles';
import { MODAL_VARIANTS } from 'shared/constants';
import { isEnter, isExited } from 'shared/utils/transitionStates';

export const TRANSITION_DURATION = 200;

interface OverlayProps {
  isActive: boolean;
  transitionState: TransitionStatus;
}

export const Overlay = styled.div<OverlayProps>`
  z-index: ${props =>
    props.isActive ? zIndexValues.modalOverlay : zIndexValues.modalOverlayInactive};
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: ${COLOR.overlay};
  transition: opacity ${TRANSITION_DURATION}ms;
  will-change: opacity;
  ${props =>
    isEnter(props.transitionState)
      ? css`
          opacity: 1;
        `
      : css`
          opacity: 0;
          pointer-events: none;
        `}
`;

interface LayoutProps {
  isActive: boolean;
  transitionState: TransitionStatus;
  variant: MODAL_VARIANTS;
}

export const Layout = styled.div<LayoutProps>`
  z-index: ${props => (props.isActive ? zIndexValues.modal : zIndexValues.modalInactive)};
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  ${props => overlayStyles[props.variant]}
  ${mixin.scrollableY}
  ${props =>
    isExited(props.transitionState) &&
    css`
      opacity: 0;
      pointer-events: none;
    `}
`;

const overlayStyles = {
  [MODAL_VARIANTS.center]: css<LayoutProps>`
    display: flex;
    justify-content: center;
    align-items: center;

    @media ${mediaQueries.mobile} {
      padding: 10px;
    }

    @media ${mediaQueries.desktop} {
      padding: 50px;
    }
  `,
  [MODAL_VARIANTS.aside]: css<LayoutProps>``,
  [MODAL_VARIANTS.fullSize]: css<LayoutProps>`
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 25px;
  `,
  [MODAL_VARIANTS.aside]: css<LayoutProps>``,
};

interface CardProps {
  transitionState: TransitionStatus;
  variant: MODAL_VARIANTS;
}

export const Card = styled.div<CardProps>`
  position: relative;
  display: block;
  width: 100%;
  background: #fff;
  transition: transform ${TRANSITION_DURATION}ms, opacity ${TRANSITION_DURATION}ms;
  ${props => cardStyles[props.variant]}
`;

const cardStyles = {
  [MODAL_VARIANTS.center]: css<CardProps>`
    max-width: 600px;
    vertical-align: middle;
    will-change: transform, opacity;
    ${mixin.boxShadowMedium}
    ${props =>
      isEnter(props.transitionState)
        ? css`
            opacity: 1;
            transform: translateY(0);
          `
        : css`
            opacity: 0;
            transform: translateY(50%);
          `}
  `,
  [MODAL_VARIANTS.aside]: css<CardProps>`
    min-height: 100vh;
    max-width: 500px;
    margin-left: auto;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.15);
    will-change: transform;
    ${props =>
      isEnter(props.transitionState)
        ? css`
            transform: translateX(0);
          `
        : css`
            transform: translateX(100%);
          `}
  `,
  [MODAL_VARIANTS.fullSize]: css<CardProps>`
    /* Same as Page component */
    min-width: ${sizes.minViewportWidth}px;
    max-width: 1440px;
    padding: 50px;
    @media (max-width: 1200px) {
      padding: 25px 30px 50px;
    }

    height: calc(100vh - 50px);
    vertical-align: middle;
    will-change: transform, opacity;
    ${mixin.boxShadowMedium};
    ${mixin.scrollableY};
    ${props =>
      isEnter(props.transitionState)
        ? css`
            opacity: 1;
            transform: translateY(0);
          `
        : css`
            opacity: 0;
            transform: translateY(50%);
          `}
  `,
};

interface CloseIconProps {
  variant: MODAL_VARIANTS;
}

export const CloseIcon = styled(Icon)<CloseIconProps>`
  position: absolute;
  font-size: 25px;
  color: ${COLOR.textDark};
  z-index: ${zIndexValues.modalCloseIcon};
  ${mixin.clickable}
  ${props => closeIconStyles[props.variant]}
`;

const closeIconStyles = {
  [MODAL_VARIANTS.center]: css`
    top: 8px;
    right: 10px;
    padding: 7px 7px 0;
  `,
  [MODAL_VARIANTS.aside]: css`
    top: 10px;
    width: 40px;
    height: 40px;
    padding-top: 8px;
    border-radius: 40px;
    text-align: center;
    background: #fff;
    opacity: 0.5;

    @media ${mediaQueries.mobile} {
      left: 10px;
    }

    @media ${mediaQueries.desktop} {
      left: -50px;
    }
  `,
  [MODAL_VARIANTS.fullSize]: css`
    top: 8px;
    right: 10px;
    padding: 7px 7px 0;
  `,
};
