import gql from 'graphql-tag';

export const queryOffices = gql`
  query OfficesForIndex(
    $query: String
    $offset: Int!
    $limit: Int!
    $sortBy: String
    $sortDirection: SortDirection
  ) {
    offices(
      query: $query
      offset: $offset
      limit: $limit
      sortBy: $sortBy
      sortDirection: $sortDirection
    ) {
      offices {
        id
        name
        logoKey
        email
        phone
        fax
        website
        street
        postalCode
        city
        countryCode
      }
      totalRecords
    }
  }
`;

export const mutationCreateOffice = gql`
  mutation CreateOffice($input: OfficeInput!) {
    createOffice(input: $input) {
      id
    }
  }
`;

export const mutationUpdateOffice = gql`
  mutation UpdateOffice($id: ID!, $input: OfficeInput!) {
    updateOffice(id: $id, input: $input) {
      id
    }
  }
`;
