import uuidv4 from 'uuid/v4';

import { ParsedFileTree, Item as SerializedItem } from '../types/parsedFileTree';
import { CreatedDocument } from '../types/createdDocument';
import { DocumentsTable_documents } from '../__graphql__/DocumentsTable_documents';

import Item from './Item';
import File from './File';

interface FolderArgs {
  name: string;
  contents?: ParsedFileTree;
  id: string;
  ancestor?: Folder;
  saveCallback?: (rootFolder: SerializedItem) => void;
}

class Folder extends Item {
  ancestor: Folder | undefined;

  id: string;

  name: string;

  saveCallback: ((rootFolder: SerializedItem) => void) | undefined;

  children: Item[];

  constructor({
    name,
    id,
    contents = [],
    ancestor = undefined,
    saveCallback = undefined,
  }: FolderArgs) {
    super();
    this.ancestor = ancestor;
    this.id = id;
    this.name = name;
    this.children = contents.map(item => {
      if (item.type === 'folder') {
        return new Folder({
          ancestor: this,
          id: item.id,
          name: item.name,
          contents: item.contents,
          saveCallback: this.save,
        });
      }
      return new File({ ancestor: this, id: item.id, name: item.name, saveCallback: this.save });
    });
    this.saveCallback = saveCallback;
  }

  serializable(): SerializedItem {
    return {
      id: this.id,
      type: 'folder',
      name: this.name,
      contents: this.children.map(item => item.serializable()),
    };
  }

  getChildFolders() {
    return this.children
      .filter(item => item instanceof Folder)
      .sort((a, b) => a.name.localeCompare(b.name)) as Folder[];
  }

  getFiles(): File[] {
    return this.children.filter(item => item instanceof File) as File[];
  }

  getFilesWithDocs(documents: DocumentsTable_documents[]): Array<[File, DocumentsTable_documents]> {
    return this.getFiles()
      .sort((a, b) => a.name.localeCompare(b.name))
      .map(file => {
        const doc = documents.find(({ id }) => id === file.id);
        return [file, doc];
      })
      .filter(([, doc]) => doc) as Array<[File, DocumentsTable_documents]>;
  }

  addChild(item: Item) {
    this.children.push(item);
  }

  removeChild(item: Item) {
    this.children = this.children.filter(childItem => childItem.id !== item.id);
  }

  isEmpty() {
    return this.children.length === 0;
  }

  isRoot() {
    return !this.ancestor;
  }

  addDocuments(createdDocuments: CreatedDocument[]) {
    createdDocuments.forEach(({ documentId, sourceDirectoryPathArray, filename }) => {
      const wasUploadedFileInFolder = sourceDirectoryPathArray.length > 0;
      const file = new File({
        ancestor: this,
        id: documentId,
        name: filename,
        saveCallback: this.save,
      });

      if (wasUploadedFileInFolder) {
        this.getOrCreateFolderAtPath(sourceDirectoryPathArray).addChild(file);
      } else {
        this.addChild(file);
      }
    });
    this.save();
  }

  createFolderAndSave(name: string) {
    this.createFolder(name);
    this.save();
  }

  createFolder(name: string) {
    const folder = new Folder({ name, id: uuidv4(), ancestor: this, saveCallback: this.save });
    this.addChild(folder);
    return folder;
  }

  moveTo(folder: Folder) {
    const isMovingToDescendant = this.findItembyId(folder.id);
    if (isMovingToDescendant) return;
    super.moveTo(folder);
  }

  findItembyId(id: string): Item | null {
    if (this.id === id) return this;
    for (const item of this.children) {
      const result = item.findItembyId(id);
      if (result) return result;
    }
    return null;
  }

  getOrCreateFolderAtPath(pathArray: string[]) {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    let currentFolder: Folder = this;

    pathArray.forEach(folderName => {
      const existingFolder = currentFolder.getChildFolders().find(item => item.name === folderName);
      if (existingFolder) {
        currentFolder = existingFolder;
      } else {
        currentFolder = currentFolder.createFolder(folderName);
      }
    });
    return currentFolder;
  }
}

export default Folder;
