import React from 'react';
import { useQuery } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import {
  Tabs,
  HtmlHeadTags,
  DocumentsTableSingle,
  QueryErrors,
  Page,
  PageLoader,
} from 'shared/components';
import { HeadingLarge, SpaceBetween } from 'shared/components/Styles';
import { ERROR_TYPES } from 'shared/constants';
import { PROJECT_STATUS, DOCUMENT_ENTITY } from '__graphql__/globalTypes';
import { doesErrorMatchCode, isDoneLoading } from 'shared/utils/apollo';
import routes from 'shared/constants/routes';
import PageNotFound from 'components/PageNotFound';

import { queryProjectForShow } from './api';
import BidPackages from './BidPackages';
import ProjectStatusAction from './ProjectStatusAction';
import OverviewEdit from './OverviewEdit';
import { ProjectForShow, ProjectForShowVariables } from './__graphql__/ProjectForShow';

const ShowProject: React.FC<void> = () => {
  const { t } = useTranslation('projects');

  const { projectId } = useParams<{ projectId: string }>();

  const { error, ...queryResults } = useQuery<ProjectForShow, ProjectForShowVariables>(
    queryProjectForShow,
    {
      variables: { id: projectId },
    },
  );

  if (error) {
    if (doesErrorMatchCode(error, ERROR_TYPES.notFound)) {
      return <PageNotFound.Authenticated />;
    }

    return <QueryErrors error={error} />;
  }

  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const { data, refetch } = queryResults;
  const { project } = data;

  const isProjectClosed = project.status === PROJECT_STATUS.closed;

  return (
    <Page backLink={{ to: routes.projectsRoot(), label: t('backToProjects') }}>
      <HtmlHeadTags title={project.name} />
      <SpaceBetween>
        <HeadingLarge data-cy-project-name>{project.name}</HeadingLarge>
        <ProjectStatusAction project={project} refetchProject={refetch} />
      </SpaceBetween>
      <Tabs.Container>
        <Tabs.Links>
          <Tabs.Link to="overview">{t('overviewLabel')}</Tabs.Link>
          <Tabs.Link to="bid-packages" isDefault>
            {t('bidPackages')}
          </Tabs.Link>
          <Tabs.Link to="documents">{t('documents')}</Tabs.Link>
        </Tabs.Links>
        <Tabs.Content>
          <Tabs.Route path="overview" render={() => <OverviewEdit projectId={project.id} />} />
          <Tabs.Route
            path="bid-packages"
            render={() => <BidPackages projectId={project.id} projectStatus={project.status} />}
          />
          <Tabs.Route
            path="documents"
            render={() => (
              <DocumentsTableSingle
                attachableType={DOCUMENT_ENTITY.Project}
                attachableId={project.id}
                attachableName={project.name}
                isReadOnly={isProjectClosed}
                printPath={`${routes.printProjectDocuments(project.id)}`}
              />
            )}
          />
        </Tabs.Content>
      </Tabs.Container>
    </Page>
  );
};

export default ShowProject;
