import React from 'react';
import { useQuery, useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';
import { useLocation } from 'react-router-dom';

import { queryStringToObject } from 'shared/utils/url';
import { QueryErrors, PageLoader } from 'shared/components';
import { isDoneLoading } from 'shared/utils/apollo';
import toast from 'shared/utils/toast';
import { BidPackageInput } from '__graphql__/globalTypes';

import { queryBidPackageForOverview, mutationUpdateBidPackage } from './api';
import {
  BidPackageForOverview,
  BidPackageForOverviewVariables,
} from './__graphql__/BidPackageForOverview';
import { UpdateBidPackage, UpdateBidPackageVariables } from './__graphql__/UpdateBidPackage';
import Overview from './Overview';

interface OverviewEditProps {
  bidPackageId: string;
  projectId: string;
}

const OverviewEdit: React.FC<OverviewEditProps> = ({ bidPackageId, projectId }) => {
  const { t } = useTranslation('bidPackages');

  const location = useLocation();

  const query = useQuery<BidPackageForOverview, BidPackageForOverviewVariables>(
    queryBidPackageForOverview,
    { variables: { id: bidPackageId } },
  );

  const [updateBidPackage, { loading: mutating }] = useMutation<
    UpdateBidPackage,
    UpdateBidPackageVariables
  >(mutationUpdateBidPackage, {
    refetchQueries: () => ['BidPackageForShow', 'BidPackageForOverview'],
    awaitRefetchQueries: true,
  });

  if (query.error) {
    return <QueryErrors error={query.error} />;
  }

  if (!isDoneLoading(query) || mutating) {
    return <PageLoader />;
  }

  const { bidPackage } = query.data;

  const submitForm = (input: BidPackageInput) =>
    updateBidPackage({
      variables: {
        id: bidPackageId,
        input,
      },
    }).then(() => {
      toast.success(t('updated'));
    }, toast.error);

  const editByDefault = queryStringToObject<{ edit: boolean }>(location.search).edit;

  return (
    <Overview
      bidPackage={bidPackage}
      projectId={projectId}
      submitForm={submitForm}
      editByDefault={editByDefault}
    />
  );
};

export default OverviewEdit;
