import React from 'react';
import { FieldArray } from 'formik';
import { useTranslation } from 'react-i18next';

import Form from 'shared/components/Form';
import Field from 'shared/components/Field';
import Icon from 'shared/components/Icon';
import InputModal, { InputModalProps } from 'shared/components/InputModal';
import { USER_TYPE } from '__graphql__/globalTypes';

import { FieldRow, DeleteIcon, AddLink } from './Styles';

interface Props {
  loading: boolean;
  renderLink: InputModalProps<FormValues>['renderLink'];
  onSubmit: (values: FormValues, confirm: () => void) => void;
}

interface FormValues {
  users: Array<{ email: string; type: USER_TYPE }>;
}

const getNewUser = () => ({ email: '', type: USER_TYPE.employee });

const UsersInviteModal: React.FC<Props> = ({ loading, renderLink, onSubmit }) => {
  const { t } = useTranslation('company');

  const getFormOptions = (confirm: () => void) => ({
    initialValues: { users: [getNewUser()] },
    onSubmit: (values: FormValues) => onSubmit(values, confirm),
    validations: {
      users: {
        email: [Form.is.required(), Form.is.maxLength(200), Form.is.email()],
        type: Form.is.required(),
      },
    },
  });

  return (
    <InputModal<FormValues>
      getFormOptions={getFormOptions}
      isWorking={loading}
      title="company:inviteUsers"
      confirmText="company:sendInvitations"
      cancelText="company:cancel"
      renderLink={renderLink}
      renderContent={({ values }) => (
        <FieldArray
          name="users"
          render={({ remove, push }) => (
            <>
              {values.users.map((_user, i) => (
                <FieldRow key={i}>
                  <Field.Input
                    name={`users.${i}.email`}
                    type="email"
                    icon="email"
                    placeholder={t('exampleEmail')}
                  />
                  <Field.SelectSingle
                    required
                    name={`users.${i}.type`}
                    options={[
                      { label: t('user.employee'), value: 'employee' },
                      { label: t('user.external'), value: 'external' },
                    ]}
                  />
                  {values.users.length > 1 && (
                    <DeleteIcon type="delete" onClick={() => remove(i)} />
                  )}
                </FieldRow>
              ))}
              <AddLink onClick={() => push(getNewUser())}>
                <Icon type="plus" top={2} left={-2} />
                {t('addAnother')}
              </AddLink>
            </>
          )}
        />
      )}
    />
  );
};

export default UsersInviteModal;
