import React, { useState, useContext } from 'react';
import { useTranslation } from 'react-i18next';

import Icon from 'shared/components/Icon';

import FolderDropzone from './FolderDropzone';
import File from './File';
import FolderRow from './FolderRow';
import DocumentsTableContext from './DocumentsTableContext';
import { RootDropzoneArea } from './Styles';
import Folder from './classes/Folder';

interface Props {
  folder: Folder;
  nestingLevel: number;
}

const FolderWithContents: React.FC<Props> = ({ folder, nestingLevel }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(nestingLevel === 0);
  const { documents } = useContext(DocumentsTableContext);

  const files = folder.getFilesWithDocs(documents);
  const folders = folder.getChildFolders();

  return (
    <>
      <FolderDropzone folder={folder}>
        {folder.isRoot() ? (
          <RootDropzoneArea>
            <Icon top={1} type="upload" />
            {t('documentsTable.dragToRoot')}
          </RootDropzoneArea>
        ) : (
          <FolderRow
            nestingLevel={nestingLevel}
            folder={folder}
            isFolderOpen={open}
            toggleFolder={() => setOpen(!open)}
          />
        )}
        {open &&
          files.map(([file, document]) => (
            <File
              key={document.id}
              nestingLevel={nestingLevel + 1}
              folder={folder}
              file={file}
              document={document}
            />
          ))}
      </FolderDropzone>
      {open &&
        folders.map(item => (
          <FolderWithContents key={item.id} folder={item} nestingLevel={nestingLevel + 1} />
        ))}
    </>
  );
};

export default FolderWithContents;
