import React from 'react';
import { useTranslation } from 'react-i18next';
import { FieldArray } from 'formik';

import { SectionTitle } from 'shared/components/Styles';
import { Modal } from 'shared/components';
import { MODAL_VARIANTS } from 'shared/constants';

import ContactsList from './ContactsList';
import ContactsForm from './ContactsForm';
import { StyledAddMoreLink } from './Styles';
import { SubcontractorForEdit_subcontractor_contacts } from './__graphql__/SubcontractorForEdit';

interface Props {
  values: SubcontractorForEdit_subcontractor_contacts[];
  hasError: boolean;
}

const Contacts: React.FC<Props> = ({ values, hasError }) => {
  const { t } = useTranslation();

  return (
    <FieldArray name="contacts">
      {({ push, remove, replace }) => (
        <>
          <SectionTitle>{t('subcontractorForm.contacts.heading')}</SectionTitle>
          <ContactsList
            contacts={values}
            removeContact={remove}
            replaceContact={replace}
            hasError={hasError}
          />
          <Modal
            variant={MODAL_VARIANTS.center}
            renderContent={modal => (
              <ContactsForm
                allValues={values}
                onSubmit={contactValues => {
                  push(contactValues);
                }}
                closeModal={modal.close}
              />
            )}
            renderLink={modal => (
              <StyledAddMoreLink
                data-cy-add-contact
                onClick={() => {
                  modal.open();
                }}
              >
                {t('subcontractorForm.contacts.addAnotherButton')}
              </StyledAddMoreLink>
            )}
          />
        </>
      )}
    </FieldArray>
  );
};

export default Contacts;
