import React from 'react';
import { useQuery } from '@apollo/react-hooks';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

import { QueryErrors, PageLoader } from 'shared/components';
import { BID_PACKAGE_STATUS } from '__graphql__/globalTypes';
import SubcontractorsList from 'shared/components/SubcontractorsList';
import SubcontractorsListItem from 'shared/components/SubcontractorsListItem';
import { isDoneLoading } from 'shared/utils/apollo';

import ContactAction from './ContactAction';
import { StyledHeadingMedium, ModalDescription } from './Styles';
import { queryBidPackageForAddBidders } from './api';
import {
  BidPackageForAddBidders,
  BidPackageForAddBiddersVariables,
} from './__graphql__/BidPackageForAddBidders';

const propTypes = {
  bidPackageId: PropTypes.string.isRequired,
  bidPackageRefetch: PropTypes.func.isRequired,
};

interface Props {
  bidPackageId: string;
  bidPackageRefetch: () => void;
}

const BiddersAdd: React.FC<Props> = ({ bidPackageId, bidPackageRefetch }) => {
  const { t } = useTranslation('bidPackages');

  const { error, ...queryResults } = useQuery<
    BidPackageForAddBidders,
    BidPackageForAddBiddersVariables
  >(queryBidPackageForAddBidders, {
    variables: { bidPackageId },
    fetchPolicy: 'no-cache',
  });

  if (error) return <QueryErrors error={error} />;

  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const {
    data: { bidPackage },
  } = queryResults;

  return (
    <>
      <StyledHeadingMedium>
        {bidPackage.status !== BID_PACKAGE_STATUS.draft ? t('inviteBidders') : t('addBidders')}
      </StyledHeadingMedium>
      <ModalDescription>
        {bidPackage.status !== BID_PACKAGE_STATUS.draft
          ? t('inviteDescription')
          : t('addDescription')}
      </ModalDescription>
      <SubcontractorsList
        withShadow
        location={{
          lat: bidPackage.project.lat !== null ? String(bidPackage.project.lat) : undefined,
          long: bidPackage.project.long !== null ? String(bidPackage.project.long) : undefined,
          location: bidPackage.project.location,
        }}
      >
        {subcontractors =>
          subcontractors.map(subcontractor => (
            <SubcontractorsListItem
              key={subcontractor.id}
              subcontractor={subcontractor}
              renderContactAction={(contactId, subcontractorId) => (
                <ContactAction
                  bidPackage={bidPackage}
                  bidPackageRefetch={bidPackageRefetch}
                  subcontractorId={subcontractorId}
                  contactId={contactId}
                  isBanned={subcontractor.isBanned}
                />
              )}
            />
          ))
        }
      </SubcontractorsList>
    </>
  );
};

BiddersAdd.propTypes = propTypes;

export default BiddersAdd;
