import {
  TransitionStatus,
  ENTERING,
  ENTERED,
  EXITING,
  EXITED,
} from 'react-transition-group/Transition';

export const isEntering = (state: TransitionStatus): boolean => state === ENTERING;
export const isEntered = (state: TransitionStatus): boolean => state === ENTERED;
export const isExiting = (state: TransitionStatus): boolean => state === EXITING;
export const isExited = (state: TransitionStatus): boolean => state === EXITED;
export const isEnter = (state: TransitionStatus): boolean => isEntering(state) || isEntered(state);
export const isExit = (state: TransitionStatus): boolean => isExiting(state) || isExited(state);
