import React, { useContext } from 'react';
import { useTranslation } from 'react-i18next';

import Icon from 'shared/components/Icon';

import DocumentsTableContext from './DocumentsTableContext';
import DropzoneArea from './DropzoneArea';
import { NoResultsReadOnly, NoResultsUploadable } from './Styles';
import Folder from './classes/Folder';

interface Props {
  rootFolder: Folder;
}

const NoResultsMessage: React.FC<Props> = ({ rootFolder }) => {
  const { t } = useTranslation();

  const { isReadOnly } = useContext(DocumentsTableContext);

  return isReadOnly ? (
    <NoResultsReadOnly>
      <Icon top={2} type="file" />
      {t('documentsTable.noResults')}
    </NoResultsReadOnly>
  ) : (
    <DropzoneArea targetFolder={rootFolder}>
      <NoResultsUploadable>
        <Icon top={2} type="upload" />
        {t('documentsTable.dragToEmptyTable')}
      </NoResultsUploadable>
    </DropzoneArea>
  );
};

export default NoResultsMessage;
