import React, { ReactElement } from 'react';

import Icon, { IconProps } from 'shared/components/Icon';

import { StyledClickable } from './Styles';

interface IconClickableProps {
  className?: string;
  title?: string;
  onClick: (...args: unknown[]) => unknown;
}

const IconClickable = ({
  className = '',
  title,
  onClick,
  ...iconProps
}: IconClickableProps & IconProps): ReactElement => (
  <StyledClickable className={className} onClick={onClick} title={title}>
    <Icon {...iconProps} />
  </StyledClickable>
);

export default IconClickable;
