import React, { ReactNode } from 'react';

import Page, { PageProps } from 'shared/components/Page';
import { ContentWithScroll } from 'shared/components/Styles';

import { SidebarContainer } from './Styles';

interface PageWithSidebarProps {
  className?: string;
  children: ReactNode;
  renderSidebar: () => ReactNode;
}

const PageWithSidebar: React.FC<PageWithSidebarProps & PageProps> = ({
  className,
  children,
  renderSidebar,
  ...pageProps
}) => {
  return (
    <SidebarContainer className={className}>
      {renderSidebar()}
      <ContentWithScroll>
        <Page {...pageProps}>{children}</Page>
      </ContentWithScroll>
    </SidebarContainer>
  );
};

export default PageWithSidebar;
