import { TFunction } from 'i18next';

import { countryCodes } from 'shared/constants';

interface FormatAddressArguments {
  street?: string | null;
  postalCode?: string | null;
  city?: string | null;
  countryCode?: string | null;
  t: TFunction;
}

export const formatAddress = ({
  street,
  postalCode,
  city,
  countryCode,
  t,
}: FormatAddressArguments): string => {
  const addressComponents = [];

  if (street) {
    addressComponents.push(street);
  }

  if (postalCode && city) {
    addressComponents.push(`${postalCode} ${city}`);
  } else if (postalCode) {
    addressComponents.push(postalCode);
  } else if (city) {
    addressComponents.push(city);
  }

  if (countryCode) {
    const foundCountryCode = countryCodes.find(code => code === countryCode);
    if (foundCountryCode) {
      addressComponents.push(t(`countries:${foundCountryCode}`));
    }
  }

  return addressComponents.join(', ');
};
