import React, {
  ReactNode,
  ButtonHTMLAttributes,
  SyntheticEvent,
  AnchorHTMLAttributes,
} from 'react';

import { COLOR } from 'shared/utils/styles';
import Icon, { IconProps } from 'shared/components/Icon';
import { BUTTON_VARIANT } from 'shared/constants';
import { getColorFromButtonVariant } from 'shared/utils/enumHelpers';

import { StyledButton, StyledSpinner } from './Styles';

interface ButtonProps {
  className?: string;
  children?: ReactNode;
  type?: ButtonHTMLAttributes<HTMLButtonElement>['type'];
  hollow?: boolean;
  variant?: BUTTON_VARIANT;
  icon?: IconProps['type'];
  iconSize?: IconProps['size'];
  disabled?: boolean;
  working?: boolean;
  onClick?: (event: SyntheticEvent) => void;
  linkTo?: string;
  target?: AnchorHTMLAttributes<HTMLAnchorElement>['target'];
}

const Button: React.FC<ButtonProps & ButtonHTMLAttributes<HTMLButtonElement>> = ({
  className,
  children,
  type = 'button',
  hollow = false,
  variant = BUTTON_VARIANT.primary,
  icon,
  iconSize,
  disabled = false,
  working = false,
  onClick,
  linkTo,
  ...otherProps
}) => {
  const buttonColor = getColorFromButtonVariant(variant);
  const spinnerColor = hollow ? COLOR.textMediumBlue : COLOR.white;
  return (
    <StyledButton
      className={className}
      type={type}
      hollow={hollow}
      buttonColor={buttonColor}
      disabled={disabled || working}
      working={working}
      onClick={(event: SyntheticEvent) => {
        if (!disabled && !working && onClick) {
          onClick(event);
        }
      }}
      icon={icon}
      iconSize={iconSize}
      iconOnly={!children}
      linkTo={linkTo}
      {...otherProps}
    >
      {working && <StyledSpinner size={26} color={spinnerColor} />}
      {!working && icon && <Icon type={icon} size={iconSize} />}
      {children}
    </StyledButton>
  );
};

export default Button;
