import { useTranslation } from 'react-i18next';
import { FileWithPath } from 'react-dropzone';

import toast from 'shared/utils/toast';
import { createDocumentsFromFiles } from 'services/document';
import { UpdateDocument_updateDocument } from 'services/__graphql__/UpdateDocument';

import Folder from '../classes/Folder';
import { CreatedDocument } from '../types/createdDocument';

import useProgressState from './useProgressState';

export type HandleFilesDrop = (files: File[], targetFolder: Folder) => void;

const useFilesDropHandler = () => {
  const { t } = useTranslation();

  const [progress, updateProgress, resetProgress] = useProgressState();

  const handleFilesDrop: HandleFilesDrop = async (files, targetFolder) => {
    const createdDocuments: CreatedDocument[] = [];

    if (files.length === 0) {
      toast.warning(t('documentsTable.emptyFolderUpload'));
      return;
    }

    updateProgress({ isUploading: true, totalCount: files.length });
    try {
      await createDocumentsFromFiles({
        files,
        onFileUploaded: (
          uploadedCount: number,
          doc: UpdateDocument_updateDocument,
          file: FileWithPath,
        ) => {
          createdDocuments.push({
            documentId: doc.id,
            sourceDirectoryPathArray: (file.path || '')
              .split('/')
              .slice(0, -1) // removing file name
              .filter(part => part !== ''),
            filename: file.name,
          });
          updateProgress({ uploadedCount });
        },
        onFileProgress: (currentFileProgress: number, currentFileName: string) => {
          updateProgress({ currentFileProgress, currentFileName });
        },
      });
      targetFolder.addDocuments(createdDocuments);
      toast.success(t('documentsTable.documentsUploaded'));
    } catch (error) {
      toast.error(error);
    }
    resetProgress();
  };

  return { handleFilesDrop, progress };
};

export default useFilesDropHandler;
