import React from 'react';

const addItemIdToMoveEvent = (event: React.DragEvent, movedItemId: string) => {
  // setData's format argument must be 'text' for IE 11; see https://stackoverflow.com/questions/26213011/html5-dragdrop-issue-in-internet-explorer-datatransfer-property-access-not-pos
  if (event.dataTransfer) {
    event.dataTransfer.setData('text', JSON.stringify({ movedItemId }));
  }
};

export default addItemIdToMoveEvent;
