import { objectToQueryString } from 'shared/utils/url';

// We're defining all possible image sizes here to encourage
// reusing existing sizes to achieve efficient imgix caching
export enum IMAGE_TYPE {
  avatar = 'avatar',
  imageUploader = 'imageUploader',
  logo = 'logo',
  project = 'project',
}

const types = {
  [IMAGE_TYPE.avatar]: { w: 200, h: 200, fit: 'crop', crop: 'faces' },
  [IMAGE_TYPE.imageUploader]: { w: 500 },
  [IMAGE_TYPE.logo]: { w: 300, h: 300, fm: 'png' },
  [IMAGE_TYPE.project]: { w: 700, h: 467 },
};

export const getImageUrl = (storageKey: string, type: IMAGE_TYPE): string => {
  const path = getImagePath(storageKey);
  const query = objectToQueryString({
    fit: 'clip',
    auto: 'format,compress',
    fm: 'jpg',
    ...types[type],
  });
  return `${path}?${query}`;
};

export const getImagePath = (storageKey: string): string =>
  `https://${process.env.REACT_APP_IMGIX_SUBDOMAIN}.imgix.net/${storageKey}`;
