import React from 'react';
import { useApolloClient } from '@apollo/react-hooks';

import GoogleAutoCompleteStyles from 'shared/components/LocationSearch/Styles';
import useAuthentication from 'shared/hooks/useAuthentication';
import useModalsManager from 'shared/hooks/useModalsManager';
import useQueryMatches from 'shared/hooks/useQueryMatches';
import AuthContext from 'shared/contexts/authContext';
import ModalsContext from 'shared/contexts/modalsContext';
import QueryMatchesContext from 'shared/contexts/queryMatchesContext';
import Routes from 'components/Routes';
import { ApolloClientWithLogout } from 'apolloClient';

import VersionCheck from './VersionCheck';
import Toast from './Toast';
import NormalizeStyles from './NormalizeStyles';
import BaseStyles from './BaseStyles';

import './fontStyles.css';

const App = () => {
  const { authState, login, logout } = useAuthentication();
  const modalManagerProps = useModalsManager();
  const queryMatches = useQueryMatches();

  // This is an ugly workaround needed so we can trigger a logout from within
  // the Apollo error link. We should find a better solution if we can.
  const apolloClient: ApolloClientWithLogout = useApolloClient();
  apolloClient.logout = logout;

  return (
    <AuthContext.Provider value={{ authState, login, logout }}>
      <ModalsContext.Provider value={modalManagerProps}>
        <QueryMatchesContext.Provider value={queryMatches}>
          <NormalizeStyles />
          <BaseStyles />
          <GoogleAutoCompleteStyles />
          <VersionCheck />
          <Toast />
          <Routes />
        </QueryMatchesContext.Provider>
      </ModalsContext.Provider>
    </AuthContext.Provider>
  );
};

export default App;
