import styled from 'styled-components/macro';

import { COLOR, font } from 'shared/utils/styles';
import DefaultButton from 'shared/components/Button';

const EmptyContent = styled.div`
  padding: 30px 0;
  text-align: center;
  color: ${COLOR.textLightBlue};
  ${font.size(18)};
`;

const CallToAction = styled.div`
  padding: 15px 30px;
  color: ${COLOR.textDark};
  ${font.bold}
  ${font.size(21)}

  &:first-child {
    padding-top: 0;
  }
`;

const Explanation = styled.div`
  padding: 15px 30px;
  color: ${COLOR.textLight};
  ${font.size(14)}
  line-height: 1.5;
`;

const Button = styled(DefaultButton).attrs(() => ({ hollow: true }))``;

type EmptyContentWithSubcomponents = typeof EmptyContent & {
  CallToAction: typeof CallToAction;
  Explanation: typeof Explanation;
  Button: typeof Button;
};

(EmptyContent as EmptyContentWithSubcomponents).CallToAction = CallToAction;
(EmptyContent as EmptyContentWithSubcomponents).Explanation = Explanation;
(EmptyContent as EmptyContentWithSubcomponents).Button = Button;

