import React, { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { useMutation } from '@apollo/react-hooks';

import DropdownMenu from 'shared/components/DropdownMenu';
import AuthContext from 'shared/contexts/authContext';
import toast from 'shared/utils/toast';

import { mutationAdminLoginAsUser } from './api';

interface Props {
  userId: string;
}

const LoginAsUser: React.FC<Props> = ({ userId }) => {
  const { t } = useTranslation('company');
  const { login, logout } = useContext(AuthContext);
  const [loginAsUser] = useMutation(mutationAdminLoginAsUser);

  return (
    <DropdownMenu.Action
      onClick={() => {
        loginAsUser({ variables: { id: userId } }).then(
          ({ data: { adminLoginAsUser } }) => {
            logout();
            login(adminLoginAsUser);
          },
          error => {
            toast.error(error);
          },
        );
      }}
    >
      {t('admin:loginAsUser')}
    </DropdownMenu.Action>
  );
};

export default LoginAsUser;
