import styled, { css } from 'styled-components/macro';

import {
  BID_REQUEST_STATUS,
  COMPANY_STATUS,
  BID_PACKAGE_STATUS,
  PROJECT_STATUS,
  USER_STATUS,
} from '__graphql__/globalTypes';
import { font, COLOR } from 'shared/utils/styles';

const statusTagListStyles = css`
  margin-left: 15px;
  padding: 4px 8px;
  ${font.size(12)}
`;

const statusTagHeaderStyles = css`
  padding: 6px 12px;
  ${font.size(14)}
`;

interface StatusTagProps {
  inList?: boolean;
  color?: string; // TODO: Explore turning 'color' into an enum
  status: BID_REQUEST_STATUS | COMPANY_STATUS | BID_PACKAGE_STATUS | PROJECT_STATUS | USER_STATUS;
}

export const StatusTag = styled.div<StatusTagProps>`
  display: inline-block;
  vertical-align: middle;
  border-radius: 4px;
  color: #fff;
  background: ${props => props.color};
  ${font.bold}
  ${props => (props.inList ? statusTagListStyles : statusTagHeaderStyles)}
`;

interface UserTagProps {
  status: USER_STATUS;
}
const getUserStatusColor = (status: USER_STATUS) => {
  switch (status) {
    case USER_STATUS.active:
      return COLOR.success;
    case USER_STATUS.inactive:
    case USER_STATUS.inviteFailed:
    case USER_STATUS.inviteCanceled:
      return COLOR.danger;
    default:
      return COLOR.backgroundDark;
  }
};
export const UserStatusTag = styled(StatusTag).attrs(({ status }: UserTagProps) => ({
  color: getUserStatusColor(status),
}))``;

interface CompanyTagProps {
  status: COMPANY_STATUS;
}
const getCompanyStatusColor = (status: COMPANY_STATUS) => {
  switch (status) {
    case COMPANY_STATUS.active:
      return COLOR.success;
    default:
      return COLOR.backgroundDark;
  }
};
export const CompanyStatusTag = styled(StatusTag).attrs(({ status }: CompanyTagProps) => ({
  color: getCompanyStatusColor(status),
}))``;

interface BidRequestTagProps {
  status: BID_REQUEST_STATUS;
}
const getBidRequestStatusColor = (status: BID_REQUEST_STATUS) => {
  switch (status) {
    case BID_REQUEST_STATUS.inviteDeclined:
    case BID_REQUEST_STATUS.withdrawn:
      return COLOR.danger;
    case BID_REQUEST_STATUS.inviteAccepted:
      return COLOR.success;
    case BID_REQUEST_STATUS.bidSubmitted:
      return COLOR.success;
    case BID_REQUEST_STATUS.bidAwarded:
      return COLOR.success;
    default:
      return COLOR.backgroundDark;
  }
};
export const BidRequestStatusTag = styled(StatusTag).attrs(({ status }: BidRequestTagProps) => ({
  color: getBidRequestStatusColor(status),
}))``;

interface BidPackageTagProps {
  status: BID_PACKAGE_STATUS;
}
const getBidPackageStatusColor = (status: BID_PACKAGE_STATUS) => {
  switch (status) {
    case BID_PACKAGE_STATUS.draft:
      return COLOR.backgroundDark;
    case BID_PACKAGE_STATUS.published:
      return COLOR.success;
    case BID_PACKAGE_STATUS.bidAwarded:
      return COLOR.success;
    default:
      return COLOR.success;
  }
};
export const BidPackageStatusTag = styled(StatusTag).attrs(({ status }: BidPackageTagProps) => ({
  color: getBidPackageStatusColor(status),
}))``;

interface ProjectTagProps {
  status: PROJECT_STATUS;
}
const getProjectStatusColor = (status: PROJECT_STATUS) => {
  switch (status) {
    case PROJECT_STATUS.draft:
      return COLOR.backgroundDark;
    case PROJECT_STATUS.published:
      return COLOR.success;
    case PROJECT_STATUS.completed:
      return COLOR.success;
    default:
      return COLOR.success;
  }
};
export const ProjectStatusTag = styled(StatusTag).attrs(({ status }: ProjectTagProps) => ({
  color: getProjectStatusColor(status),
}))``;
