import React from 'react';
import { useQuery } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import { SpaceBetween, TableSearch } from 'shared/components/Styles';
import { Table, Cell, PageLoader, QueryErrors, Icon, Button, Modal } from 'shared/components';
import { formatAddress } from 'shared/utils/address';
import { FormTypes, TABLE_ITEMS_PER_PAGE, MODAL_VARIANTS, SortDirection } from 'shared/constants';
import { isDoneLoading } from 'shared/utils/apollo';

import { queryOffices } from './api';
import Form from './Form';
import { OfficesForIndex } from './__graphql__/OfficesForIndex';

const Offices = () => {
  const { t } = useTranslation('company');

  const { error, ...queryResults } = useQuery<OfficesForIndex>(queryOffices, {
    variables: {
      offset: 0,
      limit: TABLE_ITEMS_PER_PAGE,
      sortBy: 'name',
      sortDirection: SortDirection.asc,
    },
  });

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const { data, refetch, variables } = queryResults;

  return (
    <>
      <SpaceBetween>
        <TableSearch onChange={search => refetch({ query: search })} />
        <Modal
          variant={MODAL_VARIANTS.aside}
          renderContent={modal => <Form type={FormTypes.new} closeModal={modal.close} />}
          renderLink={modal => (
            <Button data-cy-button-name="new" onClick={modal.open}>
              {t('newOffice')}
            </Button>
          )}
        />
      </SpaceBetween>
      <Table
        columns={[
          { label: t('office.name'), sortField: 'name', percentWidth: 30 },
          { label: t('office.address') },
          { fixedWidth: 50 },
        ]}
        rows={data.offices.offices.map(office => (
          <Table.Row rowId={office.id}>
            <Cell.Text>{office.name}</Cell.Text>
            <Cell.Text>{formatAddress({ t, ...office }) || ''}</Cell.Text>
            <Cell.Icon>
              <Modal
                variant={MODAL_VARIANTS.aside}
                renderContent={modal => (
                  <Form type={FormTypes.edit} office={office} closeModal={modal.close} />
                )}
                renderLink={modal => <Icon type="edit" onClick={modal.open} />}
              />
            </Cell.Icon>
          </Table.Row>
        ))}
        totalRecords={data.offices.totalRecords}
        refetch={refetch}
        variables={variables}
      />
    </>
  );
};

export default Offices;
