import React, { FC } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { useQuery } from '@apollo/react-hooks';

import { DOCUMENT_ENTITY } from '__graphql__/globalTypes';
import { DocumentsTableSingle } from 'shared/components/DocumentsTable';
import { isDoneLoading } from 'shared/utils/apollo';
import PageLoader from 'shared/components/PageLoader';
import QueryErrors from 'shared/components/QueryErrors';

import { queryProjectForPrintingDocuments } from './api';
import { ProjectForPrintingDocuments } from './__graphql__/ProjectForPrintingDocuments';

interface PrintDocumentsProps {
  projectId: string;
}

const PrintDocuments: FC<RouteComponentProps<PrintDocumentsProps>> = ({ match }) => {
  const {
    params: { projectId },
  } = match;

  const { error, ...documentsQueryResults } = useQuery<ProjectForPrintingDocuments>(
    queryProjectForPrintingDocuments,
    {
      variables: {
        id: projectId,
      },
    },
  );

  if (error) return <QueryErrors error={error} />;

  if (!isDoneLoading(documentsQueryResults)) return <PageLoader />;

  const {
    data: {
      project: { name },
    },
  } = documentsQueryResults;

  return (
    <DocumentsTableSingle
      attachableId={projectId}
      attachableType={DOCUMENT_ENTITY.Project}
      attachableName={name}
      printDocuments
    />
  );
};

export default PrintDocuments;
