import React from 'react';

import { StyledRadioButton, HiddenInput, Label, StyledRadioGroup } from './Styles';

interface RadioButtonGroupProps<T = string | boolean> {
  className?: string;
  value: T;
  name: string;
  values: Array<{ optionValue: T; label: string }>;
  onChange: (value: T) => void;
}

const RadioButtonGroup: React.FC<RadioButtonGroupProps> = ({
  className,
  value,
  values,
  onChange,
  name,
}) => (
  <StyledRadioGroup className={className}>
    {values.map(({ optionValue, label }, index) => (
      <StyledRadioButton key={index}>
        <HiddenInput
          id={`${name}-${index}`}
          name={name}
          type="radio"
          checked={optionValue === value}
          value={String(optionValue)}
          onChange={() => onChange(optionValue)}
        />
        <Label
          tabIndex={0}
          data-cy-radio-button={`${name}-${optionValue}`}
          htmlFor={`${name}-${index}`}
        >
          {label}
        </Label>
      </StyledRadioButton>
    ))}
  </StyledRadioGroup>
);

export default RadioButtonGroup;
