import React, { ReactNode } from 'react';
import { Field, FieldProps } from 'formik';

import { nullString } from 'shared/constants';

import { Wrapper } from './styles';

const isValueMissing = (value: unknown) => value === undefined || value === null || value === '';

export interface DisplayFieldProps {
  className?: string;
  name: string;
  // We don't know what the value is going to be, and we match the type Formik
  // uses for it
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  mapValue?: (value: any) => ReactNode;
}

const DisplayField: React.FC<DisplayFieldProps> = ({ className, name, mapValue = x => x }) => (
  <Field name={name}>
    {({ field: { value } }: FieldProps) => (
      <Wrapper className={className}>
        {isValueMissing(value) ? nullString : mapValue(value)}
      </Wrapper>
    )}
  </Field>
);

export default DisplayField;
