import React from 'react';
import styled, { css } from 'styled-components/macro';

import Form from 'shared/components/Form';
import { COLOR, mediaQueries } from 'shared/utils/styles';

interface ContainerProps {
  fullHeight: boolean;
}

const ContainerBase: React.FC<ContainerProps> = ({ fullHeight, ...rest }) => (
  <Form.Element {...rest} />
);

export const Container = styled(ContainerBase)<ContainerProps>`
  /* display: flex will cause issues here on IE11 if we don't set height, so
  let's only use it when fullHeight === true */
  ${props =>
    props.fullHeight &&
    css`
      display: flex;
      flex-direction: column;
      height: 100vh;
    `}
`;

interface FormElementsProps {
  scrollable: boolean;
}

export const FormElements = styled.div<FormElementsProps>`
  flex: 1;
  overflow-y: ${props => (props.scrollable ? 'auto' : 'visible')};

  @media ${mediaQueries.mobile} {
    padding: 20px;
    padding-top: 50px;
  }

  @media ${mediaQueries.desktop} {
    padding: 50px;
  }
`;

export const Buttons = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 30px 50px;
  border-top: 1px solid ${COLOR.borderLight};
  background: ${COLOR.backgroundLight};
`;
