import React from 'react';

import Icon from 'shared/components/Icon';
import AddMoreLink from 'shared/components/AddMoreLink';

import { Item, OnCreate } from './types';
import SelectBase from './SelectBase';
import { ValueMulti, ValueMultiItem } from './Styles';

interface SelectMultiProps {
  className?: string;
  name: string;
  value: string[];
  placeholder?: string;
  options: Item[];
  onChange: (value: string[]) => void;
  onCreate?: OnCreate;
}

const SelectMulti: React.FC<SelectMultiProps> = ({
  className,
  name,
  value,
  placeholder,
  options,
  onChange,
  onCreate,
}) => {
  const selectedItems = options.filter(option => value.includes(option.value));
  const remainingOptions = options.filter(option => !value.includes(option.value));
  const isValueEmpty = selectedItems.length === 0;

  const removeItem = (item: Item) => {
    onChange(value.filter(optionValue => optionValue !== item.value));
  };

  return (
    <SelectBase
      className={className}
      name={name}
      options={remainingOptions}
      placeholder={placeholder}
      onSelect={selectedOption => {
        onChange(selectedOption ? [...value, selectedOption.value] : []);
      }}
      onCreate={onCreate}
      selectedItem={selectedItems}
      isValueEmpty={isValueEmpty}
      renderValue={() => (
        <ValueMulti>
          {selectedItems.map(item => (
            <ValueMultiItem
              key={item.value}
              onClick={event => {
                removeItem(item);
                event.stopPropagation();
              }}
            >
              {item.label}
              <Icon type="close" />
            </ValueMultiItem>
          ))}
          {remainingOptions.length !== 0 && <AddMoreLink />}
        </ValueMulti>
      )}
      showChevron={isValueEmpty}
      showNullOption={false}
      closeDropdownOnSelect={false}
      getIsLabelInOptions={label =>
        options.some(item => item.label.toLowerCase() === label.toLowerCase())
      }
    />
  );
};

export default SelectMulti;
