import React from 'react';
import { useTranslation } from 'react-i18next';

import { COMPANY_TYPE } from '__graphql__/globalTypes';
import routes from 'shared/constants/routes';
import { COLOR } from 'shared/utils/styles';

import { Bar, LogoLink, StyledLogo, IconLink, LinkText, StyledIcon } from './Styles';

interface NavbarProps {
  companyType: COMPANY_TYPE;
}

const Navbar: React.FC<NavbarProps> = ({ companyType }) => {
  const { t } = useTranslation();

  return (
    <Bar data-cy-main-navbar>
      <LogoLink to={routes.root()}>
        <StyledLogo color={COLOR.white} />
      </LogoLink>
      {companyType === COMPANY_TYPE.principal && (
        <>
          <IconLink to={routes.projectsRoot()}>
            <StyledIcon type="archive" size={16} />
            <LinkText>{t('projects:projects')}</LinkText>
          </IconLink>
          <IconLink to={routes.subcontractors()}>
            <StyledIcon type="briefcase" size={16} />
            <LinkText>{t('subcontractors:subcontractors')}</LinkText>
          </IconLink>
          <IconLink to={routes.bids()}>
            <StyledIcon type="file-text" size={20} left={-2} />
            <LinkText>{t('bids:bids')}</LinkText>
          </IconLink>
        </>
      )}
      {companyType === COMPANY_TYPE.agent && (
        <IconLink to={routes.bidRequests()}>
          <StyledIcon type="file-text" size={20} left={-2} />
          <LinkText>{t('bidInvites:requests.bidRequests')}</LinkText>
        </IconLink>
      )}
    </Bar>
  );
};

export default Navbar;
