import React from 'react';
import { Link } from 'react-router-dom';

import Icon, { IconProps } from 'shared/components/Icon';

interface IconLinkProps {
  className?: string;
  to: string;
  replace?: boolean;
}

const IconLink: React.FC<IconLinkProps & IconProps> = ({ className, to, replace, ...props }) => (
  <Link className={className} to={to} replace={replace}>
    <Icon {...props} />
  </Link>
);

export default IconLink;
