import React, { FunctionComponent } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useQuery } from '@apollo/react-hooks';

import { isDoneLoading } from 'shared/utils/apollo';
import { getContactNameWithTitle } from 'shared/utils/getContactName';
import { QueryErrors, PageLoader, PrintDialog } from 'shared/components';
import { SortDirection, nullString } from 'shared/constants';
import { formatCurrency } from 'shared/components/CurrencyFormatter';

import { queryBidRequestsForPrint, queryBidPackageHeaderForPrint } from './api';
import {
  Header,
  OuterTable,
  InnerTable,
  TableHeader,
  BiddersPrintContainer,
  BidRequestRow,
  ContactRow,
  Cell,
  StatusColumn,
  NameColumn,
  EmailColumn,
  PhoneColumn,
  FaxColumn,
  CurrentBidColumn,
} from './Print.styles';
import {
  BidRequestsForPrint,
  BidRequestsForPrint_bidRequests_bidRequests_bidInvites,
} from './__graphql__/BidRequestsForPrint';
import { BidPackageHeaderForPrint } from './__graphql__/BidPackageHeaderForPrint';

interface BiddersRouteProps {
  bidPackageId: string;
}

function hasContactEmail<T extends { contact: { email: string | null } }>(
  bidInvite: T,
): bidInvite is T & {
  contact: {
    email: string;
  };
} {
  return !!bidInvite.contact.email;
}

const filterAndSortBidInvites = (
  bidInvites: BidRequestsForPrint_bidRequests_bidRequests_bidInvites[],
) =>
  bidInvites.filter(hasContactEmail).sort((a, b) => {
    if (a.contact.email < b.contact.email) {
      return -1;
    }
    if (a.contact.email > b.contact.email) {
      return 1;
    }
    return 0;
  });

export const PrintBidders: FunctionComponent<RouteComponentProps<BiddersRouteProps>> = ({
  match,
}) => {
  const { t, i18n } = useTranslation('bidInvites');

  const { error: biddersError, ...biddersQueryResults } = useQuery<BidRequestsForPrint>(
    queryBidRequestsForPrint,
    {
      variables: {
        bidPackageId: match.params.bidPackageId,
        sortBy: 'subcontractorName',
        sortDirection: SortDirection.asc,
      },
      fetchPolicy: 'no-cache',
    },
  );

  const { error: bidPackageHeaderError, ...bidPackageHeaderResults } = useQuery<
    BidPackageHeaderForPrint
  >(queryBidPackageHeaderForPrint, {
    variables: {
      bidPackageId: match.params.bidPackageId,
    },
    fetchPolicy: 'no-cache',
  });

  const error = biddersError || bidPackageHeaderError;

  if (error) return <QueryErrors error={error} />;

  if (!isDoneLoading(biddersQueryResults) || !isDoneLoading(bidPackageHeaderResults))
    return <PageLoader />;

  const {
    data: {
      bidRequests: { bidRequests },
    },
  } = biddersQueryResults;

  const {
    data: {
      bidPackage: {
        name: bidPackageName,
        project: { name: projectName },
      },
    },
  } = bidPackageHeaderResults;

  const ColumnGroup = () => (
    <colgroup>
      <StatusColumn />
      <NameColumn />
      <EmailColumn />
      <PhoneColumn />
      <FaxColumn />
      <CurrentBidColumn />
    </colgroup>
  );

  return (
    <BiddersPrintContainer>
      <PrintDialog />
      <Header>
        {t('printBidders.title', { bidPackage: `${projectName} - ${bidPackageName}` })}
      </Header>
      <OuterTable>
        <ColumnGroup />
        <thead>
          <tr>
            <TableHeader>{t('printBidders.tableHeader.status')}</TableHeader>
            <TableHeader>{t('printBidders.tableHeader.name')}</TableHeader>
            <TableHeader>{t('printBidders.tableHeader.email')}</TableHeader>
            <TableHeader>{t('printBidders.tableHeader.phone')}</TableHeader>
            <TableHeader>{t('printBidders.tableHeader.fax')}</TableHeader>
            <TableHeader>{t('printBidders.tableHeader.currentBid')}</TableHeader>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td colSpan={6}>
              {bidRequests.map(({ id, status, subcontractor, currentBid, bidInvites }) => (
                <InnerTable key={id}>
                  <ColumnGroup />
                  <BidRequestRow>
                    <Cell>{t(`request.${status}`)}</Cell>
                    <Cell colSpan={2}>{subcontractor.name}</Cell>
                    <Cell />
                    <Cell />
                    <Cell>
                      {typeof currentBid?.totalAmount === 'number'
                        ? formatCurrency(i18n.language, currentBid.totalAmount)
                        : nullString}
                    </Cell>
                  </BidRequestRow>
                  {filterAndSortBidInvites(bidInvites).map(
                    (
                      { contact: { isPerson, title, firstName, lastName, email, phone, fax } },
                      index,
                    ) => {
                      return (
                        <ContactRow key={index}>
                          <Cell />
                          <Cell>
                            {isPerson
                              ? getContactNameWithTitle({ firstName, lastName, isPerson, title })
                              : nullString}
                          </Cell>
                          <Cell>{email}</Cell>
                          <Cell>{phone || nullString}</Cell>
                          <Cell>{fax || nullString}</Cell>
                          <Cell />
                        </ContactRow>
                      );
                    },
                  )}
                </InnerTable>
              ))}
            </td>
          </tr>
        </tbody>
      </OuterTable>
    </BiddersPrintContainer>
  );
};
