import React from 'react';
import { useTranslation } from 'react-i18next';
import { useMutation } from '@apollo/react-hooks';

import toast from 'shared/utils/toast';
import { Form, Field, ModalFormBody, Modal } from 'shared/components';
import { ModalProps } from 'shared/components/Modal';
import { HeadingMedium } from 'shared/components/Styles';
import { MODAL_VARIANTS } from 'shared/constants';
import { mutationUpdateBidRequestDeclinedReasonBySubcontractor } from 'shared/utils/queries';
import {
  mutationUpdateBidRequestDeclinedReasonVariables,
  mutationUpdateBidRequestDeclinedReason,
} from 'shared/utils/__graphql__/mutationUpdateBidRequestDeclinedReason';

import { DeclinedReasonAsk } from './DeclinedReasonForm.styles';
import { BidRequestForSubcontractor_bidRequestForSubcontractor } from './__graphql__/BidRequestForSubcontractor';

interface Props {
  renderLink: NonNullable<ModalProps['renderLink']>;
  bidRequest: BidRequestForSubcontractor_bidRequestForSubcontractor;
  onSubmit: () => Promise<void>;
}

const DeclinedReasonForm: React.FC<Props> = ({ renderLink, bidRequest, onSubmit }) => {
  const { t } = useTranslation('bidInvites');
  const [mutateDeclinedReason, { loading }] = useMutation<
    mutationUpdateBidRequestDeclinedReason,
    mutationUpdateBidRequestDeclinedReasonVariables
  >(mutationUpdateBidRequestDeclinedReasonBySubcontractor, {
    refetchQueries: () => ['BidRequestForSubcontractor'],
    awaitRefetchQueries: true,
  });

  return (
    <Modal
      renderLink={modal => renderLink(modal)}
      variant={MODAL_VARIANTS.center}
      renderContent={modal => (
        <Form<{ declinedReason: string }>
          initialValues={{
            declinedReason: bidRequest.declinedReason || '',
          }}
          onSubmit={async ({ declinedReason }) => {
            try {
              await mutateDeclinedReason({ variables: { id: bidRequest.id, declinedReason } });
              toast.success(t('overview.declinedReasonSuccess'));
              await onSubmit();
            } catch (error) {
              toast.error(error);
            }
            modal.close();
          }}
        >
          <ModalFormBody useFullPageHeight={false} working={loading} close={modal.close}>
            <HeadingMedium>{t('overview.declinedReasonHeader')}</HeadingMedium>
            <DeclinedReasonAsk>{t('overview.declinedReasonAsk')}</DeclinedReasonAsk>
            <Field.Textarea name="declinedReason" />
          </ModalFormBody>
        </Form>
      )}
    />
  );
};

export default DeclinedReasonForm;
