import React from 'react';
import gql from 'graphql-tag';
import { useTranslation } from 'react-i18next';

import { COMPANY_TYPE } from '__graphql__/globalTypes';
import { ThreadsList_messageThread } from 'shared/components/Messages/__graphql__/ThreadsList_messageThread';

import FormattedMessageTime from './FormattedMessageTime';
import {
  ThreadsListWrapper,
  OtherParticipant,
  Subject,
  MessageSnippet,
  ThreadHeader,
  Time,
  ThreadsListItem,
} from './ThreadsList.styles';

export const fragmentThreadsList = gql`
  fragment ThreadsList_messageThread on MessageThread {
    id
    subject
    participatingSubcontractors {
      id
      name
    }
    participatingCompanyOffice {
      id
      name
    }
    lastMessage {
      id
      createdAt
      messageText
      recipientNames
    }
  }
`;

export interface ThreadsListProps {
  threads: ThreadsList_messageThread[];
  onSelectThread: (id: string) => void;
  activeUserType: COMPANY_TYPE;
  selectedThreadId: string | null;
}

const ThreadsList: React.FC<ThreadsListProps> = ({
  threads,
  onSelectThread,
  activeUserType,
  selectedThreadId,
}) => {
  const { t } = useTranslation('messages');

  const getOtherParticipantNames = (thread: ThreadsList_messageThread): string => {
    if (activeUserType === COMPANY_TYPE.principal) {
      if (thread.lastMessage.recipientNames.length === 1) {
        return thread.lastMessage.recipientNames[0];
      }

      return t('threadsList.threadParticipants', {
        name: thread.lastMessage.recipientNames[0],
        numberOfRemainingParticipants: thread.lastMessage.recipientNames.length - 1,
      });
    }

    return thread.participatingCompanyOffice.name;
  };

  return (
    <ThreadsListWrapper data-cy-threads-list>
      {threads.map(thread => {
        const otherParticipantNames = getOtherParticipantNames(thread);

        return (
          <ThreadsListItem
            key={thread.id}
            onClick={() => onSelectThread(thread.id)}
            isSelected={thread.id === selectedThreadId}
          >
            <ThreadHeader>
              <OtherParticipant>{otherParticipantNames}</OtherParticipant>
              <Time data-cy-threads-list-time>
                <FormattedMessageTime date={thread.lastMessage.createdAt} shortFormat />
              </Time>
            </ThreadHeader>
            <Subject>{thread.subject}</Subject>
            <MessageSnippet>{thread.lastMessage.messageText}</MessageSnippet>
          </ThreadsListItem>
        );
      })}
    </ThreadsListWrapper>
  );
};

export default ThreadsList;
