import gql from 'graphql-tag';

import { fragmentCurrentUserForAuthentication } from 'shared/hooks/useAuthentication';

export const queryBidInvite = gql`
  query BidInvite($bidInviteId: ID!) {
    bidInvite(bidInviteId: $bidInviteId) {
      id
      bidRequest {
        id
        bidPackage {
          id
          project {
            id
            name
            forceSubcontractorSignup
            office {
              id
              name
            }
          }
        }
      }
    }
  }
`;

export const mutationClaimBidInvite = gql`
  mutation ClaimBidInvite($bidInviteId: ID!) {
    claimBidInvite(bidInviteId: $bidInviteId)
  }
`;

export const mutationAgentSignup = gql`
  mutation AgentSignup($input: AgentSignupInput!) {
    agentSignup(input: $input) {
      token
      user {
        ...CurrentUserForAuthentication
      }
    }
  }
  ${fragmentCurrentUserForAuthentication}
`;
