import { createContext } from 'react';

import { DocumentsTable_documents } from './__graphql__/DocumentsTable_documents';
import Item from './classes/Item';
import { HandleFilesDrop } from './hooks/useFilesDropHandler';
import { MobileNavigation } from './hooks/useMobileNavigation';
import { MoveHandler } from './utils/getMoveHandler';

interface DocumentsTableContextProps {
  documents: DocumentsTable_documents[];
  isReadOnly: boolean;
  downloadURL: (item: Item) => string;
  handleFilesDrop: HandleFilesDrop;
  handleFileOrFolderMove: MoveHandler;
  refetch: () => Promise<unknown>;
  mobileNavigation: MobileNavigation;
}

const DocumentsTableContext = createContext<DocumentsTableContextProps>({
  documents: [],
  isReadOnly: false,
  downloadURL: () => '',
  handleFilesDrop: () => {},
  handleFileOrFolderMove: () => {},
  refetch: async () => {},
  mobileNavigation: {
    getIsOpen: () => false,
    toggleFolder: () => {},
    getIsCurrentlySelected: () => false,
    currentlyVisibleRows: 0,
    setCurrentlyVisibleRows: () => {},
    rootId: '',
  },
});

export default DocumentsTableContext;
