import React, { useRef } from 'react';
import Cleave from 'cleave.js/react';
import { useTranslation } from 'react-i18next';

import Input, { InputProps, InputRenderProps } from 'shared/components/Input';

const formatWithLocale = (locale: string, value: number | string) =>
  value === '' ? '' : new Intl.NumberFormat(locale).format(Number(value));

// As the Intl API does not provide any introspection capabilities, we have to use slightly hacky
// ways of getting localized separators from it. In the future, using `formatToParts` might be
// better, but it is not supported on IE11.
const getDecimalSeparator = (locale: string) => {
  const numThatIncludesDecimalSeparator = formatWithLocale(locale, 1.5);

  return numThatIncludesDecimalSeparator.replace(/\d/g, '');
};

const getNumeralGroupSeparator = (locale: string) => {
  const numThatIncludesGroupSeparator = formatWithLocale(locale, 1000000000);

  return numThatIncludesGroupSeparator.replace(/\d/g, '')[0];
};

interface CurrencyInputProps {
  value: string;
}

const CurrencyInput: React.FC<CurrencyInputProps & InputProps> = ({ value, ...props }) => {
  const { i18n } = useTranslation();
  const locale = i18n.language;
  const formattedValue = useRef(formatWithLocale(locale, value));
  const renderInput = ({ onChange, ...inputProps }: InputRenderProps) => (
    <Cleave
      {...inputProps}
      ref={inputProps.ref as React.ComponentPropsWithRef<typeof Cleave>['ref']}
      value={formattedValue.current}
      onChange={event => {
        formattedValue.current = event.target.value;

        onChange(event.target.rawValue, event);
      }}
      options={{
        numeral: true,
        numeralThousandsGroupStyle: 'thousand',
        numeralDecimalMark: getDecimalSeparator(locale),
        delimiter: getNumeralGroupSeparator(locale),
      }}
    />
  );

  return <Input {...props} renderInput={renderInput} />;
};

export default CurrencyInput;
