import gql from 'graphql-tag';
import axios from 'axios';
import { FileWithPath } from 'react-dropzone';

import apolloClient from 'apolloClient';

import { UpdateDocument_updateDocument } from './__graphql__/UpdateDocument';

const mutationCreateDocument = gql`
  mutation CreateDocument($input: CreateDocumentInput!) {
    createDocument(input: $input) {
      id
      uploadUrl
    }
  }
`;

const mutationUpdateDocument = gql`
  mutation UpdateDocument($id: ID!, $input: UpdateDocumentInput!) {
    updateDocument(id: $id, input: $input) {
      id
    }
  }
`;

const createDocument = async (file: File) => {
  const response = await apolloClient.mutate({
    mutation: mutationCreateDocument,
    variables: {
      input: { filename: encodeURIComponent(file.name), size: file.size },
    },
  });
  return response.data.createDocument;
};

const uploadDocumentToStorage = async (
  file: File,
  uploadUrl: string,
  onFileProgress: (complete: number, fileName: string) => void,
) =>
  axios.put(uploadUrl, file, {
    headers: {
      'Content-Type': file.type,
      'Content-Disposition': `attachment; filename="${encodeURIComponent(
        file.name,
      )}"; filename*=UTF-8''${encodeURIComponent(file.name)}`,
    },
    onUploadProgress: progressEvent => {
      const percentComplete = progressEvent.total
        ? Math.round((progressEvent.loaded * 100) / progressEvent.total)
        : 0;

      onFileProgress(percentComplete, file.name);
    },
  });

const updateDocument = async (documentId: string) =>
  apolloClient.mutate({
    mutation: mutationUpdateDocument,
    variables: {
      id: documentId,
      input: { uploaded: true },
    },
  });

export const createDocumentsFromFiles = async ({
  files,
  onFileUploaded,
  onFileProgress,
}: {
  files: File[];
  onFileUploaded: (
    count: number,
    document: UpdateDocument_updateDocument,
    file: FileWithPath,
  ) => void;
  onFileProgress: (currentFileProgress: number, currentFileName: string) => void;
}) => {
  let completedCount = 0;

  for (const file of files) {
    const { data } = await createDocumentFromFile(file, onFileProgress);
    completedCount += 1;
    onFileUploaded(completedCount, data.updateDocument, file);
  }
};

const createDocumentFromFile = async (
  file: File,
  onFileProgress: (complete: number, fileName: string) => void,
) => {
  const data = await createDocument(file);
  await uploadDocumentToStorage(file, data.uploadUrl, onFileProgress);
  return updateDocument(data.id);
};
