import React, { ReactElement } from 'react';

import { Header, HeaderText, SortIcon } from './Styles';
import { GetSortIcon, ChangeSort, Column } from './types';

interface HeadersProps {
  columns: Column[];
  getSortIcon: GetSortIcon;
  changeSort: ChangeSort;
}

const Headers = ({ columns, getSortIcon, changeSort }: HeadersProps): ReactElement => {
  return (
    <thead>
      <tr>
        {columns.map((column, i) => (
          <Header
            key={i}
            percentWidth={column.percentWidth}
            fixedWidth={column.fixedWidth}
            textAlign={column.textAlign}
            style={column.style}
          >
            {column.label && (
              <HeaderText hasSort={!!column.sortField} onClick={() => changeSort(column.sortField)}>
                {column.label}
                {column.sortField && <SortIcon type={getSortIcon(column.sortField)} />}
              </HeaderText>
            )}
          </Header>
        ))}
      </tr>
    </thead>
  );
};

export default Headers;
