import styled from 'styled-components/macro';
import { NavLink } from 'react-router-dom';

import { font, sizes, COLOR, mixin, zIndexValues, mediaQueries } from 'shared/utils/styles';
import Logo from 'shared/components/Logo';
import Icon, { IconProps } from 'shared/components/Icon';

const desktopBarHeight = 60;

export const Bar = styled.aside`
  z-index: ${zIndexValues.navbar};
  position: absolute;
  background: ${COLOR.primary};

  @media ${mediaQueries.mobile} {
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    height: ${sizes.navbarHeightMobile}px;
    width: 100%;
    overflow-x: auto;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.6);
  }

  @media ${mediaQueries.desktop} {
    top: 0;
    left: 0;
    height: 100%;
    width: ${sizes.navbarWidthDesktop}px;
    transition: all 0.1s;
    overflow-x: hidden;
    ${mixin.hardwareAccelerate}
    &:hover {
      width: 260px;
      box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.6);
    }
  }
`;

export const LogoLink = styled(NavLink)`
  display: block;
  position: relative;
  left: 0;
  margin: 40px 0 40px;
  transition: left 0.1s;
  &::before {
    display: inline-block;
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    height: 50px;
    width: 20px;
    background: ${COLOR.primary};
  }
  ${/* sc-selector */ Bar}:hover & {
    left: 3px;
    &::before {
      display: none;
    }
  }

  @media ${mediaQueries.mobile} {
    display: none;
  }
`;

export const StyledLogo = styled(Logo)`
  display: inline-block;
  margin-left: 13px;
  padding: 10px;
  ${mixin.clickable}
`;

export const IconLink = styled(NavLink)`
  display: flex;
  align-items: center;
  position: relative;
  color: rgba(255, 255, 255, 0.75);
  transition: color 0.1s;
  ${mixin.clickable};

  @media ${mediaQueries.mobile} {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-width: 70px;
    max-width: 140px;
    margin: 0 5px;
    padding: 0 10px;
    text-align: center;

    &.active {
      color: #fff;
    }

    &::before {
      content: '';
      display: none;
      position: absolute;
      bottom: 0;
      left: 0;
      height: 2px;
      width: 100%;
      background: #fff;
      border-radius: 6px 6px 0 0;
    }

    &.active::before {
      display: inline-block;
    }
  }

  @media ${mediaQueries.desktop} {
    width: 100%;
    height: ${desktopBarHeight}px;
    line-height: ${desktopBarHeight}px;
    padding-left: 67px;

    &::before {
      content: '';
      display: none;
      position: absolute;
      top: 5px;
      right: 0;
      height: 50px;
      width: 5px;
      background: #fff;
      border-radius: 6px 0 0 6px;
    }
    &.active,
    &:hover {
      color: #fff;
    }
    &.active::before {
      display: inline-block;
    }
    &:hover {
      background: ${COLOR.highlightPrimary};
    }
  }
`;

export const StyledIcon = styled(Icon)<IconProps>`
  @media ${mediaQueries.mobile} {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    margin-top: 5px;
  }

  @media ${mediaQueries.desktop} {
    position: absolute;
    left: 27px;
    top: ${props => (desktopBarHeight - (props.size || 16)) / 2}px;
  }
`;

export const LinkText = styled.div`
  text-transform: uppercase;
  white-space: no-wrap;
  ${font.bold};
  ${font.size(12)};

  @media ${mediaQueries.mobile} {
    margin: 5px 0 5px;
    ${mixin.truncateText}
  }

  @media ${mediaQueries.desktop} {
    position: relative;
    right: 12px;
    visibility: hidden;
    opacity: 0;
    transition: all 0.1s;
    transition-property: right, visibility, opacity;

    ${/* sc-selector */ Bar}:hover & {
      right: 0;
      visibility: visible;
      opacity: 1;
    }
  }
`;
