import React, { useContext } from 'react';
import { useQuery, useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';
import { useLocation } from 'react-router-dom';

import AuthContext, { isAuthenticated } from 'shared/contexts/authContext';
import { QueryErrors, PageLoader } from 'shared/components';
import { isDoneLoading } from 'shared/utils/apollo';
import toast from 'shared/utils/toast';
import { queryStringToObject } from 'shared/utils/url';
import { ProjectInput } from '__graphql__/globalTypes';

import Overview from './Overview';
import {
  queryOfficesForProjectOverview,
  mutationUpdateProject,
  queryProjectForOverview,
} from './api';
import { ProjectForOverview, ProjectForOverviewVariables } from './__graphql__/ProjectForOverview';

interface OverviewEditProps {
  projectId: string;
}

const OverviewEdit: React.FC<OverviewEditProps> = ({ projectId }) => {
  const { t } = useTranslation('projects');

  const location = useLocation();

  const authContext = useContext(AuthContext);

  const projectQuery = useQuery<ProjectForOverview, ProjectForOverviewVariables>(
    queryProjectForOverview,
    {
      variables: { id: projectId },
    },
  );

  const officesQuery = useQuery(queryOfficesForProjectOverview, {
    variables: { offset: 0, limit: 200 },
    fetchPolicy: 'no-cache',
  });

  const [updateProject, { loading: mutating }] = useMutation(mutationUpdateProject, {
    refetchQueries: () => ['ProjectForShow', 'ProjectForOverview'],
    awaitRefetchQueries: true,
  });

  if (!isAuthenticated(authContext)) {
    return null;
  }

  if (projectQuery.error) {
    return <QueryErrors error={projectQuery.error} />;
  }

  if (officesQuery.error) {
    return <QueryErrors error={officesQuery.error} />;
  }

  if (!isDoneLoading(officesQuery) || !isDoneLoading(projectQuery) || mutating) {
    return <PageLoader />;
  }

  const {
    authState: { user },
  } = authContext;

  const {
    data: { offices },
  } = officesQuery;

  const {
    data: { project },
  } = projectQuery;

  const submitForm = (input: ProjectInput) =>
    updateProject({
      variables: {
        id: project.id,
        input,
      },
    }).then(() => {
      toast.success(t('updated'));
    }, toast.error);

  const editByDefault = queryStringToObject<{ edit: boolean }>(location.search).edit;

  return (
    <Overview
      project={project}
      offices={offices}
      currentUser={user}
      submitForm={submitForm}
      editByDefault={editByDefault}
    />
  );
};

export default OverviewEdit;
