import React, { useState, useCallback, useRef, ReactNode } from 'react';
import { useTranslation } from 'react-i18next';

import { MODAL_VARIANTS, KEY_CODES, BUTTON_VARIANT } from 'shared/constants';
import { useModalState, ModalProps } from 'shared/components/Modal';
import useKeyboardEventCallback from 'shared/hooks/useKeyboardEventCallback';

import { StyledModalContent, Title, Message, StyledButton } from './Styles';

export interface ConfirmationModalConfirmHandlerParam {
  close: () => void;
}

type HandleConfirm = (modal: ConfirmationModalConfirmHandlerParam) => void;

export interface ConfirmationModalProps {
  className?: string;
  title?: string;
  message?: string | ReactNode;
  confirmText?: string;
  cancelText?: string;
  confirmationButtonVariant?: BUTTON_VARIANT;
  onConfirm: HandleConfirm;
  onCancel?: () => void;
  renderLink: ModalProps['renderLink'];
  renderContent?: () => ReactNode;
  isWorking?: boolean;
}

const ConfirmationModal: React.FC<ConfirmationModalProps> = ({
  className = '',
  title = 'warning',
  message = 'confirmMessage',
  confirmText = 'confirm',
  cancelText = 'cancel',
  confirmationButtonVariant = BUTTON_VARIANT.primary,
  onConfirm,
  onCancel = () => {},
  renderLink,
  isWorking: isWorkingProp = undefined,
  renderContent = () => null,
}) => {
  const { t } = useTranslation();
  const modalRef = useRef<HTMLDivElement>(null);
  const [isWorkingState, setIsWorkingState] = useState(false);
  const isWorking = isWorkingProp === undefined ? isWorkingState : isWorkingProp;
  const { isOpen, openModal, closeModal, isActive } = useModalState(undefined, modalRef);

  const handleCancel = useCallback(() => {
    setIsWorkingState(false);
    closeModal();
    onCancel();
  }, [closeModal, onCancel]);

  const handleConfirm = useCallback(() => {
    setIsWorkingState(true);
    onConfirm({
      close: () => {
        if (modalRef.current) {
          setIsWorkingState(false);
          closeModal();
        }
      },
    });
  }, [onConfirm, closeModal]);

  const handleEnter = () => {
    if (isActive && !isWorking) {
      handleConfirm();
    }
  };

  useKeyboardEventCallback(document, KEY_CODES.enter, handleEnter, { condition: isOpen });

  return (
    <StyledModalContent
      className={className}
      isOpen={isOpen}
      openModal={openModal}
      closeModal={handleCancel}
      isActive={isActive}
      variant={MODAL_VARIANTS.center}
      renderLink={renderLink}
      modalCardRef={modalRef}
      renderContent={() => (
        <>
          <Title>{t(title)}</Title>
          {message && <Message>{t(message as string)}</Message>}
          {renderContent()}
          <StyledButton hollow onClick={handleCancel} data-testid="cancel-button">
            {t(cancelText)}
          </StyledButton>
          <StyledButton
            variant={confirmationButtonVariant}
            working={isWorking}
            onClick={handleConfirm}
            data-cy-button-name="confirm"
            data-testid="confirm-button"
          >
            {t(confirmText)}
          </StyledButton>
        </>
      )}
    />
  );
};

export default ConfirmationModal;
