import React from 'react';
import { useTranslation } from 'react-i18next';
import { useQuery } from '@apollo/react-hooks';

import {
  HtmlHeadTags,
  Table,
  Cell,
  RowBadge,
  BidModal,
  QueryErrors,
  PageLoader,
  Page,
} from 'shared/components';
import { HeadingLarge } from 'shared/components/Styles';
import CurrencyFormatter from 'shared/components/CurrencyFormatter';
import EmptyContent from 'shared/components/EmptyContent';
import { formatDateTime } from 'shared/utils/dateTime';
import { TABLE_ITEMS_PER_PAGE, SortDirection } from 'shared/constants';
import { isDoneLoading } from 'shared/utils/apollo';

import { queryBids } from './api';
import { Bids as BidsQuery } from './__graphql__/Bids';

export function hasTotalAmount<T extends { totalAmount: number | null }>(
  bid: T,
): bid is T & { totalAmount: number } {
  return bid.totalAmount !== null;
}

const Bids: React.FC<void> = () => {
  const { t } = useTranslation('bids');
  const { error, ...queryResults } = useQuery<BidsQuery>(queryBids, {
    variables: {
      offset: 0,
      limit: TABLE_ITEMS_PER_PAGE,
      sortBy: 'submittedAt',
      sortDirection: SortDirection.desc,
    },
  });

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const {
    bids: { bids, totalRecords },
  } = queryResults.data;

  return (
    <Page>
      <HtmlHeadTags title="bids" />
      <HeadingLarge data-cy-bids-title>{t('bids')}</HeadingLarge>
      <Table
        columns={[
          { label: t('tableHeader.project'), testIdentifier: 'project', sortField: 'projectName' },
          { label: t('tableHeader.bidPackage'), testIdentifier: 'bidPackage' },
          { label: t('tableHeader.subcontractor'), testIdentifier: 'subcontractor' },
          { label: t('tableHeader.amount'), testIdentifier: 'amount' },
          { label: t('tableHeader.date'), testIdentifier: 'date', sortField: 'submittedAt' },
        ]}
        rows={bids.filter(hasTotalAmount).map(bid => (
          <Table.Row rowId={bid.id}>
            <RowBadge displayName={bid.bidRequest.bidPackage.project.name} />
            <Cell.Text>{bid.bidRequest.bidPackage.name}</Cell.Text>
            <Cell.Text>{bid.bidRequest.subcontractor.name}</Cell.Text>
            <BidModal
              bid={bid}
              subcontractor={bid.bidRequest.subcontractor}
              bidInvites={bid.bidRequest.bidInvites}
              renderLink={modal => (
                <Cell.Link onClick={modal.open}>
                  <CurrencyFormatter value={bid.totalAmount} />
                </Cell.Link>
              )}
            />
            <Cell.Text data-cy-submitted-at-timestamp={bid.submittedAt}>
              {formatDateTime(bid.submittedAt)}
            </Cell.Text>
          </Table.Row>
        ))}
        totalRecords={totalRecords}
        refetch={queryResults.refetch}
        variables={queryResults.variables}
        contentIfEmpty={() => (
          <>
            <EmptyContent.CallToAction>{t('emptyContentTitle')}</EmptyContent.CallToAction>
            <EmptyContent.Explanation>{t('emptyContentExplanation')}</EmptyContent.Explanation>
          </>
        )}
      />
    </Page>
  );
};

export default Bids;
