import React, { useContext } from 'react';
import gql from 'graphql-tag';
import { useTranslation } from 'react-i18next';
import { useQuery } from '@apollo/react-hooks';

import QueryMatchesContext from 'shared/contexts/queryMatchesContext';
import { COMPANY_TYPE } from '__graphql__/globalTypes';
import { isDoneLoading } from 'shared/utils/apollo';
import PageLoader from 'shared/components/PageLoader';
import QueryErrors from 'shared/components/QueryErrors';
import IconClickable from 'shared/components/IconClickable';

import FormattedMessageTime from './FormattedMessageTime';
import ThreadReplyForm from './ThreadReplyForm';
import {
  Subject,
  Message,
  MessageHeader,
  MessageHeaderFirstRow,
  Author,
  Time,
  Recipient,
  StyledAvatar,
  MessageContent,
  avatarSize,
  ReplyWrapper,
  ThreadDetailsWrapper,
  MessageText,
  GoBackWrapper,
} from './ThreadDetails.styles';
import { MessageThreadInMessages } from './__graphql__/MessageThreadInMessages';

const queryMessageThread = gql`
  query MessageThreadInMessages($messageThreadId: ID!) {
    messageThread(messageThreadId: $messageThreadId) {
      id
      subject
      participatingSubcontractors {
        id
        name
      }
      messages {
        id
        createdAt
        authorType
        authorName
        recipientNames
        messageText
      }
    }
  }
`;

export interface ThreadDetailsProps {
  messageThreadId: string;
  activeUserType: COMPANY_TYPE;
  refetchQueries: string[];
  unselectThread: () => void;
}

const ThreadDetails: React.FC<ThreadDetailsProps> = ({
  messageThreadId,
  activeUserType,
  refetchQueries,
  unselectThread,
}) => {
  const { t } = useTranslation('messages');
  const { isMobile } = useContext(QueryMatchesContext);

  const query = useQuery<MessageThreadInMessages>(queryMessageThread, {
    variables: { messageThreadId },
  });

  if (!isDoneLoading(query)) {
    return <PageLoader />;
  }

  if (query.error) {
    return <QueryErrors error={query.error} />;
  }

  const thread = query.data.messageThread;

  return (
    <ThreadDetailsWrapper data-cy-thread-details>
      <Subject>
        {isMobile && (
          <GoBackWrapper>
            <IconClickable onClick={unselectThread} type="arrow-left" size={24} />
          </GoBackWrapper>
        )}
        {thread.subject}
      </Subject>
      {thread.messages.map(message => {
        const from = message.authorName;
        const to =
          message.authorType !== activeUserType
            ? t('messageList.toMe')
            : t('messageList.toAnother', {
                names: message.recipientNames.join(', '),
              });

        return (
          <Message key={message.id}>
            <StyledAvatar name={from} size={avatarSize} />
            <MessageContent>
              <MessageHeader>
                <MessageHeaderFirstRow>
                  <Author>{from}</Author>
                  <Time data-cy-thread-details-time>
                    <FormattedMessageTime date={message.createdAt} />
                  </Time>
                </MessageHeaderFirstRow>
                <Recipient>{to}</Recipient>
              </MessageHeader>
              <MessageText>{message.messageText}</MessageText>
            </MessageContent>
          </Message>
        );
      })}
      <ReplyWrapper>
        <ThreadReplyForm
          activeUserType={activeUserType}
          messageThreadId={messageThreadId}
          refetchQueries={refetchQueries}
        />
      </ReplyWrapper>
    </ThreadDetailsWrapper>
  );
};

export default ThreadDetails;
