import React, { useContext } from 'react';
import { useTranslation } from 'react-i18next';

import toast from 'shared/utils/toast';
import ConfirmationModal from 'shared/components/ConfirmationModal';

import NameFormModal from './NameFormModal';
import DropzoneLink from './DropzoneLink';
import DocumentsTableContext from './DocumentsTableContext';
import { ItemActions, ItemActionsAction, ItemActionsTrigger } from './Styles';
import Item from './classes/Item';
import Folder from './classes/Folder';

interface Props {
  item: Item;
}

const ItemControls: React.FC<Props> = ({ item }) => {
  const { t } = useTranslation();

  const { isReadOnly, downloadURL } = useContext(DocumentsTableContext);

  return (
    <ItemActions
      renderTrigger={({ isTriggered, toggleTrigger }) => (
        <ItemActionsTrigger
          hollow
          data-cy-dropdown-trigger
          icon={isTriggered ? 'chevron-up' : 'chevron-down'}
          iconSize={18}
          onClick={toggleTrigger}
        />
      )}
    >
      <ItemActionsAction forwardedAs="a" data-cy-button-name="download" href={downloadURL(item)}>
        {t('documentsTable.download')}
      </ItemActionsAction>
      {!isReadOnly && (
        <>
          <NameFormModal
            name={item.name}
            formLabel={t(
              `documentsTable.${item instanceof Folder ? 'renameFolder' : 'renameFile'}`,
            )}
            onSubmit={newName => {
              item.updateName(newName);
            }}
            renderLink={modal => (
              <ItemActionsAction data-cy-button-name="rename" onClick={modal.open}>
                {t('documentsTable.rename')}
              </ItemActionsAction>
            )}
          />
          <ConfirmationModal
            title="confirmDeleteMessage"
            message="confirmDeleteDescription"
            confirmText="delete"
            onConfirm={() => {
              item.delete();
              toast.success(t('documentsTable.documentsRemoved'));
            }}
            renderLink={modal => (
              <ItemActionsAction data-cy-button-name="delete" onClick={modal.open}>
                {t('documentsTable.delete')}
              </ItemActionsAction>
            )}
          />
          {item instanceof Folder && (
            <>
              <DropzoneLink targetFolder={item} inputProps={{}}>
                <ItemActionsAction>{t('documentsTable.uploadFiles')}</ItemActionsAction>
              </DropzoneLink>
              <DropzoneLink targetFolder={item} inputProps={{ webkitdirectory: '' }}>
                <ItemActionsAction>{t('documentsTable.uploadFolder')}</ItemActionsAction>
              </DropzoneLink>
              <NameFormModal
                formLabel="documentsTable.createFolder"
                onSubmit={newFolderName => item.createFolderAndSave(newFolderName)}
                renderLink={modal => (
                  <ItemActionsAction data-cy-button-name="createSubfolder" onClick={modal.open}>
                    {t('documentsTable.createFolder')}
                  </ItemActionsAction>
                )}
              />
            </>
          )}
        </>
      )}
    </ItemActions>
  );
};

export default ItemControls;
