import React from 'react';
import { useQuery } from '@apollo/react-hooks';

import PageLoader from 'shared/components/PageLoader';
import QueryErrors from 'shared/components/QueryErrors';
import { isDoneLoading } from 'shared/utils/apollo';
import { buildAttachableName } from 'shared/utils/documents';
import { DOCUMENT_ENTITY } from '__graphql__/globalTypes';

import DocumentsTable from './DocumentsTable';
import { queryCombinedFileTreeWithDocuments } from './api';
import Folder from './classes/Folder';
import {
  CombinedFileTreeWithDocuments,
  CombinedFileTreeWithDocumentsVariables,
} from './__graphql__/CombinedFileTreeWithDocuments';

interface Attachable {
  attachableType: DOCUMENT_ENTITY;
  attachableId: string;
  attachableName: string;
}

interface Props {
  attachables: Attachable[];
}

const DocumentsTableCombined: React.FC<Props> = ({ attachables }) => {
  const { error, ...queryResults } = useQuery<
    CombinedFileTreeWithDocuments,
    CombinedFileTreeWithDocumentsVariables
  >(queryCombinedFileTreeWithDocuments, {
    variables: {
      attachables: attachables.map(({ attachableId, attachableType }) => ({
        attachableId,
        attachableType,
      })),
    },
  });

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const { data, refetch } = queryResults;

  const projectAttachable = attachables.find(({ attachableType: type }) => type === 'Project');
  const projectName = projectAttachable ? projectAttachable.attachableName : undefined;
  const bidPackageAttachable = attachables.find(
    ({ attachableType: type }) => type === 'BidPackage',
  );
  const bidPackageName = bidPackageAttachable ? bidPackageAttachable.attachableName : undefined;

  const name = buildAttachableName([projectName, bidPackageName]);

  const parsedFileTree = data.combinedFileTreeWithDocuments.fileTrees.flatMap(fileTree => {
    return JSON.parse(fileTree.fileTree);
  });

  const rootFolder = new Folder({ id: '_root', name: 'root', contents: parsedFileTree });

  return (
    <DocumentsTable
      name={name}
      fileTreeIds={data.combinedFileTreeWithDocuments.fileTrees.map(fileTree => fileTree.id)}
      isReadOnly
      rootFolder={rootFolder}
      documents={data.combinedFileTreeWithDocuments.documents}
      refetch={refetch}
    />
  );
};

export default DocumentsTableCombined;
