import React from 'react';
import { useTranslation } from 'react-i18next';
import { useMutation } from '@apollo/react-hooks';

import { Modal, Form, Field, ModalFormBody } from 'shared/components';
import { HeadingMedium } from 'shared/components/Styles';
import { ModalContentProps } from 'shared/components/Modal/ModalContent';
import { BID_REQUEST_STATUS } from '__graphql__/globalTypes';
import toast from 'shared/utils/toast';

import { mutationUpdateBidRequestStatusInBidders } from './api';
import { BidRequestsForBidPackage_bidRequests_bidRequests } from './__graphql__/BidRequestsForBidPackage';
import {
  UpdateBidRequestStatusInBidders,
  UpdateBidRequestStatusInBiddersVariables,
} from './__graphql__/UpdateBidRequestStatusInBidders';

interface Props {
  bidRequest: BidRequestsForBidPackage_bidRequests_bidRequests;
  renderLink: ModalContentProps['renderLink'];
}

interface FormValues {
  status: BID_REQUEST_STATUS;
  declinedReason?: string | null;
}

const ChangeStatusModal: React.FC<Props> = ({ bidRequest, renderLink }) => {
  const { t } = useTranslation('bidPackages');

  const [changeStatus, { loading: mutating }] = useMutation<
    UpdateBidRequestStatusInBidders,
    UpdateBidRequestStatusInBiddersVariables
  >(mutationUpdateBidRequestStatusInBidders, {
    refetchQueries: ['BidRequestsForBidPackage'],
  });

  return (
    <Modal
      renderLink={renderLink}
      renderContent={modal => (
        <Form<FormValues>
          initialValues={{
            status: bidRequest.status,
            declinedReason: bidRequest.declinedReason,
          }}
          validations={{
            status: [Form.is.required()],
          }}
          onSubmit={async values => {
            try {
              await changeStatus({ variables: { id: bidRequest.id, ...values } });
              toast.success(t('changeStatusSuccess'));
              modal.close();
            } catch (error) {
              toast.error(error);
            }
          }}
        >
          {({ values }) => (
            <>
              <ModalFormBody
                scrollable={false}
                close={modal.close}
                working={mutating}
                useFullPageHeight={false}
              >
                <HeadingMedium>{t('changeStatus')}</HeadingMedium>
                <Field.SelectSingle
                  name="status"
                  label={t('request.status')}
                  options={[
                    BID_REQUEST_STATUS.invited,
                    BID_REQUEST_STATUS.inviteAccepted,
                    BID_REQUEST_STATUS.inviteDeclined,
                    BID_REQUEST_STATUS.undecided,
                    BID_REQUEST_STATUS.withdrawn,
                  ].map(status => ({
                    value: status,
                    label: t(`request.${status}`),
                  }))}
                />
                {values.status === BID_REQUEST_STATUS.inviteDeclined && (
                  <Field.Textarea name="declinedReason" label={t('requestDeclinedReason')} />
                )}
              </ModalFormBody>
            </>
          )}
        </Form>
      )}
    />
  );
};

export default ChangeStatusModal;
