import React from 'react';
import { useQuery } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import { TABLE_ITEMS_PER_PAGE, SortDirection } from 'shared/constants';
import { isDoneLoading } from 'shared/utils/apollo';
import { PageLoader, QueryErrors, Table, Cell } from 'shared/components';
import { TableSearch } from 'shared/components/Styles';
import { BidRequestStatusTag } from 'shared/components/TagStyles';

import { queryAdminBidRequests } from './api';
import Dropdown from './Dropdown';
import { AdminBidRequests as AdminBidRequestsData } from './__graphql__/AdminBidRequests';

const AdminBidRequests: React.FC = () => {
  const { t } = useTranslation('admin');

  const { error, ...queryResults } = useQuery<AdminBidRequestsData>(queryAdminBidRequests, {
    variables: {
      offset: 0,
      limit: TABLE_ITEMS_PER_PAGE,
      sortBy: 'projectName',
      sortDirection: SortDirection.asc,
    },
  });

  if (error) return <QueryErrors error={error} />;

  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const {
    refetch,
    variables,
    data: {
      adminBidRequests: { bidRequests, totalRecords },
    },
  } = queryResults;

  return (
    <>
      <TableSearch onChange={(search: string) => refetch({ query: search || undefined })} />
      <Table
        columns={[
          { label: t('bidRequestTableHeader.project'), sortField: 'projectName' },
          { label: t('bidRequestTableHeader.bidPackage'), sortField: 'bidPackageName' },
          { label: t('bidRequestTableHeader.subcontractor'), sortField: 'subcontractorName' },
          { label: t('request.status') },
          { fixedWidth: 50 },
        ]}
        rows={bidRequests.map(bidRequest => {
          const {
            id: bidRequestId,
            status,
            bidPackage: {
              name: bidPackageName,
              project: { name: projectName },
            },
            subcontractor: { name: subcontractorName },
          } = bidRequest;
          return (
            <Table.Row rowId={bidRequestId}>
              <Cell.Text>{projectName}</Cell.Text>
              <Cell.Text>{bidPackageName}</Cell.Text>
              <Cell.Text>{subcontractorName}</Cell.Text>
              <BidRequestStatusTag inList status={status}>
                {t(`request.${status}`)}
              </BidRequestStatusTag>
              <Dropdown bidRequest={bidRequest} />
            </Table.Row>
          );
        })}
        totalRecords={totalRecords}
        refetch={refetch}
        variables={variables}
      />
    </>
  );
};

export default AdminBidRequests;
