import { useState, useEffect } from 'react';
import { useApolloClient } from '@apollo/react-hooks';
import gql from 'graphql-tag';

import history from 'browserHistory';
import { trackPageview, trackUserIdentity, resetUserIdentity } from 'shared/utils/analytics';
import Console from 'shared/utils/console';
import { AUTH_STATUS } from 'shared/constants';
import useAuthToken from 'shared/hooks/useAuthToken';
import routes from 'shared/constants/routes';
import { COMPANY_TYPE } from '__graphql__/globalTypes';

import { CurrentUserForAuthentication } from './__graphql__/CurrentUserForAuthentication';
import { CurrentUserForReAuthentication } from './__graphql__/CurrentUserForReAuthentication';
import { RefreshToken } from './__graphql__/RefreshToken';

const mutationRefresh = gql`
  mutation RefreshToken {
    refreshToken
  }
`;

export const fragmentCurrentUserForAuthentication = gql`
  fragment CurrentUserForAuthentication on User {
    id
    email
    firstName
    lastName
    jobTitle
    phone
    fax
    intercomHash
    company {
      id
      name
      type
    }
    isAdmin
    type
  }
`;

const queryCurrentUserForReAuthentication = gql`
  query CurrentUserForReAuthentication {
    me {
      ...CurrentUserForAuthentication
    }
  }
  ${fragmentCurrentUserForAuthentication}
`;

export interface LoginParams {
  token: string;
  user: CurrentUserForAuthentication;
  redirectTo?: string;
}

export interface AuthState {
  status: AUTH_STATUS;
  companyType?: COMPANY_TYPE;
  isAdmin?: boolean;
  user?: CurrentUserForAuthentication;
}

const useAuthentication = () => {
  const [authState, setAuthState] = useState<AuthState>({ status: AUTH_STATUS.authenticating });
  const apolloClient = useApolloClient();
  const { isTokenValid, getTimeToExpiration, removeStoredToken } = useAuthToken();

  const login = ({ token, user, redirectTo = routes.root() }: LoginParams) => {
    if (user) {
      trackUserIdentity(user);
    }
    localStorage.setItem('token', token);
    setAuthState({
      status: AUTH_STATUS.authenticated,
      companyType: COMPANY_TYPE[user.company.type],
      isAdmin: user.isAdmin,
      user,
    });

    history.push(redirectTo);
  };

  const logout = () => {
    resetUserIdentity();
    removeStoredToken();
    setAuthState({ status: AUTH_STATUS.anonymous, isAdmin: undefined });
    // eslint-disable-next-line @typescript-eslint/no-floating-promises
    apolloClient.resetStore();
    history.push(routes.login());
  };

  useEffect(() => {
    const refresh = () =>
      apolloClient
        .mutate<RefreshToken>({ mutation: mutationRefresh })
        .then(({ data }) => {
          if (data?.refreshToken) {
            localStorage.setItem('token', data.refreshToken);
          }
        });

    const refreshIfTokenIsValidAndHourPassed = () => {
      if (isTokenValid() && getTimeToExpiration() < 23 * 60 * 60) {
        // eslint-disable-next-line @typescript-eslint/no-floating-promises
        refresh();
      }
    };

    // The reason we need this function is related to user tracking for analytics.
    // The first time that `trackPageview` is called must happen after `trackUserIdentity`
    // is called, otherwise the first pageview has no user data associated with it.
    const setFirstAuthState = (newAuthState: AuthState) => {
      trackPageview();
      setAuthState(newAuthState);
    };

    setTimeout(refreshIfTokenIsValidAndHourPassed, 1000);
    setInterval(refreshIfTokenIsValidAndHourPassed, 1000 * 60 * 10);

    if (!isTokenValid()) {
      removeStoredToken();
      setFirstAuthState({ status: AUTH_STATUS.anonymous });
      return;
    }

    apolloClient
      .query({ query: queryCurrentUserForReAuthentication })
      .then(({ data }: { data: CurrentUserForReAuthentication }) => {
        trackUserIdentity(data.me);
        setFirstAuthState({
          status: AUTH_STATUS.authenticated,
          companyType: COMPANY_TYPE[data.me.company.type],
          isAdmin: data.me.isAdmin,
          user: data.me,
        });
      })
      .catch(error => {
        Console.error(error);
        setFirstAuthState({ status: AUTH_STATUS.anonymous });
      });
  }, [apolloClient, getTimeToExpiration, isTokenValid, removeStoredToken]);

  return { authState, login, logout };
};

export default useAuthentication;
