import React from 'react';
import gql from 'graphql-tag';
import { useTranslation } from 'react-i18next';

import { Text, Name, Anchor } from './Styles';
import { SubcontractorInfo_subcontractorInfo } from './__graphql__/SubcontractorInfo_subcontractorInfo';

const fragmentSubcontractorInfo = gql`
  fragment SubcontractorInfo_subcontractorInfo on Subcontractor {
    name
    street
    postalCode
    city
    countryCode
    website
  }
`;

export interface SubcontractorInfoProps {
  subcontractor: SubcontractorInfo_subcontractorInfo;
}

const SubcontractorInfo = ({
  subcontractor: { name, street, postalCode, city, countryCode, website },
}: SubcontractorInfoProps) => {
  const { t } = useTranslation();

  return (
    <div>
      <Name>{name}</Name>
      {street && <Text>{street}</Text>}
      {(postalCode || city) && <Text>{`${postalCode || ''} ${city || ''}`}</Text>}
      {countryCode && <Text>{t(`countries:${countryCode}`)}</Text>}
      <br />
      {website && (
        <Text>
          {`${t('companyInfo.website')}: `}
          <Anchor to={website}>{website}</Anchor>
        </Text>
      )}
    </div>
  );
};

export { fragmentSubcontractorInfo };
export default SubcontractorInfo;
