import React from 'react';
import { useTranslation } from 'react-i18next';
import gql from 'graphql-tag';

import { formatDate } from 'shared/utils/dateTime';
import BidModal, { fragmentBidModal, BidModalProps } from 'shared/components/BidModal';
import Cell from 'shared/components/Table/Cell';
import CurrencyFormatter from 'shared/components/CurrencyFormatter';
import Table from 'shared/components/Table';
import Row from 'shared/components/Table/Row';

import { TotalAmountCell } from './Styles';

const fragmentBidsList = gql`
  fragment BidsList_bidsList on BidRequest {
    currentBidId
    ...BidModal_bidRequest
    bids {
      id
      totalAmount
      submittedAt
      ...BidModal_bid
    }
  }
  ${fragmentBidModal}
`;

interface Bid {
  id: string;
  totalAmount: number;
  submittedAt: string;
}

interface BidsListProps {
  isResponsive?: boolean;
  bids: Array<BidModalProps['bid'] & Bid>;
  subcontractor: BidModalProps['subcontractor'];
  currentBidId: string | null;
  bidInvites: BidModalProps['bidInvites'];
}

const BidsList: React.FC<BidsListProps> = ({
  bids,
  currentBidId,
  subcontractor,
  bidInvites,
  isResponsive = false,
}) => {
  const { t } = useTranslation();
  const currentBid = bids.find(({ id }) => id === currentBidId);
  const previousBids = bids.filter(({ id }) => id !== currentBidId);
  const sortedBids = previousBids.sort(
    (a, b) => new Date(b.submittedAt).getTime() - new Date(a.submittedAt).getTime(),
  );
  if (currentBid) {
    sortedBids.unshift(currentBid);
  }

  const bidRows = sortedBids.map(bid => (
    <Row rowId={bid.id}>
      <BidModal
        bid={bid}
        subcontractor={subcontractor}
        bidInvites={bidInvites}
        renderLink={modal => (
          <TotalAmountCell data-cy-button-name="totalAmount" onClick={modal.open}>
            <CurrencyFormatter value={bid.totalAmount} />
            {bid.id === currentBidId ? ` ${t('bidList.latest')}` : ''}
          </TotalAmountCell>
        )}
      />
      <Cell.Text>{formatDate(bid.submittedAt)}</Cell.Text>
    </Row>
  ));
  return (
    <Table
      data-cy-bids-list
      isResponsive={isResponsive}
      columns={[{ label: t('bid.totalAmount'), percentWidth: 58 }, { label: t('bid.submittedOn') }]}
      rows={bidRows}
    />
  );
};

export { fragmentBidsList };
export default BidsList;
