import React from 'react';
import { useTranslation } from 'react-i18next';

import Form from 'shared/components/Form';
import Field from 'shared/components/Field';

import InputModal, { InputModalProps } from '../InputModal';

interface FormValues {
  name?: string;
}

interface Props {
  name?: string;
  formLabel: string;
  renderLink: InputModalProps<FormValues>['renderLink'];
  onSubmit: (name: string) => void;
}

const NameFormModal: React.FC<Props> = ({ name, formLabel, renderLink, onSubmit }) => {
  const { t } = useTranslation();

  const getFormOptions: InputModalProps<FormValues>['getFormOptions'] = confirm => ({
    initialValues: { name: name || '' },
    validations: {
      name: [Form.is.required(), Form.is.maxLength(50)],
    },
    onSubmit: values => {
      onSubmit((values.name || '').trim());
      confirm();
    },
  });

  return (
    <InputModal
      getFormOptions={getFormOptions}
      title={formLabel}
      renderLink={renderLink}
      renderContent={() => <Field.Input label={t('documentsTable.name')} name="name" autoFocus />}
    />
  );
};

export default NameFormModal;
