import React, { useState, useRef } from 'react';

import { StyledDragHighlightZone } from './Styles';

interface Props {
  onDrop: (event: React.DragEvent) => void;
  children: React.ReactNode;
}

const DragHighlightZone: React.FC<Props> = ({ onDrop, children }) => {
  const [isHighlighted, setHighlighted] = useState(false);
  const dragTargets = useRef<HTMLElement[]>([]);
  const moveZone = useRef<HTMLDivElement>(null);

  return (
    <StyledDragHighlightZone
      isHighlighted={isHighlighted}
      onDragEnter={event => {
        if (!dragTargets.current.includes(event.currentTarget)) {
          dragTargets.current = [...dragTargets.current, event.currentTarget];
        }
        if (!isHighlighted) {
          setHighlighted(true);
        }
      }}
      onDragLeave={event => {
        dragTargets.current = [
          ...dragTargets.current.filter(
            target =>
              target !== event.currentTarget &&
              moveZone.current &&
              moveZone.current.contains(target),
          ),
        ];
        if (dragTargets.current.length === 0 && isHighlighted) {
          setHighlighted(false);
        }
      }}
      onDrop={event => {
        onDrop(event);
        dragTargets.current = [];
        setHighlighted(false);
      }}
      onDragOver={event => event.preventDefault()}
      ref={moveZone}
    >
      {children}
    </StyledDragHighlightZone>
  );
};

export default DragHighlightZone;
