import React, { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { NavLink } from 'react-router-dom';

import AuthContext, { isAuthenticated } from 'shared/contexts/authContext';
// Have to import the following components like that due to the cyclic dependency problem with Page
import Icon from 'shared/components/Icon';
import Avatar from 'shared/components/Avatar';
import DropdownMenu from 'shared/components/DropdownMenu';
import routes from 'shared/constants/routes';
import { USER_TYPE } from '__graphql__/globalTypes';

import { Bar, UserDropdownTrigger, Username, HelpLink } from './Styles';

const Topbar: React.FC<{}> = () => {
  const { t } = useTranslation();
  const authContext = useContext(AuthContext);

  if (!isAuthenticated(authContext)) {
    return null;
  }
  const { authState, logout } = authContext;
  const { firstName, lastName, isAdmin, type: userType } = authState.user;

  return (
    <Bar data-cy-topbar>
      <DropdownMenu.Container
        isLarge
        renderTrigger={({ isTriggered, toggleTrigger }) => (
          <UserDropdownTrigger onClick={toggleTrigger}>
            <Avatar name={firstName} />
            <Username>{`${firstName} ${lastName}`}</Username>
            <Icon type={isTriggered ? 'chevron-up' : 'chevron-down'} />
          </UserDropdownTrigger>
        )}
      >
        {isAdmin && (
          <DropdownMenu.Action forwardedAs={NavLink} to={routes.admin()}>
            {t('admin:adminPanel')}
          </DropdownMenu.Action>
        )}
        <DropdownMenu.Action forwardedAs={NavLink} to={routes.profile()}>
          {t('profile:myAccount')}
        </DropdownMenu.Action>
        {userType === USER_TYPE.employee && (
          <DropdownMenu.Action forwardedAs={NavLink} to={routes.company()}>
            {t('company:company')}
          </DropdownMenu.Action>
        )}
        <DropdownMenu.Action data-cy-logout onClick={logout}>
          {t('auth:signOut')}
        </DropdownMenu.Action>
      </DropdownMenu.Container>
      <HelpLink to="https://help.cosuno.de">
        <Icon size={22} type="help" />
        <span>{t('help')}</span>
      </HelpLink>
    </Bar>
  );
};

export default Topbar;
