import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import toast from 'shared/utils/toast';
import { ConfirmationModal } from 'shared/components';

import { StatusLink } from './Styles';
import { mutationDeleteBidRequest } from './api';

interface Props {
  bidRequestId: string;
}

const RemoveLink: React.FC<Props> = ({ bidRequestId }) => {
  const { t } = useTranslation('bidPackages');
  const [deleteBid, { loading }] = useMutation(mutationDeleteBidRequest, {
    refetchQueries: () => ['BidPackageForShow', 'BidRequestsForBidPackage'],
    awaitRefetchQueries: true,
  });

  return (
    <ConfirmationModal
      title={t('removeConfirmTitle')}
      message={t('removeConfirmMessage')}
      confirmText={t('remove')}
      isWorking={loading}
      renderLink={modal => (
        <StatusLink data-cy-link="remove" onClick={modal.open}>
          ({t('remove')})
        </StatusLink>
      )}
      onConfirm={async () => {
        try {
          await deleteBid({ variables: { id: bidRequestId } });
          toast.success(t('removeSuccess'));
        } catch (error) {
          toast.error(error);
        }
      }}
    />
  );
};

export default RemoveLink;
