import React, { useRef } from 'react';
import { useTranslation } from 'react-i18next';
import { useQuery, useMutation } from '@apollo/react-hooks';

import PageLoader from 'shared/components/PageLoader';
import QueryErrors from 'shared/components/QueryErrors';
import toast from 'shared/utils/toast';
import { buildAttachableName } from 'shared/utils/documents';
import Form from 'shared/components/Form';
import Field from 'shared/components/Field';
import SubcontractorInfo, { SubcontractorInfoProps } from 'shared/components/SubcontractorInfo';
import ContactsList from 'shared/components/ContactsList';
import ModalFormBody from 'shared/components/ModalFormBody';
import { DocumentsTableSingle } from 'shared/components/DocumentsTable';
import { SectionTitle, HeadingMedium } from 'shared/components/Styles';
import { isDoneLoading } from 'shared/utils/apollo';
import { DOCUMENT_ENTITY } from '__graphql__/globalTypes';

import {
  draftBid,
  subcontractorDraftBid,
  mutationSubmitBid,
  mutationSubcontractorSubmitBid,
} from './api';
import { StyledSectionTitle, SubcontractorSection, ContactsListSection, PullUp } from './Styles';
import { BidFormModal_bidRequest } from './__graphql__/BidFormModal_bidRequest';
import { SubmitBid, SubmitBidVariables } from './__graphql__/SubmitBid';
import {
  SubcontractorSubmitBid,
  SubcontractorSubmitBidVariables,
} from './__graphql__/SubcontractorSubmitBid';
import { DraftBid, DraftBidVariables } from './__graphql__/DraftBid';
import {
  SubcontractorDraftBid,
  SubcontractorDraftBidVariables,
} from './__graphql__/SubcontractorDraftBid';

interface Props {
  subcontractor?: SubcontractorInfoProps['subcontractor'];
  bidRequest: BidFormModal_bidRequest;
  isUserSubcontractor: boolean;
  refetchQueries?: () => string[];
  closeModal: () => void;
}

interface FormValues {
  message: string;
  totalAmount: string;
}

const BidFormModalForm: React.FC<Props> = ({
  subcontractor,
  bidRequest,
  isUserSubcontractor,
  refetchQueries = [],
  closeModal,
}) => {
  const { t } = useTranslation();
  const documentsCount = useRef(0);

  const { error, ...results } = useQuery<
    DraftBid & SubcontractorDraftBid,
    DraftBidVariables & SubcontractorDraftBidVariables
  >(isUserSubcontractor ? subcontractorDraftBid : draftBid, {
    variables: { bidRequestId: bidRequest.id },
  });

  const [submitBid, { loading: mutating }] = useMutation<
    SubmitBid & SubcontractorSubmitBid,
    SubmitBidVariables & SubcontractorSubmitBidVariables
  >(isUserSubcontractor ? mutationSubcontractorSubmitBid : mutationSubmitBid, {
    refetchQueries,
    awaitRefetchQueries: true,
  });

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(results)) return <PageLoader />;

  const { data } = results;
  const bid = data[isUserSubcontractor ? 'subcontractorDraftBid' : 'draftBid'];

  const attachableName = subcontractor
    ? buildAttachableName([t<string>('documentsTable.bidDocuments'), subcontractor.name])
    : t('documentsTable.bidDocuments');

  return (
    <Form<FormValues>
      initialValues={{
        message: bid.message || '',
        totalAmount: String(bid.totalAmount || ''),
      }}
      validations={{
        message: Form.is.maxLength(60000),
        totalAmount: [Form.is.required(), Form.is.min(1)],
      }}
      onSubmit={values => {
        if (documentsCount.current === 0) {
          toast.error(t('bidFormModal.documentsError'));
          return;
        }
        submitBid({
          variables: {
            bidRequestId: bidRequest.id,
            input: {
              ...values,
              totalAmount: parseFloat(values.totalAmount),
            },
          },
        }).then(() => {
          toast.success(t('bidFormModal.bidAdded'));
          closeModal();
        }, toast.error);
      }}
    >
      <ModalFormBody close={closeModal} working={mutating} submitText="submit">
        <HeadingMedium>{t('bidFormModal.addBid')}</HeadingMedium>
        {subcontractor ? (
          <SubcontractorSection>
            <SubcontractorInfo subcontractor={subcontractor} />
            <ContactsListSection>
              <ContactsList bidInvites={bidRequest.bidInvites} />
            </ContactsListSection>
          </SubcontractorSection>
        ) : (
          <PullUp />
        )}
        <SectionTitle>{t('bidFormModal.enterDetails')}</SectionTitle>
        <Field.Textarea name="message" label={t('bid.message')} />
        <Field.CurrencyInput
          required
          name="totalAmount"
          placeholder={t('bidFormModal.totalAmountPlaceholder')}
          label={t('bid.totalAmount')}
          icon="euro"
        />
        <Form.RequiredFieldsNote />
        <StyledSectionTitle>{t('bidFormModal.uploadDocuments')}</StyledSectionTitle>
        <DocumentsTableSingle
          attachableType={DOCUMENT_ENTITY.Bid}
          attachableId={bid.id}
          attachableName={attachableName}
          onDocumentsCountChange={count => (documentsCount.current = count)}
        />
      </ModalFormBody>
    </Form>
  );
};

export default BidFormModalForm;
