import React from 'react';
import { useTranslation } from 'react-i18next';
import { useQuery } from '@apollo/react-hooks';
import { useParams } from 'react-router-dom';

import { ERROR_TYPES } from 'shared/constants';
import { PROJECT_STATUS, BID_PACKAGE_STATUS, DOCUMENT_ENTITY } from '__graphql__/globalTypes';
import {
  Tabs,
  PageLoader,
  QueryErrors,
  HtmlHeadTags,
  DocumentsTableSingle,
} from 'shared/components';
import { HeadingLarge, SpaceBetween } from 'shared/components/Styles';
import { BidPackageStatusTag } from 'shared/components/TagStyles';
import { doesErrorMatchCode, isDoneLoading } from 'shared/utils/apollo';
import { buildAttachableName } from 'shared/utils/documents';
import routes from 'shared/constants/routes';
import PageNotFound from 'components/PageNotFound';

import { queryBidPackageForShow } from './api';
import Sidebar from './Sidebar';
import Bidders from './Bidders';
import PublishBidPackageButton from './PublishBidPackageButton';
import MessagesTab from './MessagesTab';
import OverviewEdit from './OverviewEdit';
import {
  StyledPageWithSidebar,
  StyledTabsContainer,
  StyledTabsContent,
  BidPackageName,
  ScrollableTabsContent,
} from './Styles';
import { BidPackageForShow, BidPackageForShowVariables } from './__graphql__/BidPackageForShow';

const ShowBidPackage: React.FC<void> = () => {
  const { t } = useTranslation('bidPackages');

  const { bidPackageId, projectId } = useParams<{ bidPackageId: string; projectId: string }>();

  const { error, ...queryResults } = useQuery<BidPackageForShow, BidPackageForShowVariables>(
    queryBidPackageForShow,
    {
      variables: { id: bidPackageId },
    },
  );

  if (error) {
    if (doesErrorMatchCode(error, ERROR_TYPES.notFound)) {
      return <PageNotFound.Authenticated />;
    }
    return <QueryErrors error={error} />;
  }
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const { bidPackage } = queryResults.data;
  const isProjectClosed = bidPackage.project.status === PROJECT_STATUS.closed;

  return (
    <StyledPageWithSidebar
      renderSidebar={() => <Sidebar projectId={projectId} />}
      backLink={{ to: routes.bidPackages(projectId), label: t('backToProject') }}
    >
      <HtmlHeadTags title={bidPackage.name} />
      <SpaceBetween>
        <HeadingLarge>
          <BidPackageName data-cy-bid-package-name>{bidPackage.name}</BidPackageName>
          <BidPackageStatusTag status={bidPackage.status} data-cy-status-tag>
            {t(`bidPackage.${bidPackage.status}`)}
          </BidPackageStatusTag>
        </HeadingLarge>
        {bidPackage.status === BID_PACKAGE_STATUS.draft && !isProjectClosed && (
          <PublishBidPackageButton bidPackageId={bidPackage.id} />
        )}
      </SpaceBetween>
      <StyledTabsContainer data-cy-tabs-container>
        <Tabs.Links>
          <Tabs.Link to="overview">{t('overviewLabel')}</Tabs.Link>
          <Tabs.Link to="bidders" isDefault>
            {t('biddersLabel')}
          </Tabs.Link>
          <Tabs.Link to="documents">{t('documents')}</Tabs.Link>
          {process.env.REACT_APP_ENABLE_MESSAGING === 'true' && (
            <Tabs.Link to="messages">{t('messagesLabel')}</Tabs.Link>
          )}
        </Tabs.Links>
        <Tabs.Route
          path="overview"
          render={() => (
            <ScrollableTabsContent>
              <OverviewEdit bidPackageId={bidPackageId} projectId={projectId} />
            </ScrollableTabsContent>
          )}
        />
        <Tabs.Route path="bidders" render={() => <Bidders bidPackage={bidPackage} />} />
        <Tabs.Route
          path="documents"
          render={() => (
            <StyledTabsContent>
              <DocumentsTableSingle
                attachableType={DOCUMENT_ENTITY.BidPackage}
                attachableId={bidPackage.id}
                attachableName={buildAttachableName([bidPackage.project.name, bidPackage.name])}
                isReadOnly={isProjectClosed}
                printPath={`${routes.printBidPackageDocuments(bidPackage.id)}`}
              />
            </StyledTabsContent>
          )}
        />
        {process.env.REACT_APP_ENABLE_MESSAGING === 'true' && (
          <Tabs.Route
            path="messages"
            render={() => (
              <StyledTabsContent>
                <MessagesTab bidPackageId={bidPackage.id} />
              </StyledTabsContent>
            )}
          />
        )}
      </StyledTabsContainer>
    </StyledPageWithSidebar>
  );
};

export default ShowBidPackage;
