import React from 'react';
import { useQuery, useMutation } from '@apollo/react-hooks';

import PageLoader from 'shared/components/PageLoader';
import QueryErrors from 'shared/components/QueryErrors';
import { isDoneLoading } from 'shared/utils/apollo';
import { cloneDeep } from 'shared/utils/javascript';
import toast from 'shared/utils/toast';
import { DOCUMENT_ENTITY } from '__graphql__/globalTypes';

import DocumentsTable from './DocumentsTable';
import { queryFileTreeWithDocuments, mutationUpdateFileTree } from './api';
import Folder from './classes/Folder';
import {
  FileTreeWithDocuments,
  FileTreeWithDocumentsVariables,
} from './__graphql__/FileTreeWithDocuments';
import { UpdateFileTree, UpdateFileTreeVariables } from './__graphql__/UpdateFileTree';
import {
  ParsedFileTree,
  Item as SerializedItem,
  Folder as SerializedFolder,
} from './types/parsedFileTree';

interface Props {
  attachableType: DOCUMENT_ENTITY;
  attachableId: string;
  attachableName: string;
  isReadOnly?: boolean;
  onDocumentsCountChange?: (count: number) => void;
  printPath?: string;
  printDocuments?: boolean;
}

const DocumentsTableSingle: React.FC<Props> = ({
  attachableType,
  attachableId,
  attachableName,
  isReadOnly = false,
  onDocumentsCountChange = () => {},
  printPath,
  printDocuments = false,
}) => {
  const { error, ...queryResults } = useQuery<
    FileTreeWithDocuments,
    FileTreeWithDocumentsVariables
  >(queryFileTreeWithDocuments, {
    variables: {
      attachable: {
        attachableType,
        attachableId,
      },
    },
    onCompleted: completedData => {
      onDocumentsCountChange(completedData.fileTreeWithDocuments.documents.length);
    },
  });

  const [updateFileTree] = useMutation<UpdateFileTree, UpdateFileTreeVariables>(
    mutationUpdateFileTree,
  );

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const { data, refetch } = queryResults;

  const refetchWithCallback = async () => {
    const { data: fetchedData } = await refetch();
    onDocumentsCountChange(fetchedData.fileTreeWithDocuments.documents.length);
  };

  const parsedFileTree: ParsedFileTree = JSON.parse(data.fileTreeWithDocuments.fileTree.fileTree);

  const persistFileTree = async (rootFolder: SerializedItem) => {
    const updatedRootFolder = cloneDeep(rootFolder);
    try {
      await updateFileTree({
        variables: {
          id: data.fileTreeWithDocuments.fileTree.id,
          fileTree: JSON.stringify((updatedRootFolder as SerializedFolder).contents),
        },
      });
      await refetchWithCallback();
    } catch (updateFileTreeError) {
      toast.error(updateFileTreeError);
    }
  };

  const rootFolder = new Folder({
    id: '_root',
    name: 'root',
    contents: parsedFileTree,
    saveCallback: persistFileTree,
  });

  return (
    <DocumentsTable
      name={attachableName}
      fileTreeIds={[data.fileTreeWithDocuments.fileTree.id]}
      rootFolder={rootFolder}
      documents={data.fileTreeWithDocuments.documents}
      isReadOnly={isReadOnly}
      refetch={refetchWithCallback}
      printPath={printPath}
      printDocuments={printDocuments}
    />
  );
};

export default DocumentsTableSingle;
