import React from 'react';
import { useTranslation } from 'react-i18next';

import { Button, HtmlHeadTags, Page } from 'shared/components';
import UnauthenticatedPage from 'shared/components/UnauthenticatedPage';
import { ContentBox } from 'shared/components/UnauthenticatedPage/Styles';
import routes from 'shared/constants/routes';

import { Heading, Message, Wrapper, UnauthenticatedWrapper, WithdrawnHeading } from './Styles';

const PageNotFoundContent: React.FC = () => {
  const { t } = useTranslation('notFound');

  return (
    <>
      <Heading data-cy-404-page>{t('heading')}</Heading>
      <Message>{t('message')}</Message>
      <Button linkTo={routes.root()}>{t('home')}</Button>
    </>
  );
};

const PageNotFoundAuthenticated: React.FC = () => (
  <Page>
    <HtmlHeadTags title="notFound" />
    <Wrapper>
      <PageNotFoundContent />
    </Wrapper>
  </Page>
);

const PageNotFoundUnauthenticated: React.FC = () => (
  <UnauthenticatedPage title="notFound">
    <ContentBox as={UnauthenticatedWrapper}>
      <PageNotFoundContent />
    </ContentBox>
  </UnauthenticatedPage>
);

const RequestWithdrawn: React.FC<{ isAuthenticated: boolean }> = ({ isAuthenticated }) => {
  const { t } = useTranslation('notFound');
  const WithdrawnPage = isAuthenticated ? Page : UnauthenticatedPage;
  return (
    <WithdrawnPage title="requestWithdrawn">
      <ContentBox as={UnauthenticatedWrapper}>
        <WithdrawnHeading data-cy-404-page>{t('requestWithdrawnHeading')}</WithdrawnHeading>
        <Message>{t('requestWithdrawnMessage')}</Message>
        {isAuthenticated && <Button linkTo={routes.root()}>{t('home')}</Button>}
      </ContentBox>
    </WithdrawnPage>
  );
};

export default {
  Authenticated: PageNotFoundAuthenticated,
  Unauthenticated: PageNotFoundUnauthenticated,
  RequestWithdrawn,
};
