import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import toast from 'shared/utils/toast';
import { Form, Field, Button } from 'shared/components';
import { CurrentUserQuery } from 'shared/utils/queries';

import { mutationUpdateProfile } from './api';

interface Props {
  currentUser: CurrentUserQuery['me'];
}

interface FormValues {
  firstName: string;
  lastName: string;
  email: string;
  jobTitle: string;
  phone: string;
  fax: string;
}

const ProfilePersonal: React.FC<Props> = ({ currentUser }) => {
  const { t } = useTranslation('profile');
  const [updateProfile, { loading: mutating }] = useMutation(mutationUpdateProfile);

  return (
    <Form<FormValues>
      initialValues={{
        firstName: currentUser.firstName || '',
        lastName: currentUser.lastName || '',
        email: currentUser.email || '',
        jobTitle: currentUser.jobTitle || '',
        phone: currentUser.phone || '',
        fax: currentUser.fax || '',
      }}
      validations={{
        firstName: [Form.is.required(), Form.is.maxLength(100)],
        lastName: [Form.is.required(), Form.is.maxLength(100)],
        email: [Form.is.required(), Form.is.maxLength(200), Form.is.email()],
        jobTitle: [Form.is.maxLength(100)],
        phone: [Form.is.maxLength(100)],
        fax: [Form.is.maxLength(100)],
      }}
      onSubmit={values =>
        updateProfile({ variables: { input: values } }).then(() => {
          toast.success(t('updated'));
        }, toast.error)
      }
    >
      <Form.Element style={{ maxWidth: 500 }}>
        <Field.Input required name="firstName" label={t('user.firstName')} />
        <Field.Input required name="lastName" label={t('user.lastName')} />
        <Field.Input required name="email" label={t('user.email')} />
        <Field.Input name="jobTitle" label={t('user.jobTitle')} />
        <Field.Input name="phone" label={t('user.phone')} />
        <Field.Input name="fax" label={t('user.fax')} />
        <Form.RequiredFieldsNote />
        <Button type="submit" working={mutating}>
          {t('save')}
        </Button>
      </Form.Element>
    </Form>
  );
};

export default ProfilePersonal;
