import gql from 'graphql-tag';

export const querySubcontractorForEdit = gql`
  query SubcontractorForEdit($id: ID!) {
    subcontractor(id: $id) {
      id
      name
      website
      companySize
      street
      city
      postalCode
      countryCode
      notes
      isBanned
      workCategories {
        name
      }
      contacts {
        id
        isPerson
        title
        firstName
        lastName
        email
        jobTitle
        phone
        mobilePhone
        fax
      }
    }
  }
`;

export const mutationDeleteSubcontractor = gql`
  mutation DeleteSubcontractor($id: ID!) {
    deleteSubcontractor(id: $id)
  }
`;

export const mutationCreateSubcontractor = gql`
  mutation CreateSubcontractor($input: SubcontractorInput!) {
    createSubcontractor(input: $input) {
      id
    }
  }
`;

export const mutationUpdateSubcontractor = gql`
  mutation UpdateSubcontractor($id: ID!, $input: SubcontractorInput!) {
    updateSubcontractor(id: $id, input: $input) {
      id
    }
  }
`;
