import React, { useCallback, useState, useRef } from 'react';
import { ThemeProvider } from 'styled-components/macro';

import useClickOutside from 'shared/hooks/useClickOutside';
import Portal from 'shared/components/Portal';
import DropdownCardContent from 'shared/components/DropdownCardContent';

import Action from './Action';
import DropdownMenuContext from './DropdownMenuContext';
import { StyledContainer, StyledDropdownCard } from './Styles';

const getTheme = ({ isLarge }: { isLarge: boolean }) => (parentTheme: object) => ({
  ...parentTheme,
  isLarge,
});

interface RenderTriggerProps {
  isTriggered: boolean;
  toggleTrigger: () => void;
}

interface Props {
  className?: string;
  children: React.ReactNode;
  isLarge?: boolean;
  renderTrigger: (props: RenderTriggerProps) => React.ReactElement;
}

const Container: React.FC<Props> = ({ className, children, isLarge = false, renderTrigger }) => {
  const triggerRef = useRef<HTMLDivElement>(null);
  const cardRef = useRef<HTMLDivElement>(null);
  const refs = useRef([triggerRef, cardRef]);
  const [isTriggered, setIsTriggered] = useState(false);
  const toggleTrigger = () => setIsTriggered(!isTriggered);
  const closeModal = () => setIsTriggered(false);
  const handleClickOutside = useCallback(() => setIsTriggered(false), []);

  useClickOutside({ refs: refs.current, onClickOutside: handleClickOutside });

  return (
    <DropdownMenuContext.Provider value={{ closeModal, isTriggered }}>
      <div className={className}>
        <StyledContainer>
          <div ref={triggerRef}>{renderTrigger({ isTriggered, toggleTrigger })}</div>
          <ThemeProvider theme={getTheme({ isLarge })}>
            <Portal>
              <StyledDropdownCard ref={cardRef} isOpen={isTriggered} triggerRef={triggerRef}>
                <DropdownCardContent>{children}</DropdownCardContent>
              </StyledDropdownCard>
            </Portal>
          </ThemeProvider>
        </StyledContainer>
      </div>
    </DropdownMenuContext.Provider>
  );
};

export default {
  Container,
  Action,
};
