import React, { useContext } from 'react';
import { useQuery, useMutation } from '@apollo/react-hooks';
import { Link, useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import toast from 'shared/utils/toast';
import { Form, Field, QueryErrors, PageLoader } from 'shared/components';
import {
  Heading,
  Tip,
  Message,
  SubmitButton,
  BottomLinkLeftAligned,
} from 'shared/components/UnauthenticatedStyles';
import UnauthenticatedPage from 'shared/components/UnauthenticatedPage';
import { ContentBox } from 'shared/components/UnauthenticatedPage/Styles';
import AuthContext from 'shared/contexts/authContext';
import { isDoneLoading } from 'shared/utils/apollo';
import routes from 'shared/constants/routes';
import { USER_STATUS } from '__graphql__/globalTypes';
import { COLOR } from 'shared/utils/styles';

import { queryInvitation, mutationActivateInvitee } from './api';
import { Invitation, InvitationVariables } from './__graphql__/Invitation';
import { ActivateInvitee, ActivateInviteeVariables } from './__graphql__/ActivateInvitee';

interface FormValues {
  firstName: string;
  lastName: string;
  password: string;
}

const AuthInvitation: React.FC<void> = () => {
  const { t } = useTranslation('auth');
  const { login } = useContext(AuthContext);
  const { token } = useParams<{ token: string }>();

  const { error: queryError, ...queryResults } = useQuery<Invitation, InvitationVariables>(
    queryInvitation,
    {
      variables: { token },
    },
  );

  const [activateInvitee, { error: mutationError, loading: mutating }] = useMutation<
    ActivateInvitee,
    ActivateInviteeVariables
  >(mutationActivateInvitee);

  if (queryError) return <QueryErrors error={queryError} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const {
    invitation: { inviter, invitee },
  } = queryResults.data;

  if (invitee.status !== USER_STATUS.invited) {
    return (
      <UnauthenticatedPage title="invitation">
        <ContentBox>
          <Heading>{t('invitationNotActiveHeading')}</Heading>
          <Tip>{t('invitationNotActiveTip')}</Tip>
          <BottomLinkLeftAligned>
            <Link to={routes.login()}>{t('loginNow')}</Link>
          </BottomLinkLeftAligned>
        </ContentBox>
      </UnauthenticatedPage>
    );
  }

  return (
    <UnauthenticatedPage title="invitation">
      <Form<FormValues>
        initialValues={{
          firstName: '',
          lastName: '',
          password: '',
        }}
        validations={{
          firstName: [Form.is.required(), Form.is.maxLength(100)],
          lastName: [Form.is.required(), Form.is.maxLength(100)],
          password: [Form.is.required(), Form.is.maxLength(250)],
        }}
        onSubmit={async input => {
          const { data } = await activateInvitee({
            variables: { token, input },
          });

          if (data) {
            login(data.activateInvitee);
            toast.success(t('accountCreated'));
          }
        }}
      >
        <ContentBox as={Form.Element}>
          <Heading>
            {t('invitationHeading', {
              ...inviter,
              company: invitee.company.name,
            })}
          </Heading>
          <Tip>{t('invitationMessage')}</Tip>
          {mutationError && <Message color={COLOR.danger}>{mutationError.message}</Message>}
          <Field.Input required name="firstName" label={t('user.firstName')} />
          <Field.Input required name="lastName" label={t('user.lastName')} />
          <Field.Input required name="password" label={t('password')} type="password" icon="lock" />
          <Form.RequiredFieldsNote />
          <SubmitButton type="submit" working={mutating}>
            {t('signupSubmit')}
          </SubmitButton>
        </ContentBox>
      </Form>
    </UnauthenticatedPage>
  );
};

export default AuthInvitation;
