import React from 'react';
import i18n from 'i18next';
import { useTranslation } from 'react-i18next';

import { CONTACT_TITLE } from 'shared/constants/contact';
import { ModalFormBody, Form, Field } from 'shared/components';
import { Validator } from 'shared/utils/validation/types';

import { ModalHeading } from './Styles';
import { SubcontractorForEdit_subcontractor_contacts } from './__graphql__/SubcontractorForEdit';

interface Props {
  contact?: SubcontractorForEdit_subcontractor_contacts;
  onSubmit: (values: FormValues) => void;
  allValues: SubcontractorForEdit_subcontractor_contacts[];
  closeModal: () => void;
}

interface FormValues {
  id: string;
  isPerson: boolean;
  email: string;
  title: string;
  firstName: string;
  lastName: string;
  jobTitle: string;
  phone: string;
  mobilePhone: string;
  fax: string;
}

const validateIfIsPerson = (validation: Validator) => (
  ...validationArgs: Parameters<Validator>
) => {
  const [, values] = validationArgs;

  if (values.isPerson) {
    return validation(...validationArgs);
  }

  return false;
};

const makeValidateEmailIsUnique = (allValues: FormValues[], isEditing: boolean) => (
  value: string | null,
) => {
  const valueOccurences = allValues.filter(contactValues => contactValues.email === value);

  if (value && Boolean(valueOccurences.length) > isEditing ? 1 : 0) {
    return i18n.t('validation:contactEmailNotUnique');
  }

  return false;
};

const ContactsForm: React.FC<Props> = ({ contact = {}, onSubmit, allValues, closeModal }) => {
  const { t } = useTranslation();

  const isEditing = Object.values(contact).length > 0;

  return (
    <Form<FormValues>
      initialValues={{
        id: contact.id || '',
        isPerson: contact.isPerson !== undefined ? contact.isPerson : true,
        email: contact.email || '',
        title: contact.title || '',
        firstName: contact.firstName || '',
        lastName: contact.lastName || '',
        jobTitle: contact.jobTitle || '',
        phone: contact.phone || '',
        mobilePhone: contact.mobilePhone || '',
        fax: contact.fax || '',
      }}
      validations={{
        email: [
          Form.is.maxLength(200),
          Form.is.email(),
          makeValidateEmailIsUnique(allValues as FormValues[], isEditing),
        ],
        firstName: [Form.is.maxLength(100)],
        lastName: [Form.is.maxLength(100), validateIfIsPerson(Form.is.required())],
      }}
      onSubmit={values => {
        onSubmit(values);
        closeModal();
      }}
    >
      {({ handleSubmit, values }) => (
        <ModalFormBody
          close={closeModal}
          working={false}
          useFullPageHeight={false}
          submitOnEnter
          onSubmit={event => {
            event.preventDefault();
            handleSubmit();
          }}
        >
          <ModalHeading>
            {isEditing
              ? t('subcontractorForm.contacts.modalHeading.existingContact')
              : t('subcontractorForm.contacts.modalHeading.newContact')}
          </ModalHeading>
          <Field.Checkbox
            name="isPerson"
            checkboxLabel={t('subcontractorForm.contacts.isPerson')}
          />
          {values.isPerson && (
            <>
              <Field.SelectSingle
                name="title"
                label={t('subcontractorForm.contacts.titleLabel')}
                options={[
                  {
                    value: CONTACT_TITLE.mr,
                    label: t('subcontractorForm.contacts.title.mr'),
                  },
                  {
                    value: CONTACT_TITLE.ms,
                    label: t('subcontractorForm.contacts.title.ms'),
                  },
                ]}
              />
              <Field.Input name="firstName" label={t('subcontractorForm.contacts.firstName')} />
              <Field.Input
                required
                name="lastName"
                label={t('subcontractorForm.contacts.lastName')}
              />
              <Field.Input name="jobTitle" label={t('subcontractorForm.contacts.jobTitle')} />
            </>
          )}
          <Field.Input
            required
            name="email"
            label={t('subcontractorForm.contacts.email')}
            type="email"
          />
          <Field.Input name="phone" label={t('subcontractorForm.contacts.phone')} />
          <Field.Input name="mobilePhone" label={t('subcontractorForm.contacts.mobilePhone')} />
          <Field.Input name="fax" label={t('subcontractorForm.contacts.fax')} />
        </ModalFormBody>
      )}
    </Form>
  );
};

export default ContactsForm;
