import gql from 'graphql-tag';

import { fragmentBidModal } from 'shared/components/BidModal';

export const queryBids = gql`
  query Bids(
    $offset: Int!
    $limit: Int!
    $sortBy: BIDS_SORT_FIELD!
    $sortDirection: SortDirection!
  ) {
    bids(offset: $offset, limit: $limit, sortBy: $sortBy, sortDirection: $sortDirection) {
      totalRecords
      bids {
        id
        totalAmount
        message
        submittedAt
        ...BidModal_bid
        bidRequest {
          id
          ...BidModal_bidRequest
          subcontractor {
            name
          }
          bidPackage {
            name
            project {
              name
            }
          }
        }
      }
    }
  }
  ${fragmentBidModal}
`;
