import React, { forwardRef } from 'react';
import styled, { css } from 'styled-components/macro';

import { COLOR, font, mixin } from 'shared/utils/styles';
import DropdownAction, { DropdownActionProps } from 'shared/components/DropdownAction';
import DropdownCardContent from 'shared/components/DropdownCardContent';
import Clickable from 'shared/components/Clickable';
import Icon from 'shared/components/Icon';
import IconClickable from 'shared/components/IconClickable';

export const Wrapper = styled.div`
  position: relative;
`;

interface StyledSelectProps {
  icon?: boolean;
  invalid?: boolean;
}

export const StyledSelect = styled.div<StyledSelectProps>`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  border-radius: 4px;
  border: 1px solid ${COLOR.borderLight};
  ${font.size(14)}
  &:focus {
    outline: none;
    border: 1px solid ${COLOR.borderMedium};
  }
  ${props => (props.icon ? 'padding-left: 25px;' : '')}
  ${props => (props.invalid ? `&, &:focus { border: 1px solid ${COLOR.danger}; }` : '')}
`;

export const ValueContainer = styled.div`
  position: relative;
  width: 100%;
  min-height: 40px;
  height: 100%;
  text-align: left;
  z-index: 1;
  pointer-events: none;
`;

export const ChevronIcon = styled(Icon)`
  position: absolute;
  top: 11px;
  right: 11px;
  font-size: 18px;
  color: ${COLOR.textMedium};
`;

export const Placeholder = styled.div`
  padding: 11px 0 0 15px;
  color: ${COLOR.textLightBlue};
`;

export const ValueSingle = styled.div`
  padding: 11px 0 0 15px;
`;

export const ValueMulti = styled.div`
  padding: 15px 5px 10px 10px;
`;

export const ValueMultiItem = styled(Clickable)`
  margin: 0 5px 5px 0;
  pointer-events: auto;
  ${mixin.tag}

  &:focus {
    border-color: ${COLOR.borderMedium};
  }
`;

export const DropdownInput = styled.input`
  padding: 10px 15px 8px;

  /* Account for the "clear" icon */
  padding-right: 32px;

  width: 100%;
  border: none;
  color: ${COLOR.textDarkest};
  background: none;
  &:focus {
    outline: none;
  }
  &::-ms-clear {
    display: none;
  }
`;

export const ClearIcon = styled(IconClickable)`
  position: absolute;
  top: 4px;
  right: 7px;
  padding: 5px;
  font-size: 16px;
  color: ${COLOR.textMedium};
  ${mixin.clickable}
`;

export const StyledDropdownCardContent = styled(DropdownCardContent)`
  padding-top: 0;
  max-height: 200px;
  overflow: auto;
`;

interface OptionProps {
  isDisplayed: boolean;
  isSelected: boolean;
  isHighlighted: boolean;
}

const OptionBase = forwardRef<HTMLButtonElement, OptionProps & DropdownActionProps>(
  ({ isSelected, isHighlighted, ...props }, ref) => <DropdownAction ref={ref} {...props} />,
);

export const Option = styled(OptionBase)<OptionProps>`
  ${props =>
    props.isSelected &&
    css`
      &,
      &:hover {
        background-color: ${COLOR.primary};
        color: white;
      }
    `}

  ${props =>
    props.isHighlighted &&
    css`
      background: ${COLOR.highlightWhite};
    `}
`;

export const OptionsMessage = styled.div`
  padding: 5px 15px 5px;
  color: ${COLOR.textLight};
`;
