import gql from 'graphql-tag';

import { fragmentBidModal } from 'shared/components/BidModal';
import { fragmentBidFormModal } from 'shared/components/BidFormModal';
import { fragmentBidsList } from 'shared/components/BidsList';
import { fragmentMessages } from 'shared/components/Messages';

export const queryBidRequestForSubcontractor = gql`
  query BidRequestForSubcontractor($bidRequestId: ID!) {
    bidRequestForSubcontractor(mysteriousId: $bidRequestId) {
      id
      status
      declinedReason
      currentBidId
      ...BidModal_bidRequest
      ...BidFormModal_bidRequest
      ...BidsList_bidsList
      currentBid {
        totalAmount
      }
      bids {
        id
        message
        totalAmount
        submittedAt
        ...BidModal_bid
        ...BidFormModal_bid
      }
      bidPackage {
        id
        name
        bidsDueAt
        startsOn
        endsOn
        status
        description
        project {
          id
          name
          location
          customer
          startsOn
          endsOn
          description
          status
          forceSubcontractorSignup
          imageKey
          office {
            id
            name
            logoKey
            email
            website
            phone
            fax
            street
            postalCode
            city
            countryCode
            company {
              id
              name
            }
          }
          projectUsers {
            id
            user {
              id
              firstName
              lastName
              email
              phone
              fax
              jobTitle
            }
            userRole
          }
          primaryUser {
            id
          }
        }
      }
    }
  }
  ${fragmentBidModal}
  ${fragmentBidFormModal}
  ${fragmentBidsList}
`;

export const queryMessageThreadsInBidRequest = gql`
  query MessageThreadsInBidRequest($bidRequestId: ID!) {
    messageThreadsInBidRequest(bidRequestId: $bidRequestId) {
      messageThreads {
        ...Messages_messageThread
      }
    }
  }
  ${fragmentMessages}
`;
