import React from 'react';
import { useTranslation } from 'react-i18next';
import { NavLink } from 'react-router-dom';

import { DropdownMenuWrapper } from 'shared/components/Styles';
import { DropdownMenu, Clickable, Icon } from 'shared/components';
import { BID_REQUEST_STATUS } from '__graphql__/globalTypes';
import routes from 'shared/constants/routes';

import { AdminBidRequests_adminBidRequests_bidRequests } from './__graphql__/AdminBidRequests';

interface DropdownProps {
  bidRequest: AdminBidRequests_adminBidRequests_bidRequests;
}

const Dropdown: React.FC<DropdownProps> = ({ bidRequest }) => {
  const { t } = useTranslation('admin');
  return (
    <DropdownMenuWrapper>
      <DropdownMenu.Container
        renderTrigger={({ isTriggered, toggleTrigger }) => (
          <Clickable
            onClick={toggleTrigger}
            disabled={bidRequest.status === BID_REQUEST_STATUS.pendingInvite}
          >
            <Icon type={isTriggered ? 'chevron-up' : 'chevron-down'} size={18} />
          </Clickable>
        )}
      >
        <DropdownMenu.Action
          forwardedAs={NavLink}
          to={routes.bidRequest(bidRequest.id)}
          data-cy-link-name={bidRequest.id}
          target="_blank"
        >
          {t('viewAsSubcontractor')}
        </DropdownMenu.Action>
      </DropdownMenu.Container>
    </DropdownMenuWrapper>
  );
};

export default Dropdown;
