import React from 'react';
import { useQuery } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import { Tabs, HtmlHeadTags, PageLoader, QueryErrors, Page } from 'shared/components';
import { HeadingLarge } from 'shared/components/Styles';
import { queryCurrentUser, CurrentUserQuery } from 'shared/utils/queries';
import { isDoneLoading } from 'shared/utils/apollo';

import Personal from './Personal';
import Password from './Password';

const Profile: React.FC<void> = () => {
  const { t } = useTranslation('profile');

  const { error, ...queryResults } = useQuery<CurrentUserQuery>(queryCurrentUser);

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  return (
    <Page>
      <HtmlHeadTags title="account" />
      <HeadingLarge>{t('myAccount')}</HeadingLarge>
      <Tabs.Container>
        <Tabs.Links>
          <Tabs.Link to="personal" isDefault>
            {t('personalInfo')}
          </Tabs.Link>
          <Tabs.Link to="password">{t('password')}</Tabs.Link>
        </Tabs.Links>
        <Tabs.Content>
          <Tabs.Route
            path="personal"
            render={() => <Personal currentUser={queryResults.data.me} />}
          />
          <Tabs.Route path="password" render={() => <Password />} />
        </Tabs.Content>
      </Tabs.Container>
    </Page>
  );
};

export default Profile;
