import React from 'react';
import { useTranslation } from 'react-i18next';

const formatCurrency = (locale: string, value: number) => {
  if (!value && value !== 0) return value;

  const currencyCode = 'EUR';
  const formattedValue = new Intl.NumberFormat(locale, {
    style: 'currency',
    currency: currencyCode,
  })
    .format(value)
    .replace('', '')
    .trim();
  const nonBreakingSpace = '\u00A0';

  return formattedValue + nonBreakingSpace + currencyCode;
};

interface CurrencyFormatterProps {
  value: number;
}

const CurrencyFormatter: React.FC<CurrencyFormatterProps> = ({ value }) => {
  const { i18n } = useTranslation();

  return <>{formatCurrency(i18n.language, value)}</>;
};

export { formatCurrency };
export default CurrencyFormatter;
