import gql from 'graphql-tag';

import { fragmentCurrentUserForAuthentication } from 'shared/hooks/useAuthentication';

export const mutationDeactivateUser = gql`
  mutation DeactivateUser($id: ID!) {
    deactivateUser(id: $id)
  }
`;

export const mutationReactivateUser = gql`
  mutation ReactivateUser($id: ID!) {
    reactivateUser(id: $id)
  }
`;

export const mutationResendUserInvite = gql`
  mutation ResendUserInvite($id: ID!) {
    resendUserInvite(id: $id)
  }
`;

export const mutationCancelInvite = gql`
  mutation CancelInvite($id: ID!) {
    cancelInvite(id: $id)
  }
`;

export const mutationRestoreInvite = gql`
  mutation RestoreInvite($id: ID!) {
    restoreInvite(id: $id)
  }
`;

export const mutationAdminLoginAsUser = gql`
  mutation AdminLoginAsUser($id: ID!) {
    adminLoginAsUser(id: $id) {
      token
      user {
        ...CurrentUserForAuthentication
      }
    }
  }
  ${fragmentCurrentUserForAuthentication}
`;

export const mutationUpdateUserType = gql`
  mutation UpdateUserType($id: ID!, $type: USER_TYPE!) {
    updateUserType(id: $id, type: $type)
  }
`;
