import queryString from 'query-string';

import Item from '../classes/Item';
import Folder from '../classes/Folder';

interface Args {
  name: string;
  fileTreeIds: string[];
}

const getDownloadURLBuilder = ({ name, fileTreeIds }: Args) => (item: Item) => {
  const filename = item instanceof Folder && item.isRoot() ? name : item.name;
  const path = item.getPath();

  if (fileTreeIds.length > 1) {
    const queryParams = {
      fileTreeIds,
      filename,
      path,
    };

    return queryString.stringifyUrl({
      url: `${process.env.REACT_APP_API_BASE}/downloadCombined`,
      query: queryParams,
    });
  }

  const queryParams = {
    fileTreeId: fileTreeIds[0],
    filename,
    path,
  };

  return queryString.stringifyUrl({
    url: `${process.env.REACT_APP_API_BASE}/download`,
    query: queryParams,
  });
};

export default getDownloadURLBuilder;
