import React from 'react';
import { useLazyQuery, useQuery } from '@apollo/react-hooks';

import { isDoneLoading } from 'shared/utils/apollo';
import { Messages, QueryErrors, PageLoader } from 'shared/components';
import { COMPANY_TYPE } from '__graphql__/globalTypes';

import { queryMessageThreadsInBidPackage, queryBidRequestsForMessages } from './api';
import {
  BidRequestsForMessages,
  BidRequestsForMessagesVariables,
} from './__graphql__/BidRequestsForMessages';

interface Props {
  bidPackageId: string;
}

const MessagesTab: React.FC<Props> = ({ bidPackageId }) => {
  const activeUserType = COMPANY_TYPE.principal;
  const threadsQuery = useLazyQuery<{}>(queryMessageThreadsInBidPackage, {
    variables: { bidPackageId },
  });

  const { error, ...queryResults } = useQuery<
    BidRequestsForMessages,
    BidRequestsForMessagesVariables
  >(queryBidRequestsForMessages, {
    variables: { bidPackageId },
  });

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const potentialRecipients = queryResults.data.bidRequests.bidRequests.map(bidRequest => ({
    bidRequestId: bidRequest.id,
    name: bidRequest.subcontractor.name,
  }));

  return (
    <Messages
      threadsQuery={threadsQuery}
      threadsQueryName="messageThreadsInBidPackage"
      refetchQueries={['MessageThreadsInBidPackage']}
      activeUserType={activeUserType}
      potentialRecipients={potentialRecipients}
      bidPackageId={bidPackageId}
    />
  );
};

export default MessagesTab;
