import React from 'react';
import { Link as RouterLink, LinkProps as RouterLinkProps } from 'react-router-dom';

import Clickable, { ClickableProps } from 'shared/components/Clickable';

import { CellText, CellIcon, CellLink } from './Styles';

type LinkProps = ClickRedirectLink | RoutingLink;

interface ClickRedirectLink {
  onClick: ClickableProps['onClick'];
}

interface RoutingLink {
  to: RouterLinkProps['to'];
  children?: React.ReactNode;
}

const isClickLink = (linkProps: LinkProps): linkProps is ClickRedirectLink =>
  !!(linkProps as ClickRedirectLink).onClick;

const Link = (linkProps: LinkProps) => {
  return isClickLink(linkProps) ? (
    <CellLink as={Clickable} {...linkProps} />
  ) : (
    <CellLink as={RouterLink} {...linkProps} />
  );
};

const Cell = {
  Text: CellText,
  Icon: CellIcon,
  Link,
};

export default Cell;
