import React, { useContext } from 'react';
import styled, { css } from 'styled-components/macro';

import DropdownAction, { DropdownActionProps } from 'shared/components/DropdownAction';
import { COLOR, mediaQueries } from 'shared/utils/styles';
import { As } from 'shared/constants/styledComponents';

import DropdownMenuContext from './DropdownMenuContext';

interface ActionProps {
  danger?: boolean;
  as?: As;
}

const ActionBase: React.FC<DropdownActionProps & ActionProps> = ({
  onClick,
  danger: _,
  as,
  ...props
}) => {
  const { closeModal, isTriggered } = useContext(DropdownMenuContext);

  const handleClick: DropdownActionProps['onClick'] = (...args) => {
    closeModal();
    if (onClick) {
      onClick(...args);
    }
  };

  return (
    <DropdownAction {...props} forwardedAs={as} isDisplayed={isTriggered} onClick={handleClick} />
  );
};

// Cast `ActionBase` as `any` so that Typescript doesn't go crazy when receiving
// props that don't belong to `ActionBase` in conjunction with the `as` prop
// eslint-disable-next-line @typescript-eslint/no-explicit-any
const Action = styled(ActionBase as any)<ActionProps>`
  color: ${props => (props.danger ? COLOR.danger : COLOR.textDark)};

  ${props =>
    props.theme.isLarge &&
    css`
      @media ${mediaQueries.desktop} {
        padding: 10px 20px;
      }
    `}
`;

export default Action;
