import gql from 'graphql-tag';

import { fragmentUsersTableDropdown } from 'shared/components/UsersTableDropdown';

export const queryAdminUsers = gql`
  query AdminUsers(
    $query: String
    $offset: Int!
    $limit: Int!
    $sortBy: String
    $sortDirection: SortDirection
  ) {
    adminUsers(
      query: $query
      offset: $offset
      limit: $limit
      sortBy: $sortBy
      sortDirection: $sortDirection
    ) {
      users {
        id
        email
        firstName
        lastName
        status
        ...UsersTableDropdown_user
        company {
          id
          name
        }
      }
      totalRecords
    }
  }
  ${fragmentUsersTableDropdown}
`;
