import React from 'react';
import { Switch, Route } from 'react-router-dom';

import routes from 'shared/constants/routes';

import ShowBidPackage from './ShowBidPackage';
import CreateBidPackage from './CreateBidPackage';

const BidPackage: React.FC<{}> = () => (
  <Switch>
    <Route path={routes.createBidPackage()} component={CreateBidPackage} />
    <Route path={routes.showBidPackage()} component={ShowBidPackage} />
  </Switch>
);

export default BidPackage;
