import React from 'react';
import { useQuery } from '@apollo/react-hooks';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import {
  Tabs,
  HtmlHeadTags,
  DocumentsTableCombined,
  Page,
  PageLoader,
  QueryErrors,
} from 'shared/components';
import routes from 'shared/constants/routes';
import PageNotFound from 'components/PageNotFound';
import { isDoneLoading } from 'shared/utils/apollo';
import useUserAuthStatus from 'shared/hooks/useUserAuthStatus';
import { BID_REQUEST_STATUS, DOCUMENT_ENTITY } from '__graphql__/globalTypes';

import { queryBidRequestForSubcontractor } from './api';
import Overview from './Overview';
import { BidPackageName, ProjectName, Header } from './Styles';
import Actions from './Actions';
import Bids from './Bids';
import CurrentBid from './CurrentBid';
import MessagesTab from './MessagesTab';
import {
  BidRequestForSubcontractor,
  BidRequestForSubcontractorVariables,
} from './__graphql__/BidRequestForSubcontractor';

const BidRequest: React.FC<void> = () => {
  const { t } = useTranslation('bidInvites');
  const { isUserAuthenticated, isUserAdmin } = useUserAuthStatus();
  const { bidRequestId } = useParams<{ bidRequestId: string }>();

  const { error, ...queryResults } = useQuery<
    BidRequestForSubcontractor,
    BidRequestForSubcontractorVariables
  >(queryBidRequestForSubcontractor, {
    variables: { bidRequestId },
  });

  if (error) {
    return <QueryErrors error={error} />;
  }

  if (!isDoneLoading(queryResults)) {
    return <PageLoader />;
  }

  const bidRequest = queryResults.data.bidRequestForSubcontractor;

  if (bidRequest.status === BID_REQUEST_STATUS.withdrawn) {
    return <PageNotFound.RequestWithdrawn isAuthenticated={isUserAuthenticated} />;
  }

  const potentialMessageRecipients = [
    {
      bidRequestId: bidRequest.id,
      name: bidRequest.bidPackage.project.office.name,
    },
  ];

  const isNonAdminUser = isUserAuthenticated && !isUserAdmin;

  return (
    <>
      <HtmlHeadTags title={bidRequest.bidPackage.name} />
      <Actions bidRequest={bidRequest} />
      <Page
        data-cy-page="bid-invite"
        withTopbar={isNonAdminUser}
        isResponsive
        backLink={
          isNonAdminUser ? { to: routes.bidRequests(), label: t('backToBidRequests') } : undefined
        }
      >
        <Header>
          <div>
            <ProjectName>{bidRequest.bidPackage.project.name}</ProjectName>
            <BidPackageName>{bidRequest.bidPackage.name}</BidPackageName>
          </div>
          <CurrentBid bidRequest={bidRequest} />
        </Header>
        <Tabs.Container>
          <Tabs.Links>
            <Tabs.Link to="overview" isDefault>
              {t('overview.overviewTabName')}
            </Tabs.Link>
            <Tabs.Link to="documents">{t('documents.documents')}</Tabs.Link>
            <Tabs.Link to="bids">{t('overview.bidsTabName')}</Tabs.Link>
            {process.env.REACT_APP_ENABLE_MESSAGING === 'true' && (
              <Tabs.Link to="messages">{t('overview.messagesTabName')}</Tabs.Link>
            )}
          </Tabs.Links>
          <Tabs.Content>
            <Tabs.Route
              path="overview"
              render={() => <Overview bidPackage={bidRequest.bidPackage} />}
            />
            <Tabs.Route
              path="documents"
              render={() => (
                <DocumentsTableCombined
                  attachables={[
                    {
                      attachableType: DOCUMENT_ENTITY.BidPackage,
                      attachableId: bidRequest.bidPackage.id,
                      attachableName: bidRequest.bidPackage.name,
                    },
                    {
                      attachableType: DOCUMENT_ENTITY.Project,
                      attachableId: bidRequest.bidPackage.project.id,
                      attachableName: bidRequest.bidPackage.project.name,
                    },
                  ]}
                />
              )}
            />
            <Tabs.Route path="bids" render={() => <Bids bidRequest={bidRequest} />} />
            {process.env.REACT_APP_ENABLE_MESSAGING === 'true' && (
              <Tabs.Route
                path="messages"
                render={() => (
                  <MessagesTab
                    bidRequestId={bidRequest.id}
                    bidPackageId={bidRequest.bidPackage.id}
                    potentialRecipients={potentialMessageRecipients}
                  />
                )}
              />
            )}
          </Tabs.Content>
        </Tabs.Container>
      </Page>
    </>
  );
};

export default BidRequest;
