import React from 'react';
import { useLazyQuery } from '@apollo/react-hooks';

import { COMPANY_TYPE } from '__graphql__/globalTypes';
import Messages from 'shared/components/Messages';

import { queryMessageThreadsInBidRequest } from './api';

interface Props {
  bidRequestId: string;
  bidPackageId: string;
  potentialRecipients: {
    bidRequestId: string;
    name: string;
  }[];
}

const MessagesTab: React.FC<Props> = ({ bidRequestId, bidPackageId, potentialRecipients }) => {
  const activeUserType = COMPANY_TYPE.agent;
  const threadsQuery = useLazyQuery<{}>(queryMessageThreadsInBidRequest, {
    variables: { bidRequestId },
  });

  return (
    <Messages
      threadsQuery={threadsQuery}
      threadsQueryName="messageThreadsInBidRequest"
      refetchQueries={['MessageThreadsInBidRequest']}
      activeUserType={activeUserType}
      potentialRecipients={potentialRecipients}
      bidPackageId={bidPackageId}
    />
  );
};

export default MessagesTab;
