import React, { ReactElement, ReactNode } from 'react';
import { Transition } from 'react-transition-group';

import { MODAL_VARIANTS } from 'shared/constants';
import { isEntered, isExiting, isExited } from 'shared/utils/transitionStates';
import Portal from 'shared/components/Portal';

import { Overlay, Layout, Card, CloseIcon, TRANSITION_DURATION } from './Styles';

export interface ModalContentProps {
  className?: string;
  isOpen: boolean;
  openModal: () => void;
  closeModal: () => void;
  isActive: boolean;
  variant?: MODAL_VARIANTS;
  renderLink?: ({ open }: { open: () => void }) => ReactNode;
  renderContent: ({
    close,
    isReadyToLoad,
    isActive,
  }: {
    close: () => void;
    isReadyToLoad: boolean;
    isActive: boolean;
  }) => ReactNode;
  modalCardRef: React.Ref<HTMLDivElement>;
}

const ModalContent = ({
  className,
  isOpen,
  openModal,
  closeModal,
  isActive,
  variant = MODAL_VARIANTS.center,
  renderLink = () => null,
  renderContent,
  modalCardRef,
}: ModalContentProps): ReactElement => (
  <>
    {renderLink({ open: openModal })}
    <Portal>
      <Transition in={isOpen} timeout={TRANSITION_DURATION}>
        {state => (
          <>
            <Overlay transitionState={state} isActive={isActive || isExiting(state)} />
            <Layout
              data-testid="modal-layout"
              data-test-is-active={isActive}
              variant={variant}
              transitionState={state}
              isActive={isActive || isExiting(state)}
            >
              <Card
                ref={modalCardRef}
                className={className}
                variant={variant}
                transitionState={state}
              >
                {!isExited(state) && (
                  <CloseIcon
                    data-cy-close-modal
                    data-testid="modal-close-button"
                    type="close"
                    variant={variant}
                    onClick={closeModal}
                  />
                )}
                {!isExited(state) &&
                  renderContent({
                    close: closeModal,
                    isReadyToLoad: isEntered(state) || isExiting(state),
                    isActive,
                  })}
              </Card>
            </Layout>
          </>
        )}
      </Transition>
    </Portal>
  </>
);

export default ModalContent;
