import React, { FC } from 'react';
import { useTranslation } from 'react-i18next';

import Folder from 'shared/components/DocumentsTable/classes/Folder';
import File from 'shared/components/DocumentsTable/classes/File';
import { formatFileSize } from 'shared/utils/files';
import { formatDateTime } from 'shared/utils/dateTime';
import PrintDialog from 'shared/components/PrintDialog';

import {
  DocumentsPrintContainer,
  Heading,
  Table,
  Row,
  Cell,
  TableHeader,
  FileNameColumn,
  SizeColumn,
  LastChangedColumn,
} from './PrintDocuments.Styles';
import { DocumentsTable_documents } from './__graphql__/DocumentsTable_documents';

interface PrintDocumentsProps {
  rootFolder: Folder;
  documents: DocumentsTable_documents[];
  title: string;
}

const buildDocumentRows = ({
  folder,
  documents,
}: {
  folder: Folder;
  documents: DocumentsTable_documents[];
}): Array<{
  fullPathName: string;
  document: DocumentsTable_documents;
}> => {
  const files: Array<[File, DocumentsTable_documents]> = folder.getFilesWithDocs(documents);
  const folders: Folder[] = folder.getChildFolders();
  const rows = files.map(([file, document]) => ({ fullPathName: file.getPath(), document }));
  return rows.concat(
    ...folders.map(childFolder => buildDocumentRows({ folder: childFolder, documents })),
  );
};

const PrintDocuments: FC<PrintDocumentsProps> = ({ rootFolder, documents, title }) => {
  const { t } = useTranslation('documentsPrint');

  const documentRows = buildDocumentRows({ folder: rootFolder, documents });

  return (
    <DocumentsPrintContainer>
      <PrintDialog />
      <Heading>{t('title', { title })}</Heading>
      <Table>
        <colgroup>
          <FileNameColumn />
          <SizeColumn />
          <LastChangedColumn />
        </colgroup>
        <thead>
          <tr>
            <TableHeader>{t('document.name')}</TableHeader>
            <TableHeader>{t('document.size')}</TableHeader>
            <TableHeader>{t('document.lastChanged')}</TableHeader>
          </tr>
        </thead>
        <tbody>
          {documentRows.map(({ fullPathName, document }, index) => {
            return (
              <Row key={index}>
                <Cell>{fullPathName}</Cell>
                <Cell>{formatFileSize(document.size)}</Cell>
                <Cell>{formatDateTime(document.updatedAt)}</Cell>
              </Row>
            );
          })}
        </tbody>
      </Table>
    </DocumentsPrintContainer>
  );
};

export default PrintDocuments;
