import React from 'react';
import { useTranslation } from 'react-i18next';

import { HtmlHeadTags, Tabs, Page } from 'shared/components';
import { HeadingLarge } from 'shared/components/Styles';

import Companies from './Companies';
import Users from './Users';
import BidRequests from './BidRequests';

const Admin = () => {
  const { t } = useTranslation('admin');

  return (
    <Page>
      <HtmlHeadTags title="adminPanel" />
      <HeadingLarge>{t('adminPanel')}</HeadingLarge>
      <Tabs.Container>
        <Tabs.Links>
          <Tabs.Link isDefault to="companies">
            {t('companies')}
          </Tabs.Link>
          <Tabs.Link to="users">{t('users')}</Tabs.Link>
          <Tabs.Link to="bid-requests">{t('bidRequests')}</Tabs.Link>
        </Tabs.Links>
        <Tabs.Content>
          <Tabs.Route path="companies" component={Companies} />
          <Tabs.Route path="users" component={Users} />
          <Tabs.Route path="bid-requests" component={BidRequests} />
        </Tabs.Content>
      </Tabs.Container>
    </Page>
  );
};

export default Admin;
