import React, { useState, useEffect, useContext } from 'react';
import gql from 'graphql-tag';
import { useTranslation } from 'react-i18next';
import { QueryTuple } from '@apollo/react-hooks';

import QueryMatchesContext from 'shared/contexts/queryMatchesContext';
import { COMPANY_TYPE } from '__graphql__/globalTypes';
import { isDoneLoading } from 'shared/utils/apollo';
import Button from 'shared/components/Button';
import EmptyContent from 'shared/components/EmptyContent';
import PageLoader from 'shared/components/PageLoader';
import QueryErrors from 'shared/components/QueryErrors';
import useQueryParamInitialAction from 'shared/hooks/useQueryParamInitialAction';

import ThreadsList, { fragmentThreadsList, ThreadsListProps } from './ThreadsList';
import ThreadDetails from './ThreadDetails';
import NewMessage, { NewMessageProps } from './NewMessage';
import { MessagesWrapper, ControlsArea, StyledSearchInput, DetailsArea } from './index.styles';

export const fragmentMessages = gql`
  fragment Messages_messageThread on MessageThread {
    ...ThreadsList_messageThread
  }

  ${fragmentThreadsList}
`;

interface Data {
  // We don't know the name of the query that gets us message threads
  [key: string]: { messageThreads: ThreadsListProps['threads'] };
}

export interface MessagesProps {
  threadsQuery: QueryTuple<Data, { query?: string }>;
  threadsQueryName: string;
  refetchQueries: string[];
  potentialRecipients: NewMessageProps['potentialRecipients'];
  activeUserType: COMPANY_TYPE;
  bidPackageId: string;
}

type State =
  | {
      name: 'newMessage';
    }
  | {
      name: 'showThread';
      selectedThreadId?: string;
    };

const Messages: React.FC<MessagesProps> = ({
  threadsQuery,
  threadsQueryName,
  refetchQueries,
  activeUserType,
  potentialRecipients,
  bidPackageId,
}) => {
  const [queryThreads, threadsQueryMeta] = threadsQuery;

  const { t } = useTranslation('messages');
  const [state, setState] = useState<State>({ name: 'showThread' });
  const [searchString, setSearchString] = useState<string>('');
  const { isDesktop } = useContext(QueryMatchesContext);

  const loadThreads = async () => {
    if (threadsQueryMeta.called) {
      const results = await threadsQueryMeta.refetch({ query: searchString });

      if (results.data) {
        const threads = results.data[threadsQueryName].messageThreads;

        if (threads.length > 0 && isDesktop) {
          selectThread(threads[0].id);
        }
      }
    } else {
      // Can't execute lazy query immediately after the initial render; says
      // "Cannot read property 'getCurrentResult' of undefined"
      setTimeout(() => queryThreads({ variables: { query: searchString } }));
    }
  };
  const selectThread = (id: string) => {
    setState({ name: 'showThread', selectedThreadId: id });
  };
  const openNewMessage = () => setState({ name: 'newMessage' });

  useEffect(() => {
    // eslint-disable-next-line @typescript-eslint/no-floating-promises
    loadThreads();
    // Only run this code when the searchString variable changes
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchString]);

  useQueryParamInitialAction('threadId', selectThread);

  if (!isDoneLoading(threadsQueryMeta)) {
    return <PageLoader />;
  }

  if (threadsQueryMeta.error) {
    return <QueryErrors error={threadsQueryMeta.error} />;
  }

  const threads = threadsQueryMeta.data[threadsQueryName].messageThreads;

  const displayThreadsList = isDesktop || (state.name === 'showThread' && !state.selectedThreadId);
  const displayDetailsArea =
    isDesktop ||
    (state.name === 'showThread' && state.selectedThreadId) ||
    state.name === 'newMessage';
  const displayControlsArea = displayThreadsList;

  const renderMainArea = () => {
    if (threads.length > 0) {
      return (
        <>
          {displayThreadsList && (
            <ThreadsList
              threads={threads}
              onSelectThread={selectThread}
              activeUserType={activeUserType}
              selectedThreadId={
                state.name === 'showThread' ? state.selectedThreadId || threads[0].id : null
              }
            />
          )}
          {displayDetailsArea && (
            <DetailsArea>
              {state.name === 'showThread' && (
                <ThreadDetails
                  messageThreadId={state.selectedThreadId || threads[0].id}
                  activeUserType={activeUserType}
                  unselectThread={() => setState({ name: 'showThread' })}
                  refetchQueries={refetchQueries}
                />
              )}
              {state.name === 'newMessage' && (
                <NewMessage
                  potentialRecipients={potentialRecipients}
                  activeUserType={activeUserType}
                  selectThread={selectThread}
                  refetchQueries={refetchQueries}
                  goBack={() => setState({ name: 'showThread' })}
                  bidPackageId={bidPackageId}
                />
              )}
            </DetailsArea>
          )}
        </>
      );
    }

    if (state.name === 'newMessage') {
      return (
        <DetailsArea>
          <NewMessage
            potentialRecipients={potentialRecipients}
            activeUserType={activeUserType}
            selectThread={selectThread}
            refetchQueries={refetchQueries}
            goBack={() => setState({ name: 'showThread' })}
            bidPackageId={bidPackageId}
          />
        </DetailsArea>
      );
    }

    if (threadsQueryMeta.variables.query) {
      return (
        <EmptyContent>
          <EmptyContent.CallToAction>
            {t('noMatchingMessages.callToAction')}
          </EmptyContent.CallToAction>
          <Button hollow onClick={() => setSearchString('')} data-cy-button-name="resetSearch">
            {t('buttons.resetSearchQuery')}
          </Button>
        </EmptyContent>
      );
    }

    return (
      <EmptyContent>
        <EmptyContent.CallToAction>{t('noMessages.callToAction')}</EmptyContent.CallToAction>
        <EmptyContent.Explanation>{t('noMessages.explanation')}</EmptyContent.Explanation>{' '}
        <Button hollow onClick={openNewMessage} data-cy-button-name="noContentNewMessage">
          {t('buttons.newMessage')}
        </Button>
      </EmptyContent>
    );
  };

  return (
    <>
      {displayControlsArea && (
        <ControlsArea>
          <StyledSearchInput
            value={searchString || ''}
            onChange={setSearchString}
            data-cy-input-name="messageSearch"
          />
          <Button onClick={openNewMessage} data-cy-button-name="newMessage">
            {t('buttons.newMessage')}
          </Button>
        </ControlsArea>
      )}
      <MessagesWrapper useBorder={displayThreadsList}>{renderMainArea()}</MessagesWrapper>
    </>
  );
};

export default Messages;
