import React from 'react';
import { RouteComponentProps, Redirect } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { PageLoader, UnauthenticatedPage, Form, Field } from 'shared/components';
import { ContentBox } from 'shared/components/UnauthenticatedPage/Styles';
import {
  SubmitButton,
  Heading,
  SuccessMessage,
  Tip,
} from 'shared/components/UnauthenticatedStyles';
import routes from 'shared/constants/routes';

import { MatchParams, useBidInviteFeedback } from './useBidInviteFeedback';

interface FormValues {
  declinedReason: string;
}

const BidInviteFeedback: React.FC<RouteComponentProps<MatchParams>> = routeProps => {
  const { t } = useTranslation('bidInvites');

  const { isFeedbackNeeded, isLoading, hasGivenFeedback, setDeclinedReason } = useBidInviteFeedback(
    routeProps.match.params,
  );

  if (isLoading) {
    return <PageLoader />;
  }

  if (hasGivenFeedback) {
    return (
      <UnauthenticatedPage title={t('overview.declinedReasonSuccess')}>
        <ContentBox>
          <Heading>{t('overview.declinedReasonHeader')}</Heading>
          <SuccessMessage data-cy-success>{t('overview.declinedReasonSuccess')}</SuccessMessage>
        </ContentBox>
      </UnauthenticatedPage>
    );
  }

  if (isFeedbackNeeded) {
    return (
      <UnauthenticatedPage title="inviteDeclined">
        <Form<FormValues>
          initialValues={{
            declinedReason: '',
          }}
          validations={{
            declinedReason: [Form.is.required()],
          }}
          onSubmit={setDeclinedReason}
        >
          <ContentBox as={Form.Element}>
            <Heading>{t('overview.declinedReasonHeader')}</Heading>
            <Tip>{t('overview.declinedReasonAsk')}</Tip>
            <Field.Textarea name="declinedReason" minRows={4} />
            <SubmitButton type="submit">{t('common:submit')}</SubmitButton>
          </ContentBox>
        </Form>
      </UnauthenticatedPage>
    );
  }

  return <Redirect to={routes.bidInvite(routeProps.match.params.bidInviteId)} />;
};

export default BidInviteFeedback;
