import { useCallback, useEffect, useContext, Ref, useRef } from 'react';

import { KEY_CODES } from 'shared/constants';
import ModalsContext from 'shared/contexts/modalsContext';
import useKeyboardEventCallback from 'shared/hooks/useKeyboardEventCallback';

export interface ModalStateData {
  isOpen: boolean;
  openModal: () => void;
  closeModal: () => void;
  isActive: boolean;
}

const useModalState = (
  isOpenProp: boolean | undefined,
  ref: Ref<HTMLElement>,
  onOpen: () => void = () => {},
  onClose: () => void = () => {},
): ModalStateData => {
  const { getIsActive, setAsActive, setAsInactive, getIsOpen } = useContext(ModalsContext);
  const isOpen = getIsOpen(ref);
  const isActive = getIsActive(ref);
  const upToDateSetAsInactive = useRef(setAsInactive);

  upToDateSetAsInactive.current = setAsInactive;

  const openModal = () => {
    setAsActive(ref);
    onOpen();
  };

  const closeModal = useCallback(() => {
    if (isActive) {
      setAsInactive(ref);
      onClose();
    }
  }, [isActive, setAsInactive, ref, onClose]);

  useEffect(() => {
    if (isOpenProp) {
      setAsActive(ref);
    }
    // Only run when the isOpenProp changes
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isOpenProp]);

  useEffect(() => {
    return () => {
      upToDateSetAsInactive.current(ref);
    };
    // Run when unmounting
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useKeyboardEventCallback(window, KEY_CODES.escape, closeModal, { condition: isOpen });

  return {
    isOpen: isOpenProp === undefined ? isOpen : isOpenProp,
    openModal,
    closeModal,
    isActive,
  };
};

export default useModalState;
