import 'react-app-polyfill/ie11';
import 'react-app-polyfill/stable';

import React from 'react';
import ReactDOM from 'react-dom';
import { ApolloProvider } from '@apollo/react-hooks';
import * as Sentry from '@sentry/browser';
import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import moment from 'moment';
import { de } from 'moment/locale/de';
import axios from 'axios';

import apolloClient from 'apolloClient';
import history from 'browserHistory';
import App from 'components/App/App';
import * as translations from 'shared/translations';
import * as Analytics from 'shared/utils/analytics';
import applyGoogleTranslateFix from 'shared/utils/gtranslateFix';
import { rootElementId } from 'shared/constants';

if (process.env.NODE_ENV === 'production') {
  Sentry.init({
    dsn: 'https://40a32219710749fe9fe776c9d609689f@sentry.io/1439389',
    environment: process.env.NODE_ENV,
  });

  // Segment.com analytics integration
  Analytics.load(process.env.REACT_APP_SEGMENT_WRITE_KEY);
  history.listen(Analytics.trackPageview);
}

axios.interceptors.response.use(
  response => response,
  error => {
    Sentry.captureException(error);
    throw error;
  },
);

// eslint-disable-next-line @typescript-eslint/no-floating-promises
i18n.use(initReactI18next).init({
  resources: translations,
  lng: process.env.REACT_APP_DEFAULT_LANG,
  fallbackLng: 'de',
  defaultNS: 'common',
  fallbackNS: 'common',
  interpolation: { escapeValue: false },
});

moment.locale(process.env.REACT_APP_DEFAULT_LANG === 'de' ? de : 'en');

applyGoogleTranslateFix();

ReactDOM.render(
  <ApolloProvider client={apolloClient}>
    <App />
  </ApolloProvider>,
  document.getElementById(rootElementId),
);
