import React from 'react';
import { useTranslation } from 'react-i18next';

import { BidRequestForSubcontractor_bidRequestForSubcontractor_bidPackage_project_office } from './__graphql__/BidRequestForSubcontractor';
import { Name, Text, Anchor } from './CustomerInfo.styles';

type Props = BidRequestForSubcontractor_bidRequestForSubcontractor_bidPackage_project_office;

const CustomerInfo: React.FC<Props> = ({
  name,
  street,
  postalCode,
  city,
  countryCode,
  email,
  phone,
  fax,
  website,
}) => {
  const { t } = useTranslation();

  return (
    <>
      <Name>{name}</Name>
      {street && <Text>{street}</Text>}
      {(postalCode || city) && <Text>{[postalCode, city].join(' ')}</Text>}
      {countryCode && <Text>{t(`countries:${countryCode}`)}</Text>}
      <br />
      {email && (
        <Text>
          {`${t('companyInfo.email')}: `}
          <Anchor to={`mailto:${email}`}>{email}</Anchor>
        </Text>
      )}
      {phone && (
        <Text>
          {`${t('companyInfo.phone')}: `}
          <Anchor to={`tel:${phone}`}>{phone}</Anchor>
        </Text>
      )}
      {fax && (
        <Text>
          {`${t('companyInfo.fax')}: `}
          <Anchor to={`tel:${fax}`}>{fax}</Anchor>
        </Text>
      )}
      {website && (
        <Text>
          {`${t('companyInfo.website')}: `}
          <Anchor to={website}>{website}</Anchor>
        </Text>
      )}
    </>
  );
};

export default CustomerInfo;
