import React from 'react';
import { useTranslation } from 'react-i18next';
import { useMutation } from '@apollo/react-hooks';

import { Modal, Form, Field, ModalFormBody } from 'shared/components';
import { HeadingMedium } from 'shared/components/Styles';
import { ModalContentProps } from 'shared/components/Modal/ModalContent';
import { USER_TYPE } from '__graphql__/globalTypes';
import toast from 'shared/utils/toast';

import { mutationUpdateUserType } from './api';
import { UsersTableDropdown_user } from './__graphql__/UsersTableDropdown_user';
import { UpdateUserType, UpdateUserTypeVariables } from './__graphql__/UpdateUserType';

interface Props {
  user: UsersTableDropdown_user;
  renderLink: ModalContentProps['renderLink'];
}

interface FormValues {
  type: USER_TYPE;
}

const ChangeTypeModal: React.FC<Props> = ({ user, renderLink }) => {
  const { t } = useTranslation('company');

  const [updateUserType, { loading: mutating }] = useMutation<
    UpdateUserType,
    UpdateUserTypeVariables
  >(mutationUpdateUserType, {
    refetchQueries: ['Users', 'AdminUsers'],
  });

  return (
    <Modal
      renderLink={renderLink}
      renderContent={modal => (
        <Form<FormValues>
          initialValues={{
            type: user.type,
          }}
          validations={{
            type: [Form.is.required()],
          }}
          onSubmit={async values => {
            try {
              await updateUserType({ variables: { id: user.id, ...values } });
              toast.success(t('updateUserTypeSuccess'));
              modal.close();
            } catch (error) {
              toast.error(error);
            }
          }}
        >
          <ModalFormBody
            scrollable={false}
            close={modal.close}
            working={mutating}
            useFullPageHeight={false}
          >
            <HeadingMedium>{t('updateUserType')}</HeadingMedium>
            <Field.SelectSingle
              required
              name="type"
              label={t('user.type')}
              options={[USER_TYPE.employee, USER_TYPE.external].map(type => ({
                value: type,
                label: t(`user.${type}`),
              }))}
            />
          </ModalFormBody>
        </Form>
      )}
    />
  );
};

export default ChangeTypeModal;
