import React, { useEffect, useState } from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import { BID_REQUEST_STATUS, PROJECT_STATUS, BID_PACKAGE_STATUS } from '__graphql__/globalTypes';
import toast from 'shared/utils/toast';
import { mutationUpdateBidRequestStatusBySubcontractor } from 'shared/utils/queries';
import {
  UpdateBidRequestStatusBySubcontractor,
  UpdateBidRequestStatusBySubcontractorVariables,
} from 'shared/utils/__graphql__/UpdateBidRequestStatusBySubcontractor';
import useQueryParamInitialAction from 'shared/hooks/useQueryParamInitialAction';

import DeclinedReasonForm from './DeclinedReasonForm';
import {
  ActionsBar,
  ActionsContent,
  StyledProjectClosedMessage,
  ChangeFeedbackButton,
  DecisionButton,
  DecisionIcon,
} from './Actions.styles';
import { BidRequestForSubcontractor_bidRequestForSubcontractor } from './__graphql__/BidRequestForSubcontractor';

interface Props {
  bidRequest: BidRequestForSubcontractor_bidRequestForSubcontractor;
}

const Actions: React.FC<Props> = ({ bidRequest }) => {
  const { t } = useTranslation('bidInvites');
  const [selectedStatus, setSelectedStatus] = useState<BID_REQUEST_STATUS | null>(
    bidRequest.status,
  );

  const [mutateStatus, { loading: statusLoading }] = useMutation<
    UpdateBidRequestStatusBySubcontractor,
    UpdateBidRequestStatusBySubcontractorVariables
  >(mutationUpdateBidRequestStatusBySubcontractor, {
    refetchQueries: () => ['BidRequestForSubcontractor'],
    awaitRefetchQueries: true,
  });

  useQueryParamInitialAction('status', setSelectedStatus);

  const selectStatus = (status: BID_REQUEST_STATUS | null) => {
    const updateStatus = async () => {
      if (status !== null) {
        try {
          await mutateStatus({ variables: { id: bidRequest.id, status } });
        } catch {
          toast.error();
        }
      }
    };

    // Run the mutation before setting `selectedStatus` to trigger the loading
    // state, but don't wait until it finishes
    // eslint-disable-next-line @typescript-eslint/no-floating-promises
    updateStatus();
    setSelectedStatus(status);
  };

  useEffect(() => {
    setSelectedStatus(bidRequest.status);
  }, [bidRequest.status]);

  if (bidRequest.bidPackage.project.status === PROJECT_STATUS.closed) {
    return (
      <ActionsBar>
        <ActionsContent>
          <StyledProjectClosedMessage>{t('overview.projectClosed')}</StyledProjectClosedMessage>
        </ActionsContent>
      </ActionsBar>
    );
  }

  if (bidRequest.bidPackage.status === BID_PACKAGE_STATUS.bidAwarded) {
    return (
      <ActionsBar>
        <ActionsContent>
          <StyledProjectClosedMessage>
            {t('overview.actionsBar.bidAwarded')}
          </StyledProjectClosedMessage>
        </ActionsContent>
      </ActionsBar>
    );
  }

  if (!statusLoading && selectedStatus === BID_REQUEST_STATUS.bidSubmitted) {
    return null;
  }

  if (
    !statusLoading &&
    (selectedStatus === BID_REQUEST_STATUS.inviteAccepted ||
      selectedStatus === BID_REQUEST_STATUS.inviteDeclined)
  ) {
    return (
      <ActionsBar>
        <ActionsContent>
          {selectedStatus === BID_REQUEST_STATUS.inviteAccepted && (
            <>
              <DecisionIcon type="check-fat" size={28} />
              <h3>{t('overview.youWillBid')}</h3>
            </>
          )}
          {selectedStatus === BID_REQUEST_STATUS.inviteDeclined && (
            <>
              <DecisionIcon type="close-fat" size={28} />
              <h3>{t('overview.youWillNotBid')}</h3>
            </>
          )}
          <ChangeFeedbackButton
            onClick={() => {
              selectStatus(null);
            }}
          >
            {t('overview.changeFeedback')}
          </ChangeFeedbackButton>
        </ActionsContent>
      </ActionsBar>
    );
  }

  return (
    <ActionsBar>
      <ActionsContent>
        <h3>{t('overview.willYouBid')}</h3>
        <DecisionButton
          hollow={bidRequest.status !== BID_REQUEST_STATUS.inviteAccepted}
          icon={bidRequest.status === BID_REQUEST_STATUS.inviteAccepted ? 'check-fat' : undefined}
          working={statusLoading && selectedStatus === BID_REQUEST_STATUS.inviteAccepted}
          onClick={() => {
            if (bidRequest.status !== BID_REQUEST_STATUS.inviteAccepted) {
              selectStatus(BID_REQUEST_STATUS.inviteAccepted);
            }
          }}
        >
          {t('overview.yes')}
        </DecisionButton>
        <DeclinedReasonForm
          bidRequest={bidRequest}
          onSubmit={() => Promise.resolve(selectStatus(BID_REQUEST_STATUS.inviteDeclined))}
          renderLink={modal => (
            <DecisionButton
              hollow={bidRequest.status !== BID_REQUEST_STATUS.inviteDeclined}
              icon={
                bidRequest.status === BID_REQUEST_STATUS.inviteDeclined ? 'check-fat' : undefined
              }
              working={statusLoading && selectedStatus === BID_REQUEST_STATUS.inviteDeclined}
              onClick={() => {
                if (bidRequest.status !== BID_REQUEST_STATUS.inviteDeclined) {
                  modal.open();
                }
              }}
            >
              {t('overview.no')}
            </DecisionButton>
          )}
        />
        <DecisionButton
          hollow={bidRequest.status !== BID_REQUEST_STATUS.undecided}
          icon={bidRequest.status === BID_REQUEST_STATUS.undecided ? 'check-fat' : undefined}
          working={statusLoading && selectedStatus === BID_REQUEST_STATUS.undecided}
          onClick={() => {
            if (bidRequest.status !== BID_REQUEST_STATUS.undecided) {
              selectStatus(BID_REQUEST_STATUS.undecided);
            }
          }}
        >
          {t('overview.maybe')}
        </DecisionButton>
      </ActionsContent>
    </ActionsBar>
  );
};

export default Actions;
