import React from 'react';
import { useTranslation } from 'react-i18next';
import moment from 'moment';

interface FormattedMessageTimeProps {
  date: string;
  shortFormat?: boolean;
}

const FormattedMessageTime: React.FC<FormattedMessageTimeProps> = ({
  date,
  shortFormat = false,
}) => {
  const { t } = useTranslation('datesAndTime');

  const yesterday = t('yesterday');

  const format = shortFormat
    ? {
        sameDay: 'LT',
        lastDay: `[${yesterday}]`,
        lastWeek: 'L',
        sameElse: 'L',
      }
    : {
        sameDay: 'LT',
        lastDay: `[${yesterday}], LT`,
        lastWeek: 'L, LT',
        sameElse: 'L, LT',
      };

  // Have to return a fragment due to React typings not being able to handle
  // string components:
  // https://github.com/DefinitelyTyped/DefinitelyTyped/issues/18912
  return <>{moment(date).calendar(undefined, format)}</>;
};

export default FormattedMessageTime;
