import { useState, useEffect, useCallback } from 'react';
import throttle from 'just-throttle';

import { mediaQueries } from 'shared/utils/styles';

export interface QueryMatches {
  isMobile: boolean;
  isDesktop: boolean;
}

const getMatches = () => ({
  mobile: window.matchMedia(mediaQueries.mobile).matches,
  desktop: window.matchMedia(mediaQueries.desktop).matches,
});

const useQueryMatches = (): QueryMatches => {
  const [matches, setMatches] = useState(getMatches());

  const handleResize = useCallback(
    throttle(() => {
      setMatches(getMatches());
    }, 500),
    [],
  );

  useEffect(() => {
    window.addEventListener('resize', handleResize);

    return () => window.removeEventListener('resize', handleResize);
  }, [handleResize]);

  return { isMobile: matches.mobile, isDesktop: matches.desktop };
};

export default useQueryMatches;
