import gql from 'graphql-tag';

export const queryAdminCompanies = gql`
  query AdminCompanies(
    $query: String
    $offset: Int!
    $limit: Int!
    $sortBy: String
    $sortDirection: SortDirection
  ) {
    adminCompanies(
      query: $query
      offset: $offset
      limit: $limit
      sortBy: $sortBy
      sortDirection: $sortDirection
    ) {
      companies {
        id
        name
        status
        remindersEnabled
      }
      totalRecords
    }
  }
`;

export const mutationAdminUpdateCompany = gql`
  mutation AdminUpdateCompany($id: ID!, $input: CompanyInput!) {
    adminUpdateCompany(id: $id, input: $input) {
      id
    }
  }
`;

export const mutationAdminActivateCompany = gql`
  mutation AdminActivateCompany($id: ID!) {
    adminActivateCompany(id: $id)
  }
`;

export const mutationAdminInviteUsers = gql`
  mutation AdminInviteUsers($companyId: ID!, $users: [UserToInviteInput!]!) {
    adminInviteUsers(companyId: $companyId, users: $users)
  }
`;
