import { useEffect, useCallback } from 'react';

import { KEY_CODES, KEY_CODE_VALUES } from 'shared/constants';

const useKeyboardEventCallback = (
  element: Node | Window,
  keyCode: KEY_CODES,
  callback: (event: KeyboardEvent) => void,
  { condition = true },
): void => {
  const handleEvent = useCallback(
    event => {
      if (event.keyCode === KEY_CODE_VALUES[keyCode]) {
        callback(event);
      }
    },
    [keyCode, callback],
  );

  useEffect(() => {
    if (condition) {
      element.addEventListener('keydown', handleEvent);
    }

    return () => {
      element.removeEventListener('keydown', handleEvent);
    };
  }, [condition, element, handleEvent]);
};

export default useKeyboardEventCallback;
