import { useCallback, useRef } from 'react';
import moment from 'moment';
import jwtDecode from 'jwt-decode';
import { useTranslation } from 'react-i18next';

import toast from 'shared/utils/toast';

const useAuthToken = () => {
  const { t } = useTranslation('apiErrors');
  const hasDisplayedLocalStorageError = useRef(false);

  // Safari will throw an error when trying to access local storage if the
  // setting "Block all cookies" is enabled.
  const getStoredToken = useCallback((): string | null => {
    try {
      return localStorage.getItem('token');
    } catch (error) {
      if (!hasDisplayedLocalStorageError.current) {
        hasDisplayedLocalStorageError.current = true;
        toast.error(t('localStorageNotPermitted'));
      }
      return null;
    }
  }, [t]);

  const removeStoredToken = useCallback((): void => {
    try {
      localStorage.removeItem('token');
    } catch (error) {
      if (!hasDisplayedLocalStorageError.current) {
        hasDisplayedLocalStorageError.current = true;
        toast.error(t('localStorageNotPermitted'));
      }
    }
  }, [t]);

  const tokenContent = useCallback((): { exp: number } | null => {
    const token = getStoredToken();

    return token === null ? token : jwtDecode(token);
  }, [getStoredToken]);

  const getTimeToExpiration = useCallback((): number => {
    const token = tokenContent();

    return token ? token.exp - moment().unix() : 0;
  }, [tokenContent]);

  const isTokenValid = useCallback((): boolean => !!getStoredToken() && getTimeToExpiration() > 0, [
    getStoredToken,
    getTimeToExpiration,
  ]);

  return { isTokenValid, getTimeToExpiration, removeStoredToken };
};

export default useAuthToken;
