import React from 'react';
import { Transition } from 'react-transition-group';

import { isEnter } from 'shared/utils/transitionStates';
import Input from 'shared/components/Input';
import { TRANSITION_DURATION } from 'shared/components/DropdownCard';
import onlyOnKeyboardFocus from 'shared/utils/onlyOnKeyboardFocus';

import { StyledDatePicker, StyledDropdownCard } from './Styles';
import useDatePicker, { DatePickerHookOptions } from './useDatePicker';
import DateSection from './DateSection';
import TimeSection from './TimeSection';

export interface DatePickerProps {
  className?: string;
  // TODO: extend `DatePickerProps` from `InputProps` from 'shared/components/Input' instead
  name?: string;
  withTime?: boolean;
  value?: DatePickerHookOptions['value'];
  onChange: DatePickerHookOptions['onChange'];
  disabled?: boolean;
}

const DatePicker: React.FC<DatePickerProps> = ({
  className,
  onChange,
  value = null,
  withTime = true,
  ...inputProps
}) => {
  const {
    displayedDate,
    inputValue,
    isOpen,
    selectedDate,

    openDropdown,
    changeYear,
    goToPrevMonth,
    goToNextMonth,
    changeDay,
    changeTime,
    handleManualInput,

    datePickerRef,
    cardRef,
    inputRef,
  } = useDatePicker({ value, onChange, withTime });
  const { handleMouseDown, whenKeyboardFocus } = onlyOnKeyboardFocus();

  return (
    <StyledDatePicker ref={datePickerRef} data-cy-date-picker-name={inputProps.name}>
      <Input
        icon="calendar"
        {...inputProps}
        ref={inputRef}
        className={className}
        autoComplete="off"
        value={inputValue}
        onClick={openDropdown}
        onMouseDown={handleMouseDown}
        onChange={handleManualInput}
        onFocus={whenKeyboardFocus(openDropdown)}
        data-testid="date-picker-input"
      />
      <Transition in={isOpen} timeout={TRANSITION_DURATION} unmountOnExit>
        {state => (
          <StyledDropdownCard withTime={withTime} isOpen={isEnter(state)} ref={cardRef}>
            <DateSection
              selectedDate={selectedDate}
              displayedDate={displayedDate}
              changeDay={changeDay}
              goToPrevMonth={goToPrevMonth}
              goToNextMonth={goToNextMonth}
              changeYear={changeYear}
            />
            {withTime && <TimeSection selectedDate={selectedDate} changeTime={changeTime} />}
          </StyledDropdownCard>
        )}
      </Transition>
    </StyledDatePicker>
  );
};

export { DATE_FORMAT_WITH_TIME, DATE_FORMAT_WITHOUT_TIME } from './constants';
export default DatePicker;
