import React, { useContext, FC } from 'react';
import { useTranslation } from 'react-i18next';

import QueryMatchesContext from 'shared/contexts/queryMatchesContext';

import DropdownMenu from '../DropdownMenu';

import DocumentsTableContext from './DocumentsTableContext';
import NameFormModal from './NameFormModal';
import DropzoneLink from './DropzoneLink';
import { StyledTopControls, TopControlsDropdownTrigger, TopControlsButton } from './Styles';
import Folder from './classes/Folder';

interface TopControlsProps {
  rootFolder: Folder;
  printPath?: string;
}

const TopControls: FC<TopControlsProps> = ({ rootFolder, printPath }) => {
  const { t } = useTranslation();
  const { isMobile, isDesktop } = useContext(QueryMatchesContext);

  const { handleFilesDrop, isReadOnly, refetch, downloadURL } = useContext(DocumentsTableContext);

  return (
    <StyledTopControls>
      <div>
        <TopControlsButton icon="refresh" onClick={refetch} />
        {!rootFolder.isEmpty() && (
          <>
            {printPath && (
              <TopControlsButton isLink icon="printer" href={printPath} target="_blank" />
            )}
            <TopControlsButton
              isLink
              icon="download"
              data-cy-button-name="downloadAll"
              href={downloadURL(rootFolder)}
            >
              {t('documentsTable.downloadAll')}
            </TopControlsButton>
          </>
        )}
      </div>
      {!isReadOnly && isMobile && (
        <DropdownMenu.Container
          renderTrigger={({ isTriggered, toggleTrigger }) => (
            <TopControlsDropdownTrigger
              hollow
              icon={isTriggered ? 'chevron-up' : 'chevron-down'}
              iconSize={18}
              onClick={toggleTrigger}
            />
          )}
        >
          <DropdownMenu.Action
            forwardedAs={DropzoneLink}
            targetFolder={rootFolder}
            inputProps={{}}
            handleFilesDrop={handleFilesDrop}
          >
            {t('documentsTable.uploadFiles')}
          </DropdownMenu.Action>
          <DropdownMenu.Action
            forwardedAs={DropzoneLink}
            targetFolder={rootFolder}
            inputProps={{ webkitdirectory: '' }}
            handleFilesDrop={handleFilesDrop}
          >
            {t('documentsTable.uploadFolder')}
          </DropdownMenu.Action>
          <NameFormModal
            formLabel="documentsTable.createFolder"
            onSubmit={newFolderName => rootFolder.createFolderAndSave(newFolderName)}
            renderLink={modal => (
              <DropdownMenu.Action onClick={modal.open}>
                {t('documentsTable.createFolder')}
              </DropdownMenu.Action>
            )}
          />
        </DropdownMenu.Container>
      )}
      {!isReadOnly && isDesktop && (
        <div>
          <DropzoneLink targetFolder={rootFolder} inputProps={{}}>
            <TopControlsButton icon="upload">{t('documentsTable.uploadFiles')}</TopControlsButton>
          </DropzoneLink>
          <DropzoneLink targetFolder={rootFolder} inputProps={{ webkitdirectory: '' }}>
            <TopControlsButton icon="upload">{t('documentsTable.uploadFolder')}</TopControlsButton>
          </DropzoneLink>
          <NameFormModal
            formLabel="documentsTable.createFolder"
            onSubmit={newFolderName => rootFolder.createFolderAndSave(newFolderName)}
            renderLink={modal => (
              <TopControlsButton
                icon="plus"
                data-cy-button-name="createFolder"
                onClick={modal.open}
              >
                {t('documentsTable.createFolder')}
              </TopControlsButton>
            )}
          />
        </div>
      )}
    </StyledTopControls>
  );
};

export default TopControls;
