import React from 'react';
import styled, { css } from 'styled-components/macro';

import { COLOR, mixin, mediaQueries } from 'shared/utils/styles';
import Clickable from 'shared/components/Clickable';

export const ThreadsListWrapper = styled.div`
  flex-shrink: 0;
  width: 375px;

  /* For when we need to display this element with a fixed height, as in the bid
  package view */
  overflow: auto;

  @media ${mediaQueries.mobile} {
    max-width: 100%;
  }

  @media ${mediaQueries.desktop} {
    border-right: 1px solid ${COLOR.borderLight};

    /* If the list of threads is longer than the thread details, the border at
    the bottom of the last thread list item will touch the messages content
    border. Since there's not really a way for us to detect when this happens,
    we just add some whitespace between the last thread list item and the bottom
    of the messages content. */
    padding-bottom: 100px;
  }
`;

interface ThreadsListItemProps {
  isSelected: boolean;
}

// Prettier conflicts with ESLint here
// eslint-disable-next-line no-unexpected-multiline
export const ThreadsListItem = styled(({ isSelected, ...props }) => <Clickable {...props} />)<
  ThreadsListItemProps
>`
  display: block;
  width: 100%;
  padding: 15px;
  border-bottom: 1px solid ${COLOR.borderLight};
  text-align: left;

  @media ${mediaQueries.mobile} {
    &:last-child {
      border-bottom: 0;
    }
  }

  ${props =>
    props.isSelected &&
    css`
      background-color: ${COLOR.backgroundMedium};

      ${/* sc-selector */ Time},
      ${/* sc-selector */ MessageSnippet} {
        color: ${COLOR.textDark};
      }
    `}
`;

export const ThreadHeader = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
`;

export const OtherParticipant = styled.div`
  ${mixin.link()};

  ${/* sc-selector */ ThreadsListItem}:hover & {
    text-decoration: underline;
  }
`;

export const Time = styled.div`
  margin-left: 1em;
  color: ${COLOR.textLight};
  white-space: nowrap;
`;

export const Subject = styled.div`
  margin-bottom: 10px;
`;

export const MessageSnippet = styled.div`
  max-height: 2.4em;
  color: ${COLOR.textLight};
  line-height: 1.2em;
  overflow: hidden;

  /* Display ellipsis at the end of the second line in Webkit/Blink browsers */
  /* stylelint-disable value-no-vendor-prefix, property-no-vendor-prefix */
  -webkit-line-clamp: 2;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  /* stylelint-enable value-no-vendor-prefix, property-no-vendor-prefix */
`;
