import React, { ReactNode, useState, useEffect, useCallback, useRef } from 'react';
import { useTranslation } from 'react-i18next';
import debounce from 'just-debounce-it';
import { useQuery } from '@apollo/react-hooks';

import { SortDirection } from 'shared/constants';
import Pagination from 'shared/components/Pagination';
import QueryErrors from 'shared/components/QueryErrors';
import PageLoader from 'shared/components/PageLoader';
import Button from 'shared/components/Button';
import { SpaceBetween, TableSearch } from 'shared/components/Styles';
import { isDoneLoading } from 'shared/utils/apollo';

import { querySubcontractors } from './api';
import Filters from './Filters';
import { Container, Content, Subs, SubsNoResults } from './Styles';
import { FilterVariables, FilterValues } from './types';
import {
  SubcontractorsForFilterableList,
  SubcontractorsForFilterableList_subcontractors_subcontractors,
} from './__graphql__/SubcontractorsForFilterableList';

interface SubcontractorsListProps {
  location?: FilterValues['location'];
  renderHeaderAction?: () => ReactNode;
  renderNoResults?: (isFiltered: boolean) => ReactNode;
  withShadow?: boolean;
  children: (
    subcontractors: SubcontractorsForFilterableList_subcontractors_subcontractors[],
  ) => ReactNode;
}

const ITEMS_PER_PAGE = 12;
const DISTANCE = 200;

const getLatLong = (location: FilterValues['location']): string | undefined =>
  location.lat && location.long ? `${location.lat},${location.long}` : undefined;

const getQueryVariablesForFilters = (filters: FilterValues): FilterVariables => ({
  query: filters.query || undefined,
  latLong: getLatLong(filters.location),
  distance: Number(filters.distance) || undefined,
  workCategoryIds: filters.workCategoryIds,
  companySizes: filters.companySizes,
});

const SubcontractorsList: React.FC<SubcontractorsListProps> = ({
  location = { lat: '', long: '', location: '' },
  renderHeaderAction = () => null,
  renderNoResults = () => null,
  withShadow = false,
  children,
}) => {
  const { t } = useTranslation('bidPackages');
  const defaultFilters: FilterValues = {
    location,
    query: '',
    distance: String(DISTANCE),
    workCategoryIds: [],
    companySizes: [],
  };
  const [filters, setFilters] = useState<FilterValues>(defaultFilters);
  const [locationString, setLocationString] = useState(defaultFilters.location.location);

  const { refetch, variables, loading, error, ...subcontractorsQueryResults } = useQuery<
    SubcontractorsForFilterableList
  >(querySubcontractors, {
    variables: {
      sortBy: 'name',
      sortDirection: SortDirection.asc,
      offset: 0,
      limit: ITEMS_PER_PAGE,
      ...getQueryVariablesForFilters(filters),
    },
  });
  const data = subcontractorsQueryResults.data as SubcontractorsForFilterableList;
  const displayContent = isDoneLoading({ loading, data });
  const isMounted = useRef(true);

  const reloadFilters = useCallback(
    debounce((filtersToReloadWith: FilterValues) => {
      // react-apollo@3.1.1 had the following change: "Be a bit more defensive
      // when it comes to accessing the internal ObservableQuery instance, to
      // avoid attempting to use it after a component has unmounted.". This
      // means that trying to refetch when the component has been unmounted will
      // result in an error. This isn't a problem in practice, but it does
      // appear in Cypress.
      if (isMounted.current) {
        // eslint-disable-next-line @typescript-eslint/no-floating-promises
        refetch(getQueryVariablesForFilters(filtersToReloadWith));
      }
    }, 200),
    [refetch],
  );

  useEffect(() => {
    reloadFilters(filters);
  }, [reloadFilters, filters]);

  useEffect(() => {
    return () => {
      isMounted.current = false;
    };
  }, []);

  if (error) return <QueryErrors error={error} />;

  const handleReset = () => {
    setFilters(defaultFilters);
    setLocationString(defaultFilters.location.location);
  };

  const isFiltered =
    variables.query ||
    variables.workCategoryIds.length > 0 ||
    variables.companySizes.length > 0 ||
    variables.latLong ||
    variables.distance !== DISTANCE;

  const renderNoResultsWithFallback = () => (
    <SubsNoResults>
      {renderNoResults(isFiltered) || (
        <>
          <div>{t('noResults')}</div>
          <Button onClick={handleReset}>{t('resetFilters')}</Button>
        </>
      )}
    </SubsNoResults>
  );

  return (
    <Container withShadow={withShadow}>
      <Filters
        refetch={refetch}
        filters={filters}
        setFilters={setFilters}
        locationString={locationString}
        setLocationString={setLocationString}
      />
      <Content>
        <SpaceBetween>
          <TableSearch
            value={filters.query}
            onChange={(query: string) => setFilters({ ...filters, query: query || '' })}
          />
          {renderHeaderAction()}
        </SpaceBetween>
        <Subs>
          {!displayContent && <PageLoader />}
          {displayContent && children(data.subcontractors.subcontractors)}
          {displayContent &&
            data.subcontractors.subcontractors.length === 0 &&
            renderNoResultsWithFallback()}
        </Subs>
        {displayContent && data.subcontractors.totalRecords !== 0 && (
          <Pagination
            currentOffset={variables.offset}
            itemsPerPage={variables.limit}
            totalRecords={data.subcontractors.totalRecords}
            onChange={offset => refetch({ offset })}
          />
        )}
      </Content>
    </Container>
  );
};

export default SubcontractorsList;
