import React from 'react';
import { useTranslation } from 'react-i18next';
import { useMutation } from '@apollo/react-hooks';

import DropdownMenu from 'shared/components/DropdownMenu';
import ConfirmationModal from 'shared/components/ConfirmationModal';
import toast from 'shared/utils/toast';

import { mutationRestoreInvite } from './api';

interface Props {
  userId: string;
}

const RestoreInvite: React.FC<Props> = ({ userId }) => {
  const { t } = useTranslation('company');
  const [restoreInvite, { loading }] = useMutation(mutationRestoreInvite, {
    refetchQueries: () => ['Users', 'AdminUsers'],
  });

  return (
    <ConfirmationModal
      title="company:restoreInviteMessage"
      message="company:restoreInviteDescription"
      isWorking={loading}
      renderLink={modal => (
        <DropdownMenu.Action onClick={modal.open}>{t('restoreInvite')}</DropdownMenu.Action>
      )}
      onConfirm={async modal => {
        try {
          await restoreInvite({ variables: { id: userId } });
          toast.success(t('inviteRestored'));
          modal.close();
        } catch (error) {
          toast.error(error);
        }
      }}
    />
  );
};

export default RestoreInvite;
