import styled, { css } from 'styled-components/macro';
import * as CSS from 'csstype';

import Spinner from 'shared/components/Spinner';
import Icon from 'shared/components/Icon';
import { COLOR, font, mixin, mediaQueries } from 'shared/utils/styles';

export const Wrapper = styled.div`
  position: relative;
  width: 100%;
  border-radius: 4px;
  border: 1px solid ${COLOR.borderLight};
  background: #fff;
`;

export const TableElement = styled.table`
  width: 100%;
  table-layout: fixed;
  border-spacing: 0;

  ${props =>
    props.theme.isResponsive &&
    css`
      @media ${mediaQueries.mobile} {
        display: block;
      }
    `}
`;

interface HeaderProps {
  textAlign?: CSS.TextAlignProperty;
  fixedWidth?: number;
  percentWidth?: number;
}

export const Header = styled.th<HeaderProps>`
  position: sticky;
  left: 0;
  top: 0;
  padding: 0;
  z-index: 1;
  background: #fff;
  line-height: 1.2;
  color: ${COLOR.textMedium};
  border-bottom: 1px solid ${COLOR.borderLight};
  text-align: ${props => props.textAlign || 'left'};

  /*
    Fix for Edge, where border-bottom can be covered by the background on sticky elements:
    https://stackoverflow.com/questions/41882616/why-border-is-not-visible-with-position-sticky-when-background-color-exists
  */
  background-clip: padding-box;

  ${mixin.truncateText}
  ${font.size(13)};
  ${font.regular}
  ${props =>
    props.fixedWidth &&
    css`
      width: ${props.fixedWidth}px;
    `}
  ${props =>
    props.percentWidth &&
    css`
      width: ${props.percentWidth}%;
    `}
  &:first-child {
    padding-left: 5px;
    border-top-left-radius: 4px;
  }
  &:last-child {
    border-top-right-radius: 4px;
  }

  ${props =>
    props.theme.isResponsive &&
    css`
      @media ${mediaQueries.mobile} {
        display: none;
      }
    `}
`;

interface HeaderTextProps {
  hasSort: boolean;
}

export const HeaderText = styled.div<HeaderTextProps>`
  display: inline-block;
  padding: 14px 10px 14px 12px;
  ${props => props.hasSort && headerTextSortStyles};
`;

const headerTextSortStyles = `
  position: relative;
  padding-right: 15px;
  ${mixin.clickable}
`;

export const SortIcon = styled(Icon)`
  position: absolute;
  top: 15px;
  right: 0;
  font-size: 13px;
  color: ${COLOR.textLight};
`;

export const TableBody = styled.tbody`
  position: relative;
  opacity: 1;
  transition: opacity 0.15s;

  ${props =>
    props.theme.isResponsive &&
    css`
      @media ${mediaQueries.mobile} {
        display: block;
      }
    `}
`;

interface StyledRowProps {
  isHighlighted: boolean;
}

export const StyledRow = styled.tr<StyledRowProps>`
  ${props => props.isHighlighted && `background: ${COLOR.backgroundLight};`}

  ${props =>
    props.theme.isResponsive &&
    css`
      @media ${mediaQueries.mobile} {
        display: block;
        border-top: 1px solid ${COLOR.borderLight};

        ${TableBody} &:first-child {
          border-top: 0;
        }
      }
    `}
`;

interface CellProps {
  textAlign?: CSS.TextAlignProperty;
}

export const Cell = styled.td<CellProps>`
  position: relative;
  padding: 0;
  border-top: 1px solid ${COLOR.borderLight};
  line-height: 1.2;
  color: ${COLOR.textDark};
  ${font.size(14)};

  &:first-child {
    padding-left: 5px;
  }

  ${props =>
    props.textAlign &&
    css`
      text-align: ${props.textAlign};
      div {
        margin: auto;
      }
    `}

  ${/* sc-selector */ StyledRow}:first-child & {
    border-top: 0;
  }

  ${props =>
    props.theme.isResponsive &&
    css`
      @media ${mediaQueries.mobile} {
        display: block;
        margin: 20px 0;
        border-top: 0;

        &:first-child {
          padding-left: 0;
        }
      }
    `}
`;

export const CellMobileLabel = styled.div`
  padding: 0 10px 0 12px;
  color: ${COLOR.textMedium};
`;

export const CellIcon = styled.div`
  i {
    position: absolute;
    top: 18px;
    left: 10px;
    font-size: 18px;
    color: ${COLOR.textDark};
    ${mixin.clickable};
  }

  ${props =>
    props.theme.isResponsive &&
    css`
      @media ${mediaQueries.mobile} {
        width: 24px;
        height: 24px;

        i {
          top: 0px;
          left: 10px;
        }
      }
    `}
`;

export const CellText = styled.div`
  padding: 20px 10px 20px 12px;
  ${mixin.truncateText}

  ${props =>
    props.theme.isResponsive &&
    css`
      @media ${mediaQueries.mobile} {
        padding: 10px 10px 0 12px;
      }
    `}
`;

export const CellLink = styled.span`
  display: block;
  width: 100%;
  padding: 20px 10px 20px 12px;
  text-align: left;
  ${mixin.link()}
  ${mixin.truncateText}

${props =>
  props.theme.isResponsive &&
  css`
    @media ${mediaQueries.mobile} {
      padding: 10px 10px 0 12px;
    }
  `}
`;

export const StyledSpinner = styled(Spinner)`
  position: absolute;
  top: 7px;
  right: 16px;
`;
