import React from 'react';
import { useTranslation } from 'react-i18next';
import { useQuery, useMutation } from '@apollo/react-hooks';

import {
  Button,
  Table,
  Cell,
  PageLoader,
  QueryErrors,
  UsersTableDropdown,
} from 'shared/components';
import { SpaceBetween, TableSearch } from 'shared/components/Styles';
import { UserStatusTag } from 'shared/components/TagStyles';
import UsersInviteModal from 'shared/components/UsersInviteModal';
import { nullString, TABLE_ITEMS_PER_PAGE } from 'shared/constants';
import { isDoneLoading } from 'shared/utils/apollo';
import { queryCurrentUser } from 'shared/utils/queries';
import { CurrentUser } from 'shared/utils/__graphql__/CurrentUser';
import toast from 'shared/utils/toast';
import { SortDirection } from '__graphql__/globalTypes';

import { queryUsers, mutationInviteUsers } from './api';
import { InviteUsers } from './__graphql__/InviteUsers';
import { Users } from './__graphql__/Users';

const formatUserName = (firstName: null | string = nullString, lastName: null | string) =>
  [firstName, lastName].filter(Boolean).join(' ');

const CompanyUsers = () => {
  const { t } = useTranslation('company');

  // For components that have multiple useQuery hooks, we have to use the
  // no-cache fetch policy, otherwise errors are not handled correctly.
  // We do not know the reason for this issue.

  const currentUserRequest = useQuery<CurrentUser>(queryCurrentUser, {
    fetchPolicy: 'no-cache',
  });

  const usersRequest = useQuery<Users>(queryUsers, {
    variables: {
      offset: 0,
      limit: TABLE_ITEMS_PER_PAGE,
      sortBy: 'name',
      sortDirection: SortDirection.ASC,
    },
    fetchPolicy: 'no-cache',
  });

  const [inviteUsers, { loading: inviteUsersMutating }] = useMutation<InviteUsers>(
    mutationInviteUsers,
    {
      refetchQueries: () => ['Users'],
      awaitRefetchQueries: true,
    },
  );

  for (const error of [currentUserRequest.error, usersRequest.error]) {
    if (error) return <QueryErrors error={error} />;
  }

  if (!isDoneLoading(currentUserRequest) || !isDoneLoading(usersRequest)) {
    return <PageLoader />;
  }

  const { data, refetch, variables } = usersRequest;

  return (
    <>
      <SpaceBetween>
        <TableSearch onChange={search => refetch({ query: search })} />
        <UsersInviteModal
          loading={inviteUsersMutating}
          renderLink={modal => (
            <Button onClick={modal.open} data-cy-button-name="inviteUsers">
              {t('inviteUsers')}
            </Button>
          )}
          onSubmit={({ users }, confirm) => {
            inviteUsers({ variables: { users } }).then(() => {
              toast.success(t('invitationsSent'));
              confirm();
            }, toast.error);
          }}
        />
      </SpaceBetween>
      <Table
        columns={[
          { label: t('user.name'), sortField: 'name' },
          { label: t('user.jobTitle'), sortField: 'jobTitle' },
          { label: t('user.email') },
          { label: t('user.type') },
          { label: t('user.status') },
          { fixedWidth: 50 },
        ]}
        rows={data.users.users.map(user => (
          <Table.Row rowId={user.id}>
            <Cell.Text>{formatUserName(user.firstName, user.lastName)}</Cell.Text>
            <Cell.Text>{user.jobTitle || nullString}</Cell.Text>
            <Cell.Text>{user.email}</Cell.Text>
            <Cell.Text>{t(`user.${user.type}`)}</Cell.Text>
            <UserStatusTag inList status={user.status}>
              {t(`userStatus.${user.status}`)}
            </UserStatusTag>
            <UsersTableDropdown
              isCurrentUser={currentUserRequest.data.me.id === user.id}
              user={user}
            />
          </Table.Row>
        ))}
        totalRecords={data.users.totalRecords}
        refetch={refetch}
        variables={variables}
      />
    </>
  );
};

export default CompanyUsers;
