import React, { useEffect, useState } from 'react';
import { useDropzone } from 'react-dropzone';
import { useTranslation } from 'react-i18next';
import axios from 'axios';

import apolloClient from 'apolloClient';
import { getImageUrl, IMAGE_TYPE } from 'shared/utils/images';
import toast from 'shared/utils/toast';
import Spinner from 'shared/components/Spinner';
import Icon from 'shared/components/Icon';

import { queryImageUploadUrl } from './api';
import {
  StyledImageUploader,
  Dropzone,
  Message,
  SpinnerContainer,
  Image,
  RemoveLinkContainer,
  RemoveLink,
} from './Styles';

const uploadImageToStorage = async (uploadUrl: string, file: File) =>
  axios.put(uploadUrl, file, {
    headers: { 'Content-Type': file.type },
  });

interface Props {
  name: string;
  value: string | null;
  onChange: (storageKey: string | null) => void;
}

const ImageUploader: React.FC<Props> = ({ name, value, onChange }) => {
  const { t } = useTranslation();
  const [preview, setPreview] = useState<string | null>(null);
  const [isUploading, setUploading] = useState(false);

  const { getRootProps, getInputProps } = useDropzone({
    accept: 'image/*',
    multiple: false,
    onDrop: async ([file]) => {
      if (!file) return;
      setUploading(true);
      setPreview(URL.createObjectURL(file));
      try {
        const { data } = await apolloClient.query({
          query: queryImageUploadUrl,
          variables: { filename: file.name },
        });
        await uploadImageToStorage(data.imageUploadUrl.uploadUrl, file);
        onChange(data.imageUploadUrl.storageKey);
      } catch (error) {
        toast.error(error);
      }
      setUploading(false);
    },
  });

  useEffect(() => {
    return () => {
      if (preview) {
        URL.revokeObjectURL(preview);
      }
    };
  }, [preview]);

  return (
    <StyledImageUploader data-cy-image-uploader={name}>
      {isUploading && (
        <SpinnerContainer>
          <Spinner size={60} />
        </SpinnerContainer>
      )}
      {preview || value ? (
        <>
          <Dropzone {...getRootProps()} paddingBottom={10}>
            <input {...getInputProps()} />
            <Image
              src={preview || (value && getImageUrl(value, IMAGE_TYPE.imageUploader)) || ''}
              alt={t('imageUploader.uploadedImage')}
            />
          </Dropzone>
          <RemoveLinkContainer>
            <RemoveLink
              onClick={() => {
                setPreview(null);
                onChange(null);
              }}
            >
              {t('imageUploader.removeImage')}
            </RemoveLink>
          </RemoveLinkContainer>
        </>
      ) : (
        <Dropzone {...getRootProps()}>
          <input {...getInputProps()} />
          <Message>
            <Icon type="upload" top={1} />
            {t('imageUploader.uploadImage')}
          </Message>
        </Dropzone>
      )}
    </StyledImageUploader>
  );
};

export { Image };
export default ImageUploader;
