import React, { FC, useContext } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { useMutation, useQuery } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import toast from 'shared/utils/toast';
import { Form, Field, PageLoader } from 'shared/components';
import { SubmitButton } from 'shared/components/UnauthenticatedStyles';
import UnauthenticatedPage from 'shared/components/UnauthenticatedPage';
import { ContentBox } from 'shared/components/UnauthenticatedPage/Styles';
import AuthContext from 'shared/contexts/authContext';
import routes from 'shared/constants/routes';

import { mutationCreatePassword, queryCheckResetPasswordToken } from './api';
import {
  CheckResetPasswordToken,
  CheckResetPasswordTokenVariables,
} from './__graphql__/CheckResetPasswordToken';
import { CreatePassword, CreatePasswordVariables } from './__graphql__/CreatePassword';

interface CreatePasswordProps {
  token: string;
}

interface CreatePasswordFormValues {
  password: string;
}

const AuthCreatePassword: FC<RouteComponentProps<CreatePasswordProps>> = ({ match, history }) => {
  const { t } = useTranslation('auth');
  const { login } = useContext(AuthContext);

  const redirectOnError = (error: Error) => {
    history.push(routes.resetPassword());
    toast.error(error.message);
  };

  const {
    params: { token },
  } = match;

  const { error: tokenError, loading } = useQuery<
    CheckResetPasswordToken,
    CheckResetPasswordTokenVariables
  >(queryCheckResetPasswordToken, {
    variables: { token },
  });
  const [createPassword, { loading: mutating }] = useMutation<
    CreatePassword,
    CreatePasswordVariables
  >(mutationCreatePassword);

  if (loading) return <PageLoader />;
  if (tokenError) redirectOnError(tokenError);

  return (
    <UnauthenticatedPage title="createPassword">
      <Form<CreatePasswordFormValues>
        initialValues={{
          password: '',
        }}
        validations={{
          password: [Form.is.required(), Form.is.maxLength(250)],
        }}
        onSubmit={({ password }) =>
          createPassword({ variables: { password, token } }).then(({ data }) => {
            if (data) {
              login(data.createPassword);
              toast.success(t('createPasswordSuccess'));
            }
          }, redirectOnError)
        }
      >
        <ContentBox as={Form.Element}>
          <Field.Input name="password" label={t('newPassword')} type="password" icon="lock" />
          <SubmitButton type="submit" working={mutating}>
            {t('createPassword')}
          </SubmitButton>
        </ContentBox>
      </Form>
    </UnauthenticatedPage>
  );
};

export default AuthCreatePassword;
