import React from 'react';
import { useTranslation } from 'react-i18next';
import gql from 'graphql-tag';

import { formatDateTime } from 'shared/utils/dateTime';
import { buildAttachableName } from 'shared/utils/documents';
import CurrencyFormatter from 'shared/components/CurrencyFormatter';
import { DocumentsTableSingle } from 'shared/components/DocumentsTable';
import SubcontractorInfo, {
  fragmentSubcontractorInfo,
  SubcontractorInfoProps,
} from 'shared/components/SubcontractorInfo';
import ContactsList, {
  fragmentContactsList,
  ContactsListProps,
} from 'shared/components/ContactsList';
import { HeadingMedium, SectionTitle } from 'shared/components/Styles';
import { MODAL_VARIANTS } from 'shared/constants';
import { DOCUMENT_ENTITY } from '__graphql__/globalTypes';

import { ModalProps } from '../Modal';

import {
  StyledBidModal,
  SubcontractorSection,
  ContactsListSection,
  PullUp,
  Details,
  DetailsSection,
  DetailsTitle,
  DetailsTotalAmount,
  DetailsDateTime,
  DetailsMessage,
} from './Styles';

export const fragmentBidModal = gql`
  fragment BidModal_bid on Bid {
    id
    totalAmount
    submittedAt
    message
  }
  fragment BidModal_bidRequest on BidRequest {
    id
    ...ContactsList_bidRequest
    subcontractor {
      id
      ...SubcontractorInfo_subcontractorInfo
    }
  }
  ${fragmentSubcontractorInfo}
  ${fragmentContactsList}
`;

export interface BidModalProps {
  bid: {
    id: string;
    totalAmount: number;
    submittedAt: string;
    message: string | null;
  };
  subcontractor?: SubcontractorInfoProps['subcontractor'];
  bidInvites: ContactsListProps['bidInvites'];
  renderLink: ModalProps['renderLink'];
}

const BidModal = ({ bid, subcontractor, bidInvites, renderLink }: BidModalProps) => {
  const { t } = useTranslation();

  return (
    <StyledBidModal
      variant={MODAL_VARIANTS.aside}
      renderLink={renderLink}
      renderContent={() => (
        <>
          <HeadingMedium>{t('bidModal.bidSummary')}</HeadingMedium>
          {subcontractor ? (
            <SubcontractorSection>
              <SubcontractorInfo subcontractor={subcontractor} />
              <ContactsListSection>
                <ContactsList bidInvites={bidInvites} />
              </ContactsListSection>
            </SubcontractorSection>
          ) : (
            <PullUp />
          )}
          <SectionTitle>{t('bidModal.details')}</SectionTitle>
          <Details>
            <DetailsSection>
              <DetailsTitle>{t('bid.totalAmount')}</DetailsTitle>
              <DetailsTotalAmount data-cy-bid-modal-amount>
                <CurrencyFormatter value={bid.totalAmount} />
              </DetailsTotalAmount>
            </DetailsSection>
            <DetailsSection>
              <DetailsTitle>{t('bid.submittedOn')}</DetailsTitle>
              <DetailsDateTime data-cy-bid-modal-submitted-on>
                {formatDateTime(bid.submittedAt)}
              </DetailsDateTime>
            </DetailsSection>
            <DetailsSection>
              <DetailsTitle>{t('bid.message')}</DetailsTitle>
              <DetailsMessage data-cy-bid-modal-message>{bid.message || '-'}</DetailsMessage>
            </DetailsSection>
          </Details>
          <SectionTitle>{t('bidModal.documents')}</SectionTitle>
          <DocumentsTableSingle
            attachableType={DOCUMENT_ENTITY.Bid}
            attachableId={bid.id}
            attachableName={buildAttachableName([
              String(t('documentsTable.bidDocuments')),
              subcontractor?.name,
            ])}
            isReadOnly
          />
        </>
      )}
    />
  );
};

export default BidModal;
