import React from 'react';
import { useQuery } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import {
  Page,
  HtmlHeadTags,
  Table,
  QueryErrors,
  PageLoader,
  Cell,
  BidModal,
  CurrencyFormatter,
} from 'shared/components';
import { HeadingLarge } from 'shared/components/Styles';
import { BidRequestStatusTag } from 'shared/components/TagStyles';
import { nullString, TABLE_ITEMS_PER_PAGE } from 'shared/constants';
import { formatDateTime } from 'shared/utils/dateTime';
import { isDoneLoading } from 'shared/utils/apollo';
import routes from 'shared/constants/routes';
import { SortDirection } from '__graphql__/globalTypes';

import { queryBidRequestsForAgent } from './api';
import { BidRequestsForAgent } from './__graphql__/BidRequestsForAgent';

function hasTotalAmount<T extends { totalAmount: number | null }>(
  bid: T,
): bid is NonNullable<T> & { totalAmount: number } {
  return bid.totalAmount !== null;
}

const BidRequests: React.FC<void> = () => {
  const { t } = useTranslation('bidInvites');

  const { error, ...queryResults } = useQuery<BidRequestsForAgent>(queryBidRequestsForAgent, {
    variables: {
      offset: 0,
      limit: TABLE_ITEMS_PER_PAGE,
      sortBy: 'createdAt',
      sortDirection: SortDirection.ASC,
    },
  });

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const {
    bidRequestsForAgent: { bidRequests, totalRecords },
  } = queryResults.data;

  const { variables, refetch } = queryResults;

  return (
    <Page>
      <HtmlHeadTags title="bidRequests" />
      <HeadingLarge>{t('requests.bidRequests')}</HeadingLarge>
      <Table
        columns={[
          { label: t('requests.tableHeader.project') },
          { label: t('requests.tableHeader.customer') },
          { label: t('requests.tableHeader.bidsDueAt'), percentWidth: 20 },
          { label: t('requests.tableHeader.status'), percentWidth: 15 },
          { label: t('requests.tableHeader.currentBid'), percentWidth: 15 },
        ]}
        rows={bidRequests.map(bidRequest => {
          const { currentBid } = bidRequest;
          return (
            <Table.Row rowId={bidRequest.id}>
              <Cell.Link to={routes.bidRequest(bidRequest.id)}>
                {bidRequest.bidPackage.project.name}
              </Cell.Link>
              <Cell.Text>{bidRequest.bidPackage.project.office.name}</Cell.Text>
              <Cell.Text>{formatDateTime(bidRequest.bidPackage.bidsDueAt)}</Cell.Text>
              <BidRequestStatusTag inList status={bidRequest.status}>
                {t(`request.${bidRequest.status}`)}
              </BidRequestStatusTag>
              {currentBid && hasTotalAmount(currentBid) ? (
                <BidModal
                  bid={currentBid}
                  bidInvites={bidRequest.bidInvites}
                  renderLink={modal => (
                    <Cell.Link onClick={modal.open}>
                      <CurrencyFormatter value={currentBid.totalAmount} />
                    </Cell.Link>
                  )}
                />
              ) : (
                <Cell.Text>{nullString}</Cell.Text>
              )}
            </Table.Row>
          );
        })}
        totalRecords={totalRecords}
        refetch={refetch}
        variables={variables}
      />
    </Page>
  );
};

export default BidRequests;
