import React, { useEffect } from 'react';

const PrintDialog: React.FC<{}> = () => {
  useEffect(() => {
    if (document.fonts) {
      // eslint-disable-next-line @typescript-eslint/no-floating-promises
      document.fonts.ready.then(() => {
        window.print();
      });
    }
  }, []);

  return null;
};

export default PrintDialog;
