import gql from 'graphql-tag';

import { fragmentSubcontractorsListItem } from 'shared/components/SubcontractorsListItem';

export const querySubcontractors = gql`
  query SubcontractorsForFilterableList(
    $query: String
    $companySizes: [COMPANY_SIZE!]
    $workCategoryIds: [ID]
    $latLong: String
    $distance: Float
    $offset: Int!
    $limit: Int!
    $sortBy: String
    $sortDirection: SortDirection
  ) {
    subcontractors(
      query: $query
      companySizes: $companySizes
      workCategoryIds: $workCategoryIds
      latLong: $latLong
      distance: $distance
      offset: $offset
      limit: $limit
      sortBy: $sortBy
      sortDirection: $sortDirection
    ) {
      subcontractors {
        id
        isBanned
        ...SubcontractorsListItem_subcontractorsListItem
      }
      totalRecords
    }
  }
  ${fragmentSubcontractorsListItem}
`;
