import { useState } from 'react';

import Console from 'shared/utils/console';

interface StackMethods<T> {
  peek: () => T;
  pop: () => void;
  push: (value: T) => void;
  toggle: (value: T) => void;
  contains: (value: T) => boolean;
  remove: (value: T) => void;
  debug: () => void;
}

const useStack = <T>(initialValue?: T): StackMethods<T> => {
  const [stack, setStack] = useState(initialValue ? [initialValue] : []);

  const peek = () => stack[stack.length - 1];
  const pop = () => setStack(stack.slice(0, -1));
  const push = (value: T) => setStack([...stack, value]);
  const toggle = (value: T) => (peek() === value ? pop() : push(value));
  const contains = (value: T) => stack.includes(value);
  const remove = (value: T) => setStack(stack.filter(item => item !== value));
  const debug = () => {
    Console.log(stack);
  };

  return { peek, pop, push, toggle, contains, remove, debug };
};

export default useStack;
