import gql from 'graphql-tag';

export const queryProjects = gql`
  query Projects(
    $query: String
    $offset: Int!
    $limit: Int!
    $status: String
    $sortBy: String
    $sortDirection: SortDirection
  ) {
    projects(
      query: $query
      offset: $offset
      limit: $limit
      status: $status
      sortBy: $sortBy
      sortDirection: $sortDirection
    ) {
      projects {
        id
        number
        name
        customer
        primaryUser {
          id
          firstName
          lastName
        }
        status
      }
      totalRecords
    }
  }
`;

export const mutationDeleteProject = gql`
  mutation DeleteProject($id: ID!) {
    deleteProject(id: $id)
  }
`;
