import gql from 'graphql-tag';

import { fragmentBidsList } from 'shared/components/BidsList';
import { fragmentBidFormModal } from 'shared/components/BidFormModal';

export const queryBidRequestsForBidPackage = gql`
  query BidRequestsForBidPackage(
    $query: String
    $sortBy: BID_REQUESTS_SORT_FIELD!
    $sortDirection: SortDirection!
    $bidPackageId: ID!
  ) {
    bidRequests(
      query: $query
      sortBy: $sortBy
      sortDirection: $sortDirection
      bidPackageId: $bidPackageId
    ) {
      totalRecords
      bidRequests {
        id
        updatedAt
        currentBidId
        ...BidsList_bidsList
        ...BidFormModal_bidRequest
        currentBid {
          id
          message
          totalAmount
          submittedAt
          ...BidFormModal_bid
        }
        status
        declinedReason
        bids {
          id
          message
          totalAmount
          submittedAt
        }
        subcontractor {
          id
          name
          website
          companySize
          street
          postalCode
          city
          countryCode
        }
        bidInvites {
          id
          status
          contact {
            id
            firstName
            lastName
            email
            isPerson
          }
        }
      }
    }
  }
  ${fragmentBidsList}
  ${fragmentBidFormModal}
`;

export const queryBidPackageHeaderForPrint = gql`
  query BidPackageHeaderForPrint($bidPackageId: ID!) {
    bidPackage(id: $bidPackageId) {
      id
      name
      project {
        id
        name
      }
    }
  }
`;

export const queryBidRequestsForPrint = gql`
  query BidRequestsForPrint(
    $sortBy: BID_REQUESTS_SORT_FIELD!
    $sortDirection: SortDirection!
    $bidPackageId: ID!
  ) {
    bidRequests(sortBy: $sortBy, sortDirection: $sortDirection, bidPackageId: $bidPackageId) {
      bidRequests {
        id
        status
        currentBid {
          id
          totalAmount
        }
        bidInvites {
          id
          contact {
            id
            isPerson
            title
            firstName
            lastName
            email
            phone
            fax
          }
        }
        subcontractor {
          id
          name
        }
      }
    }
  }
`;

export const mutationDeleteBidRequest = gql`
  mutation DeleteBidRequest($id: ID!) {
    deleteBidRequest(id: $id)
  }
`;

export const mutationAwardBidRequest = gql`
  mutation AwardBidRequest($id: ID!) {
    awardBidRequest(id: $id)
  }
`;

export const mutationUnAwardBidRequest = gql`
  mutation UnAwardBidRequest($id: ID!) {
    unAwardBidRequest(id: $id)
  }
`;

export const mutationRejectBidRequest = gql`
  mutation RejectBidRequest($id: ID!, $shouldSendNotification: Boolean!) {
    rejectBidRequest(id: $id, shouldSendNotification: $shouldSendNotification)
  }
`;

export const mutationUnRejectBidRequest = gql`
  mutation UnRejectBidRequest($id: ID!) {
    unRejectBidRequest(id: $id)
  }
`;

export const mutationUpdateBidRequestStatusInBidders = gql`
  mutation UpdateBidRequestStatusInBidders($id: ID!, $status: String!, $declinedReason: String) {
    updateBidRequestStatus(id: $id, status: $status, declinedReason: $declinedReason)
  }
`;
