import styled, { css } from 'styled-components/macro';

import Avatar from 'shared/components/Avatar';
import { font, mixin, COLOR } from 'shared/utils/styles';

export const Wrapper = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }
`;

interface StyledAvatarProps {
  isCentered: boolean;
}

export const StyledAvatar = styled(Avatar)<StyledAvatarProps>`
  ${props =>
    !props.isCentered &&
    css`
      align-self: start;
    `}
`;

interface TextWrapperProps {
  isAlignedWithAvatar: boolean;
}

export const TextWrapper = styled.div<TextWrapperProps>`
  margin-left: 20px;

  ${props =>
    props.isAlignedWithAvatar &&
    css`
      margin-top: 2px;
    `}

  /* Necessary for text truncation in ContactName to work: */
  overflow: hidden;
`;

export const Name = styled.div`
  ${font.size(16)};
  ${mixin.truncateText};
`;

export const JobTitle = styled.div`
  margin-top: 5px;
  ${font.size(14)};
  color: ${COLOR.textMedium};
  ${mixin.truncateText};
`;
