import React, { useState } from 'react';
import gql from 'graphql-tag';
import { useTranslation } from 'react-i18next';
import { useQuery } from '@apollo/react-hooks';

import { MODAL_VARIANTS } from 'shared/constants';
import {
  BID_REQUEST_STATUS,
  BID_PACKAGE_STATUS,
  PROJECT_STATUS,
  BID_INVITE_STATUS,
  BID_REQUESTS_SORT_FIELD,
  SortDirection,
} from '__graphql__/globalTypes';
import { formatDate } from 'shared/utils/dateTime';
import getContactName from 'shared/utils/getContactName';
import { QueryErrors, Table, Button, Modal } from 'shared/components';
import CurrencyFormatter from 'shared/components/CurrencyFormatter';
import { SpaceBetween, ProjectClosedMessage } from 'shared/components/Styles';
import EmptyContent from 'shared/components/EmptyContent';
import PageLoader from 'shared/components/PageLoader';
import { isDoneLoading } from 'shared/utils/apollo';
import routes from 'shared/constants/routes';

import AddModal from './Add';
import Request from './Request';
import RemoveLink from './RemoveLink';
import {
  Container,
  List,
  StyledSearchInput,
  Status,
  StatusIcon,
  TableCell,
  TableName,
  TableSecondary,
  RequestContainer,
  NoSelectionMessage,
  PrintButton,
  BidderActions,
  StyledTable,
} from './Styles';
import { queryBidRequestsForBidPackage } from './api';
import { BidRequestsForBidPackage } from './__graphql__/BidRequestsForBidPackage';
import { Bidders_bidPackage } from './__graphql__/Bidders_bidPackage';

interface Props {
  bidPackage: Bidders_bidPackage;
}

export const fragmentBidPackage = gql`
  fragment Bidders_bidPackage on BidPackage {
    id
    status
    project {
      status
    }
  }
`;

const Bidders: React.FC<Props> = ({ bidPackage }) => {
  const { t } = useTranslation('bidPackages');
  const [activeRequestId, setActiveRequestId] = useState<string | null>(null);
  const { pendingInvite } = BID_REQUEST_STATUS;
  const { error, ...queryResults } = useQuery<BidRequestsForBidPackage>(
    queryBidRequestsForBidPackage,
    {
      variables: {
        bidPackageId: bidPackage.id,
        sortBy: BID_REQUESTS_SORT_FIELD.subcontractorName,
        sortDirection: SortDirection.ASC,
      },
    },
  );

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const { data, refetch, variables } = queryResults;

  const activeRequest = data.bidRequests.bidRequests.find(({ id }) => id === activeRequestId);
  const isProjectClosed = bidPackage.project.status === PROJECT_STATUS.closed;

  return (
    <Container>
      <List>
        <SpaceBetween>
          <StyledSearchInput onChange={search => refetch({ query: search })} />
          <BidderActions>
            <PrintButton
              hollow
              icon="printer"
              iconSize={20}
              linkTo={routes.printBidders(bidPackage.id)}
              target="_blank"
            />
            <Modal
              variant={MODAL_VARIANTS.fullSize}
              renderLink={modal => (
                <Button
                  data-cy-button-name="new"
                  disabled={bidPackage.project.status === PROJECT_STATUS.closed}
                  onClick={modal.open}
                >
                  {bidPackage.status !== BID_PACKAGE_STATUS.draft
                    ? t('inviteBidders')
                    : t('addBidders')}
                </Button>
              )}
              renderContent={() => (
                <AddModal bidPackageId={bidPackage.id} bidPackageRefetch={refetch} />
              )}
            />
          </BidderActions>
        </SpaceBetween>
        <StyledTable
          columns={[
            { label: t('subcontractor.name'), percentWidth: 50, sortField: 'subcontractorName' },
            { label: t('request.status'), sortField: 'bidRequestStatus' },
          ]}
          rows={data.bidRequests.bidRequests.map(bidRequest => {
            const { currentBid } = bidRequest;
            const lastChanged = currentBid ? currentBid.submittedAt : bidRequest.updatedAt;
            const { bidInvites, status } = bidRequest;
            const contactName = getContactName(bidRequest.bidInvites[0].contact);
            const haveAllInvitesFailed =
              status === BID_REQUEST_STATUS.invited &&
              bidInvites.every(bidInvite => bidInvite.status === BID_INVITE_STATUS.failed);

            return (
              <Table.Row rowId={bidRequest.id}>
                <TableCell
                  onClick={() =>
                    setActiveRequestId(bidRequest.id === activeRequestId ? null : bidRequest.id)
                  }
                >
                  <TableName>{bidRequest.subcontractor.name}</TableName>
                  <TableSecondary>
                    {bidInvites.length === 1 && contactName}
                    {bidInvites.length > 1 &&
                      t('request.invitedContacts', {
                        contact: contactName,
                        n: bidInvites.length - 1,
                      })}
                  </TableSecondary>
                </TableCell>
                <TableCell>
                  <Status data-cy-status-container haveAllInvitesFailed={haveAllInvitesFailed}>
                    <StatusIcon status={status} haveAllInvitesFailed={haveAllInvitesFailed} />
                    {typeof currentBid?.totalAmount === 'number' ? (
                      <CurrencyFormatter value={currentBid.totalAmount} />
                    ) : (
                      t(`request.${haveAllInvitesFailed ? 'allInvitesFailed' : status}`)
                    )}

                    {status === pendingInvite && !isProjectClosed && (
                      <RemoveLink bidRequestId={bidRequest.id} />
                    )}
                  </Status>
                  <TableSecondary>
                    {t('request.updatedAt')}:{' '}
                    <span data-cy-bid-request-last-changed>{formatDate(lastChanged)}</span>
                  </TableSecondary>
                </TableCell>
              </Table.Row>
            );
          })}
          highlightedRowId={activeRequestId === null ? undefined : activeRequestId}
          refetch={refetch}
          variables={variables}
          contentIfEmpty={() =>
            isProjectClosed ? (
              <ProjectClosedMessage>{t('projectClosed')}</ProjectClosedMessage>
            ) : (
              <>
                <EmptyContent.CallToAction>
                  {t('addOrInviteBiddersCallToAction')}
                </EmptyContent.CallToAction>
                <EmptyContent.Explanation>
                  {t('addOrInviteBiddersExplanation')}
                </EmptyContent.Explanation>
                <Modal
                  variant={MODAL_VARIANTS.fullSize}
                  renderLink={modal => (
                    <EmptyContent.Button
                      data-cy-button-name="new"
                      disabled={bidPackage.project.status === PROJECT_STATUS.closed}
                      onClick={modal.open}
                    >
                      {bidPackage.status !== BID_PACKAGE_STATUS.draft
                        ? t('inviteBidders')
                        : t('addBidders')}
                    </EmptyContent.Button>
                  )}
                  renderContent={() => (
                    <AddModal bidPackageId={bidPackage.id} bidPackageRefetch={refetch} />
                  )}
                />
              </>
            )
          }
        />
      </List>
      <RequestContainer>
        {activeRequest ? (
          <Request bidPackage={bidPackage} bidRequest={activeRequest} />
        ) : (
          <NoSelectionMessage>{t('selectToViewDetails')}</NoSelectionMessage>
        )}
      </RequestContainer>
    </Container>
  );
};

export default Bidders;
