import gql from 'graphql-tag';

export const fragmentDocumentsTableDocuments = gql`
  fragment DocumentsTable_documents on Document {
    id
    filename
    size
    updatedAt
  }
`;

export const fragmentDocumentsTableFileTree = gql`
  fragment DocumentsTable_fileTree on FileTree {
    id
    fileTree
  }
`;

export const queryFileTreeWithDocuments = gql`
  query FileTreeWithDocuments($attachable: AttachableInput!) {
    fileTreeWithDocuments(attachable: $attachable) {
      documents {
        ...DocumentsTable_documents
      }
      fileTree {
        ...DocumentsTable_fileTree
      }
    }
  }
  ${fragmentDocumentsTableDocuments}
  ${fragmentDocumentsTableFileTree}
`;

export const queryCombinedFileTreeWithDocuments = gql`
  query CombinedFileTreeWithDocuments($attachables: [AttachableInput!]!) {
    combinedFileTreeWithDocuments(attachables: $attachables) {
      documents {
        ...DocumentsTable_documents
      }
      fileTrees {
        ...DocumentsTable_fileTree
      }
    }
  }
  ${fragmentDocumentsTableDocuments}
  ${fragmentDocumentsTableFileTree}
`;

export const mutationUpdateFileTree = gql`
  mutation UpdateFileTree($id: ID!, $fileTree: String!) {
    updateFileTree(id: $id, fileTree: $fileTree) {
      id
    }
  }
`;
