import gql from 'graphql-tag';

export const queryBidPackageForAddBidders = gql`
  query BidPackageForAddBidders($bidPackageId: ID!) {
    bidPackage(id: $bidPackageId) {
      id
      status
      addedContactIds
      project {
        id
        status
        lat
        long
        location
      }
    }
  }
`;

export const mutationAddContactToBidRequest = gql`
  mutation AddContactToBidRequest($bidPackageId: ID!, $subcontractorId: ID!, $contactId: ID!) {
    addContactToBidRequest(
      bidPackageId: $bidPackageId
      subcontractorId: $subcontractorId
      contactId: $contactId
    ) {
      id
    }
  }
`;
