import React, { useEffect, ReactNode, ReactElement } from 'react';
import { useTranslation } from 'react-i18next';
import { ThemeProvider } from 'styled-components/macro';

import { TABLE_ITEMS_PER_PAGE } from 'shared/constants';
import Pagination from 'shared/components/Pagination';
import EmptyContent from 'shared/components/EmptyContent';

import { QueryVariables, Refetch, Column, RowType } from './types';
import { TableElement, Wrapper } from './Styles';
import useSort from './useSort';
import Headers from './Headers';
import Rows from './Rows';
import Row from './Row';

export interface TableProps {
  columns: Column[];
  rows: RowType[];
  highlightedRowId?: string;
  totalRecords?: number;
  refetch?: Refetch;
  variables?: QueryVariables;
  contentIfEmpty?: () => ReactNode;
  isResponsive?: boolean;
}

const Table = ({
  columns,
  rows,
  highlightedRowId,
  totalRecords,
  refetch = () => {},
  variables = {},
  contentIfEmpty,
  isResponsive,
  ...props
}: TableProps): ReactElement | null => {
  const { t } = useTranslation();

  const { getSortIcon, changeSort } = useSort({ refetch, variables });

  const getTheme = (parentTheme: {}) => ({ ...parentTheme, isResponsive });

  const isFiltered = variables.query && variables.query.length > 0;
  const isEmpty = rows.length === 0;
  const currentOffset = variables.offset || 0;
  const itemsPerPage = variables.limit || TABLE_ITEMS_PER_PAGE;

  useEffect(() => {
    if (isEmpty && currentOffset !== 0) {
      refetch({ offset: currentOffset - itemsPerPage });
    }
  }, [isEmpty, currentOffset, itemsPerPage, refetch]);

  if (isEmpty && currentOffset !== 0) {
    return null;
  }

  if (isEmpty) {
    return (
      <Wrapper {...props}>
        <EmptyContent>
          {contentIfEmpty && !isFiltered ? contentIfEmpty() : t('noResults')}
        </EmptyContent>
      </Wrapper>
    );
  }

  return (
    <>
      <ThemeProvider theme={getTheme}>
        <Wrapper {...props}>
          <TableElement>
            <Headers columns={columns} getSortIcon={getSortIcon} changeSort={changeSort} />
            <Rows
              rows={rows}
              highlightedRowId={highlightedRowId}
              columns={columns}
              isResponsive={isResponsive}
            />
          </TableElement>
        </Wrapper>
      </ThemeProvider>
      {!!totalRecords && (
        <Pagination
          totalRecords={totalRecords}
          currentOffset={currentOffset}
          itemsPerPage={itemsPerPage}
          onChange={offset => refetch({ offset })}
        />
      )}
    </>
  );
};

Table.Row = Row;

export default Table;
