import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';

import toast from 'shared/utils/toast';
import ConfirmationModal from 'shared/components/ConfirmationModal';

import { StatusActionButton, ModalCheckbox } from './Styles';

interface RejectBidRequestModalProps {
  isRejecting: boolean;
  onConfirm: (shouldSendNotification: boolean) => Promise<void>;
}

const RejectBidRequestModal: React.FC<RejectBidRequestModalProps> = ({
  isRejecting,
  onConfirm,
}) => {
  const { t } = useTranslation('bidPackages');
  const [shouldSendNotification, setShouldSendNotification] = useState(false);

  return (
    <ConfirmationModal
      title={t('rejectBidConfirmTitle')}
      message={t('rejectBidConfirmMessage')}
      confirmText={t('rejectBid')}
      isWorking={isRejecting}
      renderLink={modal => (
        <StatusActionButton hollow data-cy-button-name="rejectBid" onClick={modal.open}>
          {t('rejectBid')}
        </StatusActionButton>
      )}
      renderContent={() => (
        <ModalCheckbox
          name="rejectBidRequestCheckbox"
          checked={shouldSendNotification}
          checkboxLabel={t('rejectBidConfirmCheckboxLabel')}
          onChange={setShouldSendNotification}
        />
      )}
      onConfirm={async () => {
        try {
          await onConfirm(shouldSendNotification);
          toast.success(t('bidRejectedSuccess'));
        } catch (error) {
          toast.error(error);
        }
        setShouldSendNotification(false);
      }}
      onCancel={() => {
        setShouldSendNotification(false);
      }}
    />
  );
};

export default RejectBidRequestModal;
