import { GraphQLError } from 'graphql';
import ApolloClient from 'apollo-client';
import { InMemoryCache } from 'apollo-cache-inmemory';
import { setContext } from 'apollo-link-context';
import { onError, ErrorResponse } from 'apollo-link-error';
import { HttpLink } from 'apollo-link-http';
import { ApolloLink } from 'apollo-link';
import i18n from 'i18next';

import toast from 'shared/utils/toast';

const authLink = setContext((_, { headers }) => {
  const token = localStorage.getItem('token');
  return {
    headers: {
      ...headers,
      authorization: token ? `Bearer ${token}` : '',
    },
  };
});

const errorLink = onError(({ operation, graphQLErrors = [], networkError }: ErrorResponse) => {
  graphQLErrors.forEach(formatError);
  if (
    graphQLErrors.some(
      error =>
        error.extensions?.code === 'UNAUTHENTICATED' ||
        error.message === 'Access denied! You need to be authorized to perform this action!',
    )
  ) {
    const { logout } = apolloClient;
    if (logout) logout();
  }

  // Ignore network errors for RefreshToken call because otherwise, if the user
  // goes offline for an extended period of time, a large number of network errors
  // may accumulate.
  if (operation.operationName === 'RefreshToken') {
    return;
  }

  if (networkError) {
    toast.error(i18n.t('networkError'));
  }
});

const formatError = (error: GraphQLError) => {
  switch (error.extensions?.code) {
    case 'FAILED_EMAIL_DELIVERY':
      formatEmailDeliveryError(error);
      break;
    case 'BAD_USER_INPUT':
      formatBadUserInputError(error);
      break;
    default:
      error.message = i18n.t(`apiErrors:${error.message}`, error.extensions?.exception);
  }
};

const formatEmailDeliveryError = (error: GraphQLError) => {
  const msg = i18n.t('apiErrors:emailsNotDelivered');
  const emailErrors = error.extensions?.exception.failedEmails
    .map(({ email, messageKey }: { email: string; messageKey: string }) =>
      messageKey && messageKey !== 'emailsNotDelivered'
        ? i18n.t(`apiErrors:${messageKey}`, { email })
        : i18n.t('apiErrors:emailFailed', { email }),
    )
    .join('\n');
  error.message = `${msg}:\n\n${emailErrors}`;
};

const formatBadUserInputError = (error: GraphQLError) => {
  const { fields } = error.extensions?.exception;
  error.message = i18n.t('apiErrors:thereWereValidationErrors');

  Object.keys(fields).forEach(fieldName => {
    fields[fieldName] = fields[fieldName].map((err: { name: string; args: string }) =>
      i18n.t(`validation:${err.name}`, err.args),
    );
    error.message = `${error.message}\n\n${fieldName}\n${fields[fieldName].join('\n')}`;
  });
};

const httpLink = new HttpLink({
  uri: `${process.env.REACT_APP_API_BASE}/graphql`,
});

const cache = new InMemoryCache();
const link = ApolloLink.from([authLink, errorLink, httpLink]);

// This is an ugly workaround needed so we can trigger a logout from within
// the Apollo error link. We should find a better solution if we can.
// See components/App/App.jsx
export type ApolloClientWithLogout = ApolloClient<object> & { logout?: () => void };

const apolloClient: ApolloClientWithLogout = new ApolloClient({
  cache,
  link,
  defaultOptions: {
    query: { fetchPolicy: 'network-only' },
    watchQuery: { fetchPolicy: 'network-only' },
  },
});

export default apolloClient;
