import React, { useRef, ReactNode } from 'react';
import { useTranslation } from 'react-i18next';
import { FormikProps, FormikConfig } from 'formik';

import { MODAL_VARIANTS, BUTTON_VARIANT } from 'shared/constants';
import { useModalState, ModalProps } from 'shared/components/Modal';
import Form from 'shared/components/Form';

import { StyledModalContent, Title, StyledButton } from './Styles';

export interface InputModalProps<FormValues> {
  className?: string;
  title: string;
  confirmText?: string;
  cancelText?: string;
  submitButtonVariant?: BUTTON_VARIANT;
  onConfirm?: () => void;
  onCancel?: () => void;
  renderLink: ModalProps['renderLink'];
  renderContent: (formData: FormikProps<FormValues>) => ReactNode;
  getFormOptions: (
    confirm: () => void,
  ) => FormikConfig<FormValues> & { initialValues?: Required<FormValues> };
  isWorking?: boolean;
}

type ComponentReturnType<Props> = ReturnType<React.FC<Props>>;

const InputModal = <FormValues extends object>({
  className = '',
  title,
  confirmText = 'confirm',
  cancelText = 'cancel',
  submitButtonVariant = BUTTON_VARIANT.primary,
  onConfirm = () => {},
  onCancel = () => {},
  renderLink,
  renderContent,
  getFormOptions,
  isWorking,
}: InputModalProps<FormValues>): ComponentReturnType<InputModalProps<FormValues>> => {
  const { t } = useTranslation();
  const modalRef = useRef<HTMLDivElement>(null);
  const { isOpen, openModal, closeModal, isActive } = useModalState(undefined, modalRef);

  const handleCancel = () => {
    closeModal();
    onCancel();
  };

  const confirm = () => {
    closeModal();
    onConfirm();
  };

  const formOptions = getFormOptions(confirm);

  return (
    <StyledModalContent
      className={className}
      isOpen={isOpen}
      openModal={openModal}
      closeModal={handleCancel}
      isActive={isActive}
      variant={MODAL_VARIANTS.center}
      renderLink={renderLink}
      modalCardRef={modalRef}
      renderContent={() => (
        <>
          <Title>{t(title)}</Title>
          <Form<FormValues> {...formOptions}>
            {formData => (
              <>
                <Form.Element>
                  {renderContent(formData)}
                  <div>
                    <StyledButton hollow onClick={handleCancel} data-testid="cancel-button">
                      {t(cancelText)}
                    </StyledButton>
                    <StyledButton
                      type="submit"
                      variant={submitButtonVariant}
                      working={isWorking}
                      data-cy-button-name="confirm"
                      data-testid="confirm-button"
                    >
                      {t(confirmText)}
                    </StyledButton>
                  </div>
                </Form.Element>
              </>
            )}
          </Form>
        </>
      )}
    />
  );
};

export default InputModal;
