import React, { ReactNode, forwardRef } from 'react';
import styled, { css } from 'styled-components/macro';

import { mediaQueries, mixin } from 'shared/utils/styles';

export const TRANSITION_DURATION = 100;

interface CardProps {
  className?: string;
  children: ReactNode;
  isOpen: boolean;
  hasClosed: boolean;
}

const CardContent = forwardRef<HTMLDivElement, CardProps>(({ className, children }, ref) => (
  <div className={className} ref={ref}>
    {children}
  </div>
));

const Card = styled(CardContent)<CardProps>`
  display: block;
  background: #fff;
  border-radius: 4px;
  opacity: ${props => (props.isOpen ? 1 : 0)};
  transform: scale(${props => (props.isOpen ? 1 : 0.97)});
  transition: opacity ${TRANSITION_DURATION}ms, transform ${TRANSITION_DURATION}ms;

  ${props =>
    props.hasClosed &&
    css`
      height: 0;
      overflow: hidden;
    `}

  @media ${mediaQueries.mobile} {
    width: 300px;
    max-height: 80%;
  }

  @media ${mediaQueries.desktop} {
    ${mixin.boxShadowBorderMedium};
  }
`;

export default Card;
