import React, { forwardRef } from 'react';
import styled, { css } from 'styled-components/macro';

import { COLOR, font, mixin } from 'shared/utils/styles';
import DropdownCard, {
  Card as DropdownCardInternal,
  DropdownCardProps,
} from 'shared/components/DropdownCard';

export const StyledDatePicker = styled.div`
  position: relative;
`;

interface StyledDropdownCardProps {
  withTime: boolean;
}

const StyledDropdownCardBase = forwardRef<
  HTMLDivElement,
  StyledDropdownCardProps & DropdownCardProps
>(({ withTime, ...props }, ref) => <DropdownCard ref={ref} {...props} />);

export const StyledDropdownCard = styled(StyledDropdownCardBase)<StyledDropdownCardProps>`
  ${DropdownCardInternal} {
    width: 270px;

    ${props =>
      props.withTime &&
      css`
        width: 360px;
        padding-right: 90px;
      `}
  }
`;

export const StyledDateSection = styled.div`
  position: relative;
  padding: 20px;
`;

export const CurrentMonthYear = styled.div`
  display: inline-block;
  padding-left: 7px;
  ${font.bold}
  ${font.size(16)}
`;

export const YearSelect = styled.select`
  margin-left: 5px;
  width: 60px;
  height: 22px;
  ${font.size(13)}
`;

export const PrevNextIcons = styled.div`
  position: absolute;
  top: 12px;
  right: 19px;
  i {
    padding: 7px 5px 4px;
    font-size: 22px;
    color: ${COLOR.textLight};
    ${mixin.clickable}
    &:hover {
      color: ${COLOR.textDarkest};
    }
  }
`;

export const Grid = styled.div`
  display: flex;
  flex-wrap: wrap;
  padding-top: 15px;
  text-align: center;
`;

export const Header = styled.div`
  width: calc(100% / 8);
  height: 30px;
  line-height: 30px;
  color: ${COLOR.textLight};
  ${font.size(13)}
`;

const Cell = styled.div`
  width: calc(100% / 8);
  height: 30px;
  line-height: 30px;
  ${font.size(15)}
`;

interface DayProps {
  isFiller?: boolean;
  isToday?: boolean;
  isSelected?: boolean;
}

export const Day = styled(Cell)<DayProps>`
  border-radius: 4px;

  ${props => !props.isFiller && hoverStyles};
  ${props => props.isToday && font.bold};
  ${props => props.isSelected && selectedStyles};
`;

interface WeekProps {
  isFirst?: boolean;
  isLast?: boolean;
}

export const Week = styled(Cell)<WeekProps>`
  color: ${COLOR.textMedium};
  background-color: ${COLOR.backgroundLight};

  ${props =>
    props.isFirst &&
    css`
      border-radius: 4px 4px 0 0;
    `}
  ${props =>
    props.isLast &&
    css`
      border-radius: 0 0 4px 4px;
    `}
`;

export const StyledTimeSection = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 90px;
  padding: 5px 0;
  border-left: 1px solid ${COLOR.borderLight};
  ${mixin.scrollableY}
`;

interface TimeProps {
  isFiller?: boolean;
  isSelected?: boolean;
}

export const Time = styled.div<TimeProps>`
  padding: 5px 0 5px 20px;
  ${font.size(14)}
  ${props => (!props.isFiller ? hoverStyles : '')}
  ${props => (props.isSelected ? selectedStyles : '')}
`;

const hoverStyles = `
  ${mixin.clickable}
  &:hover {
    background: ${COLOR.backgroundMedium};
  }
`;

const selectedStyles = `
  color: #fff;
  &:hover, & {
    background: ${COLOR.primary};
  }
`;
