import styled from 'styled-components/macro';

import { Button, Tabs, PageWithSidebar } from 'shared/components';
import { PageContent, PageContainer } from 'shared/components/Page';
import { sizes } from 'shared/utils/styles';
import {
  DocumentsTableContainer,
  StyledDocumentsTable,
  StyledHeaders,
  RootDropzoneArea,
} from 'shared/components/DocumentsTable/Styles';
import { MessagesWrapper } from 'shared/components/Messages/index.styles';

export const StyledPageWithSidebar = styled(PageWithSidebar)`
  /* Required for IE11: it has problems with flex column containers with
  overflowing content, and will not respect the max-width set on these elements
  unless, for some reason, they themselves are also inside of a flex column
  container. */
  ${PageContainer} {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  ${PageContent} {
    display: flex;
    flex-direction: column;
    max-height: calc(100vh - ${sizes.topbarHeight}px);
    overflow: hidden;

    /* Make sure that content stays at full width when loading */
    width: 100%;
  }
`;

export const StyledTabsContainer = styled(Tabs.Container)`
  display: flex;
  flex-direction: column;
`;

export const StyledTabsContent = styled(Tabs.Content)`
  display: flex;
  flex-direction: column;
  overflow: hidden;

  ${DocumentsTableContainer} {
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }

  ${StyledDocumentsTable} {
    display: flex;
    flex-direction: column;
    overflow: auto;
  }

  ${StyledHeaders} {
    position: sticky;
    top: 0;
    background: white;
    z-index: 1;
  }

  /* IE11 squishes this element for some reason */
  ${RootDropzoneArea} {
    height: 59px;
  }

  ${MessagesWrapper} {
    overflow: hidden;
  }
`;

export const ScrollableTabsContent = styled(Tabs.Content)`
  overflow: auto;
`;

export const BidPackageName = styled.span`
  padding-right: 20px;
  word-break: break-word;
`;

export const MainActionButton = styled(Button)`
  margin-top: 9px;
`;
