import React from 'react';
import { useTranslation } from 'react-i18next';

import DropdownMenu from 'shared/components/DropdownMenu';
import toast from 'shared/utils/toast';

interface Props {
  invitationLink: string;
}

const CopyInviteLink: React.FC<Props> = ({ invitationLink }) => {
  const { t } = useTranslation('company');

  const copyInviteLinkToClipboard = async () => {
    return navigator.clipboard.writeText(invitationLink).then(() => {
      toast.success(t('copyInviteLinkSuccess'));
    });
  };

  return (
    <DropdownMenu.Action onClick={copyInviteLinkToClipboard}>
      {t('copyInviteLink')}
    </DropdownMenu.Action>
  );
};

export default CopyInviteLink;
