import React from 'react';

import { getColorFromName } from './colors';
import { Image, Letter } from './Styles';

interface AvatarProps {
  className?: string;
  avatarUrl?: string;
  name?: string | null;
  size?: number;
}

const Avatar: React.FC<AvatarProps> = ({ className, avatarUrl, name = '', size = 24 }) => {
  if (avatarUrl) {
    return <Image className={className} size={size} avatarUrl={avatarUrl} />;
  }
  return (
    <Letter className={className} size={size} color={getColorFromName(name)}>
      {name && <span>{name.charAt(0)}</span>}
    </Letter>
  );
};

export default Avatar;
