import React from 'react';
import { useTranslation } from 'react-i18next';

import { IconClickable, ConfirmationModal, Modal } from 'shared/components';
import EmptyContent from 'shared/components/EmptyContent';
import { MODAL_VARIANTS } from 'shared/constants';

import {
  ContactsListWrapper,
  ContactsListItem,
  ContactsListActions,
  StyledContactInfo,
  ContactsListAction,
  StyledExplanation,
} from './Styles';
import ContactsForm from './ContactsForm';
import { SubcontractorForEdit_subcontractor_contacts } from './__graphql__/SubcontractorForEdit';

interface Props {
  contacts: SubcontractorForEdit_subcontractor_contacts[];
  removeContact: <T>(index: number) => T | undefined;
  replaceContact: (index: number, value: unknown) => void;
  hasError: boolean;
}

const ContactsList: React.FC<Props> = ({ contacts, removeContact, replaceContact, hasError }) => {
  const { t } = useTranslation();
  return (
    <ContactsListWrapper>
      {contacts.length === 0 && (
        <EmptyContent>
          <StyledExplanation hasError={hasError}>
            {t('subcontractorForm.contacts.oneContactRequired')}
          </StyledExplanation>
        </EmptyContent>
      )}
      {contacts.map((contact, i) => (
        <ContactsListItem key={contact.id} data-cy-contact-id={contact.id}>
          <StyledContactInfo action={null} contact={contact} centerAvatar />
          <ContactsListActions>
            <ContactsListAction>
              <Modal
                variant={MODAL_VARIANTS.center}
                renderLink={modal => <IconClickable onClick={modal.open} type="edit" />}
                renderContent={modal => (
                  <ContactsForm
                    closeModal={modal.close}
                    contact={contact}
                    allValues={contacts}
                    onSubmit={contactValues => {
                      replaceContact(i, contactValues);
                    }}
                  />
                )}
              />
            </ContactsListAction>
            <ContactsListAction>
              <ConfirmationModal
                title={t('subcontractorForm.contacts.removeModalTitle')}
                message={null}
                renderLink={modal => <IconClickable onClick={modal.open} type="delete" />}
                onConfirm={({ close }) => {
                  removeContact(i);
                  close();
                }}
              />
            </ContactsListAction>
          </ContactsListActions>
        </ContactsListItem>
      ))}
    </ContactsListWrapper>
  );
};

export default ContactsList;
