import React, { useContext } from 'react';
import Dropzone from 'react-dropzone';

import DocumentsTableContext from './DocumentsTableContext';
import Folder from './classes/Folder';

interface Props {
  className?: string;
  targetFolder: Folder;
  inputProps: React.InputHTMLAttributes<HTMLInputElement> & { webkitdirectory?: string };
  children: React.ReactNode;
}

const DropzoneLink: React.FC<Props> = ({ className, targetFolder, inputProps, children }) => {
  const { handleFilesDrop } = useContext(DocumentsTableContext);

  return (
    <Dropzone onDrop={files => handleFilesDrop(files, targetFolder)}>
      {({ getRootProps, getInputProps }) => (
        <span className={className} {...getRootProps()}>
          <input {...getInputProps()} {...inputProps} />
          {children}
        </span>
      )}
    </Dropzone>
  );
};

export default DropzoneLink;
