import React, { useContext, ReactElement } from 'react';

import QueryMatchesContext from 'shared/contexts/queryMatchesContext';

import { TableBody, StyledRow, Cell, CellMobileLabel } from './Styles';
import { Column, RowType } from './types';

interface RowsProps {
  columns: Column[];
  rows: RowType[];
  highlightedRowId?: string;
  isResponsive?: boolean;
}

const Rows = ({ rows, highlightedRowId = '', columns, isResponsive }: RowsProps): ReactElement => {
  const { isMobile } = useContext(QueryMatchesContext);
  return (
    <TableBody>
      {rows.map(cells => (
        <StyledRow
          key={cells.props.rowId}
          isHighlighted={Boolean(highlightedRowId === cells.props.rowId)}
          data-cy-row-key={cells.props.rowId}
        >
          {React.Children.toArray(cells.props.children)
            .filter(Boolean)
            .map((cell, j) => (
              <Cell
                key={j}
                data-cy-cell-name={columns[j].testIdentifier}
                textAlign={columns[j].textAlign}
              >
                {isResponsive && isMobile && columns[j].label && (
                  <CellMobileLabel>{columns[j].label}:</CellMobileLabel>
                )}
                {cell}
              </Cell>
            ))}
        </StyledRow>
      ))}
    </TableBody>
  );
};

export default Rows;
