import gql from 'graphql-tag';

import { fragmentMessages } from 'shared/components/Messages';

import { fragmentBidPackage } from './Bidders';

export const queryProjectWithBidPackages = gql`
  query ProjectWithBidPackages($id: ID!) {
    project(id: $id) {
      id
      number
      name
      bidPackages {
        id
        number
        name
      }
    }
  }
`;

export const queryBidPackageForShow = gql`
  query BidPackageForShow($id: ID!) {
    bidPackage(id: $id) {
      id
      name
      status
      project {
        status
        name
      }
      ...Bidders_bidPackage
    }
  }
  ${fragmentBidPackage}
`;

export const queryBidPackageForOverview = gql`
  query BidPackageForOverview($id: ID!) {
    bidPackage(id: $id) {
      id
      number
      name
      bidsDueAt
      startsOn
      endsOn
      description
      budget
      notes
      createdAt
    }
  }
`;

export const queryBidRequestsForMessages = gql`
  query BidRequestsForMessages($bidPackageId: ID!) {
    bidRequests(
      sortBy: subcontractorName
      sortDirection: ASC
      bidPackageId: $bidPackageId
      limit: 10000
    ) {
      bidRequests {
        id
        subcontractor {
          id
          name
        }
      }
    }
  }
`;

export const queryMessageThreadsInBidPackage = gql`
  query MessageThreadsInBidPackage($bidPackageId: ID!, $query: String) {
    messageThreadsInBidPackage(bidPackageId: $bidPackageId, query: $query) {
      messageThreads {
        ...Messages_messageThread
      }
    }
  }
  ${fragmentMessages}
`;

export const queryBidPackageForPrintingDocuments = gql`
  query BidPackageForPrintingDocuments($id: ID!) {
    bidPackage(id: $id) {
      id
      name
      project {
        id
        name
      }
    }
  }
`;

export const mutationPublishBidPackage = gql`
  mutation PublishBidPackage($id: ID!) {
    publishBidPackage(id: $id)
  }
`;

export const mutationCreateBidPackage = gql`
  mutation CreateBidPackage($input: BidPackageInput!) {
    createBidPackage(input: $input) {
      id
    }
  }
`;

export const mutationUpdateBidPackage = gql`
  mutation UpdateBidPackage($id: ID!, $input: BidPackageInput!) {
    updateBidPackage(id: $id, input: $input) {
      id
    }
  }
`;
