import React, { useState } from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation, Trans } from 'react-i18next';

import { Form, Field, ExternalLink } from 'shared/components';
import {
  Heading,
  Tip,
  Message,
  SuccessMessage,
  SubmitButton,
} from 'shared/components/UnauthenticatedStyles';
import { COLOR } from 'shared/utils/styles';
import UnauthenticatedPage from 'shared/components/UnauthenticatedPage';
import { ContentBox } from 'shared/components/UnauthenticatedPage/Styles';

import { mutationSignup } from './api';
import { Signup as SignupData, SignupVariables } from './__graphql__/Signup';

interface FormValues {
  companyName: string;
  email: string;
  firstName: string;
  lastName: string;
  password: string;
  termsAccepted: boolean;
}

const Signup: React.FC<void> = () => {
  const { t } = useTranslation('auth');
  const [completed, setCompleted] = useState(false);

  const [signup, { error: mutationError, loading: mutating }] = useMutation<
    SignupData,
    SignupVariables
  >(mutationSignup);

  if (completed) {
    return (
      <UnauthenticatedPage title="signup">
        <ContentBox>
          <Heading>{t('signupSuccessHeading')}</Heading>
          <SuccessMessage data-cy-success>{t('signupSuccessMessage')}</SuccessMessage>
        </ContentBox>
      </UnauthenticatedPage>
    );
  }

  return (
    <UnauthenticatedPage title="signup">
      <Form<FormValues>
        initialValues={{
          companyName: '',
          email: '',
          firstName: '',
          lastName: '',
          password: '',
          termsAccepted: false,
        }}
        validations={{
          companyName: [Form.is.required()],
          email: [Form.is.required()],
          firstName: [Form.is.required(), Form.is.maxLength(100)],
          lastName: [Form.is.required(), Form.is.maxLength(100)],
          password: [Form.is.required(), Form.is.minLength(8), Form.is.maxLength(250)],
          termsAccepted: Form.is.match(value => !!value, t('validation:termsMustBeAccepted')),
        }}
        onSubmit={async values => {
          const { termsAccepted, ...submitValues } = values;

          const success = await signup({
            variables: { input: submitValues },
          });
          if (success) setCompleted(true);
        }}
      >
        <ContentBox as={Form.Element}>
          <Heading>{t('signupHeading')}</Heading>
          <Tip>{t('signupMessage')}</Tip>
          {mutationError && <Message color={COLOR.danger}>{mutationError.message}</Message>}
          <Field.Input required name="companyName" label={t('company.name')} />
          <Field.Input required name="email" label={t('user.email')} />
          <Field.Input required name="firstName" label={t('user.firstName')} />
          <Field.Input required name="lastName" label={t('user.lastName')} />
          <Field.Input required name="password" label={t('password')} type="password" icon="lock" />
          <Field.Checkbox
            name="termsAccepted"
            checkboxLabel={
              <Trans i18nKey="termsAccepted" t={t}>
                I accept the{' '}
                <ExternalLink to="https://www.cosuno.de/terms">terms and conditions</ExternalLink>{' '}
                and the{' '}
                <ExternalLink to="https://www.cosuno.de/privacy">privacy policy</ExternalLink>.
              </Trans>
            }
          />
          <Form.RequiredFieldsNote />
          <SubmitButton type="submit" working={mutating}>
            {t('signupSubmit')}
          </SubmitButton>
        </ContentBox>
      </Form>
    </UnauthenticatedPage>
  );
};

export default Signup;
