import React from 'react';
import { useField } from 'formik';

import { uniqueId } from 'shared/utils/javascript';
import Input from 'shared/components/Input';
import { SelectSingle, SelectMulti } from 'shared/components/Select';
import Textarea from 'shared/components/Textarea';
import DatePicker from 'shared/components/DatePicker';
import LocationSearch from 'shared/components/LocationSearch';
import Checkbox from 'shared/components/Checkbox';
import RadioButtonGroup from 'shared/components/RadioButtonGroup';
import ImageUploader from 'shared/components/ImageUploader';
import CurrencyInput from 'shared/components/CurrencyInput';

import { FieldWrapper, FieldLabel, FieldError } from './Styles';

interface FieldProps {
  name: string;
  id?: string;
  className?: string;
  label?: string;
  required?: boolean;
}

type InjectedProps = 'value' | 'onChange';

const generateField = <Props extends {}>(FormComponent: React.ComponentType<Props>) => {
  const FieldComponent: React.FC<FieldProps & Omit<Props, InjectedProps>> = ({
    className,
    id,
    name,
    label,
    required,
    ...props
  }) => {
    const [inputProps, { touched, error }, { setValue }] = useField(name);

    const fieldId = id || uniqueId('field-');
    const hasError = touched && error;

    return (
      <FieldWrapper className={className} hasLabel={!!label}>
        {label && (
          <FieldLabel htmlFor={fieldId}>
            {label}
            {required && ' *'}
          </FieldLabel>
        )}
        <FormComponent
          // Seems like we have to cast props as any, even though they should be a
          // subset of Props
          // eslint-disable-next-line @typescript-eslint/no-explicit-any
          {...(props as any)}
          {...inputProps}
          id={fieldId}
          invalid={hasError}
          required={required}
          onChange={setValue}
        />
        {hasError && <FieldError>{error}</FieldError>}
      </FieldWrapper>
    );
  };

  FieldComponent.displayName = `Field.${FormComponent.displayName || FormComponent.name}`;

  return FieldComponent;
};

const Field = {
  Input: generateField(Input),
  SelectSingle: generateField(SelectSingle),
  SelectMulti: generateField(SelectMulti),
  Textarea: generateField(Textarea),
  DatePicker: generateField(DatePicker),
  LocationSearch: generateField(LocationSearch),
  Checkbox: generateField(Checkbox),
  RadioButtonGroup: generateField(RadioButtonGroup),
  ImageUploader: generateField(ImageUploader),
  CurrencyInput: generateField(CurrencyInput),
};

export { FieldWrapper };
export default Field;
