import React, { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { useQuery, useMutation } from '@apollo/react-hooks';

import history from 'browserHistory';
import QueryErrors from 'shared/components/QueryErrors';
import PageLoader from 'shared/components/PageLoader';
import AuthContext, { isAuthenticated } from 'shared/contexts/authContext';
import { isDoneLoading } from 'shared/utils/apollo';
import toast from 'shared/utils/toast';
import routes from 'shared/constants/routes';
import { ProjectInput } from '__graphql__/globalTypes';

import { mutationCreateProject, queryOfficesForProjectOverview } from './api';
import Overview from './Overview';
import { CreateProject, CreateProjectVariables } from './__graphql__/CreateProject';

const OverviewCreate: React.FC<{}> = () => {
  const { t } = useTranslation('projects');

  const authContext = useContext(AuthContext);

  const [createProject, { loading: mutating }] = useMutation<CreateProject, CreateProjectVariables>(
    mutationCreateProject,
  );

  const officesQuery = useQuery(queryOfficesForProjectOverview, {
    variables: { offset: 0, limit: 200 },
    fetchPolicy: 'no-cache',
  });

  if (!isAuthenticated(authContext)) {
    return null;
  }

  if (officesQuery.error) {
    return <QueryErrors error={officesQuery.error} />;
  }

  if (!isDoneLoading(officesQuery) || mutating) {
    return <PageLoader />;
  }

  const {
    authState: { user },
  } = authContext;

  const {
    data: { offices },
  } = officesQuery;

  const submitForm = (input: ProjectInput) =>
    createProject({
      variables: { input },
    }).then(({ data }) => {
      toast.success(t('created'));

      if (data) {
        history.push(routes.showProject(data.createProject.id));
      }
    }, toast.error);

  return (
    <Overview
      project={{}}
      offices={offices}
      currentUser={user}
      submitForm={submitForm}
      onlyAllowEdit
    />
  );
};

export default OverviewCreate;
