import React, { Fragment } from 'react';
import moment, { Moment } from 'moment';
import { useTranslation } from 'react-i18next';

import { times, range } from 'shared/utils/javascript';
import Icon from 'shared/components/Icon';

import {
  StyledDateSection,
  YearSelect,
  CurrentMonthYear,
  Grid,
  PrevNextIcons,
  Header,
  Day,
  Week,
} from './Styles';

const generateYears = () =>
  times(50, i => ({
    disabled: false,
    label: `${i + 2010}`,
    value: `${i + 2010}`,
  }));

const generateMonthDays = (date: Moment) =>
  times(date.daysInMonth()).map(i => moment(date).set('date', i + 1));

const generateDaysBefore = (date: Moment) => {
  const monthStart = moment(date).startOf('month');
  const weekStart = moment(monthStart).startOf('week');
  const count = monthStart.diff(weekStart, 'days');

  return range(count);
};

const generateDaysAfter = (date: Moment) => {
  const monthEnd = moment(date).endOf('month');
  const weekEnd = moment(monthEnd).endOf('week');
  const count = weekEnd.diff(monthEnd, 'days');

  return range(count);
};

interface DateSectionProps {
  selectedDate: Moment;
  displayedDate: Moment;
  changeDay: (day: number) => void;
  goToPrevMonth: () => void;
  goToNextMonth: () => void;
  changeYear: (year: string) => void;
}

const DateSection: React.FC<DateSectionProps> = ({
  selectedDate,
  displayedDate,
  changeDay,
  goToPrevMonth,
  goToNextMonth,
  changeYear,
}) => {
  const { t } = useTranslation();

  const daysBefore = generateDaysBefore(displayedDate);
  const monthDays = generateMonthDays(displayedDate);
  const daysAfter = generateDaysAfter(displayedDate);

  const isFirstDayMonday = monthDays[0].weekday() === 0;
  const isFirstWeekInMonth = (date: Moment) =>
    moment(date)
      .subtract(1, 'week')
      .month() !== date.month();
  const isLastWeekInMonth = (date: Moment) =>
    moment(date)
      .add(1, 'week')
      .month() !== date.month();

  return (
    <StyledDateSection>
      <CurrentMonthYear>{displayedDate.format('MMM YYYY')}</CurrentMonthYear>
      <YearSelect
        value={displayedDate.get('year')}
        onChange={event => changeYear(event.target.value)}
        tabIndex={-1}
      >
        {[{ label: t('year'), value: '', disabled: true }, ...generateYears()].map(option => (
          <option key={option.label} value={option.value} disabled={option.disabled}>
            {option.label}
          </option>
        ))}
      </YearSelect>
      <PrevNextIcons>
        <Icon type="arrow-left" onClick={goToPrevMonth} />
        <Icon type="arrow-right" onClick={goToNextMonth} />
      </PrevNextIcons>
      <Grid>
        <Header />
        {moment.weekdaysMin(true).map(name => (
          <Header key={name}>{name}</Header>
        ))}
        {!isFirstDayMonday && <Week isFirst>{monthDays[0].week()}</Week>}
        {daysBefore.map(i => (
          <Day key={`prev${i}`} isFiller />
        ))}
        {monthDays.map(date => (
          <Fragment key={date.date()}>
            {date.weekday() === 0 && (
              <Week
                isFirst={isFirstDayMonday && isFirstWeekInMonth(date)}
                isLast={isLastWeekInMonth(date)}
              >
                {date.week()}
              </Week>
            )}
            <Day
              data-cy-day={moment(date).date()}
              isToday={moment().isSame(date, 'day')}
              isSelected={selectedDate.isSame(date, 'day')}
              onClick={() => changeDay(date.get('date'))}
            >
              {date.format('D')}
            </Day>
          </Fragment>
        ))}
        {daysAfter.map(i => (
          <Day key={`next${i}`} isFiller />
        ))}
      </Grid>
    </StyledDateSection>
  );
};

export default DateSection;
