import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import history from 'browserHistory';
import { PageLoader } from 'shared/components';
import toast from 'shared/utils/toast';
import routes from 'shared/constants/routes';
import { BidPackageInput } from '__graphql__/globalTypes';

import { mutationCreateBidPackage } from './api';
import { CreateBidPackage, CreateBidPackageVariables } from './__graphql__/CreateBidPackage';
import Overview from './Overview';

interface OverviewCreateProps {
  projectId: string;
}

const OverviewCreate: React.FC<OverviewCreateProps> = ({ projectId }) => {
  const { t } = useTranslation('bidPackages');

  const [createBidPackage, { loading: mutating }] = useMutation<
    CreateBidPackage,
    CreateBidPackageVariables
  >(mutationCreateBidPackage);

  if (mutating) {
    return <PageLoader />;
  }

  const submitForm = (input: BidPackageInput) =>
    createBidPackage({
      variables: { input },
    }).then(({ data }) => {
      toast.success(t('created'));

      if (data) {
        history.push(routes.showBidPackage(projectId, data.createBidPackage.id));
      }
    }, toast.error);

  return <Overview bidPackage={{}} projectId={projectId} submitForm={submitForm} onlyAllowEdit />;
};

export default OverviewCreate;
