import React, { useState, ReactElement, ReactNode } from 'react';
import { useTranslation } from 'react-i18next';

import { formatAddress } from 'shared/utils/address';
import Avatar from 'shared/components/Avatar';
import Icon from 'shared/components/Icon';
import ContactInfo, { ContactInfoProps } from 'shared/components/ContactInfo';

import {
  SubcontractorWrapper,
  SubcontractorMain,
  ContactsList,
  SubcontractorName,
  SubcontractorAddress,
  SubcontractorInfo,
  WorkCategoryTag,
  SubcontractorWorkCategoriesList,
  ShowAllContactsButton,
  ShowAllContactsText,
  SubcontractorAction,
} from './Styles';

export interface SubcontractorsListItemProps {
  subcontractor: {
    id: string;
    name: string;
    city?: string | null;
    postalCode?: string | null;
    workCategories: { id: string; name: string }[];
    contacts: ({
      id: string;
      email?: string | null;
      deletedAt?: string | null;
    } & ContactInfoProps['contact'])[];
  };
  renderContactAction?: (contactId: string, subcontractorId: string) => ReactNode;
  // Actually has to be a fragment:
  renderActions?: (subcontractorId: string) => null | ReactElement;
  showContactsWithoutEmails?: boolean;
}

const SubcontractorsListItem: React.FC<SubcontractorsListItemProps> = ({
  subcontractor,
  renderContactAction = () => null,
  renderActions = () => null,
  showContactsWithoutEmails = false,
}: SubcontractorsListItemProps) => {
  const { t } = useTranslation();
  const [areAllContactsVisible, setAreAllContactsVisible] = useState(false);

  const address = formatAddress({
    t,
    street: '',
    countryCode: '',
    city: subcontractor.city,
    postalCode: subcontractor.postalCode,
  });
  const contacts = subcontractor.contacts.filter(({ email }) => showContactsWithoutEmails || email);
  const visibleContacts = areAllContactsVisible ? contacts : contacts.slice(0, 2);
  const hasMoreContactsToShow = contacts.length > 2;
  const toggleAreAllContactsVisible = () => setAreAllContactsVisible(!areAllContactsVisible);
  const actions = renderActions(subcontractor.id);

  return (
    <SubcontractorWrapper data-cy-subcontractor-id={subcontractor.id}>
      <SubcontractorMain>
        <div>
          <Avatar name={subcontractor.name} size={48} />
        </div>
        <SubcontractorInfo>
          <SubcontractorName>{subcontractor.name}</SubcontractorName>
          <SubcontractorAddress>{address}</SubcontractorAddress>
          <SubcontractorWorkCategoriesList>
            {subcontractor.workCategories.map(workCategory => (
              <WorkCategoryTag key={workCategory.id}>{workCategory.name}</WorkCategoryTag>
            ))}
          </SubcontractorWorkCategoriesList>
        </SubcontractorInfo>
        {actions &&
          React.Children.map(actions.props.children, action => (
            <SubcontractorAction>{action}</SubcontractorAction>
          ))}
      </SubcontractorMain>
      <ContactsList>
        {visibleContacts.map(contact => (
          <ContactInfo
            key={contact.id}
            action={renderContactAction(contact.id, subcontractor.id)}
            contact={contact}
          />
        ))}
        {hasMoreContactsToShow && (
          <ShowAllContactsButton onClick={toggleAreAllContactsVisible}>
            <ShowAllContactsText>
              {t(
                areAllContactsVisible
                  ? 'subcontractor.showFewerContacts'
                  : 'subcontractor.showAllContacts',
              )}
            </ShowAllContactsText>
            <Icon type={areAllContactsVisible ? 'chevron-up' : 'chevron-down'} size={18} />
          </ShowAllContactsButton>
        )}
      </ContactsList>
    </SubcontractorWrapper>
  );
};

export { fragmentSubcontractorsListItem } from './api';
export default SubcontractorsListItem;
