import React from 'react';
import { useTranslation } from 'react-i18next';
import { useMutation } from '@apollo/react-hooks';

import { nullString } from 'shared/constants';
import { BID_PACKAGE_STATUS, BID_REQUEST_STATUS, PROJECT_STATUS } from '__graphql__/globalTypes';
import toast from 'shared/utils/toast';
import {
  ConfirmationModal,
  BidsList,
  SubcontractorInfo,
  ContactsList,
  BidFormModal,
  ClickableText,
} from 'shared/components';

import { BidRequestsForBidPackage_bidRequests_bidRequests } from './__graphql__/BidRequestsForBidPackage';
import { AwardBidRequest, AwardBidRequestVariables } from './__graphql__/AwardBidRequest';
import { UnAwardBidRequest, UnAwardBidRequestVariables } from './__graphql__/UnAwardBidRequest';
import { Bidders_bidPackage } from './__graphql__/Bidders_bidPackage';
import {
  mutationAwardBidRequest,
  mutationUnAwardBidRequest,
  mutationRejectBidRequest,
  mutationUnRejectBidRequest,
} from './api';
import ChangeStatusModal from './ChangeStatusModal';
import RejectBidRequestModal from './RejectBidRequestModal';
import {
  AdditionalInfo,
  Status,
  StatusIcon,
  RequestSidebar,
  StyledSectionTitle,
  StatusActionButton,
  RequestAddBid,
  DisplayReason,
  StatusActions,
} from './Styles';

function hasTotalAmount<T extends { totalAmount: number | null }>(
  bid: T,
): bid is NonNullable<T> & { totalAmount: number } {
  return bid.totalAmount !== null;
}

interface Props {
  bidPackage: Bidders_bidPackage;
  bidRequest: BidRequestsForBidPackage_bidRequests_bidRequests;
}

const BiddersRequest: React.FC<Props> = ({ bidPackage, bidRequest }) => {
  const { t } = useTranslation('bidPackages');
  const isAnyBidRequestAwarded = bidPackage.status === BID_PACKAGE_STATUS.bidAwarded;
  const [awardBid, { loading: isAwarding }] = useMutation<
    AwardBidRequest,
    AwardBidRequestVariables
  >(mutationAwardBidRequest, {
    refetchQueries: () => ['BidPackageForShow', 'BidRequestsForBidPackage'],
    awaitRefetchQueries: true,
  });
  const [unAwardBid, { loading: isUnAwarding }] = useMutation<
    UnAwardBidRequest,
    UnAwardBidRequestVariables
  >(mutationUnAwardBidRequest, {
    refetchQueries: () => ['BidPackageForShow', 'BidRequestsForBidPackage'],
    awaitRefetchQueries: true,
  });
  const [rejectBid, { loading: isRejecting }] = useMutation(mutationRejectBidRequest, {
    refetchQueries: () => ['BidPackageForShow', 'BidRequestsForBidPackage'],
    awaitRefetchQueries: true,
  });
  const [unRejectBid, { loading: isUnRejecting }] = useMutation(mutationUnRejectBidRequest, {
    refetchQueries: () => ['BidPackageForShow', 'BidRequestsForBidPackage'],
    awaitRefetchQueries: true,
  });

  const isProjectClosed = bidPackage.project.status === PROJECT_STATUS.closed;
  const isStatusEditable = [
    BID_REQUEST_STATUS.invited,
    BID_REQUEST_STATUS.opened,
    BID_REQUEST_STATUS.viewed,
    BID_REQUEST_STATUS.inviteAccepted,
    BID_REQUEST_STATUS.undecided,
    BID_REQUEST_STATUS.inviteDeclined,
    BID_REQUEST_STATUS.withdrawn,
  ].includes(bidRequest.status);

  const bids = bidRequest.bids.filter(hasTotalAmount);

  const { subcontractor, currentBidId, bidInvites, status } = bidRequest;

  return (
    <RequestSidebar>
      <SubcontractorInfo subcontractor={subcontractor} />
      <StyledSectionTitle>{t('request.invitees')}</StyledSectionTitle>
      <AdditionalInfo data-cy-contacts-container>
        <ContactsList bidInvites={bidInvites} isProjectClosed={isProjectClosed} showResendLink />
      </AdditionalInfo>
      <StyledSectionTitle>{t('request.status')}</StyledSectionTitle>
      <AdditionalInfo>
        <Status data-cy-status>
          <StatusIcon status={status} />
          <div>
            {t(`request.${status}`)}
            <StatusActions>
              {isStatusEditable && (
                <ChangeStatusModal
                  bidRequest={bidRequest}
                  renderLink={modal => (
                    <StatusActionButton
                      data-cy-button-name="changeBidRequestStatus"
                      onClick={modal.open}
                    >
                      {t('changeStatus')}
                    </StatusActionButton>
                  )}
                />
              )}
              {!isAnyBidRequestAwarded &&
                bidRequest.status === BID_REQUEST_STATUS.bidSubmitted &&
                !isProjectClosed && (
                  <>
                    <ConfirmationModal
                      title={t('awardBidConfirmTitle')}
                      message={t('awardBidConfirmMessage')}
                      confirmText={t('awardBid')}
                      isWorking={isAwarding}
                      renderLink={modal => (
                        <StatusActionButton data-cy-button-name="awardBid" onClick={modal.open}>
                          {t('awardBid')}
                        </StatusActionButton>
                      )}
                      onConfirm={async () => {
                        try {
                          await awardBid({ variables: { id: bidRequest.id } });
                          toast.success(t('bidAwardedSuccess'));
                        } catch (error) {
                          toast.error(error);
                        }
                      }}
                    />
                    <RejectBidRequestModal
                      isRejecting={isRejecting}
                      onConfirm={async shouldSendNotification => {
                        await rejectBid({
                          variables: { id: bidRequest.id, shouldSendNotification },
                        });
                      }}
                    />
                  </>
                )}
              {bidRequest.status === BID_REQUEST_STATUS.bidAwarded && !isProjectClosed && (
                <ConfirmationModal
                  title={t('unAwardBidConfirmTitle')}
                  message={t('unAwardBidConfirmMessage')}
                  confirmText={t('unAwardBid')}
                  isWorking={isUnAwarding}
                  renderLink={modal => (
                    <StatusActionButton data-cy-button-name="unAwardBid" onClick={modal.open}>
                      {t('unAwardBid')}
                    </StatusActionButton>
                  )}
                  onConfirm={async () => {
                    try {
                      await unAwardBid({ variables: { id: bidRequest.id } });
                      toast.success(t('bidUnAwardedSuccess'));
                    } catch (error) {
                      toast.error(error);
                    }
                  }}
                />
              )}
              {bidRequest.status === BID_REQUEST_STATUS.bidRejected && !isProjectClosed && (
                <StatusActionButton
                  data-cy-button-name="unRejectBid"
                  working={isUnRejecting}
                  onClick={() =>
                    unRejectBid({ variables: { id: bidRequest.id } })
                      .then(() => {
                        toast.success(t('bidUnRejectedSuccess'));
                      })
                      .catch(toast.error)
                  }
                >
                  {t('unRejectBid')}
                </StatusActionButton>
              )}
            </StatusActions>
          </div>
        </Status>
      </AdditionalInfo>
      {bids.length > 0 && (
        <>
          <StyledSectionTitle>{t('request.bids')}</StyledSectionTitle>
          <BidsList
            bids={bids}
            subcontractor={subcontractor}
            currentBidId={currentBidId}
            bidInvites={bidInvites}
          />
        </>
      )}
      {!isProjectClosed && bidRequest.status !== BID_REQUEST_STATUS.pendingInvite && (
        <RequestAddBid>
          {t('missingBid')}
          <BidFormModal
            bidRequest={bidRequest}
            subcontractor={bidRequest.subcontractor}
            isUserSubcontractor={false}
            refetchQueries={() => ['BidPackageForShow', 'BidRequestsForBidPackage']}
            renderLink={modal => (
              <ClickableText data-cy-button-name="addBidManually" onClick={modal.open}>
                {t('addBidManually')}
              </ClickableText>
            )}
          />
        </RequestAddBid>
      )}
      {bidRequest.status === BID_REQUEST_STATUS.inviteDeclined && (
        <>
          <StyledSectionTitle>{t('requestDeclinedReason')}</StyledSectionTitle>
          <DisplayReason>{bidRequest.declinedReason || nullString}</DisplayReason>
        </>
      )}
    </RequestSidebar>
  );
};

export default BiddersRequest;
