const getRoutePathParams = (paramsEnum: { [key: string]: string }, name?: string): string => {
  const params = Object.keys(paramsEnum).join('|');

  return `${name || ''}(${params})`;
};

export enum PROJECTS_ROUTE_TYPE {
  open = 'open',
  closed = 'closed',
}

export enum BID_INVITE_ACTION_ROUTE_TYPE {
  'accept' = 'accept',
  'decline' = 'decline',
  'decide-later' = 'decide-later',
}

const routes = {
  root: () => '/',

  projectsRoot: () => '/projects',

  projects: (type?: PROJECTS_ROUTE_TYPE) =>
    `/projects/${type || getRoutePathParams(PROJECTS_ROUTE_TYPE)}`,

  showProject: (projectId = ':projectId') => `/projects/${projectId}`,

  createProject: () => '/projects/new',

  printProjectDocuments: (projectId = ':projectId') => `/print/projects/${projectId}/documents`,

  editProject: (projectId: string) => `/projects/${projectId}/overview?edit=true`,

  printBidPackageDocuments: (bidPackageId = ':bidPackageId') =>
    `/print/bid-packages/${bidPackageId}/documents`,

  bidPackages: (projectId = ':projectId') => `/projects/${projectId}/bid-packages`,

  showBidPackage: (projectId = ':projectId', bidPackageId = ':bidPackageId') =>
    `/projects/${projectId}/bid-packages/${bidPackageId}`,

  showBidPackageLegacy: (projectId = ':projectId', bidPackageId = ':bidPackageId') =>
    `/projects/${projectId}/bidPackages/${bidPackageId}`,

  createBidPackage: (projectId = ':projectId') => `/projects/${projectId}/bid-packages/new`,

  editBidPackage: (projectId: string, bidPackageId: string) =>
    `/projects/${projectId}/bid-packages/${bidPackageId}/overview?edit=true`,

  bidRequests: () => '/requests',

  bidInvite: (bidInviteId = ':bidInviteId') => `/bid-invitations/${bidInviteId}`,

  bidRequest: (bidRequestId = ':bidRequestId') => `/requests/${bidRequestId}`,

  bidInviteAction: (bidInviteId = ':bidInviteId', action?: BID_INVITE_ACTION_ROUTE_TYPE) =>
    `/bid-invitations/${bidInviteId}/${action ||
      getRoutePathParams(BID_INVITE_ACTION_ROUTE_TYPE, ':action')}`,

  bidInviteActionLegacy: (bidInviteId = ':bidInviteId', action?: BID_INVITE_ACTION_ROUTE_TYPE) =>
    `/requests/${bidInviteId}/${action ||
      getRoutePathParams(BID_INVITE_ACTION_ROUTE_TYPE, ':action')}`,

  subcontractors: () => '/subcontractors',

  bids: () => '/bids',

  profile: () => '/me',

  company: () => '/company',

  admin: () => '/admin',

  login: () => '/login',

  signup: () => '/signup',

  resetPassword: () => '/reset-password',

  createPassword: (token = ':token') => `/create-password/${token}`,

  invitation: (token = ':token') => `/invitations/${token}`,

  printBidders: (bidPackageId = ':bidPackageId') => `/print/bidders/${bidPackageId}`,
};

export default routes;
