import { Item as SerializedItem } from '../types/parsedFileTree';

import Item from './Item';
import Folder from './Folder';

interface FileArgs {
  name: string;
  id: string;
  ancestor?: Folder;
  saveCallback?: (rootFolder: SerializedItem) => void;
}

class File extends Item {
  ancestor: Folder | undefined;

  id: string;

  name: string;

  saveCallback: ((rootFolder: SerializedItem) => void) | undefined;

  constructor({ ancestor, id, name, saveCallback }: FileArgs) {
    super();
    this.ancestor = ancestor;
    this.id = id;
    this.name = name;
    this.saveCallback = saveCallback;
  }

  serializable(): SerializedItem {
    return {
      id: this.id,
      type: 'file',
      name: this.name,
    };
  }

  findItembyId(id: string) {
    if (this.id === id) return this;
    return null;
  }
}

export default File;
