import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import toast from 'shared/utils/toast';
import { PROJECT_STATUS } from '__graphql__/globalTypes';
import { ConfirmationModal } from 'shared/components';
import { ConfirmationModalConfirmHandlerParam } from 'shared/components/ConfirmationModal';

import { mutationCloseProject, mutationReopenProject } from './api';
import { ActionButton } from './Styles';

interface ProjectStatusActionProps {
  project: { id: string; status: PROJECT_STATUS };
  refetchProject: () => void;
}

const ProjectStatusAction: React.FC<ProjectStatusActionProps> = ({ project, refetchProject }) => {
  const { t } = useTranslation('projects');
  const isProjectClosed = project.status === PROJECT_STATUS.closed;

  const mutationProjectAvailability = isProjectClosed
    ? mutationReopenProject
    : mutationCloseProject;

  const [toggleProjectAvailability] = useMutation(mutationProjectAvailability, {
    refetchQueries: () => ['Projects', 'ProjectForShow', 'BidPackages', 'BidPackageForShow'],
    awaitRefetchQueries: true,
  });

  const handleStatusToggle = async (
    modal: ConfirmationModalConfirmHandlerParam,
    successMessage: string,
  ) => {
    try {
      await toggleProjectAvailability({
        variables: {
          id: project.id,
        },
      });
      toast.success(successMessage);
      modal.close();
    } catch (error) {
      toast.error(error);
      modal.close();
      refetchProject();
    }
  };

  if (isProjectClosed) {
    return (
      <ConfirmationModal
        key="reopenProjectModal"
        title={t('reopenProjectConfirmTitle')}
        message={t('reopenConfirmMessage')}
        confirmText={t('reopenProject')}
        onConfirm={async modal => {
          await handleStatusToggle(modal, t('reopenProjectSuccess'));
        }}
        renderLink={modal => (
          <ActionButton hollow onClick={modal.open}>
            {t('reopenProject')}
          </ActionButton>
        )}
      />
    );
  }

  return (
    <ConfirmationModal
      key="closeProjectModal"
      title={t('closeProjectConfirmTitle')}
      message={t('closeConfirmMessage')}
      confirmText={t('closeProject')}
      onConfirm={async modal => {
        await handleStatusToggle(modal, t('closeProjectSuccess'));
      }}
      renderLink={modal => (
        <ActionButton hollow onClick={modal.open}>
          {t('closeProject')}
        </ActionButton>
      )}
    />
  );
};

export default ProjectStatusAction;
