import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';
import gql from 'graphql-tag';

import { COMPANY_TYPE } from '__graphql__/globalTypes';
import toast from 'shared/utils/toast';
import Button from 'shared/components/Button';
import Form from 'shared/components/Form';
import Field from 'shared/components/Field';
import PageLoader from 'shared/components/PageLoader';

import { ButtonWrapper } from './ThreadReplyForm.styles';
import {
  ReplyToMessageThread,
  ReplyToMessageThreadVariables,
} from './__graphql__/ReplyToMessageThread';

const mutationReplyToMessageThread = gql`
  mutation ReplyToMessageThread(
    $authorType: COMPANY_TYPE!
    $messageThreadId: ID!
    $messageText: String!
  ) {
    replyToMessageThread(
      authorType: $authorType
      messageThreadId: $messageThreadId
      messageText: $messageText
    ) {
      id
    }
  }
`;

export interface ThreadReplyFormProps {
  activeUserType: COMPANY_TYPE;
  messageThreadId: string;
  refetchQueries: string[];
}

interface FormValues {
  messageThreadId: string;
  messageText: string;
}

const ThreadReplyForm: React.FC<ThreadReplyFormProps> = ({
  activeUserType,
  messageThreadId,
  refetchQueries,
}) => {
  const { t } = useTranslation('messages');
  const [replyToThread, replyToThreadMeta] = useMutation<
    ReplyToMessageThread,
    ReplyToMessageThreadVariables
  >(mutationReplyToMessageThread, {
    refetchQueries: () => ['MessageThreadInMessages', ...refetchQueries],
  });

  const isReplying = replyToThreadMeta.loading;

  const replyToThreadAndHandleErrors = async (values: FormValues) => {
    try {
      await replyToThread({ variables: { ...values, authorType: activeUserType } });
    } catch (error) {
      toast.error(error);
    }
  };

  return (
    <Form<FormValues>
      initialValues={{ messageText: '', messageThreadId }}
      validations={{ messageText: Form.is.required() }}
      onSubmit={async (values, form) => {
        form.resetForm();
        // eslint-disable-next-line @typescript-eslint/no-floating-promises
        await replyToThreadAndHandleErrors(values);
      }}
    >
      {() => (
        <Form.Element>
          {isReplying && <PageLoader />}
          {!isReplying && (
            <>
              <Field.Textarea placeholder={t('newReply.placeholder')} name="messageText" />
              <ButtonWrapper>
                <Button type="submit" data-cy-button-name="sendReply">
                  {t('buttons.send')}
                </Button>
              </ButtonWrapper>
            </>
          )}
        </Form.Element>
      )}
    </Form>
  );
};

export default ThreadReplyForm;
