import pubsub from 'sweet-pubsub';
import i18n from 'i18next';

import { get } from 'shared/utils/javascript';

interface Toast {
  type?: 'danger' | 'warning' | 'success';
  title?: string;
  message?: string;
  duration?: number;
}

const show = (toast: Toast): void => pubsub.emit('toast', toast);

const success = (title: string): void => show({ title });

const error = (err?: Error | string): void => {
  show({
    type: 'danger',
    title: i18n.t('error'),
    message: get(err, 'message', err),
    duration: 0,
  });
};

const warning = (message: { message: string } | string): void =>
  show({
    type: 'warning',
    message: get(message, 'message', message),
    duration: 0,
  });

export default {
  show,
  error,
  success,
  warning,
};
