import React from 'react';
import { useTranslation } from 'react-i18next';

import { HtmlHeadTags, Tabs, Page } from 'shared/components';
import { HeadingLarge } from 'shared/components/Styles';
import { PROJECT_STATUS } from '__graphql__/globalTypes';
import routes, { PROJECTS_ROUTE_TYPE } from 'shared/constants/routes';

import List from './List';

const Projects = () => {
  const { t } = useTranslation('projects');

  return (
    <Page>
      <HtmlHeadTags title="projects" />
      <HeadingLarge>{t('projects')}</HeadingLarge>
      <Tabs.Container>
        <Tabs.Links>
          <Tabs.Link to={routes.projects(PROJECTS_ROUTE_TYPE.open)}>{t('openProjects')}</Tabs.Link>
          <Tabs.Link to={routes.projects(PROJECTS_ROUTE_TYPE.closed)}>
            {t('closedProjects')}
          </Tabs.Link>
        </Tabs.Links>
        <Tabs.Content>
          <Tabs.Route path={routes.projects(PROJECTS_ROUTE_TYPE.open)} render={() => <List />} />
          <Tabs.Route
            path={routes.projects(PROJECTS_ROUTE_TYPE.closed)}
            render={() => <List status={PROJECT_STATUS.closed} />}
          />
        </Tabs.Content>
      </Tabs.Container>
    </Page>
  );
};

export default Projects;
