import React from 'react';

import { Item } from './types';
import { NULL_ITEM } from './constants';
import SelectBase from './SelectBase';
import { ValueSingle } from './Styles';

interface SelectSingleProps {
  className?: string;
  name: string;
  value: string;
  placeholder?: string;
  options: Item[];
  onChange: (value: string) => void;
  required?: boolean;
}

const SelectSingle: React.FC<SelectSingleProps> = ({
  className,
  name,
  value,
  placeholder,
  options,
  onChange,
  required,
}) => {
  const selectedItem = options.find(option => option.value === value) || NULL_ITEM;

  return (
    <SelectBase
      className={className}
      name={name}
      options={options}
      placeholder={placeholder}
      onSelect={selectedOption => {
        onChange(selectedOption ? selectedOption.value : '');
      }}
      selectedItem={selectedItem}
      isValueEmpty={selectedItem.value === ''}
      renderValue={() => <ValueSingle>{selectedItem.label}</ValueSingle>}
      showChevron
      showNullOption={!required}
      closeDropdownOnSelect
      getIsLabelInOptions={label =>
        options.some(item => item.label.toLowerCase() === label.toLowerCase())
      }
    />
  );
};

export default SelectSingle;
