import React, { ReactNode, SyntheticEvent } from 'react';
import { useTranslation } from 'react-i18next';

import Button from 'shared/components/Button';

import { Container, FormElements, Buttons } from './Styles';

interface ModalFormBodyProps {
  close: () => void;
  working: boolean;
  submitText?: string;
  onSubmit?: (event: SyntheticEvent) => void;
  useFullPageHeight?: boolean;
  scrollable?: boolean;
  children: ReactNode;
  submitOnEnter?: boolean;
}

const ModalFormBody: React.FC<ModalFormBodyProps> = ({
  close,
  working,
  scrollable = true,
  useFullPageHeight = true,
  submitText = 'save',
  onSubmit,
  children,
  submitOnEnter,
}) => {
  const { t } = useTranslation();

  return (
    <Container fullHeight={useFullPageHeight}>
      <FormElements scrollable={scrollable}>{children}</FormElements>
      <Buttons>
        <Button hollow onClick={close}>
          {t('cancel')}
        </Button>
        <Button
          type={onSubmit && !submitOnEnter ? 'button' : 'submit'}
          name={onSubmit && 'submit'}
          onClick={onSubmit}
          working={working}
        >
          {t(submitText)}
        </Button>
      </Buttons>
    </Container>
  );
};

export default ModalFormBody;
