import React, { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { useMutation } from '@apollo/react-hooks';
import gql from 'graphql-tag';

import QueryMatchesContext from 'shared/contexts/queryMatchesContext';
import { COMPANY_TYPE } from '__graphql__/globalTypes';
import toast from 'shared/utils/toast';
import Form from 'shared/components/Form';
import Field from 'shared/components/Field';
import Button from 'shared/components/Button';
import IconClickable from 'shared/components/IconClickable';

import {
  NewMessageWrapper,
  SingleRecipient,
  Heading,
  ButtonWrapper,
  GoBackWrapper,
} from './NewMessage.styles';
import {
  CreateMessageThread,
  CreateMessageThreadVariables,
} from './__graphql__/CreateMessageThread';

const mutationCreateMessageThread = gql`
  mutation CreateMessageThread(
    $authorType: COMPANY_TYPE!
    $bidRequestIds: [ID!]!
    $bidPackageId: ID!
    $subject: String!
    $messageText: String!
  ) {
    createMessageThread(
      authorType: $authorType
      bidRequestIds: $bidRequestIds
      bidPackageId: $bidPackageId
      subject: $subject
      messageText: $messageText
    ) {
      id
    }
  }
`;

export interface NewMessageProps {
  potentialRecipients: {
    bidRequestId: string;
    name: string;
  }[];
  activeUserType: COMPANY_TYPE;
  selectThread: (id: string) => void;
  refetchQueries: string[];
  goBack: () => void;
  bidPackageId: string;
}

interface FormValues {
  bidRequestIds: string[];
  bidPackageId: string;
  subject: string;
  messageText: string;
}

const NewMessage: React.FC<NewMessageProps> = ({
  potentialRecipients,
  activeUserType,
  selectThread,
  refetchQueries,
  goBack,
  bidPackageId,
}) => {
  const { t } = useTranslation('messages');
  const { isMobile } = useContext(QueryMatchesContext);

  const [createThread] = useMutation<CreateMessageThread, CreateMessageThreadVariables>(
    mutationCreateMessageThread,
    {
      refetchQueries: () => refetchQueries,
    },
  );

  const selectOptions = potentialRecipients.map(({ bidRequestId, name }) => ({
    value: bidRequestId,
    label: name,
  }));

  const createThreadAndNotifyUser = async (values: FormValues, recipientNames: string[]) => {
    try {
      const { data } = await createThread({ variables: { ...values, authorType: activeUserType } });

      if (data) {
        toast.success(t('newMessage.successNotification', { names: recipientNames.join(', ') }));
        selectThread(data.createMessageThread.id);
      }
    } catch (error) {
      toast.error(error);
    }
  };

  return (
    <Form<FormValues>
      initialValues={{
        bidRequestIds:
          potentialRecipients.length === 1 ? [potentialRecipients[0].bidRequestId] : [],
        subject: '',
        messageText: '',
        bidPackageId,
      }}
      validations={{
        bidRequestIds: Form.is.required(),
        subject: Form.is.required(),
        messageText: Form.is.required(),
      }}
      onSubmit={values => {
        const recipientNames = potentialRecipients
          .filter(recipient => values.bidRequestIds.includes(recipient.bidRequestId))
          .map(recipient => recipient.name);

        // eslint-disable-next-line @typescript-eslint/no-floating-promises
        createThreadAndNotifyUser(values, recipientNames);
      }}
    >
      {() => (
        <NewMessageWrapper>
          <Form.Element>
            <Heading>
              {isMobile && (
                <GoBackWrapper>
                  <IconClickable onClick={goBack} type="arrow-left" size={24} />
                </GoBackWrapper>
              )}
              {t('newMessage.heading')}
            </Heading>
            {activeUserType === COMPANY_TYPE.agent && (
              <SingleRecipient>
                {t('newMessage.toSingleRecipient', { name: potentialRecipients[0].name })}
              </SingleRecipient>
            )}
            {activeUserType === COMPANY_TYPE.principal && (
              <Field.SelectMulti
                label={t('newMessage.fieldLabels.to')}
                name="bidRequestIds"
                options={selectOptions}
              />
            )}
            <Field.Input label={t('newMessage.fieldLabels.subject')} name="subject" />
            <Field.Textarea
              label={t('newMessage.fieldLabels.message')}
              name="messageText"
              minRows={8}
            />
            <ButtonWrapper>
              <Button type="submit" data-cy-button-name="sendNewMessage">
                {t('buttons.send')}
              </Button>
            </ButtonWrapper>
          </Form.Element>
        </NewMessageWrapper>
      )}
    </Form>
  );
};

export default NewMessage;
