import React, { useRef } from 'react';
import { useTranslation } from 'react-i18next';

import { StyledProgress, ProgressLabel, ProgressBar } from './Styles';

interface Progress {
  isUploading: boolean;
  uploadedCount: number;
  totalCount: number;
  currentFileProgress: number;
  currentFileName: string;
}

interface Props {
  progress: Progress;
}

const ProgressBars: React.FC<Props> = ({ progress }) => {
  const { t } = useTranslation();
  const progressRef = useRef<HTMLDivElement>(null);

  if (!progress.isUploading) {
    return null;
  }

  const calcProgressPaddingTop = () =>
    progressRef.current && progressRef.current.getBoundingClientRect().top < 50
      ? Math.abs(progressRef.current.getBoundingClientRect().top) + 50
      : 50;

  return (
    <StyledProgress ref={progressRef} paddingTop={calcProgressPaddingTop()}>
      <ProgressLabel>{t('documentsTable.uploadProgress', progress)}</ProgressLabel>
      <ProgressBar percentComplete={progress.currentFileProgress} />
      <ProgressLabel fontSize={13}>
        {progress.currentFileProgress}% {progress.currentFileName || ''}
      </ProgressLabel>
    </StyledProgress>
  );
};

export default ProgressBars;
