import React from 'react';
import { useTranslation } from 'react-i18next';
import { useMutation } from '@apollo/react-hooks';

import toast from 'shared/utils/toast';
import { BID_PACKAGE_STATUS } from '__graphql__/globalTypes';
import { ConfirmationModal } from 'shared/components';

import { mutationAddContactToBidRequest } from './api';
import { SubButton } from './Styles';
import {
  AddContactToBidRequest,
  AddContactToBidRequestVariables,
} from './__graphql__/AddContactToBidRequest';
import { BidPackageForAddBidders_bidPackage } from './__graphql__/BidPackageForAddBidders';

interface Props {
  bidPackage: BidPackageForAddBidders_bidPackage;
  bidPackageRefetch: () => void;
  subcontractorId: string;
  contactId: string;
  isBanned: boolean;
}

const ContactAction: React.FC<Props> = ({
  bidPackage,
  bidPackageRefetch,
  subcontractorId,
  contactId,
  isBanned,
}) => {
  const { t } = useTranslation('bidPackages');

  const [addContactToBidRequest, { loading: isLoading }] = useMutation<
    AddContactToBidRequest,
    AddContactToBidRequestVariables
  >(mutationAddContactToBidRequest, {
    variables: {
      bidPackageId: bidPackage.id,
      subcontractorId,
      contactId,
    },
    refetchQueries: () => ['BidPackageForAddBidders', 'BidRequestsForBidPackage'],
    awaitRefetchQueries: true,
  });

  if (bidPackage.addedContactIds.includes(contactId)) {
    return (
      <SubButton icon="check-fat" disabled data-cy-add-contact={contactId}>
        {t('added')}
      </SubButton>
    );
  }

  if (isBanned) {
    return (
      <SubButton icon="close-fat" disabled data-cy-add-contact={contactId}>
        {t('banned')}
      </SubButton>
    );
  }

  if (bidPackage.status === BID_PACKAGE_STATUS.draft) {
    return (
      <SubButton
        data-cy-add-contact={contactId}
        icon="plus"
        working={isLoading}
        onClick={() => {
          addContactToBidRequest().then(() => toast.success(t('addSuccess')), toast.error);
        }}
      >
        {t('add')}
      </SubButton>
    );
  }

  return (
    <ConfirmationModal
      title={t('inviteConfirmTitle')}
      message={null}
      confirmText={t('sendInvitation')}
      renderLink={modal => (
        <SubButton icon="email" onClick={modal.open} data-cy-add-contact={contactId}>
          {t('sendInvitation')}
        </SubButton>
      )}
      isWorking={isLoading}
      onConfirm={async modal => {
        try {
          await addContactToBidRequest();
          toast.success(t('inviteSuccess'));
          modal.close();
        } catch (error) {
          toast.error(error);
          bidPackageRefetch();
        }
      }}
    />
  );
};

export default ContactAction;
