import { BUTTON_VARIANT } from 'shared/constants';
import { COMPANY_SIZE } from '__graphql__/globalTypes';

import { COLOR } from './styles';

export const getColorFromButtonVariant = (type: BUTTON_VARIANT): COLOR =>
  ({
    [BUTTON_VARIANT.danger]: COLOR.danger,
    [BUTTON_VARIANT.primary]: COLOR.primary,
    [BUTTON_VARIANT.success]: COLOR.success,
  }[type]);

export const getHumanReadableCompanySize = (value: COMPANY_SIZE): string =>
  ({
    [COMPANY_SIZE.size_1_10]: '1-10',
    [COMPANY_SIZE.size_11_50]: '11-50',
    [COMPANY_SIZE.size_51_200]: '51-200',
    [COMPANY_SIZE.size_201_500]: '201-500',
    [COMPANY_SIZE.size_501_1000]: '501-1000',
    [COMPANY_SIZE.size_1001_5000]: '1001-5000',
    [COMPANY_SIZE.size_5000plus]: '5000+',
  }[value]);
