import gql from 'graphql-tag';

export const queryProjectForShow = gql`
  query ProjectForShow($id: ID!) {
    project(id: $id) {
      id
      name
      status
    }
  }
`;

export const queryProjectForPrintingDocuments = gql`
  query ProjectForPrintingDocuments($id: ID!) {
    project(id: $id) {
      id
      name
    }
  }
`;

export const queryOfficesForProjectOverview = gql`
  query OfficesForProjectOverview($offset: Int!, $limit: Int!) {
    offices(offset: $offset, limit: $limit) {
      offices {
        id
        name
      }
    }
  }
`;

export const queryProjectForOverview = gql`
  query ProjectForOverview($id: ID!) {
    project(id: $id) {
      id
      number
      name
      imageKey
      customer
      location
      startsOn
      endsOn
      description
      budget
      primaryUser {
        id
      }
      projectUsers {
        id
        user {
          id
          firstName
          lastName
        }
        userRole
      }
      isEstimating
      officeId
      forceSubcontractorSignup
      notes
      createdAt
    }
  }
`;

export const mutationCloseProject = gql`
  mutation CloseProject($id: ID!) {
    closeProject(id: $id)
  }
`;

export const mutationReopenProject = gql`
  mutation ReopenProject($id: ID!) {
    reopenProject(id: $id)
  }
`;

export const mutationUpdateProject = gql`
  mutation UpdateProject($id: ID!, $input: ProjectInput!) {
    updateProject(id: $id, input: $input) {
      id
    }
  }
`;

export const mutationCreateProject = gql`
  mutation CreateProject($input: ProjectInput!) {
    createProject(input: $input) {
      id
    }
  }
`;
