import React, { useContext } from 'react';
import { useTranslation } from 'react-i18next';

import QueryMatchesContext from 'shared/contexts/queryMatchesContext';

import DocumentsTableContext from './DocumentsTableContext';
import addItemIdToMoveEvent from './utils/addItemIdToMoveEvent';
import ItemControls from './ItemControls';
import { StyledIcon, Row, RowProps, StyledFolder, FolderName } from './Styles';
import Folder from './classes/Folder';

interface MainProps {
  nestingLevel: number;
  folder: Folder;
  isFolderOpen: boolean;
  toggleFolder: () => void;
}

type Props = MainProps & RowProps;

const FolderRow: React.FC<Props> = ({
  nestingLevel,
  folder,
  isFolderOpen,
  toggleFolder,
  ...rowProps
}) => {
  const { t } = useTranslation();
  const { isMobile } = useContext(QueryMatchesContext);
  const { isReadOnly } = useContext(DocumentsTableContext);

  let icon: 'arrow-left' | 'folder-minus' | 'folder-plus';

  if (isFolderOpen) {
    icon = isMobile ? 'arrow-left' : 'folder-minus';
  } else {
    icon = 'folder-plus';
  }

  return (
    <Row data-cy-folder-name={folder.name} data-cy-nesting-level={nestingLevel} {...rowProps}>
      <StyledFolder
        nestingLevel={nestingLevel}
        onClick={toggleFolder}
        draggable={!isReadOnly}
        onDragStart={event => addItemIdToMoveEvent(event, folder.id)}
      >
        <StyledIcon nestingLevel={0} type={icon} />
        <FolderName>{`${folder.name} ${
          folder.isEmpty() ? t('documentsTable.empty') : ''
        }`}</FolderName>
      </StyledFolder>
      <ItemControls item={folder} />
    </Row>
  );
};

export default FolderRow;
