import gql from 'graphql-tag';

import { fragmentSubcontractorInfo } from 'shared/components/SubcontractorInfo';
import { fragmentContactsList } from 'shared/components/ContactsList';

export const fragmentBidFormModal = gql`
  fragment BidFormModal_bid on Bid {
    id
    totalAmount
    submittedAt
    message
  }
  fragment BidFormModal_bidRequest on BidRequest {
    subcontractor {
      id
      ...SubcontractorInfo_subcontractorInfo
    }
    ...ContactsList_bidRequest
  }
  ${fragmentSubcontractorInfo}
  ${fragmentContactsList}
`;

export const draftBid = gql`
  query DraftBid($bidRequestId: ID!) {
    draftBid(bidRequestId: $bidRequestId) {
      id
      message
      totalAmount
    }
  }
`;

export const subcontractorDraftBid = gql`
  query SubcontractorDraftBid($bidRequestId: ID!) {
    subcontractorDraftBid(bidRequestId: $bidRequestId) {
      id
      message
      totalAmount
    }
  }
`;

export const mutationSubmitBid = gql`
  mutation SubmitBid($bidRequestId: ID!, $input: BidInput!) {
    submitBid(bidRequestId: $bidRequestId, input: $input) {
      id
    }
  }
`;

export const mutationSubcontractorSubmitBid = gql`
  mutation SubcontractorSubmitBid($bidRequestId: ID!, $input: BidInput!) {
    subcontractorSubmitBid(bidRequestId: $bidRequestId, input: $input) {
      id
    }
  }
`;
