import React from 'react';
import { useQuery } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import { isDoneLoading } from 'shared/utils/apollo';
import routes from 'shared/constants/routes';
import useTabPathFragment from 'shared/hooks/useTabPathFragment';

import { queryProjectWithBidPackages } from './api';
import {
  Sidebar,
  Project,
  ProjectNumber,
  ProjectName,
  BidPackageLink,
  BidPackageNumber,
  BidPackageName,
  ListTitle,
} from './Sidebar.styles';
import {
  ProjectWithBidPackages,
  ProjectWithBidPackagesVariables,
} from './__graphql__/ProjectWithBidPackages';

interface Props {
  projectId: string;
}

const BidPackagesSidebar: React.FC<Props> = ({ projectId }) => {
  const { t } = useTranslation('bidPackages');

  const tabPathFragment = useTabPathFragment();

  const { error, ...queryResults } = useQuery<
    ProjectWithBidPackages,
    ProjectWithBidPackagesVariables
  >(queryProjectWithBidPackages, {
    variables: { id: projectId },
  });

  if (!isDoneLoading(queryResults) || error) {
    return <Sidebar />;
  }

  const { data } = queryResults;

  const sortedBidPackages = data.project.bidPackages.sort((a, b) => {
    if (a.number && b.number) {
      return a.number.localeCompare(b.number) || a.name.localeCompare(b.name);
    }

    return a.name.localeCompare(b.name);
  });

  return (
    <Sidebar>
      <ListTitle>{t('project')}</ListTitle>
      <Project>
        {data.project.number && <ProjectNumber>{data.project.number}</ProjectNumber>}
        <ProjectName>{data.project.name}</ProjectName>
      </Project>
      <ListTitle>{t('bidPackages')}</ListTitle>
      {sortedBidPackages.map(bidPackage => (
        <BidPackageLink
          key={bidPackage.id}
          to={`${routes.showBidPackage(projectId, bidPackage.id)}${tabPathFragment}`}
        >
          {bidPackage.number && <BidPackageNumber>{bidPackage.number}</BidPackageNumber>}
          <BidPackageName>{bidPackage.name}</BidPackageName>
        </BidPackageLink>
      ))}
    </Sidebar>
  );
};

export default BidPackagesSidebar;
