import React from 'react';
import { useTranslation } from 'react-i18next';

import Icon from 'shared/components/Icon';
import { ClickableProps } from 'shared/components/Clickable';

import { Wrapper, IconWrapper } from './Styles';

const AddMoreLink: React.FC<ClickableProps> = ({ children, ...props }) => {
  const { t } = useTranslation();

  return (
    <Wrapper {...props}>
      <IconWrapper>
        <Icon type="plus" />
      </IconWrapper>
      {children || t('addMore')}
    </Wrapper>
  );
};

export default AddMoreLink;
