import { Moment } from 'moment';
import React, { useLayoutEffect, useRef } from 'react';

import { range } from 'shared/utils/javascript';

import { StyledTimeSection, Time } from './Styles';

const formatTime = (date: Moment) => date.format('HH:mm');

const generateTimes = () =>
  range(48).map(i => {
    const hour = `${Math.floor(i / 2)}`;
    const paddedHour = hour.length < 2 ? `0${hour}` : hour;
    const minutes = i % 2 === 0 ? '00' : '30';
    return `${paddedHour}:${minutes}`;
  });

interface TimeSectionProps {
  changeTime: (time: string) => void;
  selectedDate: Moment;
}

const TimeSection: React.FC<TimeSectionProps> = ({ selectedDate, changeTime }) => {
  const timeSectionRef = useRef<HTMLDivElement>(null);

  const scrollToSelectedTime = () => {
    if (!timeSectionRef.current) return;

    const $selectedTime = timeSectionRef.current.querySelector(
      `[data-time="${formatTime(selectedDate)}"]`,
    );

    if (!$selectedTime) return;

    // Note: `$selectedTime` may no longer be `HTMLElement` as you change `<Time>` e.g. to <svg>
    // so `selectedTimeOffsetTop` becomes `undefined` in this case
    const selectedTimeOffsetTop = ($selectedTime as HTMLElement).offsetTop;

    timeSectionRef.current.scrollTop = selectedTimeOffsetTop - 80;
  };

  // If we used regular `useEffect`, the scroll would occur after the initial render, which creates
  // an unpleasant effect.
  useLayoutEffect(scrollToSelectedTime, []);

  return (
    <StyledTimeSection ref={timeSectionRef}>
      {generateTimes().map(time => (
        <Time
          key={time}
          data-time={time}
          isSelected={time === formatTime(selectedDate)}
          onClick={() => changeTime(time)}
        >
          {time}
        </Time>
      ))}
    </StyledTimeSection>
  );
};

export default TimeSection;
