import React from 'react';
import { useTranslation } from 'react-i18next';
import { useQuery } from '@apollo/react-hooks';

import { QueryErrors, PageLoader, Table, Cell } from 'shared/components';
import { TableSearch } from 'shared/components/Styles';
import { CompanyStatusTag } from 'shared/components/TagStyles';
import { TABLE_ITEMS_PER_PAGE } from 'shared/constants';
import { isDoneLoading } from 'shared/utils/apollo';

import { queryAdminCompanies } from './api';
import Dropdown from './Dropdown';
import { AdminCompanies } from './__graphql__/AdminCompanies';

const Companies = () => {
  const { t } = useTranslation('admin');

  const { error, ...queryResults } = useQuery<AdminCompanies>(queryAdminCompanies, {
    variables: { offset: 0, limit: TABLE_ITEMS_PER_PAGE },
  });

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const { data, refetch, variables } = queryResults;

  return (
    <>
      <TableSearch onChange={(search: string) => refetch({ query: search })} />
      <Table
        columns={[
          { label: t('company.name'), sortField: 'name' },
          { label: t('company.status'), sortField: 'status' },
          { fixedWidth: 50 },
        ]}
        rows={data.adminCompanies.companies.map(company => (
          <Table.Row rowId={company.id}>
            <Cell.Text>{company.name}</Cell.Text>
            <CompanyStatusTag inList status={company.status}>
              {t(`company.${company.status}`)}
            </CompanyStatusTag>
            <Dropdown company={company} />
          </Table.Row>
        ))}
        totalRecords={data.adminCompanies.totalRecords}
        refetch={refetch}
        variables={variables}
      />
    </>
  );
};

export default Companies;
