import React from 'react';

import Folder from '../classes/Folder';

interface Args {
  rootFolder: Folder;
}

export type MoveHandler = (event: React.DragEvent, targetFolder: Folder) => void;

const getMoveHandler = ({ rootFolder }: Args): MoveHandler => (event, targetFolder) => {
  event.preventDefault();

  // eslint-disable-next-line @typescript-eslint/unbound-method
  if (!event.dataTransfer || !event.dataTransfer.getData) return;
  const movedItemJSON = event.dataTransfer.getData('text');

  const isNotMoveEvent = !movedItemJSON;
  if (isNotMoveEvent) return;

  let parsedJSON;

  try {
    parsedJSON = JSON.parse(movedItemJSON);
  } catch {
    return;
  }

  const { movedItemId } = parsedJSON;

  const item = rootFolder.findItembyId(movedItemId);
  if (item) {
    item.moveTo(targetFolder);
  }
};

export default getMoveHandler;
