import React, { useContext } from 'react';
import { Redirect, useRouteMatch } from 'react-router-dom';
import { useQuery } from '@apollo/react-hooks';

import PageNotFound from 'components/PageNotFound';
import { ERROR_TYPES, AUTH_STATUS } from 'shared/constants';
import { doesErrorMatchCode, isDoneLoading } from 'shared/utils/apollo';
import { PageLoader, QueryErrors } from 'shared/components';
import routes from 'shared/constants/routes';
import AuthContext from 'shared/contexts/authContext';

import BidInviteSignup from './Signup';
import ClaimBidInvite from './Claim';
import { queryBidInvite } from './api';
import { BidInvite as BidInviteQuery, BidInviteVariables } from './__graphql__/BidInvite';

const BidInvite: React.FC<{}> = () => {
  const { params } = useRouteMatch();
  const { authState } = useContext(AuthContext);

  const { error, ...results } = useQuery<BidInviteQuery, BidInviteVariables>(queryBidInvite, {
    variables: { bidInviteId: params.bidInviteId },
  });

  if (error) {
    if (doesErrorMatchCode(error, ERROR_TYPES.notFound)) {
      return <PageNotFound.Authenticated />;
    }
    return <QueryErrors error={error} />;
  }

  if (!isDoneLoading(results)) {
    return <PageLoader />;
  }

  const { bidInvite } = results.data;

  // If the `forceSubcontractorSignup` flag is set on the project, the agent user
  // needs to sign up (or login) before being able to view this bid invite.
  if (
    authState.status === AUTH_STATUS.anonymous &&
    bidInvite.bidRequest.bidPackage.project.forceSubcontractorSignup
  ) {
    return <BidInviteSignup bidInvite={bidInvite} />;
  }

  return (
    <ClaimBidInvite bidInviteId={params.bidInviteId}>
      <Redirect to={routes.bidRequest(bidInvite.bidRequest.id)} />
    </ClaimBidInvite>
  );
};

export default BidInvite;
