import React, { useContext, useEffect } from 'react';
import { Transition } from 'react-transition-group';

import { isEnter, isExited } from 'shared/utils/transitionStates';

import File from './File';
import FolderRow from './FolderRow';
import DocumentsTableContext from './DocumentsTableContext';
import { MobileRowsContainer, transitionTime } from './Styles';
import Folder from './classes/Folder';

interface Props {
  folder: Folder;
  index: number;
  isParentCurrentlySelected: boolean;
}

const FolderWithContents: React.FC<Props> = ({ folder, index, isParentCurrentlySelected }) => {
  const {
    documents,
    mobileNavigation: {
      getIsOpen,
      toggleFolder,
      getIsCurrentlySelected,
      setCurrentlyVisibleRows,
      rootId,
    },
  } = useContext(DocumentsTableContext);
  const isRoot = folder.isRoot();
  const folderId = isRoot ? rootId : folder.id;
  const isOpen = getIsOpen(folderId);
  const isCurrentlySelected = getIsCurrentlySelected(folderId);

  const files = folder.getFilesWithDocs(documents);
  const folders = folder.getChildFolders();

  useEffect(() => {
    if (isCurrentlySelected) {
      setCurrentlyVisibleRows(files.length + folders.length + (isRoot ? 0 : 1));
    }
  }, [isCurrentlySelected, setCurrentlyVisibleRows, files.length, folders.length, isRoot]);

  return (
    <>
      {!isRoot && (
        <FolderRow
          nestingLevel={0}
          folder={folder}
          isFolderOpen={isOpen}
          toggleFolder={() => toggleFolder(folderId)}
          index={isOpen ? 0 : index}
          isFolderAndOpen={isOpen}
          isAtTopOfList={isOpen ? true : index === 0}
          isShown={isCurrentlySelected || isParentCurrentlySelected}
          isAboveOthers={isCurrentlySelected || isParentCurrentlySelected}
        />
      )}
      <Transition timeout={transitionTime} in={isOpen}>
        {state => (
          <MobileRowsContainer>
            {files.map(([file, document], i) => (
              <File
                key={document.id}
                nestingLevel={0}
                folder={folder}
                file={file}
                document={document}
                index={i + (isRoot ? 0 : 1)}
                isFolderAndOpen={false}
                isAtTopOfList={isRoot ? i === 0 : false}
                isShown={isCurrentlySelected && isEnter(state)}
                isAboveOthers={isCurrentlySelected && !isExited(state)}
              />
            ))}
            {folders.map((item, i) => (
              <FolderWithContents
                key={item.id}
                folder={item}
                index={files.length + (isRoot ? 0 : 1) + i}
                isParentCurrentlySelected={isCurrentlySelected}
              />
            ))}
          </MobileRowsContainer>
        )}
      </Transition>
    </>
  );
};

export default FolderWithContents;
