import React, { ReactNode, Ref, forwardRef } from 'react';

import Icon, { IconProps } from 'shared/components/Icon';

import StyledInput from './Styles';

export type InputRenderProps = Omit<React.ComponentPropsWithRef<'input'>, 'onChange' | 'ref'> & {
  onChange: (value: string, event: React.ChangeEvent<HTMLInputElement>) => void;
  ref?: Ref<HTMLInputElement>;
};

export interface InputProps extends Omit<React.ComponentPropsWithoutRef<'input'>, 'onChange'> {
  icon?: IconProps['type'];
  invalid?: boolean;
  filter?: RegExp;
  onChange?: (value: string, event: React.ChangeEvent<HTMLInputElement>) => void;
  renderInput?: (props: InputRenderProps) => ReactNode;
}

const Input = forwardRef<HTMLInputElement, InputProps>(
  (
    {
      icon,
      className,
      invalid = false,
      filter,
      onChange = () => {},
      name,
      renderInput = renderProps => (
        <input
          {...renderProps}
          onChange={event => renderProps.onChange(event.target.value, event)}
        />
      ),
      ...inputProps
    },
    ref,
  ) => {
    const handleChange: InputRenderProps['onChange'] = (value, event) => {
      if (!filter || filter.test(value)) {
        onChange(value, event);
      }
    };

    return (
      <StyledInput className={className} icon={icon} invalid={invalid}>
        {icon && <Icon type={icon} />}
        {renderInput({
          ...inputProps,
          name,
          onChange: handleChange,
          ref,
        })}
      </StyledInput>
    );
  },
);

Input.displayName = 'Input';

export default Input;
