type HandleMouseDown = () => void;
type HandleFocus = (
  callback: (event: React.FocusEvent) => void,
) => (event: React.FocusEvent) => void;

const onlyOnKeyboardFocus = () => {
  let hasReceivedMouseDown = false;

  const handleMouseDown: HandleMouseDown = () => {
    hasReceivedMouseDown = true;
  };

  const whenKeyboardFocus: HandleFocus = callback => event => {
    if (!hasReceivedMouseDown) {
      callback(event);
    }
  };

  return { handleMouseDown, whenKeyboardFocus };
};

export default onlyOnKeyboardFocus;
