import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';
import { DocumentNode } from 'graphql';

import toast from 'shared/utils/toast';
import IconClickable from 'shared/components/IconClickable';
import ConfirmationModal, { ConfirmationModalProps } from 'shared/components/ConfirmationModal';
import { BUTTON_VARIANT } from 'shared/constants';

interface DeletionModalProps {
  id?: string;
  mutation: DocumentNode;
  refetchQueries?: () => string[];
  renderLink?: ConfirmationModalProps['renderLink'];
  titleKey?: string;
  messageKey?: string;
  confirmTextKey?: string;
  successKey?: string;
}

const DeletionModal: React.FC<DeletionModalProps> = ({
  id,
  mutation,
  refetchQueries,
  renderLink = modal => <IconClickable onClick={modal.open} type="delete" />,
  titleKey = 'confirmDeleteMessage',
  messageKey = 'confirmDeleteDescription',
  confirmTextKey = 'delete',
  successKey = 'itemDeleted',
}) => {
  const { t } = useTranslation();

  const [mutate, { loading: mutating }] = useMutation(mutation, {
    refetchQueries,
    awaitRefetchQueries: true,
  });

  return (
    <ConfirmationModal
      confirmationButtonVariant={BUTTON_VARIANT.danger}
      title={titleKey}
      message={messageKey}
      confirmText={confirmTextKey}
      renderLink={renderLink}
      isWorking={mutating}
      onConfirm={async () => {
        try {
          await mutate({ variables: { id } });
          toast.success(t(successKey));
          // Closing the modal is not necessary (or possible) here
          // because by the time this function runs, the modal has
          // already been unmounted from the DOM.
        } catch (error) {
          toast.error(error);
        }
      }}
    />
  );
};

export default DeletionModal;
