import React from 'react';
import { useTranslation } from 'react-i18next';

import { BID_REQUEST_STATUS, BID_PACKAGE_STATUS, PROJECT_STATUS } from '__graphql__/globalTypes';
import { Button, BidsList, BidFormModal } from 'shared/components';
import { ProjectClosedMessage } from 'shared/components/Styles';
import { bidRequestStatusIconKey } from 'shared/components/Icon/utils';

import { BidsWrapper, StatusMessage, StatusIcon, BidsListContainer } from './Bids.styles';
import {
  BidRequestForSubcontractor_bidRequestForSubcontractor,
  BidRequestForSubcontractor_bidRequestForSubcontractor_bids,
} from './__graphql__/BidRequestForSubcontractor';

interface Props {
  bidRequest: BidRequestForSubcontractor_bidRequestForSubcontractor;
}

function isBidSubmittedToBidRequest<
  T extends { status: BID_REQUEST_STATUS; bids: unknown },
  C extends T['bids'] extends infer U ? U : never
>(
  bidRequest: T,
): bidRequest is T & {
  bids: NonNullable<C>;
} {
  return bidRequest.status === BID_REQUEST_STATUS.bidSubmitted;
}

function getBidsWithTotalAmount<
  B extends BidRequestForSubcontractor_bidRequestForSubcontractor_bids = BidRequestForSubcontractor_bidRequestForSubcontractor_bids
>(bids: B[]) {
  return bids.filter((bid): bid is B & { totalAmount: number } => bid.totalAmount !== null);
}

const Bids: React.FC<Props> = ({ bidRequest }) => {
  const { t } = useTranslation('bidInvites');

  const isProjectClosed = bidRequest.bidPackage.project.status === PROJECT_STATUS.closed;
  const isAnyBidRequestAwarded = bidRequest.bidPackage.status === BID_PACKAGE_STATUS.bidAwarded;
  const isBidRequestAwardedToOther =
    isAnyBidRequestAwarded && bidRequest.status !== BID_REQUEST_STATUS.bidAwarded;
  const isBidRequestAwardedToUs =
    isAnyBidRequestAwarded && bidRequest.status === BID_REQUEST_STATUS.bidAwarded;

  if (isBidRequestAwardedToOther || isBidRequestAwardedToUs) {
    return (
      <BidsWrapper>
        {isProjectClosed && (
          <ProjectClosedMessage addBottomMargin>{t('overview.projectClosed')}</ProjectClosedMessage>
        )}
        <StatusMessage>
          <StatusIcon type={bidRequestStatusIconKey[BID_REQUEST_STATUS.bidAwarded]} />
          <h3>
            {isBidRequestAwardedToOther ? t('overview.otherWasAwarded') : t('overview.bidAwarded')}
          </h3>
        </StatusMessage>
      </BidsWrapper>
    );
  }

  if (isBidSubmittedToBidRequest(bidRequest)) {
    const { subcontractor, currentBidId, bidInvites } = bidRequest;
    const bids = getBidsWithTotalAmount(bidRequest.bids);

    return (
      <BidsWrapper>
        <StatusMessage>
          <StatusIcon type={bidRequestStatusIconKey[BID_REQUEST_STATUS.bidSubmitted]} />
          <h3>{t('overview.bidSubmitted')}</h3>
        </StatusMessage>
        <BidsListContainer>
          <BidsList
            bids={bids}
            subcontractor={subcontractor}
            currentBidId={currentBidId}
            bidInvites={bidInvites}
            isResponsive
          />
        </BidsListContainer>
        {!isProjectClosed && (
          <BidFormModal
            bidRequest={bidRequest}
            isUserSubcontractor
            refetchQueries={() => ['BidRequestForSubcontractor']}
            renderLink={modal => <Button onClick={modal.open}>{t('overview.reviseBid')}</Button>}
          />
        )}
      </BidsWrapper>
    );
  }

  return (
    <BidsWrapper>
      <StatusMessage>
        <h3>{t('overview.bidNotSubmitted')}</h3>
      </StatusMessage>
      {!isProjectClosed && (
        <BidFormModal
          bidRequest={bidRequest}
          isUserSubcontractor
          refetchQueries={() => ['BidRequestForSubcontractor']}
          renderLink={modal => <Button onClick={modal.open}>{t('overview.submitBid')}</Button>}
        />
      )}
    </BidsWrapper>
  );
};

export default Bids;
