import React from 'react';
import { useTranslation } from 'react-i18next';
import { useQuery } from '@apollo/react-hooks';

import { Form, Field, ModalFormBody, QueryErrors, PageLoader } from 'shared/components';
import { HeadingMedium } from 'shared/components/Styles';
import { USER_STATUS } from '__graphql__/globalTypes';
import { isDoneLoading } from 'shared/utils/apollo';

import { ProjectForOverview_project_projectUsers } from '../__graphql__/ProjectForOverview';

import { queryUsersForProjectForm } from './api';
import {
  UsersForProjectForm,
  UsersForProjectForm_users_users,
  UsersForProjectFormVariables,
} from './__graphql__/UsersForProjectForm';

interface Props {
  closeModal: () => void;
  modifyUser: (newUser: { user: UsersForProjectForm_users_users; userRole: string }) => void;
  formUser?: {
    name: string;
    id: string;
    userRole?: string;
  };
  assignedUsers: ProjectForOverview_project_projectUsers[];
}

interface FormValues {
  id: string;
  name: string;
  userRole: string;
}

const UsersForm: React.FC<Props> = ({ closeModal, assignedUsers, formUser, modifyUser }) => {
  const { t } = useTranslation('projects');
  const { error, ...queryResults } = useQuery<UsersForProjectForm, UsersForProjectFormVariables>(
    queryUsersForProjectForm,
    {
      variables: { offset: 0, limit: 200 },
      fetchPolicy: 'no-cache',
    },
  );

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const { users } = queryResults.data;

  const assignedUserIds = new Set(assignedUsers.map(({ user: { id } }) => id));

  return (
    <Form<FormValues>
      initialValues={{
        id: formUser?.id || '',
        name: formUser?.name || '',
        userRole: formUser?.userRole || '',
      }}
      validations={{
        id: [Form.is.required()],
        userRole: [Form.is.required()],
      }}
      onSubmit={({ id, userRole }) => {
        const user = users.users.find(({ id: sourceId }) => id === sourceId);
        if (user) {
          modifyUser({ user, userRole });
        }
        closeModal();
      }}
    >
      {({ handleSubmit }) => (
        <ModalFormBody
          useFullPageHeight={false}
          working={queryResults.loading}
          close={closeModal}
          submitOnEnter
          onSubmit={event => {
            event.preventDefault();
            handleSubmit();
          }}
        >
          <HeadingMedium>{t(formUser ? 'editUserTitle' : 'addUserTitle')}</HeadingMedium>
          {formUser ? (
            <Field.Input disabled name="name" label={t('user.name')} />
          ) : (
            <Field.SelectSingle
              name="id"
              label={t('user.name')}
              options={users.users
                .filter(
                  sourceUser =>
                    sourceUser.status === USER_STATUS.active && !assignedUserIds.has(sourceUser.id),
                )
                .map(sourceUser => ({
                  value: sourceUser.id,
                  label: `${sourceUser.firstName} ${sourceUser.lastName}`,
                }))}
            />
          )}
          <Field.Input name="userRole" label={t('user.role')} />
        </ModalFormBody>
      )}
    </Form>
  );
};

export default UsersForm;
