import React, { useContext } from 'react';

import QueryMatchesContext from 'shared/contexts/queryMatchesContext';

import ProgressBars from './ProgressBars';
import TopControls from './TopControls';
import NoResultsMessage from './NoResultsMessage';
import Headers from './Headers';
import FolderWithContents from './FolderWithContents';
import DocumentsTableContext from './DocumentsTableContext';
import useFilesDropHandler from './hooks/useFilesDropHandler';
import useMobileNavigation from './hooks/useMobileNavigation';
import getMoveHandler from './utils/getMoveHandler';
import getDownloadURLBuilder from './utils/getDownloadURLBuilder';
import { DocumentsTableContainer, StyledDocumentsTable, MobileContentContainer } from './Styles';
import PrintDocuments from './PrintDocuments';
import Folder from './classes/Folder';
import { DocumentsTable_documents } from './__graphql__/DocumentsTable_documents';

interface Props {
  name: string;
  isReadOnly?: boolean;
  fileTreeIds: string[];
  rootFolder: Folder;
  documents: DocumentsTable_documents[];
  refetch: () => Promise<unknown>;
  printPath?: string;
  printDocuments?: boolean;
}

const DocumentsTable: React.FC<Props> = ({
  name,
  isReadOnly = false,
  fileTreeIds,
  rootFolder,
  documents,
  refetch,
  printPath,
  printDocuments = false,
}) => {
  const { handleFilesDrop, progress } = useFilesDropHandler();
  const handleFileOrFolderMove = getMoveHandler({ rootFolder });
  const downloadURL = getDownloadURLBuilder({ name, fileTreeIds });
  const { isMobile, isDesktop } = useContext(QueryMatchesContext);
  const mobileNavigation = useMobileNavigation({ rootFolder, documents });

  const context = {
    documents,
    isReadOnly,
    downloadURL,
    handleFilesDrop,
    handleFileOrFolderMove,
    refetch,
    mobileNavigation,
  };

  if (printDocuments)
    return <PrintDocuments documents={documents} rootFolder={rootFolder} title={name} />;

  return (
    <DocumentsTableContext.Provider value={context}>
      <DocumentsTableContainer>
        <ProgressBars progress={progress} />
        <TopControls rootFolder={rootFolder} printPath={printPath} />
        <StyledDocumentsTable>
          {!rootFolder.isEmpty() && (
            <>
              {isMobile && (
                <MobileContentContainer
                  currentlyVisibleRows={mobileNavigation.currentlyVisibleRows}
                >
                  <FolderWithContents.Mobile
                    index={0}
                    folder={rootFolder}
                    isParentCurrentlySelected={false}
                  />
                </MobileContentContainer>
              )}
              {isDesktop && (
                <>
                  <Headers />
                  <FolderWithContents.Desktop folder={rootFolder} nestingLevel={0} />
                </>
              )}
            </>
          )}
          {rootFolder.isEmpty() && <NoResultsMessage rootFolder={rootFolder} />}
        </StyledDocumentsTable>
      </DocumentsTableContainer>
    </DocumentsTableContext.Provider>
  );
};

export default DocumentsTable;
