import gql from 'graphql-tag';

export const queryAdminBidRequests = gql`
  query AdminBidRequests(
    $query: String
    $offset: Int!
    $limit: Int!
    $sortBy: String
    $sortDirection: SortDirection
  ) {
    adminBidRequests(
      query: $query
      offset: $offset
      limit: $limit
      sortBy: $sortBy
      sortDirection: $sortDirection
    ) {
      bidRequests {
        id
        status
        bidPackage {
          id
          name
          project {
            id
            name
          }
        }
        subcontractor {
          id
          name
        }
      }
      totalRecords
    }
  }
`;
