import React, { useEffect, useState } from 'react';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import pubsub from 'sweet-pubsub';

import { uniqueId } from 'shared/utils/javascript';
import { Icon } from 'shared/components';
import { COLOR } from 'shared/utils/styles';

import { Container, StyledToast, Title, Message } from './Styles';

const TRANSITION_TIME = 200;

interface Loaf {
  id: string;
  type: keyof typeof COLOR;
  duration?: number;
  title: string;
  message: string;
}

const Toast: React.FC<{ transitionTime?: number }> = ({ transitionTime = TRANSITION_TIME }) => {
  const [toasts, setToasts] = useState<Loaf[]>([]);

  const addToast = ({ type = 'success', title, message, duration = 5 }: Loaf) => {
    const id = uniqueId('toast-');

    setToasts(currentToasts => [...currentToasts, { id, type, title, message }]);

    if (duration) {
      setTimeout(() => removeToast(id), duration * 1000);
    }
  };

  const removeToast = (id: string) => {
    setToasts(currentToasts => currentToasts.filter(toast => toast.id !== id));
  };

  useEffect(() => {
    pubsub.on('toast', addToast);

    return () => pubsub.off('toast', addToast);
  });

  return (
    <Container>
      <TransitionGroup>
        {toasts.map(toast => (
          <CSSTransition key={toast.id} classNames="cosuno-toast" timeout={transitionTime}>
            <StyledToast
              data-testid="toast"
              data-cy-toast
              key={toast.id}
              type={toast.type}
              onClick={() => removeToast(toast.id)}
            >
              <Icon type="close" />
              {toast.title && <Title>{toast.title}</Title>}
              {toast.message && <Message>{toast.message}</Message>}
            </StyledToast>
          </CSSTransition>
        ))}
      </TransitionGroup>
    </Container>
  );
};

export default Toast;
