import i18n from 'i18next';
import moment from 'moment';

import { AnyDate } from 'shared/utils/dateTime';

import { Values, Validator } from './types';

const isNilOrEmptyString = (value: unknown): boolean =>
  value === undefined || value === null || value === '';

const match = (testFn: (value: unknown, values: object) => boolean, message = ''): Validator => (
  value,
  fieldValues,
) => !testFn(value, fieldValues) && message;

const notAfter = <V = Values>(
  getTargetDate: (values: V) => AnyDate | null,
  message: string,
): Validator<AnyDate | null, V> => {
  return (value, values) => {
    const targetDate = getTargetDate(values);

    if (!value || !targetDate || moment(value).isSameOrBefore(targetDate, 'day')) {
      return false;
    }

    return message;
  };
};

const notBefore = <V = Values>(
  getTargetDate: (values: V) => AnyDate | null,
  message: string,
): Validator<AnyDate | null, V> => {
  return (value, values) => {
    const targetDate = getTargetDate(values);

    if (!value || !targetDate || moment(value).isSameOrAfter(targetDate, 'day')) {
      return false;
    }

    return message;
  };
};

const required = (): Validator => value => {
  if (Array.isArray(value) && value.length === 0) {
    return i18n.t<string>('validation:required');
  }

  if (isNilOrEmptyString(value)) {
    return i18n.t<string>('validation:required');
  }

  return false;
};

const minLength = (min: number): Validator<string | unknown[]> => value =>
  !!value && value.length < min && i18n.t<string>('validation:minLength', { min });

const maxLength = (max: number): Validator<string | unknown[]> => value =>
  !!value && value.length > max && i18n.t<string>('validation:maxLength', { max });

const min = (minValue: number): Validator<string> => value =>
  !isNilOrEmptyString(value) &&
  parseFloat(value) < minValue &&
  i18n.t<string>('validation:min', { minValue });

const notEmptyArray = (): Validator<unknown[]> => value =>
  (!Array.isArray(value) || value.length === 0) && i18n.t<string>('validation:notEmptyArray');

const email = (): Validator<string> => value =>
  !!value && !/.+@.+\..+/.test(value) && i18n.t<string>('validation:email');

const url = (): Validator<string> => value =>
  !!value &&
  // eslint-disable-next-line no-useless-escape
  !/^(?:http(s)?:\/\/)?[\w.-]+(?:\.[\w\.-]+)+[\w\-\._~:/?#[\]@!\$&'\(\)\*\+,;=.]+$/.test(value) &&
  i18n.t<string>('validation:url');

export const is = {
  match,
  required,
  minLength,
  maxLength,
  min,
  notEmptyArray,
  email,
  url,
  notAfter,
  notBefore,
};
