import React from 'react';
import { useTranslation } from 'react-i18next';

import { PROJECT_STATUS, BID_REQUEST_STATUS, BID_PACKAGE_STATUS } from '__graphql__/globalTypes';
import { BidFormModal, CurrencyFormatter, ClickableText } from 'shared/components';

import { SubmitBidButton, CurrentPrice, Header } from './CurrentBid.styles';
import { BidRequestForSubcontractor_bidRequestForSubcontractor } from './__graphql__/BidRequestForSubcontractor';

interface Props {
  bidRequest: BidRequestForSubcontractor_bidRequestForSubcontractor;
}

function hasCurrentBid(
  bidRequest: BidRequestForSubcontractor_bidRequestForSubcontractor,
): bidRequest is BidRequestForSubcontractor_bidRequestForSubcontractor & {
  currentBid: NonNullable<BidRequestForSubcontractor_bidRequestForSubcontractor['currentBid']>;
} {
  return !!bidRequest.currentBid;
}

function getCurrentBidTotalAmount(
  bidRequest: BidRequestForSubcontractor_bidRequestForSubcontractor,
) {
  return bidRequest.currentBid?.totalAmount;
}

const CurrentBid: React.FC<Props> = ({ bidRequest }) => {
  const { t } = useTranslation('bidInvites');

  const isProjectClosed = bidRequest.bidPackage.project.status === PROJECT_STATUS.closed;
  const isBidAwarded = bidRequest.bidPackage.status === BID_PACKAGE_STATUS.bidAwarded;
  const isBidSubmittedToBidRequest = bidRequest.status === BID_REQUEST_STATUS.bidSubmitted;
  const canSubmitBid = !isProjectClosed && !isBidAwarded;
  const totalAmount = getCurrentBidTotalAmount(bidRequest);

  if (isBidSubmittedToBidRequest && hasCurrentBid(bidRequest) && totalAmount) {
    return (
      <div>
        <Header>{t('overview.bidAmount')}</Header>
        <CurrentPrice>
          <CurrencyFormatter value={totalAmount} />
        </CurrentPrice>
        {canSubmitBid && (
          <BidFormModal
            bidRequest={bidRequest}
            isUserSubcontractor
            refetchQueries={() => ['BidRequestForSubcontractor']}
            renderLink={modal => (
              <ClickableText onClick={modal.open} data-cy-button-name="reviseBid">
                {t('overview.reviseBid')}
              </ClickableText>
            )}
          />
        )}
      </div>
    );
  }

  if (canSubmitBid) {
    return (
      <BidFormModal
        bidRequest={bidRequest}
        isUserSubcontractor
        refetchQueries={() => ['BidRequestForSubcontractor']}
        renderLink={modal => (
          <SubmitBidButton icon="edit" onClick={modal.open} data-cy-button-name="submitBid">
            {t('overview.submitBid')}
          </SubmitBidButton>
        )}
      />
    );
  }

  return null;
};

export default CurrentBid;
