import React, { useContext } from 'react';
import Dropzone from 'react-dropzone';

import { omit } from 'shared/utils/javascript';

import DocumentsTableContext from './DocumentsTableContext';
import DragHighlightZone from './DragHighlightZone';
import { DropArea } from './Styles';
import Folder from './classes/Folder';

interface Props {
  targetFolder: Folder;
  children: React.ReactNode;
}

const DropzoneArea: React.FC<Props> = ({ targetFolder, children }) => {
  const { handleFilesDrop, handleFileOrFolderMove } = useContext(DocumentsTableContext);

  return (
    <Dropzone onDrop={files => handleFilesDrop(files, targetFolder)}>
      {({ getRootProps, getInputProps }) => (
        <DropArea
          {...omit(getRootProps(), ['onDragEnter', 'onDragLeave'])} // avoid performance issues
          data-cy-droparea-root={targetFolder.isRoot()}
          data-cy-droparea-folder-name={targetFolder.name}
          onClick={event => event.stopPropagation()}
        >
          <input {...getInputProps()} />
          <DragHighlightZone onDrop={event => handleFileOrFolderMove(event, targetFolder)}>
            {children}
          </DragHighlightZone>
        </DropArea>
      )}
    </Dropzone>
  );
};

export default DropzoneArea;
