import gql from 'graphql-tag';

import { CurrentUser as CurrentUserQueryType } from './__graphql__/CurrentUser';

export const queryCurrentUser = gql`
  query CurrentUser {
    me {
      id
      email
      firstName
      lastName
      jobTitle
      phone
      fax
      isAdmin
    }
  }
`;
export type CurrentUserQuery = CurrentUserQueryType;

export const queryWorkCategories = gql`
  query WorkCategories {
    workCategories {
      id
      name
    }
  }
`;

export const mutationUpdateBidRequestStatusBySubcontractor = gql`
  mutation UpdateBidRequestStatusBySubcontractor($id: ID!, $status: String!) {
    updateBidRequestStatusBySubcontractor(id: $id, status: $status)
  }
`;

export const mutationUpdateBidRequestDeclinedReasonBySubcontractor = gql`
  mutation mutationUpdateBidRequestDeclinedReason($id: ID!, $declinedReason: String!) {
    updateBidRequestDeclinedReasonBySubcontractor(id: $id, declinedReason: $declinedReason)
  }
`;
