import { useEffect, RefObject } from 'react';

import Console from 'shared/utils/console';

type UseClickOutsideOptions =
  | {
      ref: RefObject<HTMLElement>;
      refs?: undefined;
      onClickOutside: () => void;
    }
  | { ref?: undefined; refs: RefObject<HTMLElement>[]; onClickOutside: () => void };

const useClickOutside = (options: UseClickOutsideOptions): void => {
  useEffect(() => {
    const refs = options.refs || [options.ref];
    const { onClickOutside } = options;
    const handleClick = (event: MouseEvent) => {
      if (refs.length === 0) {
        Console.error('`useClickOutside` has to receive a ref with a node as value');
        return;
      }

      if (
        !refs.some(
          ref =>
            event.target instanceof HTMLElement &&
            ref.current &&
            ref.current.contains(event.target),
        )
      ) {
        onClickOutside();
      }
    };

    document.addEventListener('click', handleClick);

    return () => document.removeEventListener('click', handleClick);

    // We don't want the effect to run whenever options changes identity
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [options.refs, options.ref, options.onClickOutside]);
};

export default useClickOutside;
