import React, { useContext } from 'react';
import { useTranslation } from 'react-i18next';

import { getImageUrl, IMAGE_TYPE } from 'shared/utils/images';
import { formatDate, formatDateTime } from 'shared/utils/dateTime';
import QueryMatchesContext from 'shared/contexts/queryMatchesContext';

import { Contacts } from './Contacts';
import CustomerInfo from './CustomerInfo';
import {
  OverviewWrapper,
  Left,
  Right,
  Image,
  InfoItem,
  InfoLabel,
  InfoValue,
  Description,
  StyledSectionTitle,
  CustomerLogo,
} from './Overview.styles';
import {
  BidRequestForSubcontractor_bidRequestForSubcontractor_bidPackage,
  BidRequestForSubcontractor_bidRequestForSubcontractor_bidPackage_project_projectUsers,
} from './__graphql__/BidRequestForSubcontractor';

interface Props {
  bidPackage: BidRequestForSubcontractor_bidRequestForSubcontractor_bidPackage;
}

type ProjectUser = BidRequestForSubcontractor_bidRequestForSubcontractor_bidPackage_project_projectUsers;

const Overview: React.FC<Props> = ({ bidPackage }) => {
  const { primaryUser, projectUsers, ...project } = bidPackage.project;

  const primaryUserWithRole = (projectUsers || []).find(user => user?.id === primaryUser.id);
  const secondaryUsers = (projectUsers || []).filter(user => user?.id !== primaryUser.id);

  const projectContacts = [
    primaryUserWithRole,
    ...secondaryUsers,
  ].filter((user): user is ProjectUser => Boolean(user));

  const { startsOn, endsOn } = startsAndEndsOn(bidPackage, project);
  const { isMobile, isDesktop } = useContext(QueryMatchesContext);
  const { t } = useTranslation('bidInvites');

  const customerSection = (
    <>
      <StyledSectionTitle>{t('overview.customer')}</StyledSectionTitle>
      {project.office.logoKey && (
        <CustomerLogo
          src={getImageUrl(project.office.logoKey, IMAGE_TYPE.logo)}
          alt={project.office.name}
        />
      )}
      <CustomerInfo {...project.office} />
      <StyledSectionTitle>{t('overview.contactPersons')}</StyledSectionTitle>
      <Contacts contacts={projectContacts} />
      {project.imageKey && (
        <Image src={getImageUrl(project.imageKey, IMAGE_TYPE.project)} alt={project.name} />
      )}
    </>
  );

  return (
    <OverviewWrapper>
      <Left>
        {isMobile && customerSection}
        <StyledSectionTitle>{t('overview.importantDates')}</StyledSectionTitle>
        <InfoItem>
          <InfoLabel>{t('overview.bidsDue')}</InfoLabel>
          <InfoValue>{formatDateTime(bidPackage.bidsDueAt)}</InfoValue>
        </InfoItem>
        {startsOn && (
          <InfoItem>
            <InfoLabel>{t('overview.expectedStart')}</InfoLabel>
            <InfoValue>{startsOn}</InfoValue>
          </InfoItem>
        )}
        {endsOn && (
          <InfoItem>
            <InfoLabel>{t('overview.expectedFinish')}</InfoLabel>
            <InfoValue>{endsOn}</InfoValue>
          </InfoItem>
        )}
        <StyledSectionTitle>{t('overview.projectDetails')}</StyledSectionTitle>
        <InfoItem>
          <InfoLabel>{t('overview.siteLocation')}</InfoLabel>
          <InfoValue>{project.location}</InfoValue>
        </InfoItem>
        {project.customer && (
          <InfoItem>
            <InfoLabel>{t('overview.owner')}</InfoLabel>
            <InfoValue>{project.customer}</InfoValue>
          </InfoItem>
        )}
        <StyledSectionTitle>{t('overview.projectDescription')}</StyledSectionTitle>
        <Description>{project.description}</Description>
        <StyledSectionTitle>{t('overview.bidPackageDescription')}</StyledSectionTitle>
        <Description>{bidPackage.description}</Description>
      </Left>
      {isDesktop && <Right>{customerSection}</Right>}
    </OverviewWrapper>
  );
};

const startsAndEndsOn = (
  bidPackage: { startsOn: string | null; endsOn: string | null },
  project: { startsOn: string | null; endsOn: string | null },
) => {
  if (bidPackage.startsOn || bidPackage.endsOn) {
    return {
      startsOn: formatDate(bidPackage.startsOn || ''),
      endsOn: formatDate(bidPackage.endsOn || ''),
    };
  }
  if (project.startsOn || project.endsOn) {
    return {
      startsOn: formatDate(project.startsOn || ''),
      endsOn: formatDate(project.endsOn || ''),
    };
  }
  return { startsOn: null, endsOn: null };
};

export default Overview;
