import React from 'react';
import { Router, Switch, Redirect, Route } from 'react-router-dom';

import history from 'browserHistory';
import { PageLoader } from 'shared/components';
import routes, { PROJECTS_ROUTE_TYPE } from 'shared/constants/routes';
import Login from 'components/Auth/Login';
import BidInviteFeedback from 'components/Auth/BidInviteFeedback';
import Signup from 'components/Auth/Signup';
import ResetPassword from 'components/Auth/ResetPassword';
import CreatePassword from 'components/Auth/CreatePassword';
import Invitation from 'components/Auth/Invitation';
import Projects from 'components/Projects';
import Project from 'components/Project';
import BidPackage from 'components/BidPackage';
import Subcontractors from 'components/Subcontractors';
import Bids from 'components/Bids';
import { PrintBidders } from 'components/BidPackage/Bidders/Print';
import BidInvite from 'components/BidInvite';
import PrintProjectDocuments from 'components/Project/PrintDocuments';
import PrintBidPackageDocuments from 'components/BidPackage/PrintDocuments';
import Admin from 'components/Admin';
import Profile from 'components/Profile';
import Company from 'components/Company';
import BidRequests from 'components/BidRequests';
import BidRequest from 'components/BidRequest';
import useUserAuthStatus from 'shared/hooks/useUserAuthStatus';
import PageNotFound from 'components/PageNotFound';

import {
  PrincipalRoute,
  AdminRoute,
  UserRoute,
  AgentRoute,
  AnonymousRoute,
  EmployeeRoute,
} from './AuthRoute';

const Routes: React.FC = () => {
  const {
    isUserAdmin,
    isUserAuthenticating,
    isUserAuthenticated,
    isUserAnonymous,
    isUserAgent,
    userBaseUrl,
  } = useUserAuthStatus();

  if (isUserAuthenticating) {
    return <PageLoader />;
  }

  return (
    <Router history={history}>
      <Switch>
        {/* Routes for all users */}
        <UserRoute withLayout path={routes.profile()} component={Profile} />
        <EmployeeRoute withLayout path={routes.company()} component={Company} />

        {/* Routes for admin users */}
        <AdminRoute withLayout path={routes.admin()} component={Admin} />
        {isUserAdmin && <AdminRoute withLayout path={routes.bidRequest()} component={BidRequest} />}

        {/* Routes for principal users */}
        <Redirect
          exact
          from={routes.projectsRoot()}
          to={routes.projects(PROJECTS_ROUTE_TYPE.open)}
        />
        <PrincipalRoute withLayout path={routes.projects()} component={Projects} />
        <PrincipalRoute withLayout path={routes.createBidPackage()} component={BidPackage} />
        <PrincipalRoute withLayout path={routes.showBidPackage()} component={BidPackage} />
        <Redirect from={routes.showBidPackageLegacy()} to={routes.showBidPackage()} />
        <PrincipalRoute withLayout path={routes.createProject()} component={Project} />
        <PrincipalRoute withLayout path={routes.showProject()} component={Project} />
        <PrincipalRoute withLayout path={routes.subcontractors()} component={Subcontractors} />
        <PrincipalRoute withLayout path={routes.bids()} component={Bids} />
        <PrincipalRoute path={routes.printBidders()} component={PrintBidders} />
        <PrincipalRoute path={routes.printProjectDocuments()} component={PrintProjectDocuments} />
        <PrincipalRoute
          path={routes.printBidPackageDocuments()}
          component={PrintBidPackageDocuments}
        />

        {/* Routes for agent users */}
        {isUserAgent && (
          <AgentRoute path={routes.bidInviteAction()} component={BidInviteFeedback} />
        )}
        {isUserAgent && <AgentRoute withLayout path={routes.bidInvite()} component={BidInvite} />}
        {isUserAgent && <AgentRoute withLayout path={routes.bidRequest()} component={BidRequest} />}
        <AgentRoute exact withLayout path={routes.bidRequests()} component={BidRequests} />

        {/* Unauthenticated routes */}
        <AnonymousRoute path={routes.invitation()} component={Invitation} />
        <AnonymousRoute path={routes.login()} component={Login} />
        <AnonymousRoute path={routes.signup()} component={Signup} />
        <AnonymousRoute path={routes.resetPassword()} component={ResetPassword} />
        <AnonymousRoute path={routes.createPassword()} component={CreatePassword} />
        {isUserAnonymous && (
          <AnonymousRoute path={routes.bidInviteAction()} component={BidInviteFeedback} />
        )}
        {isUserAnonymous && <AnonymousRoute path={routes.bidInvite()} component={BidInvite} />}
        <AnonymousRoute path={routes.bidRequest()} component={BidRequest} />

        {/* Shared redirects */}
        <Redirect exact from={routes.root()} to={userBaseUrl} />
        <Redirect from={routes.bidInviteActionLegacy()} to={routes.bidInviteAction()} />

        {/* PageNotFound handling */}
        {isUserAuthenticated ? (
          <UserRoute withLayout component={PageNotFound.Authenticated} />
        ) : (
          <Route component={PageNotFound.Unauthenticated} />
        )}
      </Switch>
    </Router>
  );
};

export default Routes;
