import { CurrentUserForAuthentication } from 'shared/hooks/__graphql__/CurrentUserForAuthentication';

export interface Analytics {
  page({ title }: { title: string }): void;
  reset(): void;
  load(key?: string): void;
  identify(
    id: CurrentUserForAuthentication['id'],
    user: Pick<CurrentUserForAuthentication, 'firstName' | 'lastName' | 'email'> & {
      company: { id: string; name: string };
    },
    arg2: {
      integrations: {
        Intercom: {
          user_hash: string;
        };
      };
    },
  ): void;
}

declare let window: Window & { analytics?: Analytics };
export type WindowWithAnalytics = typeof window;

const trackPageview = () => {
  if (!window.analytics) return;

  // Segment.com tracking
  // The title property needs to be overwritten because otherwise the page title
  // is used automatically which is not correct because by the time the route changes
  // the page title has not been updated yet. It is better to have no title than to have
  // an incorrect title.
  window.analytics.page({ title: '' });
};

const trackUserIdentity = (user: CurrentUserForAuthentication) => {
  if (!window.analytics) return;

  const { firstName, lastName, email, company, intercomHash } = user;
  window.analytics.identify(
    user.id,
    {
      firstName,
      lastName,
      email,
      company: {
        id: company.id,
        name: company.name,
      },
    },
    {
      integrations: {
        Intercom: {
          user_hash: intercomHash,
        },
      },
    },
  );
};

const resetUserIdentity = () => {
  if (!window.analytics) return;

  window.analytics.reset();
};

const load = (segmentKey?: string) => {
  if (!window.analytics) return;

  window.analytics.load(segmentKey);
};

export { load, trackPageview, trackUserIdentity, resetUserIdentity };
