import React from 'react';
import { useTranslation } from 'react-i18next';
import { useMutation } from '@apollo/react-hooks';

import { Icon, UsersInviteModal, ConfirmationModal, DropdownMenu, Modal } from 'shared/components';
import Clickable from 'shared/components/Clickable';
import { COMPANY_STATUS } from '__graphql__/globalTypes';
import { DropdownMenuWrapper } from 'shared/components/Styles';
import toast from 'shared/utils/toast';
import { MODAL_VARIANTS } from 'shared/constants';

import EditForm from './EditForm';
import { mutationAdminInviteUsers, mutationAdminActivateCompany } from './api';
import { AdminCompanies_adminCompanies_companies } from './__graphql__/AdminCompanies';
import { AdminInviteUsers, AdminInviteUsersVariables } from './__graphql__/AdminInviteUsers';

interface Props {
  company: AdminCompanies_adminCompanies_companies;
}

const CompaniesDropdown: React.FC<Props> = ({ company }) => {
  const { t } = useTranslation('admin');

  const [adminInviteUsers, { loading: inviteMutating }] = useMutation<
    AdminInviteUsers,
    AdminInviteUsersVariables
  >(mutationAdminInviteUsers, {
    refetchQueries: () => ['Users', 'AdminUsers'],
  });

  const [adminActivateCompany, { loading: activateMutating }] = useMutation(
    mutationAdminActivateCompany,
    {
      variables: { id: company.id },
      refetchQueries: () => ['AdminCompanies'],
    },
  );

  return (
    <DropdownMenuWrapper>
      <DropdownMenu.Container
        renderTrigger={({ isTriggered, toggleTrigger }) => (
          <Clickable onClick={toggleTrigger}>
            <Icon type={isTriggered ? 'chevron-up' : 'chevron-down'} size={18} />
          </Clickable>
        )}
      >
        <Modal
          variant={MODAL_VARIANTS.aside}
          renderLink={modal => (
            <DropdownMenu.Action onClick={modal.open}>{t('edit')}</DropdownMenu.Action>
          )}
          renderContent={modal => <EditForm company={company} closeModal={modal.close} />}
        />
        {company.status === COMPANY_STATUS.pendingApproval && (
          <ConfirmationModal
            title={t('activateCompanyTitle')}
            message={t('activateCompanyMessage')}
            isWorking={activateMutating}
            renderLink={modal => (
              <DropdownMenu.Action onClick={modal.open}>{t('activateCompany')}</DropdownMenu.Action>
            )}
            onConfirm={async () => {
              try {
                await adminActivateCompany();
                toast.success(t('activateCompanySuccess'));
              } catch (error) {
                toast.error(error);
              }
            }}
          />
        )}
        {company.status === COMPANY_STATUS.active && (
          <UsersInviteModal
            loading={inviteMutating}
            renderLink={modal => (
              <DropdownMenu.Action onClick={modal.open}>{t('inviteUsers')}</DropdownMenu.Action>
            )}
            onSubmit={({ users }, confirm) => {
              adminInviteUsers({ variables: { users, companyId: company.id } }).then(() => {
                toast.success(t('company:invitationsSent'));
                confirm();
              }, toast.error);
            }}
          />
        )}
      </DropdownMenu.Container>
    </DropdownMenuWrapper>
  );
};

export default CompaniesDropdown;
