import React from 'react';

import Topbar from 'shared/components/Topbar';
import { ErrorBoundary } from 'shared/components/ErrorBoundary';
import Icon from 'shared/components/Icon';
import { BackLink } from 'shared/components/Styles';

import { PageContainer, PageContent } from './Styles';

export interface PageProps {
  withTopbar?: boolean;
  children: React.ReactNode;
  style?: object;
  isResponsive?: boolean;
  backLink?: {
    to: string;
    label: string;
  };
}

const Page: React.FC<PageProps> = ({
  children,
  withTopbar = true,
  style = {},
  isResponsive = false,
  backLink,
  ...props
}) => (
  <PageContainer style={style} isResponsive={isResponsive} {...props}>
    <ErrorBoundary>
      {withTopbar && <Topbar />}
      <PageContent withBackLink={Boolean(backLink)}>
        {backLink && (
          <BackLink data-cy-back-link to={backLink.to}>
            <Icon type="arrow-left" />
            <span>{backLink.label}</span>
          </BackLink>
        )}
        {children}
      </PageContent>
    </ErrorBoundary>
  </PageContainer>
);

export { PageContainer, PageContent };
export default Page;
