import React, { useEffect, useState, useRef } from 'react';
import moment, { Moment } from 'moment';

import { formatDateTimeForAPI, formatDateForAPI } from 'shared/utils/dateTime';
import useClickOutside from 'shared/hooks/useClickOutside';
import { KEY_CODE_VALUES } from 'shared/constants';

import { DATE_FORMAT_WITH_TIME, DATE_FORMAT_WITHOUT_TIME } from './constants';

const round = (min: number, max: number, value: number) => {
  return Math.round((value - min) / (max - min)) * (max - min) + min;
};

const getSelectedDate = (value: string | null) => {
  const date = moment(value || undefined);
  const minutes = date.minute();

  if (minutes < 30) {
    date.set('minute', round(0, 30, minutes));
  } else if (minutes > 30) {
    date.set('minute', round(30, 60, minutes));
  }

  date.set({ second: 0, millisecond: 0 });

  return date;
};

const EDITING_STATE = {
  empty: 'empty',
  editingManually: 'editingManually',
  selectedDate: 'selectedDate',
};

export interface DatePickerHookOptions {
  value: string | null;
  onChange: (nextDateOrDateTime: string | null) => void;
  withTime: boolean;
}

interface DatePickerAPI {
  displayedDate: Moment;
  inputValue: string;
  isOpen: boolean;
  selectedDate: Moment;

  openDropdown: () => void;
  changeYear: (year: number | string) => void;
  goToPrevMonth: () => void;
  goToNextMonth: () => void;
  changeDay: (day: number | string) => void;
  changeTime: (time: string) => void;
  handleManualInput: (manualInputValue: string) => void;

  datePickerRef: React.RefObject<HTMLDivElement>;
  cardRef: React.RefObject<HTMLDivElement>;
  inputRef: React.RefObject<HTMLInputElement>;
}

const useDatePicker = ({ value, onChange, withTime }: DatePickerHookOptions): DatePickerAPI => {
  const datePickerRef = useRef<HTMLDivElement>(null);
  const inputRef = useRef<HTMLInputElement>(null);
  const cardRef = useRef<HTMLDivElement>(null);
  const clickOutsideRefs = useRef([inputRef, cardRef]);
  const [displayedDate, setDisplayedDate] = useState(getSelectedDate(value));
  const [input, setInput] = useState('');
  const [isOpen, setIsOpen] = useState(false);
  const [editingState, setEditingState] = useState(
    value ? EDITING_STATE.selectedDate : EDITING_STATE.empty,
  );
  const selectedDate = getSelectedDate(value);

  useEffect(() => {
    setDisplayedDate(getSelectedDate(value));
  }, [value]);

  const openDropdown = () => setIsOpen(true);
  const closeDropdown = () => setIsOpen(false);

  const changeYear = (year: number | string) => {
    setDisplayedDate(moment(displayedDate).set('year', Number(year)));
  };
  const goToPrevMonth = () => setDisplayedDate(moment(displayedDate).subtract(1, 'month'));
  const goToNextMonth = () => setDisplayedDate(moment(displayedDate).add(1, 'month'));
  const changeDay = (day: number | string) => {
    const date = moment(selectedDate).set({
      date: Number(day),
      month: displayedDate.get('month'),
      year: displayedDate.get('year'),
    });

    setEditingState(EDITING_STATE.selectedDate);
    onChange(withTime ? formatDateTimeForAPI(date) : formatDateForAPI(date));

    if (!withTime) {
      closeDropdown();
    }
  };
  const changeTime = (time: string) => {
    const [hour, minute] = time.split(':').map(Number);
    const date = moment(selectedDate).set({ hour, minute });

    setEditingState(EDITING_STATE.selectedDate);
    onChange(formatDateTimeForAPI(date));
    closeDropdown();
  };

  const dateFormat = withTime ? DATE_FORMAT_WITH_TIME : DATE_FORMAT_WITHOUT_TIME;
  const handleManualInput = (manualInputValue: string) => {
    setEditingState(EDITING_STATE.editingManually);
    setInput(manualInputValue);
  };
  const handleClickOutside = () => {
    closeDropdown();

    if (editingState !== EDITING_STATE.editingManually) {
      return;
    }

    if (!input) {
      onChange(null);
      return;
    }

    const newSelectedDate = moment(input, dateFormat);

    setEditingState(EDITING_STATE.selectedDate);

    if (newSelectedDate.isValid()) {
      onChange(
        withTime ? formatDateTimeForAPI(newSelectedDate) : formatDateForAPI(newSelectedDate),
      );
    }
  };
  const inputValue = {
    [EDITING_STATE.empty]: '',
    [EDITING_STATE.editingManually]: input,
    [EDITING_STATE.selectedDate]: selectedDate.format(dateFormat),
  }[editingState];

  const handleKeyboardExit = (event: KeyboardEvent) => {
    if (event.keyCode === KEY_CODE_VALUES.tab || event.keyCode === KEY_CODE_VALUES.enter) {
      handleClickOutside();
    }
  };

  useEffect(() => {
    // Speaking of ref vs. state here, see the following discussion:
    // https://github.com/cosuno/cosuno/pull/683#discussion_r346377552
    const node = datePickerRef.current;

    if (!node) return;

    node.addEventListener('keydown', handleKeyboardExit);

    // eslint-disable-next-line consistent-return
    return () => node.removeEventListener('keydown', handleKeyboardExit);
  });

  useClickOutside({ refs: clickOutsideRefs.current, onClickOutside: handleClickOutside });

  return {
    displayedDate,
    inputValue,
    isOpen,
    selectedDate,

    openDropdown,
    changeYear,
    goToPrevMonth,
    goToNextMonth,
    changeDay,
    changeTime,
    handleManualInput,

    datePickerRef,
    cardRef,
    inputRef,
  };
};

export default useDatePicker;
