import React from 'react';
import { useTranslation } from 'react-i18next';
import { useMutation } from '@apollo/react-hooks';

import ConfirmationModal from 'shared/components/ConfirmationModal';
import DropdownMenu from 'shared/components/DropdownMenu';
import toast from 'shared/utils/toast';

import { mutationResendUserInvite } from './api';

interface Props {
  userId: string;
}

const ResendInvite: React.FC<Props> = ({ userId }) => {
  const { t } = useTranslation('company');
  const [resendInvite, { loading }] = useMutation(mutationResendUserInvite, {
    refetchQueries: () => ['Users', 'AdminUsers'],
  });

  return (
    <ConfirmationModal
      title="company:resendInviteModalTitle"
      message={null}
      isWorking={loading}
      renderLink={modal => (
        <DropdownMenu.Action onClick={modal.open}>{t('resendInvite')}</DropdownMenu.Action>
      )}
      onConfirm={async modal => {
        try {
          await resendInvite({ variables: { id: userId } });
          toast.success(t('inviteSent'));
          modal.close();
        } catch (error) {
          toast.error(error);
        }
      }}
    />
  );
};

export default ResendInvite;
