import React from 'react';

import { MODAL_VARIANTS } from 'shared/constants';
import { ModalContentProps } from 'shared/components/Modal/ModalContent';
import { SubcontractorInfoProps } from 'shared/components/SubcontractorInfo';

import BidFormModalForm from './Form';
import { StyledBidFormModal } from './Styles';
import { BidFormModal_bidRequest } from './__graphql__/BidFormModal_bidRequest';

interface Props {
  bidRequest: BidFormModal_bidRequest;
  subcontractor?: SubcontractorInfoProps['subcontractor'];
  isUserSubcontractor: boolean;
  refetchQueries: () => string[];
  renderLink: ModalContentProps['renderLink'];
}

const BidFormModal: React.FC<Props> = ({
  bidRequest,
  subcontractor,
  isUserSubcontractor,
  refetchQueries,
  renderLink,
}) => {
  return (
    <StyledBidFormModal
      variant={MODAL_VARIANTS.aside}
      renderLink={renderLink}
      renderContent={modal => (
        <BidFormModalForm
          subcontractor={subcontractor}
          bidRequest={bidRequest}
          isUserSubcontractor={isUserSubcontractor}
          refetchQueries={refetchQueries}
          closeModal={modal.close}
        />
      )}
    />
  );
};

export { fragmentBidFormModal } from './api';
export default BidFormModal;
