import React, { useContext } from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation, Trans } from 'react-i18next';
import { Link } from 'react-router-dom';

import { Form, Field, ExternalLink } from 'shared/components';
import { Heading, Message, SubmitButton } from 'shared/components/UnauthenticatedStyles';
import Console from 'shared/utils/console';
import AuthContext from 'shared/contexts/authContext';
import UnauthenticatedPage from 'shared/components/UnauthenticatedPage';
import { ContentBox, ContentBoxSmall } from 'shared/components/UnauthenticatedPage/Styles';
import routes from 'shared/constants/routes';
import { COLOR } from 'shared/utils/styles';

import {
  BidRequestHeading,
  BidRequestOffice,
  TopLink,
  FormElementRow,
  FormElementRowItem,
} from './Signup.styles';
import { mutationAgentSignup } from './api';
import { AgentSignup, AgentSignupVariables } from './__graphql__/AgentSignup';
import { BidInvite_bidInvite } from './__graphql__/BidInvite';

interface Props {
  bidInvite: BidInvite_bidInvite;
}

interface FormValues {
  companyName: string;
  firstName: string;
  lastName: string;
  email: string;
  password: string;
  termsAccepted: boolean;
}

const BidInviteSignup: React.FC<Props> = ({ bidInvite }) => {
  const { t } = useTranslation('auth');
  const { login } = useContext(AuthContext);

  const [agentSignup, { error: mutationError, loading: mutating }] = useMutation<
    AgentSignup,
    AgentSignupVariables
  >(mutationAgentSignup);

  return (
    <UnauthenticatedPage title="signup">
      <BidRequestHeading>{t('bidRequestHeading')}</BidRequestHeading>
      <BidRequestOffice>{bidInvite.bidRequest.bidPackage.project.office.name}</BidRequestOffice>
      <ContentBoxSmall>
        <TopLink>
          {t('alreadyHaveAnAccount')} <Link to={routes.login()}>{t('goToLogin')}</Link>
        </TopLink>
      </ContentBoxSmall>
      <Form<FormValues>
        initialValues={{
          companyName: '',
          firstName: '',
          lastName: '',
          email: '',
          password: '',
          termsAccepted: false,
        }}
        validations={{
          companyName: [Form.is.required()],
          firstName: [Form.is.required(), Form.is.maxLength(100)],
          lastName: [Form.is.required(), Form.is.maxLength(100)],
          email: [Form.is.required()],
          password: [Form.is.required(), Form.is.minLength(8), Form.is.maxLength(250)],
          termsAccepted: Form.is.match(value => !!value, t('validation:termsMustBeAccepted')),
        }}
        onSubmit={async values => {
          const { termsAccepted, ...submitValues } = values;

          try {
            const { data: signupData } = await agentSignup({
              variables: { input: { bidInviteId: bidInvite.id, ...submitValues } },
            });
            if (signupData) {
              login({
                ...signupData.agentSignup,
                redirectTo: routes.bidInvite(bidInvite.id),
              });
            }
          } catch (signupError) {
            Console.error(signupError);
          }
        }}
      >
        <ContentBox as={Form.Element}>
          <Heading>{t('bidRequestSignupHeading')}</Heading>
          {mutationError && <Message color={COLOR.danger}>{mutationError.message}</Message>}
          <Field.Input required name="companyName" label={t('company.name')} />
          <FormElementRow>
            <FormElementRowItem>
              <Field.Input required name="firstName" label={t('user.firstName')} />
            </FormElementRowItem>
            <FormElementRowItem>
              <Field.Input required name="lastName" label={t('user.lastName')} />
            </FormElementRowItem>
          </FormElementRow>
          <Field.Input required name="email" label={t('email')} type="email" />
          <Field.Input required name="password" label={t('password')} type="password" />
          <Field.Checkbox
            name="termsAccepted"
            checkboxLabel={
              <Trans i18nKey="termsAccepted" t={t}>
                I accept the{' '}
                <ExternalLink to="https://www.cosuno.de/terms">terms and conditions</ExternalLink>{' '}
                and the{' '}
                <ExternalLink to="https://www.cosuno.de/privacy">privacy policy</ExternalLink>.
              </Trans>
            }
          />
          <Form.RequiredFieldsNote />
          <SubmitButton type="submit" working={mutating}>
            {t('signupSubmit')}
          </SubmitButton>
        </ContentBox>
      </Form>
    </UnauthenticatedPage>
  );
};

export default BidInviteSignup;
