import gql from 'graphql-tag';

import { fragmentContactInfo } from 'shared/components/ContactInfo';

export const fragmentSubcontractorsListItem = gql`
  fragment SubcontractorsListItem_subcontractorsListItem on Subcontractor {
    id
    name
    postalCode
    city
    workCategories {
      id
      name
    }
    contacts {
      id
      ...ContactInfo_Contact
      isPerson
      email
      firstName
      lastName
      jobTitle
    }
  }
  ${fragmentContactInfo}
`;
