import React from 'react';
import { useTranslation } from 'react-i18next';
import { useMutation } from '@apollo/react-hooks';

import DropdownMenu from 'shared/components/DropdownMenu';
import ConfirmationModal from 'shared/components/ConfirmationModal';
import toast from 'shared/utils/toast';

import { mutationReactivateUser } from './api';

interface Props {
  userId: string;
}

const ReactivateUser: React.FC<Props> = ({ userId }) => {
  const { t } = useTranslation('company');
  const [reactivateUser, { loading }] = useMutation(mutationReactivateUser, {
    refetchQueries: () => ['Users', 'AdminUsers'],
  });

  return (
    <ConfirmationModal
      title="company:confirmReactivateMessage"
      message="company:confirmReactivateDescription"
      isWorking={loading}
      renderLink={modal => (
        <DropdownMenu.Action onClick={modal.open}>{t('reactivate')}</DropdownMenu.Action>
      )}
      onConfirm={async modal => {
        try {
          await reactivateUser({ variables: { id: userId } });
          toast.success(t('userReactivated'));
          modal.close();
        } catch (error) {
          toast.error(error);
        }
      }}
    />
  );
};

export default ReactivateUser;
