import React, { useEffect, useRef, useCallback } from 'react';

import { KEY_CODE_VALUES } from 'shared/constants';
import useGoogleMapsApi from 'shared/hooks/useGoogleMapsApi';
import Input, { InputProps } from 'shared/components/Input';

export interface Place {
  lat?: string;
  long?: string;
  location: string;
}

interface LocationSearchProps {
  onChange: (value: string) => void;
  onPlaceSelect?: (place: Place) => void;
}

const boundsForGermany = {
  east: 15.0169958839,
  north: 54.983104153,
  south: 47.3024876979,
  west: 5.98865807458,
};

const LocationSearch: React.FC<LocationSearchProps &
  Omit<InputProps, keyof LocationSearchProps>> = ({ onChange, onPlaceSelect, ...inputProps }) => {
  const input = useRef<HTMLInputElement>(null);
  const autocomplete = useRef<google.maps.places.Autocomplete>();

  const google = useGoogleMapsApi();

  const updateLocation = useCallback(() => {
    if (!autocomplete.current) {
      return;
    }

    const place = autocomplete.current.getPlace();
    const isPlaceValid = place.formatted_address;

    onChange(place.formatted_address || '');

    if (isPlaceValid && onPlaceSelect) {
      onPlaceSelect({
        lat: place.geometry && String(place.geometry.location.lat()),
        long: place.geometry && String(place.geometry.location.lng()),
        location: place.formatted_address || '',
      });
    }
  }, [onChange, onPlaceSelect]);

  useEffect(() => {
    if (!input.current || !google) {
      return () => {};
    }

    if (!autocomplete.current) {
      autocomplete.current = new google.maps.places.Autocomplete(input.current, {
        types: ['geocode'],
        bounds: boundsForGermany,
      });
    }

    const placeListener = autocomplete.current.addListener('place_changed', updateLocation);

    const inputListener = google.maps.event.addDomListener(input.current, 'keydown', event => {
      if ((event as KeyboardEvent).keyCode === KEY_CODE_VALUES.enter) {
        event.preventDefault();
      }
    });

    return () => {
      placeListener.remove();
      inputListener.remove();
    };
  }, [google, updateLocation]);

  return (
    <Input
      icon="search"
      {...inputProps}
      ref={input}
      onChange={value => {
        onChange(value);
        if (!value && onPlaceSelect) onPlaceSelect({ lat: '', long: '', location: '' });
      }}
    />
  );
};

export default LocationSearch;
