import React from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';

import routes from 'shared/constants/routes';
import { BID_PACKAGE_STATUS } from '__graphql__/globalTypes';
import { DropdownMenuWrapper } from 'shared/components/Styles';
import { Clickable, DropdownMenu, Icon, DeletionModal } from 'shared/components';

import { mutationDeleteBidPackage } from './api';

interface Props {
  projectId: string;
  isProjectClosed: boolean;
  bidPackageId: string;
  bidPackageStatus: BID_PACKAGE_STATUS;
}

const BidPackagesDropdown: React.FC<Props> = ({
  bidPackageId,
  projectId,
  bidPackageStatus,
  isProjectClosed,
}) => {
  const { t } = useTranslation('bidPackages');
  const history = useHistory();
  const { published, bidAwarded } = BID_PACKAGE_STATUS;

  const actions = [];

  if (!isProjectClosed) {
    actions.push(
      <DropdownMenu.Action
        key="edit"
        onClick={() => history.push(routes.editBidPackage(projectId, bidPackageId))}
      >
        {t('editBidPackage')}
      </DropdownMenu.Action>,
    );

    if (![published, bidAwarded].includes(bidPackageStatus)) {
      actions.push(
        <DeletionModal
          key="delete"
          id={bidPackageId}
          mutation={mutationDeleteBidPackage}
          refetchQueries={() => ['BidPackages']}
          renderLink={({ open }) => (
            <DropdownMenu.Action danger onClick={open}>
              {t('deleteBidPackage')}
            </DropdownMenu.Action>
          )}
        />,
      );
    }
  }

  return (
    <DropdownMenuWrapper>
      <DropdownMenu.Container
        renderTrigger={({ isTriggered, toggleTrigger }) => (
          <Clickable disabled={actions.length === 0} onClick={toggleTrigger}>
            <Icon type={isTriggered ? 'chevron-up' : 'chevron-down'} size={18} />
          </Clickable>
        )}
      >
        {actions}
      </DropdownMenu.Container>
    </DropdownMenuWrapper>
  );
};

export default BidPackagesDropdown;
