import React from 'react';

import { uniqueId } from 'shared/utils/javascript';

import { StyledCheckbox, HiddenInput, Label, CheckIcon } from './Styles';

export interface CheckboxProps {
  className?: string;
  checkboxLabel: React.ReactNode | string;
  name?: string;
  value?: string | boolean;
  checked?: boolean;
  invalid?: boolean;
  disabled?: boolean;
  onChange: (checked: boolean, event: React.ChangeEvent<HTMLInputElement>) => void;
}

const Checkbox: React.FC<CheckboxProps> = ({
  className,
  checkboxLabel,
  onChange,
  invalid,
  value,
  checked,
  ...checkboxProps
}) => {
  const checkboxId = uniqueId('form-checkbox-');

  return (
    <StyledCheckbox className={className} invalid={invalid}>
      <HiddenInput
        {...checkboxProps}
        type="checkbox"
        id={checkboxId}
        value={String(value)}
        checked={checked !== undefined ? checked : Boolean(value)}
        onChange={event => onChange(event.target.checked, event)}
      />
      <Label tabIndex={0} htmlFor={checkboxId}>
        {checkboxLabel}
        <CheckIcon type="check-fat" />
      </Label>
    </StyledCheckbox>
  );
};

export default Checkbox;
