import React from 'react';
import { useQuery } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import {
  HtmlHeadTags,
  Button,
  DeletionModal,
  Modal,
  PageLoader,
  QueryErrors,
  Page,
  IconClickable,
} from 'shared/components';
import { HeadingLarge } from 'shared/components/Styles';
import { FormTypes, MODAL_VARIANTS } from 'shared/constants';
import EmptyContent from 'shared/components/EmptyContent';
import { queryWorkCategories } from 'shared/utils/queries';
import { isDoneLoading } from 'shared/utils/apollo';
import SubcontractorsList from 'shared/components/SubcontractorsList';
import SubcontractorsListItem from 'shared/components/SubcontractorsListItem';
import { WorkCategories } from 'shared/utils/__graphql__/WorkCategories';

import { mutationDeleteSubcontractor } from './api';
import Form from './Form';
import Edit from './Edit';

const Subcontractors: React.FC<void> = () => {
  const { t } = useTranslation('subcontractors');

  const { error, ...queryResults } = useQuery<WorkCategories>(queryWorkCategories);

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const {
    data: { workCategories },
  } = queryResults;

  return (
    <Page>
      <HtmlHeadTags title="subcontractors" />
      <HeadingLarge>{t('subcontractors')}</HeadingLarge>
      <SubcontractorsList
        withShadow
        renderHeaderAction={() => (
          <Modal
            variant={MODAL_VARIANTS.aside}
            renderLink={modal => (
              <Button data-cy-button-name="new" onClick={modal.open}>
                {t('newSubcontractor')}
              </Button>
            )}
            renderContent={modal => (
              <Form workCategories={workCategories} type={FormTypes.new} closeModal={modal.close} />
            )}
          />
        )}
        renderNoResults={isFiltered =>
          isFiltered ? null : (
            <div>
              <EmptyContent.CallToAction>
                {t('newSubcontractorCallToAction')}
              </EmptyContent.CallToAction>
              <EmptyContent.Explanation>
                {t('newSubcontractorExplanation')}
              </EmptyContent.Explanation>
              <Modal
                variant={MODAL_VARIANTS.aside}
                renderLink={modal => (
                  <EmptyContent.Button data-cy-button-name="new" onClick={modal.open} hollow>
                    {t('newSubcontractor')}
                  </EmptyContent.Button>
                )}
                renderContent={modal => (
                  <Form
                    workCategories={workCategories}
                    type={FormTypes.new}
                    closeModal={modal.close}
                  />
                )}
              />
            </div>
          )
        }
      >
        {subcontractors =>
          subcontractors.map(subcontractor => (
            <SubcontractorsListItem
              key={subcontractor.id}
              showContactsWithoutEmails
              subcontractor={subcontractor}
              renderActions={subcontractorId => (
                <>
                  <Modal
                    variant={MODAL_VARIANTS.aside}
                    renderLink={modal => <IconClickable onClick={modal.open} type="edit" />}
                    renderContent={modal => (
                      <Edit
                        type={FormTypes.edit}
                        workCategories={workCategories}
                        subcontractorId={subcontractor.id}
                        closeModal={modal.close}
                      />
                    )}
                  />
                  <DeletionModal
                    id={subcontractorId}
                    mutation={mutationDeleteSubcontractor}
                    refetchQueries={() => ['SubcontractorsForFilterableList', 'WorkCategories']}
                    renderLink={modal => <IconClickable onClick={modal.open} type="delete" />}
                  />
                </>
              )}
            />
          ))
        }
      </SubcontractorsList>
    </Page>
  );
};

export default Subcontractors;
