import gql from 'graphql-tag';

import { fragmentBidModal } from 'shared/components/BidModal';

export const queryBidRequestsForAgent = gql`
  query BidRequestsForAgent(
    $offset: Int!
    $limit: Int!
    $sortBy: String!
    $sortDirection: SortDirection!
  ) {
    bidRequestsForAgent(
      offset: $offset
      limit: $limit
      sortBy: $sortBy
      sortDirection: $sortDirection
    ) {
      totalRecords
      bidRequests {
        id
        status
        recipientUserId
        ...BidModal_bidRequest
        bidPackage {
          id
          name
          bidsDueAt
          project {
            id
            name
            imageKey
            location
            customer
            startsOn
            endsOn
            description
            status
            office {
              id
              name
            }
          }
        }
        currentBid {
          id
          message
          totalAmount
          submittedAt
          ...BidModal_bid
        }
      }
    }
  }
  ${fragmentBidModal}
`;
