import React from 'react';
import gql from 'graphql-tag';
import { useTranslation } from 'react-i18next';

import DropdownMenu from 'shared/components/DropdownMenu';
import Clickable from 'shared/components/Clickable';
import { DropdownMenuWrapper } from 'shared/components/Styles';
import Icon from 'shared/components/Icon';
import { USER_STATUS } from '__graphql__/globalTypes';

import LoginAsUser from './LoginAsUser';
import ResendInvite from './ResendInvite';
import CopyInviteLink from './CopyInviteLink';
import CancelInvite from './CancelInvite';
import RestoreInvite from './RestoreInvite';
import DeactivateUser from './DeactivateUser';
import ReactivateUser from './ReactivateUser';
import ChangeTypeModal from './ChangeTypeModal';
import { UsersTableDropdown_user } from './__graphql__/UsersTableDropdown_user';

export const fragmentUsersTableDropdown = gql`
  fragment UsersTableDropdown_user on User {
    id
    status
    type
    invitationLink
  }
`;

interface Props {
  user: UsersTableDropdown_user;
  isAdmin?: boolean;
  isCurrentUser?: boolean;
}

const UsersTableDropdown: React.FC<Props> = ({ user, isAdmin = false, isCurrentUser = false }) => {
  const { t } = useTranslation('company');
  const actions = [];

  if (!isCurrentUser) {
    if (isAdmin && user.status === USER_STATUS.active) {
      actions.push(<LoginAsUser key="loginAsUser" userId={user.id} />);
    }
    if (user.status === USER_STATUS.invited) {
      actions.push(
        <ResendInvite key="resendInvite" userId={user.id} />,
        <CopyInviteLink key="copyInvite" invitationLink={user.invitationLink} />,
        <CancelInvite key="cancelInvite" userId={user.id} />,
      );
    }
    if (user.status === USER_STATUS.inviteCanceled) {
      actions.push(<RestoreInvite key="restoreInvite" userId={user.id} />);
    }
    if (user.status === USER_STATUS.inviteFailed) {
      actions.push(<ResendInvite key="resendInvite" userId={user.id} />);
    }
    if (user.status === USER_STATUS.active) {
      actions.push(
        <ChangeTypeModal
          user={user}
          renderLink={modal => (
            <DropdownMenu.Action onClick={modal.open}>{t('updateUserType')}</DropdownMenu.Action>
          )}
        />,
        <DeactivateUser key="deactivateUser" userId={user.id} />,
      );
    }
    if (user.status === USER_STATUS.inactive) {
      actions.push(<ReactivateUser key="reactivateUser" userId={user.id} />);
    }
  }

  return (
    <DropdownMenuWrapper>
      <DropdownMenu.Container
        renderTrigger={({ isTriggered, toggleTrigger }) => (
          <Clickable disabled={!actions.length} onClick={toggleTrigger}>
            <Icon type={isTriggered ? 'chevron-up' : 'chevron-down'} size={18} />
          </Clickable>
        )}
      >
        {actions}
      </DropdownMenu.Container>
    </DropdownMenuWrapper>
  );
};

export default UsersTableDropdown;
