import { Item as SerializedItem } from '../types/parsedFileTree';

// eslint-disable-next-line import/no-cycle
import Folder from './Folder';

abstract class Item {
  abstract ancestor: Folder | undefined;

  abstract id: string;

  abstract name: string;

  abstract saveCallback: ((rootFolder: SerializedItem) => void) | undefined;

  save = () => {
    if (this.saveCallback) {
      this.saveCallback(this.serializable());
    }
  };

  moveTo(folder: Folder) {
    if (this.ancestor) {
      this.ancestor.removeChild(this);
    }
    folder.addChild(this);
    this.save();
  }

  updateName(newName: string) {
    this.name = newName;
    this.save();
  }

  delete() {
    if (this.ancestor) {
      this.ancestor.removeChild(this);
    }
    this.save();
  }

  getPath(): string {
    if (!this.ancestor) {
      return '';
    }
    const ancestorPath = this.ancestor.getPath();
    return ancestorPath ? [ancestorPath, this.name].join('/') : this.name;
  }

  abstract serializable(): SerializedItem;

  abstract findItembyId(id: string): Item | null;
}

export default Item;
