import React from 'react';
import { useQuery } from '@apollo/react-hooks';

import { isDoneLoading } from 'shared/utils/apollo';
import { PageLoader, QueryErrors } from 'shared/components';
import { WorkCategories_workCategories } from 'shared/utils/__graphql__/WorkCategories';
import { FormTypes } from 'shared/constants';

import Form from './Form';
import { querySubcontractorForEdit } from './api';
import {
  SubcontractorForEdit,
  SubcontractorForEditVariables,
} from './__graphql__/SubcontractorForEdit';

interface EditSubcontractorProps {
  subcontractorId: string;

  // TODO: Have these properties inherit from Form when it is ported to TS
  workCategories: WorkCategories_workCategories[];
  type: FormTypes;
  closeModal: () => void;
}

const EditSubcontractor: React.FC<EditSubcontractorProps> = ({ subcontractorId, ...rest }) => {
  const { error: subcontractorError, ...queryData } = useQuery<
    SubcontractorForEdit,
    SubcontractorForEditVariables
  >(querySubcontractorForEdit, { variables: { id: subcontractorId } });

  if (!isDoneLoading(queryData)) return <PageLoader />;

  if (subcontractorError) return <QueryErrors error={subcontractorError} />;

  const {
    data: { subcontractor },
  } = queryData;

  return <Form subcontractor={subcontractor} {...rest} />;
};

export default EditSubcontractor;
