import React from 'react';
import { useTranslation } from 'react-i18next';

import DropdownMenu from 'shared/components/DropdownMenu';
import DeletionModal from 'shared/components/DeletionModal';

import { mutationDeactivateUser } from './api';

interface Props {
  userId: string;
}

const DeactivateUser: React.FC<Props> = ({ userId }) => {
  const { t } = useTranslation('company');

  return (
    <DeletionModal
      id={userId}
      mutation={mutationDeactivateUser}
      refetchQueries={() => ['Users', 'AdminUsers']}
      titleKey="company:confirmDeactivateMessage"
      messageKey="company:confirmDeactivateDescription"
      confirmTextKey="company:deactivate"
      successKey="company:userDeactivated"
      renderLink={modal => (
        <DropdownMenu.Action danger onClick={modal.open}>
          {t('deactivate')}
        </DropdownMenu.Action>
      )}
    />
  );
};

export default DeactivateUser;
