import React, { useContext } from 'react';
import { useMutation } from '@apollo/react-hooks';
import { Link, useLocation, useHistory } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { Form, Field } from 'shared/components';
import { Message, SubmitButton, BottomLink } from 'shared/components/UnauthenticatedStyles';
import UnauthenticatedPage from 'shared/components/UnauthenticatedPage';
import { ContentBox } from 'shared/components/UnauthenticatedPage/Styles';
import AuthContext from 'shared/contexts/authContext';
import routes from 'shared/constants/routes';
import { COLOR } from 'shared/utils/styles';

import { mutationLogin } from './api';
import { Login } from './__graphql__/Login';

interface FormValues {
  email: string;
  password: string;
}

const AuthLogin: React.FC<void> = () => {
  const { t } = useTranslation('auth');
  const { login } = useContext(AuthContext);
  const history = useHistory();
  const location = useLocation();

  const [mutateLogin, { error, loading: mutating }] = useMutation<Login>(mutationLogin);

  return (
    <UnauthenticatedPage title="login">
      <Form<FormValues>
        initialValues={{
          email: '',
          password: '',
        }}
        validations={{
          email: [Form.is.required(), Form.is.maxLength(200), Form.is.email()],
          password: [Form.is.required(), Form.is.maxLength(250)],
        }}
        onSubmit={async values => {
          const { data } = await mutateLogin({ variables: values });
          if (data) {
            login(data.login);
          }
          if (location.state && location.state.redirect) {
            history.push(location.state.redirect);
          }
        }}
      >
        <ContentBox as={Form.Element}>
          {error && error.graphQLErrors.length > 0 && (
            <Message color={COLOR.danger}>
              {error.graphQLErrors.map(graphQLError => graphQLError.message).join('\n')}
            </Message>
          )}
          <Field.Input name="email" label={t('email')} type="email" icon="email" />
          <Field.Input name="password" label={t('password')} type="password" icon="lock" />
          <SubmitButton type="submit" working={mutating}>
            {t('login')}
          </SubmitButton>
          <BottomLink>
            <Link to={routes.resetPassword()}>{t('forgotPassword')}</Link>
          </BottomLink>
        </ContentBox>
      </Form>
    </UnauthenticatedPage>
  );
};

export default AuthLogin;
