import React from 'react';
import { useQuery } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import { Tabs, HtmlHeadTags, PageLoader, QueryErrors, Page } from 'shared/components';
import { HeadingLarge } from 'shared/components/Styles';
import { isDoneLoading } from 'shared/utils/apollo';

import Users from './Users';
import Offices from './Offices';
import CompanyProfile from './CompanyProfile';
import { queryCompany } from './api';
import { Company as CompanyQuery } from './__graphql__/Company';

const Company: React.FC<void> = () => {
  const { t } = useTranslation('company');

  const { error, ...queryResults } = useQuery<CompanyQuery>(queryCompany);

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const { data } = queryResults;

  return (
    <Page>
      <HtmlHeadTags title="company" />
      <HeadingLarge>{`${t('company')}: ${data.company.name}`}</HeadingLarge>
      <Tabs.Container>
        <Tabs.Links>
          <Tabs.Link to="profile">{t('profile')}</Tabs.Link>
          <Tabs.Link to="offices">{t('offices')}</Tabs.Link>
          <Tabs.Link to="users" isDefault>
            {t('users')}
          </Tabs.Link>
        </Tabs.Links>
        <Tabs.Content>
          <Tabs.Route path="profile" render={() => <CompanyProfile company={data.company} />} />
          <Tabs.Route path="offices" render={() => <Offices />} />
          <Tabs.Route path="users" render={() => <Users />} />
        </Tabs.Content>
      </Tabs.Container>
    </Page>
  );
};

export default Company;
