import React, { ButtonHTMLAttributes, AnchorHTMLAttributes } from 'react';
import styled, { css } from 'styled-components/macro';
import { Link, LinkProps } from 'react-router-dom';

import Spinner from 'shared/components/Spinner';
import { COLOR, font, mixin } from 'shared/utils/styles';
import { omit } from 'shared/utils/javascript';

interface StyledButtonBaseProps {
  linkTo?: LinkProps['to'];
}

interface StyledButtonProps {
  icon?: string;
  iconSize?: number;
  hollow: boolean;
  working: boolean;
  iconOnly: boolean;
  disabled: boolean;
  buttonColor: COLOR;
}

const StyledButtonBase: React.FC<StyledButtonBaseProps &
  StyledButtonProps &
  (ButtonHTMLAttributes<HTMLButtonElement> | AnchorHTMLAttributes<HTMLAnchorElement>)> = ({
  linkTo,
  ...props
}) => {
  const buttonProps = omit(props, [
    'icon',
    'iconSize',
    'hollow',
    'working',
    'iconOnly',
    'buttonColor',
  ]) as ButtonHTMLAttributes<HTMLButtonElement>;

  if (linkTo) {
    const linkProps = buttonProps as AnchorHTMLAttributes<HTMLAnchorElement>;
    return <Link to={linkTo} {...linkProps} />;
  }

  // In this specific case we do not want to use an explicit type, instead we
  // rely on the consumer of this component to set it.
  // eslint-disable-next-line react/button-has-type
  return <button {...buttonProps} />;
};

export const StyledButton = styled(StyledButtonBase)<StyledButtonProps>`
  display: inline-block;
  height: 36px;
  line-height: 34px;
  padding: 0 18px;
  vertical-align: middle;
  white-space: nowrap;
  text-align: center;
  border-radius: 4px;
  transition: all 0.1s;
  appearance: none !important;
  ${mixin.clickable}
  ${font.bold}
  ${props => (!props.icon || !props.iconSize) && font.size(14)}
  ${props => (props.hollow ? hollowStyles : filledStyles)}
  ${props =>
    props.disabled &&
    css`
      opacity: 0.6;
      ${props.working ? '' : disabledStyles()}
      cursor: default;
      /* Disable clicks also on non-<button> Buttons, such as <Button linkTo={...}> */
      pointer-events: none;
    `}

  i {
    position: relative;
    top: -1px;
    right: 4px;
    margin-right: 7px;
    display: inline-block;
    vertical-align: middle;
  }
  ${props => props.iconOnly && iconOnlyStyles}
`;

const iconOnlyStyles = css`
  padding: 0 12px;
  i {
    right: 0;
    margin-right: 0;
  }
`;

export const StyledSpinner = styled(Spinner)`
  position: relative;
  right: 8px;
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
`;

const disabledStyles = () => css`
  color: ${COLOR.backgroundDark};
  background: ${COLOR.backgroundMedium};
  border: 0;
`;

const filledStyles = (props: StyledButtonProps) => css`
  color: #fff;
  background: ${props.buttonColor};
  border: 1px solid ${props.buttonColor};
  ${!props.disabled &&
    css`
      &:hover,
      &:focus {
        background: ${mixin.darken(props.buttonColor, 0.15)};
        border: 1px solid ${mixin.darken(props.buttonColor, 0.15)};
      }
      &:active {
        background: ${mixin.lighten(props.buttonColor, 0.1)};
        border: 1px solid ${mixin.lighten(props.buttonColor, 0.1)};
      }
    `}
`;

const hollowStyles = (props: StyledButtonProps) => css`
  color: ${COLOR.textMediumBlue};
  background: #fff;
  border: 1px solid ${COLOR.borderBlue};
  ${!props.disabled &&
    css`
      &:hover,
      &:focus {
        border: 1px solid ${mixin.darken(COLOR.borderBlue, 0.15)};
      }
      &:active {
        border: 1px solid ${COLOR.borderBlue};
      }
    `}
`;
