import React, { forwardRef, ReactNode, RefObject } from 'react';
import styled, { css } from 'styled-components/macro';
import { Transition } from 'react-transition-group';

import { COLOR, zIndexValues, mediaQueries } from 'shared/utils/styles';
import { isExited, isEnter, isEntered } from 'shared/utils/transitionStates';

import Card, { TRANSITION_DURATION } from './Card';

export interface DropdownCardProps {
  className?: string;
  children: ReactNode;
  isOpen: boolean;
  triggerRef?: RefObject<HTMLElement>;
  matchTriggerWidth?: boolean;
}

const DropdownCardBase = forwardRef<HTMLDivElement, DropdownCardProps>(
  ({ className, children, isOpen, triggerRef, matchTriggerWidth, ...props }, ref) => (
    <Transition in={isOpen} timeout={TRANSITION_DURATION}>
      {state => (
        <div data-cy-dropdown-card-open={isEntered(state)} className={className} {...props}>
          <Card ref={ref} isOpen={isEnter(state)} hasClosed={isExited(state)}>
            {children}
          </Card>
        </div>
      )}
    </Transition>
  ),
);

const DropdownCard = styled(DropdownCardBase)<DropdownCardProps>`
  pointer-events: ${props => (props.isOpen ? 'auto' : 'none')};
  z-index: ${zIndexValues.dropdown};

  @media ${mediaQueries.mobile} {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background: ${COLOR.overlay};
    cursor: pointer; /* Without this, closing the overlay does not work on iOS */
    opacity: ${props => (props.isOpen ? 1 : 0)};
    transition: opacity ${TRANSITION_DURATION}ms;
  }

  @media ${mediaQueries.desktop} {
    position: absolute;

    ${props => {
      if (!props.triggerRef) {
        return css`
          top: calc(100% - 2px);
          right: 0;
        `;
      }

      if (!props.triggerRef.current) {
        return '';
      }

      const { top, height, right, width } = props.triggerRef.current.getBoundingClientRect();
      const alignedToBottom = height + top;
      const alignedToRight = document.body.clientWidth - right;

      return css`
        top: ${alignedToBottom - 2}px;
        right: ${alignedToRight}px;

        ${props.matchTriggerWidth &&
          css`
            width: ${width}px;
          `}
      `;
    }}
  }
`;

export { Card, TRANSITION_DURATION };
export default DropdownCard;
