import React from 'react';
import { useQuery } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import {
  Button,
  Table,
  Cell,
  DeletionModal,
  IconLink,
  RowBadge,
  PageLoader,
  QueryErrors,
} from 'shared/components';
import { SpaceBetween, TableSearch, DisabledIcon } from 'shared/components/Styles';
import { ProjectStatusTag } from 'shared/components/TagStyles';
import { TABLE_ITEMS_PER_PAGE, SortDirection } from 'shared/constants';
import { PROJECT_STATUS } from '__graphql__/globalTypes';
import EmptyContent from 'shared/components/EmptyContent';
import { isDoneLoading } from 'shared/utils/apollo';
import routes from 'shared/constants/routes';

import { queryProjects, mutationDeleteProject } from './api';
import { Projects } from './__graphql__/Projects';

interface Props {
  status?: PROJECT_STATUS;
}

const ProjectsList: React.FC<Props> = ({ status }) => {
  const { t } = useTranslation('projects');

  const { error, ...queryResults } = useQuery<Projects>(queryProjects, {
    variables: {
      status,
      offset: 0,
      limit: TABLE_ITEMS_PER_PAGE,
      sortBy: 'number',
      sortDirection: SortDirection.asc,
    },
  });

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;

  const { refetch, data, variables } = queryResults;

  return (
    <>
      <SpaceBetween>
        <TableSearch onChange={search => refetch({ query: search })} />
        <Button data-cy-button-name="new" linkTo={routes.createProject()}>
          {t('newProject')}
        </Button>
      </SpaceBetween>
      <Table
        columns={[
          { label: t('project.numberShort'), sortField: 'number', percentWidth: 12 },
          { label: t('project.name'), sortField: 'name' },
          { label: t('project.customer'), sortField: 'customer' },
          { label: t('project.manager'), sortField: 'primaryUser' },
          ...(status !== PROJECT_STATUS.closed
            ? [
                {
                  label: t('project.status'),
                  sortField: 'status',
                  percentWidth: 15,
                },
              ]
            : []),
          { fixedWidth: 50 },
          { fixedWidth: 50 },
        ]}
        rows={data.projects.projects.map(project => (
          <Table.Row rowId={project.id}>
            <Cell.Text>{project.number}</Cell.Text>
            <Cell.Link to={routes.showProject(project.id)}>{project.name}</Cell.Link>
            <Cell.Text>{project.customer}</Cell.Text>
            <RowBadge
              displayName={`${project.primaryUser.firstName} ${project.primaryUser.lastName}`}
            />
            {status !== PROJECT_STATUS.closed && (
              <ProjectStatusTag inList status={project.status} data-cy-project-status>
                {t(`project.${project.status}`)}
              </ProjectStatusTag>
            )}
            <Cell.Icon>
              {project.status === PROJECT_STATUS.closed ? (
                <DisabledIcon type="edit" />
              ) : (
                <IconLink type="edit" to={routes.editProject(project.id)} />
              )}
            </Cell.Icon>
            <Cell.Icon>
              {project.status === PROJECT_STATUS.draft ? (
                <DeletionModal
                  id={project.id}
                  mutation={mutationDeleteProject}
                  refetchQueries={() => ['Projects']}
                />
              ) : (
                <DisabledIcon type="delete" />
              )}
            </Cell.Icon>
          </Table.Row>
        ))}
        totalRecords={data.projects.totalRecords}
        refetch={refetch}
        variables={variables}
        contentIfEmpty={() => {
          if (variables.status === PROJECT_STATUS.closed) {
            return (
              <>
                <EmptyContent.CallToAction>{t('closedProjectsHeadline')}</EmptyContent.CallToAction>
                <EmptyContent.Explanation>
                  {t('closedProjectsExplanation')}
                </EmptyContent.Explanation>
              </>
            );
          }

          return (
            <>
              <EmptyContent.CallToAction>{t('newProjectCallToAction')}</EmptyContent.CallToAction>
              <EmptyContent.Explanation>{t('newProjectExplanation')}</EmptyContent.Explanation>
              <EmptyContent.Button data-cy-button-name="new" hollow linkTo={routes.createProject()}>
                {t('newProject')}
              </EmptyContent.Button>
            </>
          );
        }}
      />
    </>
  );
};

export default ProjectsList;
