import React, { HTMLAttributes } from 'react';
import TextareaAutoSize from 'react-textarea-autosize';

import { StyledTextarea } from './Styles';

interface Props {
  minRows?: number;
  className?: string;
  invalid?: boolean;
  value?: string;
  preventResize?: boolean;
  onChange?(value: string, event: React.ChangeEvent<HTMLTextAreaElement>): void;
}

const Textarea: React.FC<Props & HTMLAttributes<HTMLTextAreaElement>> = ({
  className = undefined,
  invalid = false,
  onChange = () => {},
  minRows = 2,
  preventResize = true,
  ...textareaProps
}) => (
  <StyledTextarea className={className} invalid={invalid} preventResize={preventResize}>
    <TextareaAutoSize
      minRows={minRows}
      {...textareaProps}
      onChange={event => onChange(event.target.value, event)}
    />
  </StyledTextarea>
);

export default Textarea;
