import React from 'react';
import { useTranslation } from 'react-i18next';
import { useMutation } from '@apollo/react-hooks';

import DropdownMenu from 'shared/components/DropdownMenu';
import ConfirmationModal from 'shared/components/ConfirmationModal';
import toast from 'shared/utils/toast';
import { BUTTON_VARIANT } from 'shared/constants';

import { mutationCancelInvite } from './api';

interface Props {
  userId: string;
}

const CancelInvite: React.FC<Props> = ({ userId }) => {
  const { t } = useTranslation('company');
  const [cancelInvite, { loading }] = useMutation(mutationCancelInvite, {
    refetchQueries: () => ['Users', 'AdminUsers'],
  });

  return (
    <ConfirmationModal
      title="company:cancelInviteMessage"
      message={null}
      isWorking={loading}
      confirmationButtonVariant={BUTTON_VARIANT.danger}
      renderLink={modal => (
        <DropdownMenu.Action danger onClick={modal.open}>
          {t('cancelInvite')}
        </DropdownMenu.Action>
      )}
      onConfirm={async modal => {
        try {
          await cancelInvite({ variables: { id: userId } });
          toast.success(t('inviteCanceled'));
          modal.close();
        } catch (error) {
          toast.error(error);
        }
      }}
    />
  );
};

export default CancelInvite;
