import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { BID_PACKAGE_STATUS } from '__graphql__/globalTypes';
import { HtmlHeadTags, PageWithSidebar, Tabs } from 'shared/components';
import { HeadingLarge, SpaceBetween } from 'shared/components/Styles';
import { BidPackageStatusTag } from 'shared/components/TagStyles';
import routes from 'shared/constants/routes';

import Sidebar from './Sidebar';
import OverviewCreate from './OverviewCreate';
import { BidPackageName } from './Styles';

const CreateBidPackage = () => {
  const { t } = useTranslation('bidPackages');
  const { projectId } = useParams<{ projectId: string }>();

  return (
    <PageWithSidebar
      renderSidebar={() => <Sidebar projectId={projectId} />}
      backLink={{ to: routes.bidPackages(projectId), label: t('backToProject') }}
    >
      <HtmlHeadTags title={t('overview.newBidPackage')} />
      <SpaceBetween>
        <HeadingLarge>
          <BidPackageName data-cy-bid-package-name>{t('overview.newBidPackage')}</BidPackageName>
          <BidPackageStatusTag status={BID_PACKAGE_STATUS.draft} data-cy-status-tag>
            {t('bidPackage.draft')}
          </BidPackageStatusTag>
        </HeadingLarge>
      </SpaceBetween>
      <Tabs.Container>
        <Tabs.Content>
          <OverviewCreate projectId={projectId} />
        </Tabs.Content>
      </Tabs.Container>
    </PageWithSidebar>
  );
};

export default CreateBidPackage;
