import gql from 'graphql-tag';

import { fragmentCurrentUserForAuthentication } from 'shared/hooks/useAuthentication';

export const mutationLogin = gql`
  mutation Login($email: String!, $password: String!) {
    login(email: $email, password: $password) {
      token
      user {
        ...CurrentUserForAuthentication
      }
    }
  }
  ${fragmentCurrentUserForAuthentication}
`;

export const mutationResetPassword = gql`
  mutation ResetPassword($email: String!) {
    resetPassword(email: $email)
  }
`;

export const mutationCreatePassword = gql`
  mutation CreatePassword($password: String!, $token: String!) {
    createPassword(password: $password, token: $token) {
      token
      user {
        ...CurrentUserForAuthentication
      }
    }
  }
  ${fragmentCurrentUserForAuthentication}
`;

export const queryCheckResetPasswordToken = gql`
  query CheckResetPasswordToken($token: String!) {
    checkResetPasswordToken(token: $token)
  }
`;

export const queryInvitation = gql`
  query Invitation($token: String!) {
    invitation(token: $token) {
      inviter {
        firstName
        lastName
      }
      invitee {
        status
        company {
          id
          name
        }
      }
    }
  }
`;

export const mutationActivateInvitee = gql`
  mutation ActivateInvitee($token: String!, $input: InviteeInput!) {
    activateInvitee(token: $token, input: $input) {
      token
      user {
        ...CurrentUserForAuthentication
      }
    }
  }
  ${fragmentCurrentUserForAuthentication}
`;

export const mutationSignup = gql`
  mutation Signup($input: SignupInput!) {
    signup(input: $input)
  }
`;
