import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import { uniq } from 'shared/utils/javascript';
import { orderedCompanySizes, countryCodes, FormTypes } from 'shared/constants';
import toast from 'shared/utils/toast';
import { getHumanReadableCompanySize } from 'shared/utils/enumHelpers';
import { HeadingMedium } from 'shared/components/Styles';
import { Form, Field, ModalFormBody } from 'shared/components';
import { WorkCategories_workCategories } from 'shared/utils/__graphql__/WorkCategories';

import Contacts from './Contacts';
import { mutationCreateSubcontractor, mutationUpdateSubcontractor } from './api';
import {
  SubcontractorForEdit_subcontractor,
  SubcontractorForEdit_subcontractor_contacts,
} from './__graphql__/SubcontractorForEdit';

interface Props {
  subcontractor?: SubcontractorForEdit_subcontractor;
  workCategories: WorkCategories_workCategories[];
  closeModal: () => void;
  type: FormTypes;
}

interface FormValues {
  name: string;
  website: string;
  companySize: string;
  workCategoryNames: string[];
  street: string;
  postalCode: string;
  city: string;
  countryCode: string;
  notes: string;
  contacts: SubcontractorForEdit_subcontractor_contacts[];
  isBanned: boolean;
}

const SubcontractorsForm: React.FC<Props> = ({
  type,
  subcontractor = {},
  workCategories,
  closeModal,
}) => {
  const { t } = useTranslation('subcontractors');

  const [createOrUpdateSubcontractor, { loading: mutating }] = useMutation(
    type === FormTypes.edit ? mutationUpdateSubcontractor : mutationCreateSubcontractor,
    {
      refetchQueries: () => ['SubcontractorsForFilterableList', 'WorkCategories'],
      awaitRefetchQueries: true,
    },
  );

  const allWorkCategoryNames = workCategories.map(({ name }) => name);

  return (
    <Form<FormValues>
      initialValues={{
        name: subcontractor.name || '',
        website: subcontractor.website || '',
        companySize: subcontractor.companySize || '',
        workCategoryNames: (subcontractor.workCategories || []).map(category => category.name),
        street: subcontractor.street || '',
        postalCode: subcontractor.postalCode || '',
        city: subcontractor.city || '',
        countryCode: subcontractor.countryCode || 'DE',
        notes: subcontractor.notes || '',
        contacts: subcontractor.contacts || [],
        isBanned: subcontractor.isBanned || false,
      }}
      validations={{
        name: [Form.is.required(), Form.is.maxLength(100)],
        website: Form.is.url(),
        street: Form.is.maxLength(200),
        postalCode: Form.is.maxLength(100),
        city: Form.is.maxLength(100),
        countryCode: Form.is.maxLength(100),
        contacts: Form.is.minLength(1),
      }}
      onSubmit={async (values, form) => {
        const contacts = values.contacts.map(contact => ({
          id: contact.id,
          isPerson: contact.isPerson,
          email: contact.email,
          phone: contact.phone,
          mobilePhone: contact.mobilePhone,
          fax: contact.fax,
          ...(contact.isPerson && {
            title: contact.title || null,
            firstName: contact.firstName,
            lastName: contact.lastName,
            jobTitle: contact.jobTitle,
          }),
        }));
        const input = {
          ...values,
          contacts,
          companySize: values.companySize || null,
        };

        await createOrUpdateSubcontractor({ variables: { id: subcontractor.id, input } })
          .then(() => {
            toast.success(t(type === FormTypes.edit ? 'updated' : 'created'));
            closeModal();
          }, toast.error)
          .then(() => form.setSubmitting(false));
      }}
    >
      {({ values, errors }) => (
        <ModalFormBody close={closeModal} working={mutating}>
          <HeadingMedium>
            {t(type === FormTypes.edit ? 'editSubcontractor' : 'newSubcontractor')}
          </HeadingMedium>
          <Field.Input required name="name" label={t('subcontractor.name')} />
          <Field.Input name="website" label={t('subcontractor.website')} />
          <Field.SelectSingle
            name="companySize"
            label={t('subcontractor.companySize')}
            options={orderedCompanySizes.map(value => ({
              value,
              label: getHumanReadableCompanySize(value),
            }))}
          />
          <Field.SelectMulti
            onCreate={(name, selectOptionValue) => selectOptionValue(name)}
            name="workCategoryNames"
            label={t('subcontractor.workCategories')}
            options={uniq([...values.workCategoryNames, ...allWorkCategoryNames]).map(name => ({
              value: name,
              label: name,
            }))}
          />
          <Field.Input name="street" label={t('subcontractor.street')} />
          <Field.Input name="postalCode" label={t('subcontractor.postalCode')} />
          <Field.Input name="city" label={t('subcontractor.city')} />
          <Field.SelectSingle
            name="countryCode"
            label={t('subcontractor.country')}
            options={countryCodes
              .map(code => ({ value: code, label: t(`countries:${code}`) }))
              .sort((country, otherCountry) => country.label.localeCompare(otherCountry.label))}
          />
          <Field.Textarea name="notes" label={t('subcontractor.notes')} />
          <Field.Checkbox name="isBanned" checkboxLabel={t('subcontractor.isBanned')} />
          <Contacts values={values.contacts} hasError={Boolean(errors.contacts)} />
          <Form.RequiredFieldsNote />
        </ModalFormBody>
      )}
    </Form>
  );
};

export default SubcontractorsForm;
