import React from 'react';
import {
  Route as ReactRoute,
  Redirect,
  useRouteMatch,
  useLocation,
  RouteProps,
} from 'react-router-dom';

import { Container, Links, StyledLink, Content } from './Styles';

const isAbsolute = (str: string) => str.startsWith('/');
const prepend = (currentUrl: string, url: string) =>
  isAbsolute(url) ? url : `${currentUrl}/${url}`;

interface LinkProps {
  to: string;
  isDefault?: boolean;
  children: React.ReactNode;
}

const Link: React.FC<LinkProps> = ({ to, isDefault = false, children }) => {
  const { isExact, url } = useRouteMatch();
  const location = useLocation();

  return (
    <>
      {isDefault && isExact && url !== prepend(url, to) && (
        <Redirect
          to={{
            pathname: prepend(url, to),
            search: location.search,
          }}
        />
      )}
      <StyledLink to={prepend(url, to)}>{children}</StyledLink>
    </>
  );
};

type TabsRouteProps = RouteProps & { path: string };

const Route: React.FC<TabsRouteProps> = ({ path, ...routeProps }) => {
  const { path: matchPath } = useRouteMatch();
  return <ReactRoute path={prepend(matchPath, path)} {...routeProps} />;
};

export default {
  Container,
  Links,
  Link,
  Route,
  Content,
};
