import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import toast from 'shared/utils/toast';
import { Form, ModalFormBody, Field } from 'shared/components';
import { HeadingMedium } from 'shared/components/Styles';

import { mutationAdminUpdateCompany } from './api';
import { AdminUpdateCompany, AdminUpdateCompanyVariables } from './__graphql__/AdminUpdateCompany';
import { AdminCompanies_adminCompanies_companies } from './__graphql__/AdminCompanies';

interface Props {
  company: AdminCompanies_adminCompanies_companies;
  closeModal: () => void;
}

interface FormValues {
  name: string;
  remindersEnabled?: boolean;
}

const EditForm: React.FC<Props> = ({ company, closeModal }) => {
  const { t } = useTranslation('admin');
  const [updateCompany, { loading: mutating }] = useMutation<
    AdminUpdateCompany,
    AdminUpdateCompanyVariables
  >(mutationAdminUpdateCompany, {
    refetchQueries: () => ['AdminCompanies'],
    awaitRefetchQueries: true,
  });

  return (
    <Form<FormValues>
      initialValues={{
        name: company.name,
        remindersEnabled: company.remindersEnabled,
      }}
      validations={{
        name: [Form.is.required(), Form.is.maxLength(100)],
      }}
      onSubmit={async (values, form) => {
        await updateCompany({ variables: { id: company.id, input: values } })
          .then(() => {
            toast.success(t('companyUpdated'));
            closeModal();
          }, toast.error)
          .then(() => form.setSubmitting(false));
      }}
    >
      <ModalFormBody close={closeModal} working={mutating}>
        <HeadingMedium>{t('editCompany')}</HeadingMedium>
        <Field.Input name="name" label={t('company.name')} />
        <Field.Checkbox name="remindersEnabled" checkboxLabel={t('company.remindersEnabled')} />
      </ModalFormBody>
    </Form>
  );
};

export default EditForm;
