import React, { useState, useRef, ReactElement } from 'react';
import Downshift from 'downshift';
import { useTranslation } from 'react-i18next';
import { Transition } from 'react-transition-group';

import { isEnter } from 'shared/utils/transitionStates';
import useClickOutside from 'shared/hooks/useClickOutside';
import Clickable from 'shared/components/Clickable';
import DropdownCard, { TRANSITION_DURATION } from 'shared/components/DropdownCard';
import Portal from 'shared/components/Portal';

import { Item, GetInputPropsOptionsCompatibilityHack, OnCreate } from './types';
import { NULL_ITEM } from './constants';
import {
  Wrapper,
  StyledSelect,
  ValueContainer,
  StyledDropdownCardContent,
  Option,
  OptionsMessage,
  Placeholder,
  ChevronIcon,
  DropdownInput,
  ClearIcon,
} from './Styles';

interface SelectBaseProps {
  className?: string;
  name: string;
  placeholder?: string;
  options: Item[];
  selectedItem: Item | Item[];
  onSelect: (selectedItem: Item | null) => void;
  onCreate?: OnCreate;
  isValueEmpty: boolean;
  renderValue: () => ReactElement;
  showChevron: boolean;
  showNullOption: boolean;
  closeDropdownOnSelect: boolean;
  getIsLabelInOptions: (label: string) => boolean;
}

const SelectBase: React.FC<SelectBaseProps> = ({
  className,
  name,
  placeholder,
  options,
  selectedItem,
  onSelect,
  onCreate,
  isValueEmpty,
  renderValue,
  showChevron,
  showNullOption,
  closeDropdownOnSelect,
  getIsLabelInOptions,
}) => {
  const { t } = useTranslation();

  const [isOpen, setIsOpen] = useState(false);
  const [filterRaw, setFilterRaw] = useState('');
  const [isCreatingOption, setIsCreatingOption] = useState(false);
  const searchInput = useRef<HTMLInputElement>(null);
  const dropdown = useRef(null);
  const trigger = useRef(null);

  const filter = filterRaw.trim();
  const filteredOptions = options.filter((item: Item) =>
    item.label.toLowerCase().includes(filter.toLowerCase()),
  );

  const openDropdown = () => {
    setIsOpen(true);
    clearFilter();
  };

  const closeDropdown = () => {
    setIsOpen(false);
    // IE11 doesn't blur the filter input automatically
    if (searchInput.current && closeDropdownOnSelect) {
      searchInput.current.blur();
    }
  };

  const clearFilter = () => {
    setFilterRaw('');
    // We have to wait a tick before being able to focus on the search input
    // because of Transition
    setTimeout(() => {
      if (searchInput.current) {
        searchInput.current.focus();
      }
    });
  };

  const createOptionName = (newOptionName: string) => {
    if (onCreate) {
      setIsCreatingOption(true);

      onCreate(newOptionName, createdOptionValue => {
        setIsCreatingOption(false);
        onSelect({
          value: createdOptionValue,
          label: newOptionName,
        });
      });
    }
  };

  useClickOutside({
    refs: [dropdown, trigger],
    onClickOutside: closeDropdown,
  });

  const isBeingFiltered = filterRaw !== '';
  const canCreateOption = onCreate && isBeingFiltered && filter && !getIsLabelInOptions(filter);
  let optionHighlightOffset = 0;

  if (showNullOption) {
    optionHighlightOffset += 1;
  }
  if (canCreateOption) {
    optionHighlightOffset += 1;
  }

  return (
    <Downshift
      isOpen={isOpen}
      inputValue={filterRaw}
      itemToString={(item: Item | null) => (item ? item.value : '')}
      defaultHighlightedIndex={0}
      initialSelectedItem={NULL_ITEM}
      selectedItem={selectedItem}
      onSelect={(item: Item | null) => {
        if (!item) {
          return;
        }

        if (item.label === filter && item.value === '') {
          createOptionName(item.label);
        } else {
          onSelect(item);
        }
      }}
      onStateChange={changes => {
        if (
          changes.type === Downshift.stateChangeTypes.changeInput &&
          changes.inputValue !== null &&
          changes.inputValue !== undefined
        ) {
          setFilterRaw(changes.inputValue);
        }

        if (changes.isOpen) {
          openDropdown();
        }

        if (changes.isOpen === false) {
          closeDropdown();
        }
      }}
      stateReducer={(state, changes) => {
        if (!closeDropdownOnSelect) {
          if (
            changes.type === Downshift.stateChangeTypes.keyDownEnter ||
            changes.type === Downshift.stateChangeTypes.clickItem
          ) {
            return {
              ...changes,
              isOpen: state.isOpen,
              highlightedIndex: state.highlightedIndex,
            };
          }
        }

        return changes;
      }}
    >
      {downshift => (
        <Wrapper
          className={className}
          data-name={name}
          {...downshift.getRootProps()}
          data-testid="select"
        >
          <StyledSelect
            as={Clickable}
            {...downshift.getToggleButtonProps({ ref: trigger })}
            data-testid="select-trigger"
          />
          <ValueContainer data-testid="select-value-container">
            {showChevron && <ChevronIcon type={downshift.isOpen ? 'chevron-up' : 'chevron-down'} />}
            {isValueEmpty && placeholder && <Placeholder>{placeholder}</Placeholder>}
            {!isValueEmpty && renderValue()}
          </ValueContainer>
          <Portal>
            <Transition in={downshift.isOpen} timeout={TRANSITION_DURATION} unmountOnExit>
              {state => (
                <DropdownCard
                  isOpen={isEnter(state)}
                  triggerRef={trigger}
                  matchTriggerWidth
                  data-cy-select-dropdown-card={name}
                  {...downshift.getMenuProps({
                    ref: dropdown,
                  })}
                >
                  <DropdownInput
                    ref={searchInput}
                    {...(downshift.getInputProps({
                      onKeyDown: event => {
                        if (event.key === 'Escape' && downshift.isOpen) {
                          event.stopPropagation();
                        }
                      },
                    }) as GetInputPropsOptionsCompatibilityHack)}
                    aria-hidden={!downshift.isOpen}
                    tabIndex={downshift.isOpen ? undefined : -1}
                    placeholder={t('search')}
                  />
                  {isBeingFiltered && (
                    <ClearIcon
                      type="close"
                      onClick={clearFilter}
                      title={t('selectComponent.clearButtonLabel')}
                    />
                  )}
                  <StyledDropdownCardContent data-testid="select-options-list">
                    {showNullOption && (
                      <Option
                        {...downshift.getItemProps({ item: NULL_ITEM })}
                        hidden={isValueEmpty || isBeingFiltered}
                        isSelected={false}
                        isHighlighted={downshift.highlightedIndex === 0}
                      >
                        {NULL_ITEM.label}
                      </Option>
                    )}
                    {canCreateOption && (
                      <Option
                        data-create-option-name={filter}
                        {...downshift.getItemProps({ item: { label: filter, value: '' } })}
                        isSelected={false}
                        isHighlighted={downshift.highlightedIndex === (showNullOption ? 1 : 0)}
                      >
                        {isCreatingOption
                          ? t('selectComponent.creating', { itemName: filter })
                          : t('selectComponent.create', { itemName: filter })}
                      </Option>
                    )}
                    {filteredOptions.slice(0, 100).map((option, i) => (
                      <Option
                        key={option.value}
                        data-testid="select-option"
                        data-select-option-value={option.value}
                        {...downshift.getItemProps({
                          item: option,
                          index: i + optionHighlightOffset,
                        })}
                        isDisplayed={downshift.isOpen}
                        isSelected={downshift.selectedItem === option}
                        isHighlighted={downshift.highlightedIndex === i + optionHighlightOffset}
                      >
                        {option.label}
                      </Option>
                    ))}
                    {filteredOptions.length > 100 && (
                      <OptionsMessage>
                        {t('selectComponent.moreResults', { count: filteredOptions.length - 100 })}
                      </OptionsMessage>
                    )}
                    {filteredOptions.length === 0 && (
                      <OptionsMessage>{t('selectComponent.noResults')}</OptionsMessage>
                    )}
                  </StyledDropdownCardContent>
                </DropdownCard>
              )}
            </Transition>
          </Portal>
        </Wrapper>
      )}
    </Downshift>
  );
};

export default SelectBase;
