import { Ref } from 'react';

import useStack from './useStack';

export interface ModalsManagerMethods {
  getIsActive: (modalRef: Ref<HTMLElement>) => boolean;
  setAsActive: (modalRef: Ref<HTMLElement>) => void;
  setAsInactive: (modalRef: Ref<HTMLElement>) => void;
  getIsOpen: (modalRef: Ref<HTMLElement>) => boolean;
}

const useModalsManager = (): ModalsManagerMethods => {
  const { peek, contains, push, pop } = useStack<Ref<HTMLElement>>();

  const getIsActive = (modalRef: Ref<HTMLElement>) => {
    return peek() === modalRef;
  };
  const setAsInactive = (modalRef: Ref<HTMLElement>) => {
    if (getIsActive(modalRef)) {
      pop();
    }
  };

  return { getIsActive, setAsActive: push, setAsInactive, getIsOpen: contains };
};

export default useModalsManager;
