import { useEffect, useState } from 'react';
import pubsub from 'sweet-pubsub';

import { loadScript } from 'shared/utils/script';

type Google = typeof google;

const useGoogleMapsApi = (): Google | undefined => {
  window.gmApiCallback = () => pubsub.emit('gmApiLoaded');

  const [google, setGoogle] = useState<Google>(window.google);

  const setGoogleFromWindow = () => {
    setGoogle(window.google);
  };

  useEffect(() => {
    pubsub.on('gmApiLoaded', setGoogleFromWindow);
    loadScript(
      `https://maps.googleapis.com/maps/api/js?key=${process.env.REACT_APP_GOOGLE_MAPS_API_KEY}&libraries=places&callback=gmApiCallback`,
      'gmApiScript',
    );

    return () => {
      pubsub.off('gmApiLoaded', setGoogleFromWindow);
    };
  }, []);

  return google;
};

export default useGoogleMapsApi;
