import React, { useRef, ReactElement } from 'react';

import useModalState, { ModalStateData } from './useModalState';
import ModalContent, { ModalContentProps } from './ModalContent';

interface OwnModalProps {
  className?: string;
  isOpen?: boolean;
  onOpen?: () => void;
  onClose?: () => void;
}

type ExpectedModalContentProps = Pick<
  ModalContentProps,
  Exclude<keyof ModalContentProps, keyof ModalStateData | 'modalCardRef'>
>;

export type ModalProps = OwnModalProps & ExpectedModalContentProps;

const Modal = ({
  className,
  isOpen: isOpenProp,
  onOpen = () => {},
  onClose = () => {},
  ...props
}: ModalProps): ReactElement => {
  const modalRef = useRef<HTMLDivElement>(null);
  const modalStateData = useModalState(isOpenProp, modalRef, onOpen, onClose);

  return (
    <ModalContent className={className} modalCardRef={modalRef} {...modalStateData} {...props} />
  );
};

export { ModalContent, useModalState };
export default Modal;
