import React, { useContext } from 'react';

import { formatDateTime } from 'shared/utils/dateTime';
import { formatFileSize } from 'shared/utils/files';

import addItemIdToMoveEvent from './utils/addItemIdToMoveEvent';
import ItemControls from './ItemControls';
import DocumentsTableContext from './DocumentsTableContext';
import {
  StyledIcon,
  Row,
  StyledFile,
  FileColumnName,
  FileColumnSize,
  FileColumnLast,
  FileColumns,
  RowProps,
} from './Styles';
import Folder from './classes/Folder';
import FileClass from './classes/File';
import { DocumentsTable_documents } from './__graphql__/DocumentsTable_documents';

interface MainProps {
  nestingLevel: number;
  folder: Folder;
  file: FileClass;
  document: DocumentsTable_documents;
}

type Props = MainProps & RowProps;

const File: React.FC<Props> = ({ nestingLevel, file, folder, document, ...rowProps }) => {
  const { isReadOnly, downloadURL } = useContext(DocumentsTableContext);

  return (
    <Row data-cy-folder={folder.name} data-cy-filename={file.name} {...rowProps}>
      <StyledFile
        draggable={!isReadOnly}
        onDragStart={event => addItemIdToMoveEvent(event, file.id)}
      >
        <StyledIcon type="file" nestingLevel={nestingLevel} />
        <FileColumns>
          <FileColumnName data-cy-file-link href={downloadURL(file)} nestingLevel={nestingLevel}>
            {file.name}
          </FileColumnName>
          <FileColumnSize>{formatFileSize(document.size)}</FileColumnSize>
          <FileColumnLast>{formatDateTime(document.updatedAt)}</FileColumnLast>
        </FileColumns>
      </StyledFile>
      <ItemControls item={file} />
    </Row>
  );
};

export default File;
