import React, { useState, useRef, useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import Input, { InputProps, InputRenderProps } from 'shared/components/Input';

import { ClearQueryIcon, SearchInputElement } from './Styles';

interface SearchInputProps {
  className?: string;
  onChange: (value: string) => void;
}

const SearchInput: React.FC<SearchInputProps & Omit<InputProps, keyof SearchInputProps>> = ({
  onChange,
  className,
  ...props
}) => {
  const { t } = useTranslation();
  const [value, setValue] = useState<string | undefined>(undefined);
  const inputRef = useRef<HTMLInputElement>(null);

  const resetInput = useCallback(() => {
    setValue('');
    onChange('');
    if (inputRef.current) {
      inputRef.current.value = '';
    }
  }, [onChange]);

  const onChangeHandler = useCallback(
    changedValue => {
      setValue(changedValue);
      onChange(changedValue);
    },
    [setValue, onChange],
  );

  const renderSearchInputContent = (inputProps: InputRenderProps) => (
    <>
      <SearchInputElement
        {...inputProps}
        onChange={event => inputProps.onChange(event.target.value, event)}
        ref={inputRef}
      />
      {value && <ClearQueryIcon type="close" onClick={resetInput} />}
    </>
  );

  return (
    <Input
      className={className}
      placeholder={t('search')}
      icon="search"
      name="searchInput"
      onChange={onChangeHandler}
      {...props}
      renderInput={renderSearchInputContent}
    />
  );
};

export default SearchInput;
