import React from 'react';
import { useQuery } from '@apollo/react-hooks';
import { useTranslation } from 'react-i18next';

import { Button, Table, Cell, PageLoader, QueryErrors } from 'shared/components';
import { TABLE_ITEMS_PER_PAGE } from 'shared/constants';
import { formatDate } from 'shared/utils/dateTime';
import { SpaceBetween, TableSearch, ProjectClosedMessage } from 'shared/components/Styles';
import EmptyContent from 'shared/components/EmptyContent';
import { nullString } from 'shared/constants/nullString';
import { BidPackageStatusTag } from 'shared/components/TagStyles';
import { isDoneLoading } from 'shared/utils/apollo';
import routes from 'shared/constants/routes';
import { PROJECT_STATUS, SortDirection } from '__graphql__/globalTypes';

import { queryBidPackages } from './api';
import { StatsCell, BidsDueAtTime, BidsDueAtDate, TimeCell } from './Styles';
import BidPackagesDropdown from './BidPackagesDropdown';
import { BidPackages } from './__graphql__/BidPackages';

interface Props {
  projectId: string;
  projectStatus: PROJECT_STATUS;
}

const BidPackagesList: React.FC<Props> = ({ projectId, projectStatus }) => {
  const { t } = useTranslation('bidPackages');

  const isProjectClosed = projectStatus === PROJECT_STATUS.closed;

  const { error, ...queryResults } = useQuery<BidPackages>(queryBidPackages, {
    variables: {
      projectId,
      offset: 0,
      limit: TABLE_ITEMS_PER_PAGE,
      sortBy: 'number',
      sortDirection: SortDirection.ASC,
    },
  });

  if (error) return <QueryErrors error={error} />;
  if (!isDoneLoading(queryResults)) return <PageLoader />;
  const {
    data: { bidPackages },
    refetch,
    variables,
  } = queryResults;

  return (
    <>
      <SpaceBetween>
        <TableSearch onChange={search => refetch({ query: search })} />
        <Button
          data-cy-button-name="new"
          linkTo={routes.createBidPackage(projectId)}
          disabled={isProjectClosed}
        >
          {t('newBidPackage')}
        </Button>
      </SpaceBetween>
      <Table
        columns={[
          { label: t('bidPackage.numberShort'), sortField: 'number', percentWidth: 12 },
          { label: t('bidPackage.name'), sortField: 'name', percentWidth: 20 },
          { label: t('bidPackage.bidsDueAt'), percentWidth: 9, sortField: 'bidsDueAt' },
          { label: t('bidPackage.bidders'), percentWidth: 7, textAlign: 'right' },
          { label: t('bidPackage.viewed'), percentWidth: 7, textAlign: 'right' },
          { label: t('bidPackage.accepted'), percentWidth: 7, textAlign: 'right' },
          { label: t('bidPackage.bids'), percentWidth: 7, textAlign: 'right' },
          { label: t('bidPackage.declined'), percentWidth: 7, textAlign: 'right' },
          { label: t('bidPackage.status'), sortField: 'status', percentWidth: 10 },
          { fixedWidth: 50 },
        ]}
        rows={bidPackages.bidPackages.map(bidPackage => (
          <Table.Row rowId={bidPackage.id}>
            <Cell.Text>{bidPackage.number || nullString}</Cell.Text>
            <Cell.Link to={routes.showBidPackage(variables.projectId, bidPackage.id)}>
              {bidPackage.name}
            </Cell.Link>
            <TimeCell>
              <BidsDueAtDate>{formatDate(bidPackage.bidsDueAt, 'L')}</BidsDueAtDate>
              <BidsDueAtTime>{formatDate(bidPackage.bidsDueAt, 'LT')}</BidsDueAtTime>
            </TimeCell>
            <StatsCell>{bidPackage.requestStatusCount.bidders}</StatsCell>
            <StatsCell>{bidPackage.requestStatusCount.viewed}</StatsCell>
            <StatsCell>{bidPackage.requestStatusCount.accepted}</StatsCell>
            <StatsCell highlighted>{bidPackage.requestStatusCount.bids}</StatsCell>
            <StatsCell faded>{bidPackage.requestStatusCount.inviteDeclined}</StatsCell>
            <BidPackageStatusTag inList status={bidPackage.status}>
              {t(`bidPackage.${bidPackage.status}`)}
            </BidPackageStatusTag>
            <BidPackagesDropdown
              bidPackageId={bidPackage.id}
              bidPackageStatus={bidPackage.status}
              isProjectClosed={isProjectClosed}
              projectId={projectId}
            />
          </Table.Row>
        ))}
        totalRecords={bidPackages.totalRecords}
        refetch={refetch}
        variables={variables}
        contentIfEmpty={() =>
          isProjectClosed ? (
            <ProjectClosedMessage>{t('projectClosed')}</ProjectClosedMessage>
          ) : (
            <>
              <EmptyContent.CallToAction>
                {t('newBidPackageCallToAction')}
              </EmptyContent.CallToAction>
              <EmptyContent.Explanation>{t('newBidPackageExplanation')}</EmptyContent.Explanation>
              <EmptyContent.Button
                data-cy-button-name="new"
                hollow
                linkTo={routes.createBidPackage(projectId)}
                disabled={isProjectClosed}
              >
                {t('newBidPackage')}
              </EmptyContent.Button>
            </>
          )
        }
      />
    </>
  );
};

export default BidPackagesList;
