/* eslint-disable no-console */

type Noop = () => void;

const onlyRunInDevelopment = <T>(func: T): T | Noop => {
  if (process.env.NODE_ENV === 'development') return func;
  return () => {};
};

export default {
  log: onlyRunInDevelopment(console.log.bind(console)),
  error: onlyRunInDevelopment(console.error.bind(console)),
};
