import { useState } from 'react';

interface ProgressState {
  isUploading: boolean;
  uploadedCount: number;
  totalCount: number;
  currentFileProgress: number;
  currentFileName: string;
}

const defaultUploadProgressState: ProgressState = {
  isUploading: false,
  uploadedCount: 0,
  totalCount: 0,
  currentFileProgress: 0,
  currentFileName: '',
};

const useProgressState: () => [
  ProgressState,
  (newFields: Partial<ProgressState>) => void,
  () => void,
] = () => {
  const [progress, setProgress] = useState(defaultUploadProgressState);

  const updateProgress = (newFields: Partial<ProgressState>) =>
    setProgress(previousFields => ({
      ...previousFields,
      ...newFields,
    }));

  const resetProgress = () => setProgress(defaultUploadProgressState);

  return [progress, updateProgress, resetProgress];
};

export default useProgressState;
